<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div :class="reqFrom=='InitialSetup' ? 'dynamiccontent_base' : ''">
            <dynamic-form lang="en" ref="AddCategory" :buttons="buttons" :schema="FormSchema"
                v-on:OnSubmit="SaveSubmit"></dynamic-form>
        </div>
        <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">
            <div class="row">
                <div class="col-lg-12 text-right mt-4">
                    <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                        v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous') }}</button>
                    <!-- <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip"
                        data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                        @click="SaveSubmit('SaveAndClose')" style="margin-left: 5px" v-if="tabCount == currentTabIndex && reqFrom != 'InitialSetup'">
                        <i class="fa fa-save pr-2"></i> {{ $t("SaveAndClose") }}
                    </button>
                    <button v-if="reqFrom == 'InitialSetup'" title="Finish" class="btn btn-danger formbtn widthhalf px-4"
                            @click="SaveSubmit('SaveAndFinish')">
                            {{ $t("SaveAndFinish") }}
                        </button> -->
                        <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4" @click="Skip()"
                                v-if="tabCount > currentTabIndex">{{ $t("Skip") }}</button>

                        <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip" data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                                @click="SaveSubmit" style="margin-left: 5px" v-if="tabCount > currentTabIndex">
                                <em class="fa fa-save pr-2"></em> {{ $t("SaveAndNext") }}</button>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from './../../../../services/AdvanceSetupModuleService'
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom:''
    },
    data(){
        return{
            isLoading: false,
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [],
            buttons: [              
            ],
            companyId: null,
            userId: null,
        }
    },
    created: function () {        
        var vm = this;
        vm.getform();
    },
    methods:{  
        getform:function(){            
            var ths=this;
            var url = `recordId=0&moduleName=Company&subModuleCode=Category`;
            DataService.GetDynamicForm(url).then(function (response) {       
                debugger             
                ths.FormSchema = ths.$refs.AddCategory.ConvertCustomFieldIntoDynamicFormObj(response.data.data);
                ths.FormSchema[0].group_name='';
                debugger
            });
        },              
        SaveSubmit(){            
            var ths = this;
           
            ths.$refs.AddCategory.validateForm.validate().then(result => {   
                if(result){
                    ths.isLoading=true;
                    let fieldData = ths.$refs.AddCategory.ConvertDynamicFormObjToCustomFieldObj(this.FormSchema);
                    DataService.DepartmentAdd(fieldData,  "/Category/add").then((res) => {
                        if (res.data.Result != null && res.data.Result.MAIN_ID > 0 && res.data.Result.CHK_OPERATION !="UPDATE") {
                            ths.ShowAlert(ths.$t('AddedSuccess',true,[ths.$t('Category')]), 'success', true, ths.$t('Alert'));
                        }
                        ths.isLoading=false;
                        ths.$parent.NextTab();
                    })
                }
             });            
        },
        Skip: function () {
            var vm = this;
            vm.$parent.NextTab(true);
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        Finish: async function () {
            var vm = this;
            await vm.UpdateUserSettingConfig('HideContextmenu');
            await vm.UpdateUserSettingConfig('initialstartup');
            window.location.href='/User/Home';
        },
        async UpdateUserSettingConfig(type) {
            let url = `status=true&type=${type}`
            await CommonDataService.UpdateUserSettingConfig(url).then((response) => {

            });
        }
    }
}
</script>