<template>
    <section>
    <!-- <div class="row"> -->
        <div :class="reqFrom=='InitialSetup' ? 'dynamiccontent_base' : ''">
            <Form v-slot="{ valid,errors }" class="col-12" ref="StorageSourceForm">
                <div class="row">
                <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                    <div class="form-group"><label>{{ $t('AccountType') }}:<span class="mandatory">*</span></label>
                    <select v-bind:class="{ 'form-control': true }" placeholder="Select" 
                            v-model="StorageSource" @change="GetRegionList">
                            <option value="" disabled>Select</option>
                            <option v-for="(item, index) in StorageSourceList" v-bind:value="item.Id" v-bind:key="index" >
                                {{ item.Name }}
                            </option>
                        </select>                  
                    </div>
                </div>
                <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6" v-if="StorageSourceName=='Azure'">
                    <div class="form-group"><label>{{ $t('StorageAccount') }}:<span class="mandatory">*</span></label>
                        <Field :rules="{ 'required': true }" name="Storage Account" v-slot="{ errors }">
                            <input type="text" id="StorageAccount" @keyup="SaveBtnDisabled" v-model="StorageAccount" class="form-control" />
                            <span name="StorageAccount" id="StorageAccount" v-if="errors[0]"
                                class="invalid-feedback d-block">{{ errors[0] }}</span>
                        </Field>
                    </div>
                </div>
                </div>
                <div class="row">
                <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6 " v-if="StorageSource!==1">
                    <div class="form-group"><label v-if="StorageSourceName=='AWS'">Access Key:<span class="mandatory">*</span></label><label v-else>Storage Key:<span class="mandatory">*</span></label>
                        <Field   :rules="{ 'required': true }" name="Storage Key" v-slot="{ errors }">
                            <input type="text" id="StorageKey" @keyup="SaveBtnDisabled" v-model="StorageKey" class="form-control"  />
                            <span name="StorageKey" id="StorageKey" v-if="errors[0]"
                                class="invalid-feedback d-block">{{ errors[0]}}</span>
                        </Field>
                    </div>
                </div>
                <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6 " v-if="StorageSource!==1">
                    <div class="form-group"><label  v-if="StorageSourceName=='AWS'">Bucket Name:</label><label v-else>Blob Link:<span class="mandatory">*</span></label>
                        <Field  :rules="{ 'required': true }" name="Blob Link" v-slot="{ errors }">
                            <input type="text" id="BlobLink" @keyup="SaveBtnDisabled" v-model="BlobLink" class="form-control"/>
                            <span name="BlobLink" id="BlobLink" v-if="errors[0]"
                                class="invalid-feedback d-block">{{ errors[0] }}</span>
                        </Field>
                    </div>
                </div>
                </div>
                <div class="row">
                <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6 " v-if="StorageSource!==1">
                    <div class="form-group"><label>{{ $t('StorageRegion') }}:</label>
                        <Field  :rules="{ 'required': false }" name="Storage Region"  v-slot="{ errors }"> 
                        <select v-bind:class="{ 'form-control': true }" placeholder="Select" 
                            v-model="StorageRegion">
                            <option value="" disabled>Select</option>
                            <option v-for="(item, index) in StorageRegionList" v-bind:value="item.display_name" v-bind:key="index" >
                                {{ item.display_name }}
                            </option>
                        </select>
                            <span name="StorageRegion" id="StorageRegion" v-if="errors[0]"
                                class="invalid-feedback d-block">{{ errors[0] }}</span>
                        </Field> 
                    </div>
                </div>
                <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6" v-if="StorageSource!==1">
                    <div class="form-group"><label v-if="StorageSourceName=='AWS'">{{$t('SecretKey')}}:<span class="mandatory">*</span></label><label v-else>Storage Secondary Key:<span class="mandatory">*</span></label>
                        <Field  :rules="{ 'required': true }" name="Storage Secondary Key" v-slot="{errors }">
                            <input type="text" id="StorageSecondaryKey"  @keyup="SaveBtnDisabled" v-model="StorageSecondaryKey" class="form-control"
                                />
                            <span name="StorageSecondaryKey" id="StorageSecondaryKey" v-if="errors[0]"
                                class="invalid-feedback d-block">{{ errors[0] }}</span>
                        </Field>
                    </div>
                </div>
                </div>
            </Form>
        </div>
        <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">
            <div class="col-12 mt-3">
                <div class="row flex-row-reverse">
                    
                    <div class="col-lg-12 text-right">
                        <button title="Save" @click="TestCredential()" v-if="StorageSource==2"
                            class="popupgreenbtn btn btn-warning formbtn widthhalf pr-2"><em aria-hidden="true"
                                class="fa fa-key pr-2"></em>
                                {{$t('TestCredential')}}</button>
                                &nbsp;
                        <button title="Skip" @click="SkipButton()" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4">Skip</button>
                        &nbsp;
                            <button title="Save" @click="saveStorageSourceconfig()" v-if="StorageSource==1"
                            class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"><em aria-hidden="true"
                                class="fa fa-save pr-2"></em>
                            {{$t('SaveAndNext')}}</button>
                        <button title="Save" @click="saveStorageSourceconfig()" id="btnSave" v-if="StorageSource!==1" :class="{'disabled':StorageKey==undefined}"
                            class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"><em aria-hidden="true"
                                class="fa fa-save pr-2"></em>
                                {{$t('SaveAndNext')}}</button>
                    </div>

                </div>
            </div>
        </div>
    <!-- </div> -->
</section>
</template>
<script>
import DataService from './../../../../services/AdvanceSetupModuleService';
export default {
    props: {
        reqFrom:''
    },
    data() {

        return {
            SizeDocStorage: null,
            RetentionValue: null,
            FileTypeVaule: "",
            SizeConfigDataId: null,
            RetentionConfigDataId: null,
            FileCongfigDataId: null,
            StorageAccount: null,
            BlobLink: null,
            StorageKey:null,
            StorageRegion: null,
            StorageRegionList:[],
            StorageSecondaryKey: null,
            StorageSourceList: [],
            StorageSource: null,
            StorageSourceName:'',
            SelectedValue: [],
            FileTypeValues: '', 
            Options: [],
            FileTypeData: [{ "label": "jpg", "value": "image/jpg" },
            { "label": "jpeg", "value": "image/jpeg" },
            { "label": "png", "value": "image/png" },
            { "label": "pdf", "value": "application/pdf" },
            { "label": "text", "value": "text/plain" },
            { "label": "xls", "value": "application/vnd.ms-excel" },
            { "label": "xlsx", "value": "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" },
            { "label": "doc", "value": "application/vnd.openxmlformats-officedocument.wordprocessingml.document" },
            { "label": "docx", "value": "application/docx" },
            { "label": "msword", "value": "application/msword" }
            ],
            SetupProgress: 0,
            IsStorageManagementsCopmleted: false,
            IsUserStoragesCopmleted: false,
            IsClientStoragesCopmleted: false,
            //IsStorageConfigurationsCopmleted :false,
            IsStorageSoucesCopmleted: false,
            IsClientAutoActivated: null,
            StorageAllocatedPerUser:null,
            StorageAllocatedPerClient:null,
            containerName:null,
            tempStorageKey:null,
            tempStorageSecondaryKey:null,
            tempStorageSecondaryAccount:null
        }
    },
    created: function () {      
        this.getfileTypes();
        this.GetSourceList();
        this.BindFileTypeDDL();
        this.GetStorageSetupStatusDetail();
        // this.getConfigurationData();
        this.getClientConfigValue();
        this.containerName = this.GetUserInfo.ContainerName
    },
    watch:{
        StorageSource:function(value, pevValue){
            var vm=this;
            
            vm.StorageSourceName = vm.StorageSourceList.filter((x)=>{return x.Id==value})[0].Name;
            if(pevValue!==null){
                vm.StorageSecondaryKey=null;
                vm.BlobLink=null;
                vm.StorageKey=null;
                vm.StorageRegion= null;
                vm.StorageAccount= null;
            }
        }
    },
    methods: {
                // For enabling disabling client storage tab in doc storage via client tab in configuration.
                getClientConfigValue: function () {
                    var vm = this;
                    var params = `moduleName=Client&keyValue=SendNotificationsToClient`;
                    DataService.GetConfigDataValue(params).then((response) => {
                        vm.IsClientAutoActivated = JSON.parse(response.data.ConfigValue)[0].CONFIG_DATA_VALUE;
                    });
                },

                //Bind Project list ddl
                BindFileTypeDDL: function () {
                    var vm = this;
                    vm.Options = []
                    vm.FileTypeData.forEach((item, index) => {
                        vm.Options.push({
                            value: item.value,
                            label: item.label
                        })
                    });
                },

                SkipButton: function()
                {
                    var vm = this;
                    vm.$emit('nextTab');
                },
                saveStorageSourceconfig: function () {
                    var vm = this;                        
                    vm.$refs.StorageSourceForm.validate().then((result) => {
                        if (result) {
                            let objConfigData = {
                                ACCOUNT_TYPE: vm.StorageSource,
                                STORAGE_ACCOUNT: vm.StorageAccount,
                                STORAGE_KEY: encodeURIComponent(vm.StorageKey),
                                BLOB_LINK: vm.BlobLink,
                                STORAGE_REGION: vm.StorageRegion,
                                STORAGE_SOURCE_ID: vm.StorageSource,
                                STORAGE_SECONDARY_KEY: encodeURIComponent(vm.StorageSecondaryKey),
                                STORAGE_ALLOCATED_PER_USER: vm.StorageAllocatedPerUser,
                                STORAGE_ALLOCATED_PER_CLIENT: vm.StorageAllocatedPerClient
                            };

                            var jsonObj = JSON.stringify(objConfigData);
                            var params = `jsonData=${jsonObj}`;

                            DataService.SaveDocStorageSourceConfigData(params).then((response) => {
                                if (response.data == 1) {
                                    this.ShowAlert(this.$t('StorageConfigurationDataUpdatedSuccessfully'));
                                    vm.GetStorageSetupStatusDetail();
                                    this.$emit("nextTab");
                                }
                                else {
                                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
                                }
                            });
                        }
                        else {
                            vm.ShowAlert(vm.$t("FillRequiredField"), "failure", true, vm.$t("Alert"));
                        }
                    });


                },
                SaveBtnDisabled: function(){
                    
                    if(this.tempStorageKey==this.StorageKey && this.tempStorageAccount==this.StorageAccount && this.tempStorageSecondaryKey==this.StorageSecondaryKey)
                    {
                        $("#btnSave").removeClass("disabled")
                    }
                    else{
                        $("#btnSave").addClass("disabled")
                    }
                
                },
                TestCredential: function(){
                    var vm=this;
                    vm.$refs.StorageSourceForm.validate().then((result) => {
                    if (result) {
                        var params = `AzureStorageAccount=${vm.StorageAccount}&AzureStoragePrimaryKey=${encodeURIComponent(vm.StorageKey)}&AzureBlobLink=${vm.BlobLink}&container=${vm.containerName}`;

                        DataService.TestCredential(params).then((response) => {
                            
                            if (response.data == 'Success') {
                                this.ShowAlert(this.$t('CredentialValidateSuccesfully'));
                                $("#btnSave").removeClass("disabled")
                            }
                            else {
                                vm.ShowAlert(vm.$t('WrongCredential'), "failure", true, vm.$t("Alert"));
                            }
                        });
                    }
                    else {
                        vm.ShowAlert(vm.$t("FillRequiredField"), "failure", true, vm.$t("Alert"));
                    }
                    });
                },
                getfileTypes: function () {
                    var vm = this;
                    var params = `moduleName=DocStorage&keyValue=SizePerDoc,RetentionDays,DocStorageAllowedFileTypes`;
                    DataService.GetConfigDataValue(params).then((response) => {
                        vm.isLoading = true;
                        vm.SizeDocStorage = JSON.parse(response.data.ConfigValue)[0].CONFIG_DATA_VALUE;
                        vm.SizeConfigDataId = JSON.parse(response.data.ConfigValue)[0].core_config_id;
                        vm.RetentionValue = JSON.parse(response.data.ConfigValue)[1].CONFIG_DATA_VALUE;
                        vm.RetentionConfigDataId = JSON.parse(response.data.ConfigValue)[1].core_config_id;
                        vm.FileTypeVaule = JSON.parse(response.data.ConfigValue)[2].CONFIG_DATA_VALUE;
                        vm.FileCongfigDataId = JSON.parse(response.data.ConfigValue)[2].core_config_id;
                        vm.SelectedValue = vm.FileTypeVaule.replace(/\'/g, "").split(",");
                        vm.isLoading = false;
                    });
                },
                GetSourceList: function () {
                    
                    var vm = this;
                    DataService.GetStorageSourceList().then(function (response) {
                        if (response.data != null) {
                            var res = JSON.parse(response.data);
                            vm.StorageSourceList = res.data;
                            vm.GetSourceConfig();
                        }
                    });
                },
                GetRegionList: function () {
                    
                    var vm = this;
                    var params= `sourceType=${vm.StorageSource}`;
                    DataService.GetStorageRegionList(params).then(function (response) {
                        if (response.data != null) {
                            var res=JSON.parse(response.data);
                            if(res.DATA != null){
                            vm.StorageRegionList = res.DATA;
                            }
                        }
                    });
                },
                GetSourceConfig: function () {
                    
                    var vm = this;
                    DataService.GetCompanyStorageSourceConfig().then(function (response) {
                        if (response.data != null) {
                            
                            var res = JSON.parse(response.data);
                                vm.StorageSourceName=res.data[0].AccountType;
                                vm.StorageSource = res.data[0].STORAGE_SOURCE_ID;
                                vm.GetRegionList();
                            
                            vm.StorageAccount = res.data[0].STORAGE_ACCOUNT;
                            
                            vm.tempStorageAccount=res.data[0].STORAGE_ACCOUNT;
                            vm.StorageKey = res.data[0].STORAGE_KEY;
                            if(vm.StorageKey==undefined){
                                $("#btnSave").addClass("disabled");
                            }
                            vm.tempStorageKey=res.data[0].STORAGE_KEY;
                            vm.BlobLink = res.data[0].BLOB_LINK;
                            vm.StorageRegion = res.data[0].STORAGE_REGION;
                            vm.StorageSecondaryKey = res.data[0].STORAGE_SECONDARY_KEY;
                            vm.tempStorageSecondaryKey = res.data[0].STORAGE_SECONDARY_KEY;
                            vm.StorageAllocatedPerUser= res.data[0].STORAGE_ALLOCATED_PER_USER;
                            vm.StorageAllocatedPerClient= res.data[0].STORAGE_ALLOCATED_PER_CLIENT;
                        }
                    });
                },
                GetStorageSetupStatusDetail: function () {
                    var vm = this;
                    DataService.GetStorageSetupStatusDetail().then(function (response) {
                        if (response.data != null) {
                            var res = JSON.parse(response.data);
                            vm.SetupProgress = parseInt(res.data[0].TOTAL_PROGRESS);
                            vm.$parent.SetupProgress = parseInt(res.data[0].TOTAL_PROGRESS);
                            vm.IsStorageManagementsCopmleted = parseInt(res.data[0].STORAGE_MANAGEMENT) == 100 ? true : false;
                            vm.IsUserStoragesCopmleted = parseInt(res.data[0].USER_STORAGE) == 100 ? true : false;
                            vm.IsClientStoragesCopmleted = parseInt(res.data[0].CLIENT_STORAGE) == 100 ? true : false;
                            //vm.IsStorageConfigurationsCopmleted =  parseInt(res.data[0].STORAGE_CONFIGURATION)==100?true:false;
                            vm.IsStorageSoucesCopmleted = parseInt(res.data[0].STORAGE_SOURCE) == 100 ? true : false;
                        }
                    });
                }
    }
}
</script>