<template>
    <section class="device-mapping-popup">
        <div class="my-popups">
            <div class="modal d-block container-fluid padding-t_10">

                <div class="modal-dialog">
                    <div class="modal-content col-md-12 p-0">
                        <div class="modal-header theme-primary partition p-actions-expand" style="overflow: visible;">
                            <h4 class="modal-title">{{ $t('Device Mapping') }}</h4>
                            <button type="button" class="close" v-on:click="Close()">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body listing mt-2">
                            <div class="table-responsive table-fix-header">
                                <table id="DeviceListing" class="table table-bordered dt-responsive nowrap mt-0">
                                    <thead class="thead-bg">
                                        <tr>
                                            <!-- <th scope="col" width="30px" id="nonresize" class="text-center">
                                                <input type="checkbox" class="chkItems" name="checkbox" id="chkAll">
                                            </th> -->
                                            <th class="text-capitalize">
                                                <span href="javascript:;" class="sort" id="MachineId">
                                                    {{ $t('UniqueId') }}
                                                </span>
                                            </th>
                                            <th class="text-capitalize">
                                                <span href="javascript:;" class="sort" id="Name">
                                                    {{ $t('DeviceName') }}
                                                </span>
                                            </th>
                                            <th class="text-capitalize">
                                                <span href="javascript:;" id="ProfileName">
                                                    {{ $t('LocalSystemProfileName')}} - SilentApp User Name
                                                </span>
                                            </th>
                                            <th class="text-center"><span>{{ $t('Action') }}</span></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item, index) in DeviceListing" :key="index">

                                            <!-- <td class="text-center">
                                                <input class="chkItems" type="checkbox" :value="item.MachineId"
                                                    name="DeleteInputs" />
                                            </td> -->
                                            <td>{{ item.UniqueId }}</td>
                                            <td>{{ item.DeviceName }}</td>
                                            <td>   {{ item.ProfileName }}
                                                <!-- <span class="ml-3 ">
                                                    <a class="text-primary" href="javascript:void(0);"
                                                        onclick="OpenProfileListPopup(item.MachineId)">
                                                     
                                                    </a>
                                                </span> -->
                                            </td>
                                            <td class="text-center">
                                               
                                                    <a class="aMapUsers single-action mr-4" href="javascript:;"
                                                    machineId="MachineId" typetitle="Map" typefor="Clone" title="Map"
                                                    v-on:click="openModalUserMapping(item.MachineId)">
                                                    <i class="fa fa-link  text-success  action-icon"></i>
                                                </a>
                                                <a href="javascript:void(0)" machineId="MachineId"
                                                    class="aDeleteDevice  single-action" title="Delete" v-on:click="deleteDeviceMapping(item.MachineId)">
                                                    <i class="fa fa-trash text-danger  action-icon"></i>
                                                </a>    
                                               
                                               
                                            </td>
                                        </tr>
                                        <tr v-if="DeviceListing.length==0">
                                            <td colspan="3" class="text-center text-danger py-3">
                                                {{$t('DeviceNotMappped') }}
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <ModalUserMapping v-if="isModalUserMapping" @closedialog='closedialog' :machineId="machineId">
        </ModalUserMapping>
    </section>
</template>

<script>

import DataServices from "../../../../services/DataService"
import ModalUserMapping from "./modalUserMapping.vue";
export default {
    components: {
        ModalUserMapping
    },
    data() {
        return {
            isLoading: false,
            DeviceListing: [],
            SearchConditionByName: '',
            selected: [],
            pageSize: 10,
            machineId: 0,
            pageNumber: 1,
            isModalUserMapping: false
        }
    },
    created() {
        var vm = this;
        vm.GetDeviceListByCompanyId();
    },
    methods: {
        Close: function () {
            var vm = this;
            vm.$parent.ShowDeviceMapping = false;
        },
        GetDeviceListByCompanyId: async function () {
            var vm = this;
            vm.SearchConditionByName == null ? '' : vm.SearchConditionByName;
            var params = `search=${vm.SearchConditionByName}&deviceName=&profileName=&userids=&sortBy=&sortOrder=&pageSize=${vm.pageSize}&pageNumber=${vm.pageNumber}`;
            await DataServices.GetDeviceListByCompanyId(params).then(function (response) {
                vm.DeviceListing = [];
                if (response.data && response.data != null) {
                    var data = JSON.parse(response.data);
                    vm.DeviceListing = data.data;
                }
            });
        },
        openModalUserMapping: function (id) {
            var vm = this;
            vm.machineId = id;
            vm.isModalUserMapping = true;
        },

        deleteDeviceMapping: async function (id)
        {           
           var vm = this;
           var params = `ids=${id}`;
           await DataServices.DeleteMachines(params).then(function (response) {              
                if (response.data) {
                    vm.ShowAlert(vm.$t('RecordsDeleteSuccessfully'), "success");
                    vm.GetDeviceListByCompanyId();                   
                }
                else{
                    vm.ShowAlert(vm.$t('SomethingWentWrong'), "failure", true, vm.$t("Alert"));
                }
            });
        }
    }
}
</script>
<style>
.single-action  {
    font-size: 20px;
}

.text-center {
    text-align: center !important;
}


</style>
