<template>
    <section>
        <loader :is-visible="isLoading"></loader> 
        <div :class="reqFrom=='InitialSetup' ? 'dynamiccontent_base' : ''">
            <dynamic-form lang="en" ref="AddQuestionnaire" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="SaveSubmit">
            </dynamic-form>
            <div class="row">
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left my-2">
                    <div class="heading-border">
                        <h5 class="h5"><span>{{$t('List of Question')}}</span> </h5>
                    </div>
                </div>
            </div>
            <div class="row">             
                <div v-for="(question, index) in listQuestions" :key="index" class="col-12 float-left applyalternatebg">        
                    <div class="row question-answer-list div_question_answer mb-3">
                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                            <div class="form-group">
                                <label> {{ $t('FeedbackQuestion') }}:<span class="text-danger">*</span></label>
                                <Field :name="'ListQuestions' + index + '.QuestionText'" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <input v-bind:class="{ 'form-control': true, 'is-invalid': question.error }"
                                        v-model="question.QuestionText" :name="'ListQuestions' + index + '.QuestionText'" maxlength="200" 
                                        type="text" placeholder="Select Feedback Question"/>
                                    <span v-if="question.error" ::name="'ListQuestions' + index + '.QuestionText'"
                                        class="invalid-feedback d-block">{{ $t('FeedbackQuestionRequired') }}</span>
                                </Field>                    
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                            <div class="form-group">                                
                                <div class="col-12 p-0 float-left DivAnsweringType">  
                                    <label> {{ $t("TicketFeedbackAnsweringType") }}:</label>
                                    <Field  :name="'ListQuestions' + index + '.AnswerType'" :rules="{ 'required': false }" >
                                        <div class="input-group">
                                        <select  v-model="question.AnswerType" class="form-control" placeholder="Select Feedback Question" @change="answeringType(question.AnswerType,index)">
                                            <option selected>{{ $t('SelectTicketFeedbackAnsweringType') }}</option>
                                            <option v-for="(prty, prtyIndex) in AnsweringType" :key="prtyIndex"
                                                v-bind:value="prty.value">
                                                {{ prty.Name }}
                                            </option>
                                        </select>
                                        <div class="input-group-append">
                                            <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                            <a v-if="index === 0" href="javascript:void(0)" class="addquestionbox round-icon-small btn-success-light adinvc" title="Add More" @click="AddMore('approvarchain')">
                                                <i class="fa fa-plus pt-7 font-18"></i>
                                            </a>
                                            <a href="javascript:void(0)" class="round-icon-small btn-danger-light deletequestionbox" :class="{ 'd-none': index === 0 }" title="Delete" @click="deleteQuestion(index)">
                                                <i class="fa fa-trash"></i>
                                            </a>
                                            </span>
                                        </div>
                                    </div>
                                        <div class="objectiveAnswers form-group w-100 mt-2" v-if="question.objectiveAnswers">
                                            <ul class="options liststylenone pl-0">
                                                <li>
                                                <div class="custom-control custom-radio">
                                                    <input type="radio" :id="'OptionName_' + index + '_TwoPointScale'" :name="'OptionName_' + index + '_TwoPointScale'" value="1" class="form-check-input OptionName" tabindex="8" :checked="question.OptionName === 'TwoPointScale'" >
                                                    <label :for="'OptionName_' + index + '_TwoPointScale'" class="mb-0">Two Point Scale</label>
                                                </div>
                                                </li>
                                                <li>
                                                <div class="custom-control custom-radio">
                                                    <input type="radio" :id="'OptionName_' + index + '_ThreePointScale'" :name="'OptionName_' + index + '_TwoPointScale'" value="2" class="form-check-input OptionName" tabindex="8" :checked="question.OptionName === 'ThreePointScale'" >
                                                    <label :for="'OptionName_' + index + '_ThreePointScale'" class="mb-0">Three Point Scale</label>
                                                </div>
                                                </li>
                                                <li>
                                                <div class="custom-control custom-radio">
                                                    <input type="radio" :id="'OptionName_' + index + '_FivePointScale'" :name="'OptionName_' + index + '_TwoPointScale'" value="3" class="form-check-input OptionName" tabindex="8" :checked="question.OptionName === 'FivePointScale'" >
                                                    <label :for="'OptionName_' + index + '_FivePointScale'" class="mb-0">Five Point Scale</label>
                                                </div>
                                                </li>
                                            </ul>
                                            </div>
                                    </Field>                                    
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div> 
        </div>
        <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">      
            <div class="row">
                <div class="col-lg-12 text-right mt-4">
                    <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                                v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous') }}</button>                                               
                    <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4" @click="Skip()"
                                v-if="tabCount > currentTabIndex">{{ $t("Skip") }}</button>
                    <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip" data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                                @click="SaveSubmit" style="margin-left: 5px" v-if="tabCount > currentTabIndex">
                                <em class="fa fa-save pr-2"></em> {{ $t("SaveAndNext") }}</button>
                </div>
            </div>
        </div>
   
    </section>
</template>
<script>
import DataService from './../../../../services/AdvanceSetupModuleService'
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom:''
    },
    data(){
        return{
            isLoading: false,         
            listQuestions:[],
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [{
                    layoutType: "double",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('FeedbackName'),
                        name: "FeedbackName",
                        value: "",
                        placeholder: this.$t('Enter Feedback Name'),
                        validationRules: {
                            "required": true
                        },
                        disabled: false,
                    }, 
                    {
                        astype: "SelectField",
                        label: this.$t('Status'),
                        name: "Status",
                        value: "",
                        placeholder: this.$t('Enter Status'),
                        config: {
                            options:[]
                        },
                        validationRules: {
                            "required": true
                        },
                        disabled: false,
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('Type'),
                        name: "Type",
                        value: "",
                        placeholder: this.$t('Enter Type'),
                        config: {
                            options:[]
                        },
                        validationRules: {
                            "required": false
                        },
                        disabled: false,
                    },
                    {
                        astype: "toggleField",
                        label: this.$t('AdditionalComments'),
                        name: "AdditionalComments",
                        value: "",
                        placeholder: "",
                        config: {
                            onChange: this.chkIsAdditionalComments
                        }
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        placeholder: this.$t('EnterCommentsHere'),
                        name: "Description",
                        value: "",
                        visibility:false,
                        config: {                           
                            rows:5,cols:7
                        },
                        validationRules: { "required": false},
                        disabled: false,
                    }]
                }
            ],
            buttons: [],
            companyId: null,
            userId: null,
            AnsweringType:[
                {Name : "Comments",value:'Comments'},
                {Name : "Star Rating",value:'Star Rating'},
                {Name : "Objective",value:'Objective'}
            ]
        }
    },
    created:function(){
        debugger
        var ths=this;
        ths.GetStatusList();
        ths.GetModuleList();
        ths.userId = ths.GetUserInfo.ID;
        ths.companyId = ths.GetUserInfo.CompanyId; 
        var QuestionObj={
            QuestionId:0,
            TicketFeedbackId:0,
            QuestionText:'',
            AnswerType:'',
            OptionName:'',
            OptionValue:'',
            objectiveAnswers: false
        };
        ths.listQuestions.push(QuestionObj);
    },
    methods:{
        AddMore:function(){            
            var ths=this;
            const obj={
                QuestionId:0,
                TicketFeedbackId:0,
                QuestionText:'',
                AnswerType:'',
                OptionName:'',
                OptionValue:'',
                objectiveAnswers: false
            };
            ths.listQuestions.push(obj);        
        },
        deleteQuestion(index) { 
            var ths=this;       
            if (index !== 0) { 
                ths.listQuestions.splice(index, 1);
            }
        },
        Skip: function () {            
            var vm = this;
            vm.$parent.NextTab();
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        validateQuestions() {
        let isValid = true; 
        this.listQuestions.forEach((question, index) => {
            if (!question.QuestionText) {
                isValid = false;
                this.$set(question, 'error', 'Feedback question is required');
            } else {
                this.$set(question, 'error', '');
            }
        });
        return isValid;
        },
        SaveSubmit(){            
            var vm = this;
            vm.isLoading = true; 
            vm.$refs.AddQuestionnaire.validateForm.validate().then(result => {
                if(result && vm.validateQuestions()){ 
                    vm.isLoading = false;                    
                    var qlistQuestions=[];
                    var alistQuestions=[];
                    vm.listQuestions.forEach(function (item, key) {                            
                        var qobj={
                            QuestionId:key+1,
                            TicketFeedbackId: 0,
                            QuestionText: item.QuestionText,
                            AnswerType: item.AnswerType,
                            OptionName: item.OptionName,
                            OptionValue: item.OptionValue
                        }
                        var aobj={
                            TicketId: 0,
                            TicketFeedbackId: 0,
                            QuestionId: key+1,
                            TicketFeedbackAnswerId: 0,
                            FeedbackAnswer:0,
                            QuestionText: item.QuestionText,
                            AnswerType: item.AnswerType,
                            OptionName:item.OptionName,
                            OptionValue:item.OptionValue,                               
                            Status_Id: vm.FormSchema[0].Data[1].value,                               
                            ThreadId: 0
                        }
                        qlistQuestions.push(qobj);
                        alistQuestions.push(qobj);
                    });
                    var data = {
                        TicketFeedbackId: 0,
                        FeedbackName: vm.FormSchema[0].Data[0].value,
                        CompanyId: vm.companyId,
                        IsAdditionalComments: vm.FormSchema[0].Data[3].value,
                        AdditionalComments: vm.FormSchema[1].Data[0].value,
                        StatusId: vm.FormSchema[0].Data[1].value,
                        TypeCode: vm.FormSchema[0].Data[2].value,
                        CreatedBy: vm.userId,
                        ListQuestions: qlistQuestions,
                        ListAnswers: alistQuestions
                    }                        
                    var params = JSON.stringify(data);
                    DataService.AddFeedback(params).then((response) => { 
                        if(response.data.result=='0'){
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Feedback')]), "success", true, vm.$t("Alert"));
                            vm.$parent.NextTab();
                        }else{
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("Feedback")]), "failure", true, vm.$t("Alert"));         
                        }                        
                     })
                }
                vm.isLoading = false;  
            })
        },
        GetStatusList:function(){            
            var ths=this;
            var url=`statusType=Hiring&hasglobal=true&langCode=en`
            DataService.GetStatusList(url).then((response)=>{                
                if(response.data.length>0){
                    var statusData=response.data;
                    statusData.forEach(element => {
                        var Obj={
                            name:element.STATUS,
                            value:element.STATUS_ID
                        }
                        ths.FormSchema[0].Data[1].config.options.push(Obj);
                    });
                    ths.$refs.AddQuestionnaire.UpdateKeyValue();
                }
            });
        },
        GetModuleList:function(){            
            var ths=this;            
            DataService.GetModuleList().then((response)=>{                                
                if(response.data.result.length>0){
                    var statusData=response.data.result;
                    statusData.forEach(element => {
                        var Obj={
                            name:element.MODULE_NAME,
                            value:element.MODULE_ID
                        }
                        ths.FormSchema[0].Data[2].config.options.push(Obj);
                    });
                    ths.$refs.AddQuestionnaire.UpdateKeyValue();
                }
            });
        },
        chkIsAdditionalComments:function(x,y,z){            
            var ths=this;
            if(y.value){
                ths.FormSchema[1].Data[0].visibility=true;
            }else{
                ths.FormSchema[1].Data[0].visibility=false
            }
        },
        answeringType(answerType,i) {                        
            var ths=this;
            if (answerType == "Objective") {                  
                ths.$set(ths.listQuestions[i], 'objectiveAnswers', true);             
            }
            else {
                ths.$set(ths.listQuestions[i], 'objectiveAnswers', false);  
            }
        },
    }
}
</script>

<style>

label {
    display: flex;
}

.input-group-append, .input-group-prepend {
    display: -ms-flexbox;
    display: flex;
}

</style>