<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="col-md-12 p-0">
            <div class="tab-content">
                <div class="dynamiccontent_base">
                    <div>
                        <div>
                            <div>
                                <!-- <div
                            class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                            <h5 class="modal-title">
                                {{ refCode === 'Update' ? $t('updateratecode') : $t('addratecode') }}
                            </h5>
                            <button type="button" class="close" @click="CloseAddWorkCode">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div> -->

                                <div class="modal-body">
                                    <dynamic-form lang="en" ref="validatePlan" :buttons="false" :schema="FormSchema">
                                    </dynamic-form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="mainfooter_app">

                    <div class="col-lg-12 text-right mt-4">
                        <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                            v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous')
                            }}</button>

                        <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4"
                            @click="Skip()" v-if="tabCount > currentTabIndex">{{ $t("Skip") }}</button>

                        <button type="button" class="btn btn-success  formbtn px-4 ml-2" @click="SaveData('SaveAndNew')"
                            v-if="tabCount > currentTabIndex">
                            <em class="fa fa-save pr-2 font-16"></em>
                            {{ $t("SaveAndNew") }}
                        </button>

                        <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip"
                            data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                            @click="SaveData('SaveAndNext')" style="margin-left: 5px" v-if="tabCount > currentTabIndex">
                            <i class="fa fa-save pr-2"></i> {{ $t("SaveAndNext") }}
                        </button>

                        <button type="button" id="aGroupTicketSave" @click="SaveData('SaveAndClose')" title=""
                            data-toggle-tooltip="tooltip" data-original-title="Save"
                            class="popupgreenbtn btn btn-success formbtn widthhalf pr-2" style="margin-left: 5px;"
                            v-if="tabCount == currentTabIndex && reqFrom != 'InitialSetup'">
                            <i class="fa fa-save pr-2"></i> {{ $t('SaveAndClose') }}
                        </button>
                    </div>

                </div>

            </div>
        </div>
    </section>
</template>

<script>
import DataService from "../../../../services/DataService";
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0
    },

    data() {
        return {
            isLoading: false,
            isEditWorkCode: true,
            Id: 0,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('RateCode'),
                            name: "RateCode",
                            value: "",
                            placeholder: this.$t('RateCode'),
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: this.$t('Status'),
                            config: {
                                options: [{ value: "1001", name: this.$t("Active") }, { value: "1002", name: this.$t("Inactive") }]
                            },
                            validationRules: {
                                required: true
                            },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('DESCRIPTION'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('DESCRIPTION'),
                            validationRules: { required: true, max: 6000 },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Category'),
                            name: "Category",
                            value: "",
                            placeholder: this.$t('Select'),
                            config: {
                                options: []
                            },
                            validationRules: {
                                required: true
                            },
                        }
                    ]
                },

            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('Save'),
                    onClick: this.SaveData
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.CloseAddWorkCode,
                },
            ],
        };
    },
    mounted() {
        this.GetCategoryList();
    },

    methods: {
        GetCategoryList() {
            this.isLoading = true;
            const vueObj = this;
            const url = `companyId=${vueObj.GetUserInfo.CompanyId}&loggedinUserID=${vueObj.GetUserInfo.ID}&pageSize=10&pageNum=1&sortBy=&sortExp=&search=`;
            DataService.GetWorkCodeCategoryList(url).then((response) => {
                if (response.data) {
                    const result = JSON.parse(response.data);
                    result.forEach(element => {
                        vueObj.FormSchema[2].Data[0].config.options.push({
                            name: element.WORK_CODE_CATEGORY,
                            value: element.WORK_CODE_CATEGORY_ID
                        })
                        vueObj.$refs.validatePlan.UpdateKeyValue();
                    });
                }
            }).catch(error => {
                console.error("Error fetching category list:", error);
            }).finally(() => {
                this.isLoading = false;
            });
        },


        async SaveData(Type) {
            debugger
            this.isLoading = true;

            try {
                const isValid = await this.$refs.validatePlan.validateForm.validate();
                debugger;
                if (!isValid) {
                    this.isLoading = false;
                    return;
                }

                const workCode = this.$refs.validatePlan.getPropertyValue("RateCode", "value");
                const status = this.$refs.validatePlan.getPropertyValue("Status", "value");
                const description = this.$refs.validatePlan.getPropertyValue("Description", "value");
                const categoryId = this.$refs.validatePlan.getPropertyValue("Category", "value");

                const objData = {
                    id: this.Id,
                    name: workCode,
                    description: description,
                    status: status,
                    categoryId: categoryId
                };
                debugger;
                const payload = JSON.stringify(objData);
                var url = `name=${workCode}`
                if (!this.Id) {
                    const existResponse = await DataService.ISExistWorkCode(url);
                    if (existResponse.data.message === "Exist") {
                        this.ShowAlert(this.$t('AlreadyExists', [this.$t('RateCode')]), "failure", true, this.$t('Alert'));
                        return;
                    }
                }

                const saveResponse = await DataService.SaveUpdateWork(payload);
                debugger
                if (saveResponse.data == 1) {
                    this.ShowAlert(this.$t('AddedSuccess', [this.$t('RateCode')]), "Success", true, this.$t('Alert'));
                }
                else if (saveResponse.data == 2) {
                    this.ShowAlert(this.$t('UpdatedSuccess', [this.$t('RateCode')]), "Success", true, this.$t('Alert'))
                }
                //this.callbackfunction && this.callbackfunction();
                //this.CloseAddWorkCode();

                 this.isLoading = false;

                if (Type == 'SaveAndNew') {
                    this.resetForm();

                }
                else if (Type == 'SaveAndNext') {
                    this.Skip();
                }
                else if (Type == 'SaveAndFinish') {
                    this.Finish();
                }
                else if (Type == 'SaveAndClose') {
                    this.Finish();
                }


            } catch (error) {
                console.error("Error saving data:", error);
            } finally {
                this.isLoading = false;
            }
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        Skip: function () {

            var vm = this;
            vm.$emit("nextTab", true);
        },
        resetForm() {
            debugger
            var vm = this;
            vm.FormSchema.forEach((group, gi) => {
                group.Data.forEach((field, fi) => {
                    field.value = "";
                })
            })

        },
        Finish: async function () {

            var vm = this;
            await vm.UpdateUserSettingConfig('HideContextmenu');
            await vm.UpdateUserSettingConfig('initialstartup');
            window.location.href = '/User/Home';
        },
        async UpdateUserSettingConfig(type) {
            let url = `status=true&type=${type}`
            await DataService.UpdateUserSettingConfig(url).then((response) => {

            });
        }
    }

}

</script>