            var vm = this;
            vm.isLoading = true;
            var prams = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&${vm.searchCondition}`;
            await assetDataService.AssetCatalogueListing(prams).then((response) => {
                if (response.data.length > 0) {
                    vm.count = vm.count + 1;
                    vm.SetupProgresText = "Completed";
                } else {
                    vm.SetupProgress = 0;
                }
                vm.isLoading = false;
            });
        },
        GetProgress() {
            var vm = this;
            if (vm.count == 1) {
                vm.SetupProgress = 33.33;
            } else if (vm.count == 2) {
                vm.SetupProgress = 66.66;
            } else if (vm.count == 3) {
                vm.SetupProgress = 100;
            }
        },
        BackButton() {
            var vm = this;
            vm.SkipTab(2);
        },
