        GetAssetCost(event, value) {
            var vm = this;
            var Quantity = parseFloat(
                vm.FormSchema[2].Data[1].value == ""
                    ? 0
                    : vm.FormSchema[2].Data[1].value
            );
            var unitPrice = parseFloat(value);
            if (value.length > 5) {
                vm.FormSchema[2].Data[2].value = value.slice(0, 5);
                vm.ShowAlert(
                    vm.$t("The Unit Price field may not be greater than 5 characters"),
                    "failure",
                    vm.$t("Alert")
                );
            }
            if (unitPrice > 0 && Quantity > 0) {
                var assetCost = parseFloat(unitPrice) * parseFloat(Quantity);
                vm.FormSchema[2].Data[3].value = assetCost.toFixed(3);
            } else {
                vm.FormSchema[2].Data[3].value = "0.000";
            }
        },
        GetUnitPrice(val) {
            var vm = this;
            var Quantity = parseFloat(
                vm.FormSchema[2].Data[1].value == ""
                    ? 0
                    : vm.FormSchema[2].Data[1].value
            );
            var unitPrice = parseFloat(
                vm.FormSchema[2].Data[2].value == ""
                    ? 0
                    : vm.FormSchema[2].Data[2].value
            );
            if (Quantity > 0 && unitPrice > 0) {
                var assetCost = parseFloat(unitPrice) * parseFloat(Quantity);
                vm.FormSchema[2].Data[3].value = assetCost.toFixed(3);
            } else {
                vm.FormSchema[2].Data[3].value = "0.000";
            }
        },

