<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="dynamiccontent_base">
            <tg-list :IsShowAction="true" :showCheckBox="true" :listType="['List', 'Card']" ModuleName="Timer"
                SubModuleCode="Timer" :PageSize="pageSize" IdentityColumn="ApplicationId"
                :HeaderText="$t('AppsAndWebsites')" :ListData="shortListed" :HeaderData="Headers" :widgets="widgets"
                :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
                :RenderRowActionMethod="renderActions"
                :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="sortExp" :SortBy="sortBy"
                :NorecordfoundText="$t('NoRecordfound')"
                v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata">
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'ProductivityForDepartment'">
                        <div>
                            <em class="fa fa-eye text-primary" v-if="data.row.IsDisabled" aria-hidden="true"
                                v-on:click="ShowProductivityDepartment(data)"></em>
                            <em class="fa fa-eye text-secondary" v-if="!data.row.IsDisabled" aria-hidden="true"></em>
                        </div>
                    </template>

                    <template v-if="data.column.COLUMN_NAME == 'ApplicationName'">
                        <div class="pin pr-2 d-inline-block">

                            <em style="cursor:pointer;" class="fa fa-thumb-tack" @click="UnPinData(data.row)"
                                v-if="data.row.PinStatus == 'Pin'" title="Unpin" aria-hidden="true"></em>

                        </div>
                        <div :class="data.row.icon_class ? 'appicon ' + data.row.icon_class : 'appicon default'"></div>
                        <!-- <span v-bind:class="data.row.icon_class">   {{data.row.ApplicationName}}  </span>   -->
                        <a href="#" :title="data.row.application_display_name">
                            {{ TruncateStr(data.row.application_display_name, 20) }}
                        </a>
                        <!-- <span v-bind:class="data.row.icon_class">   {{data.row.ApplicationName}}  </span>                                                                                       -->
                    </template>

                </template>

                <template v-slot:customcard="{ data }">

                    <div class="card-box" :style="{ 'border-color': data.row.color_code }">
                        <div class="card-number">
                            <div :class="{ 'custom-control custom-checkbox': true, 'disabled': data.row.IsDisabled }">
                                <input :id="'chkAll_' + data.row.ApplicationId" type="checkbox"
                                    class="chkitems custom-control-input" :value="data.row.ApplicationId"
                                    style="display: none;" :disabled="data.row.IsDisabled"
                                    @change="handle($event, data.row)">
                                <label class="custom-control-label universal-custom-control-label pt-1 "
                                    :for="'chkAll_' + data.row.ApplicationId"></label>
                            </div>
                            <div :class="data.row.icon_class ? 'appicon ' + data.row.icon_class : 'appicon default'"></div>
                            <em style="cursor:pointer;" class="fa fa-thumb-tack" @click="UnPinData(data.row)"
                                v-if="data.row.PinStatus == 'Pin'" aria-hidden="true"></em>
                            {{ data.row.application_display_name }}

                        </div>


                        <div class="card-detail-main filterscrollForCardView">
                            <div data-isfsm="locating" class="card-detail"><em class="fa fa-newspaper-o"></em>
                                <span>Category - {{ data.row.category_name == null ? 'N/A' : data.row.category_name
                                    }}</span>
                                <span> </span>
                            </div>
                            <div class="card-detail"><em class="fa fa-pie-chart"></em>
                                <span>Productivity For Department -
                                    <span class="viewicon" v-if="data.row.IsDisabled"
                                        v-on:click="ShowProductivityDepartment(data)">
                                        <em
                                            :class="checkIsdesabled == true ? 'fa fa-eye text-primary' : 'fa fa-eye text-primary disabled'"></em>
                                        View

                                    </span>
                                    <span class="viewicon" v-if="!data.row.IsDisabled">
                                        <em :class="'fa fa-eye text-secondary disabled'"></em> View
                                    </span>
                                </span>
                            </div>
                            <div class="card-detail"><em class="fa fa-clock-o"></em>
                                <span>Usage Time - {{ data.row.TimeDuration }} </span>
                            </div>
                        </div>
                        <div class="verticle-action">
                            <em aria-hidden="true" class="fa fa-ellipsis-v action_icon"> </em>
                            <span class="action-list-box" style="width: 0px; display: block; opacity: 0;">
                                <span id="action-list" class="list-actions fsm-list" style="padding-left: 0px;">
                                    <!----><a href="javascript:;" class="update actions-onclick" style="overflow: unset;">
                                        <span v-if="data.row.PinStatus == 'Pin'">
                                            <a typetitle="Pin" typefor="Pin" title="Pin" class="actions-onclick"
                                                style="overflow: unset;" @click="actionButtonClick('PinData', data.row)">
                                                <em class="fa fa-thumb-tack disabled "></em>
                                                <span class="text"> {{ $t('Pin') }} <span></span></span></a>

                                        </span>
                                        <span v-else>
                                            <a typetitle="Pin" typefor="Pin" title="Pin" class="actions-onclick"
                                                style="overflow: unset;" @click="actionButtonClick('PinData', data.row)">
                                                <em class="fa fa-thumb-tack"></em><span class="text"> Pin
                                                    <span></span></span></a>

                                        </span>

                                        <span v-if="data.row.IsDisabled">
                                            <a typetitle="Edit" typefor="Edit" value="275" title="Edit"
                                                class="actions-onclick" style="overflow: unset;"
                                                @click="actionButtonClick('Editdata', data.row)">
                                                <em class="fa fa-pencil  text-success action-icon"></em>
                                                <span class="text"> {{ $t('Edit') }} <span></span></span>
                                            </a>
                                        </span>

                                        <span v-else>
                                            <a typetitle="Edit" typefor="Edit" value="275" title="Edit"
                                                class="actions-onclick" style="overflow: unset;">
                                                <em class="fa fa-pencil disabled text-success action-icon"></em>
                                                <span class="text">{{ $t('Edit') }}<span></span></span>
                                            </a>
                                        </span>

                                        <a typetitle="Delete" typefor="Delete" value="275" title="Delete"
                                            class="actions-onclick" style="overflow: unset;"
                                            @click="actionButtonClick('DeleteData', data.row)"><em
                                                class="fa fa-trash "></em><span class="text"> {{ $t('Delete') }}
                                                <span></span></span></a>
                                        <em aria-hidden="true" class="fa fa-times close close-action"></em>
                                    </a>
                                </span></span>
                        </div>
                    </div>

                </template>
            </tg-list>
        </div>
        <div class="mainfooter_app">
            <div class="col-lg-12 text-right mt-4 mr-3">
                <button title="Previous" class="btn btn-warning float-left px-4"
                    @click="PreviousButton()" v-if="currentTabIndex == tabCount">
                    <em class="fa fa-chevron-left pr-2"></em> 
                    {{ $t("Previous") }}
                </button>
                <button title="Finish" class="btn btn-danger formbtn widthhalf px-4"
                    @click="Skip()" v-if="tabCount == currentTabIndex">
                    {{ $t("Finish") }}
                </button>
            </div>
        </div>
        <ProductivityForDepartment v-if="IsProductivityDepartment" :AssignDpt='AssignDpt'></ProductivityForDepartment>
        <AssignDepartment :selectedRecordValues="selectedRecordValues" v-if="IsAssignDepartment" :AssignDpt='AssignDpt'>
        </AssignDepartment>
    </section>
</template>

<script>
import Schema from "../SilentApp/ManageAppsAndWebsitesSchema";
import ProductivityForDepartment from '../SilentApp/Modals/ProductivityForDepartment.vue'
 import AssignDepartment from "../SilentApp/Modals/AssignDepartment.vue"
import DataService from '../../../services/DataService';
export default {
    components: {
        ProductivityForDepartment,
         AssignDepartment
    },
    data() {
        return {
            listActions: Schema.listActions,
            shortListed: [],
            Headers: [

                {
                    "COLUMN_NAME": 'ApplicationName',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ApplicationWebsite'),
                    "DISPLAY_ORDER": 1,
                    settings: {
                        isInSlot: true,
                    },
                },
                {
                    "COLUMN_NAME": 'category_name',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Category'),
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": 'TimeDuration',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('UsageTime'),
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": 'ProductivityForDepartment',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ProductivityForDepartment'),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true,
                    },
                },

            ],
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            sortBy: '',
            sortExp: '',
            pageSize: 10,
            pageNum: 1,
            applicationCode: '',
            legendProgress: [{
                class: '',
                backgroundColor: '#17a2b8',
                name: 'Draft'
            },
            {
                class: 'legend-Progress',
                backgroundColor: '',
                name: 'Approved'
            },
            {
                class: 'legend-pending',
                backgroundColor: '',
                name: 'Pending'
            },
            {
                class: '',
                backgroundColor: '#fe6847',
                name: 'SendForCorrection'
            },
            {
                class: '',
                backgroundColor: '#ee1313',
                name: 'Rejected'
            }

            ],
            LegendArray: [
                {
                    class: 'legend-pending colorboxsquare',
                    text: this.$t('SampleData')
                }
            ],
            PageSizeOption: null,
            listheaderbuttons: Schema.buttons,
            UsedApplicationData: [],
            isLoading: false,
            IsProductivityDepartment: false,
            IsAssignDepartment: false,
            isCategoryVisible: false,
            DetailListType: 'List',
            selectedRecordValues: [],
            checkIsdesabled: false,
            colorCode: '',
            datepicker: false,
            fromDate: "",
            ToDate: "",
            widgets: [],
            PinJson: [],
            UnPinJson: []
        }
    },
    created() {
        this.FetchData();
    },
    methods: {
        Skip: function () {
            var vm = this;
            vm.$router.push('/User/Home')
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        handle(event, item) {
            var vm = this;
            if (event.target.checked == true) {

                vm.listheaderbuttons[0].isdisabled = false;
                vm.listheaderbuttons[2].isdisabled = false;
                vm.selectedRecordValues.push(item);


            }
            else {
                vm.listheaderbuttons[0].isdisabled = true;
                vm.listheaderbuttons[2].isdisabled = true;
                vm.selectedRecordValues = []


            }

        },
        async onDateChange(event) {
            var vm = this;
            vm.fromDate = new Date(event);
            vm.fromDate = vm.ConvertDateFormat(vm.fromDate);
            vm.ToDate = vm.fromDate + " 23:59:00";
            await vm.GetAllAPiMethods();
        },
        AssignDepartmentClick() {
            var vm = this;
            if (vm.shortListed.length > 0) {
                $(".chkItems:checkbox:checked").not("[id^='chkAll']").each(function () {
                    vm.selectedRecordValues.push(vm.shortListed.find(x => x.ApplicationId == this.value));
                    // vm.selectedRecordValues.unshift(vm.shortListed.find(dealer => dealer.ApplicationId == this.value));
                });
                vm.IsAssignDepartment = true;
            }


        },
        EditAssignDepartmentClick: function (value) {
            var vm = this;
            vm.AssignDpt = value;
            vm.IsAssignDepartment = true;
        },
        CloseAssignDepartmentClick() {
            var vm = this;
            vm.selectedRecordValues = [];
            vm.AssignDpt = [];
            vm.IsAssignDepartment = false;
        },
        ShowProductivityDepartment(value) {
            var vm = this;
            vm.AssignDpt = value.row.ApplicationId;
            vm.IsProductivityDepartment = true;
        },
        CloseProductivity() {
            var vm = this;
            vm.IsProductivityDepartment = false;
        },
        async FetchData() {
            var vueObj = this;
            vueObj.isLoading = true;
            var url = `pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&${vueObj.searchCondition}`;
            await DataService.GetScreenShotListing(url).then(response => {

                vueObj.shortListed = response.data
                if (vueObj.shortListed.length > 0) {


                    vueObj.shortListed.forEach((row) => {
                        row.additionalClass = '';
                        if (row.color_code) {
                            vueObj.colorCode = row.color_code;
                            //row.additionalClass = 'custom-low-bar'; 
                            row.CssStyle = 'border-left:2px solid ' + vueObj.colorCode + ' !important';
                        }
                        row.isCheckBoxDisabled = false;
                        if (row.IsDisabled == true) {
                            row.isCheckBoxDisabled = true
                            vueObj.checkIsdesabled = true;
                        }
                        if (row.is_sample_data == true) {
                            row.additionalClass = 'sample-bar warning-bar';
                            row.isCheckBoxDisabled = true;
                        }

                    })
                    vueObj.isLoading = false;
                    vueObj.TotalRecords = response.data[0].TotalRecords;
                    vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                    vueObj.CurrentPage = vueObj.pageNum;
                    vueObj.noRecord = false;
                    // $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                    vueObj.isLoading = false;
                }
                else {
                    vueObj.shortListed = []
                    vueObj.noRecord = true;
                    vueObj.isLoading = false;
                }
            })
            // setTimeout(function () {
            //     vueObj.CheckBoxBootstrap();
            // }, 100);
        },

        //       legendclass: function(color_code) {
        //     if (color_code != null && color_code !== '') {
        //         ;
        //         this.LegendArray = [{
        //             class: color_code,
        //             text: ('Pending Approval')
        //         }];
        //     }
        // },
        DeleteData: function () {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("ApplicationId")), function () {
                    let url = `ids=${val}&tableName=TALYGEN_screenshot_detail&primaryKey=track_application_id`;
                    DataService.Delete(url).then(response => {
                        if (response.data) {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.listheaderbuttons[0].isdisabled = true;
                            vm.listheaderbuttons[2].isdisabled = true;
                            vm.ShowAlert(vm.$t('DeletedSuccessfully'), "Success", true, vm.$t('Alert'));
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
                        }
                        vm.FetchData();
                    });
                });
            }
        },
        SingleDeleteData: function (item) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("ApplicationId")), function () {
                let url = `ids=${item.ApplicationId}&tableName=TALYGEN_screenshot_detail&primaryKey=track_application_id`;
                DataService.Delete(url).then(response => {
                    if (response.data == 'DeletedSuccess') {
                        vm.ShowAlert(vm.$t('DeletedSuccessfully'), "Success", true, vm.$t('Alert'));
                        // vm.$router.push({
                        //     name: 'UsedApplicationListing'
                        // });
                        vm.FetchData();
                    } else {
                        vm.ShowAlert(vm.$t('DeletedError', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
                    }
                });
            });
        },
        actionButtonClick(callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case 'Editdata':
                    vm.EditAssignDepartmentClick(item)
                    break;
                case 'DeleteData':
                    vm.SingleDeleteData(item)
                    break;
                case 'PinData':
                    vm.PinData(item)
                    break;
                case 'UnPinData':
                    vm.UnPinData(item)
                    break;
            }
        },
        PinData(itm) {
            var vm = this;
            vm.isLoading = true;
            let obj = {
                Id: itm.ApplicationId,
                ModuleName: 'Timer_UsedApplication',
                ActionType: 'Pin'
            }
            vm.PinJson.push(obj)
            let pinunpinObj = JSON.stringify(obj);
            DataService.ManagePin_UpPin(pinunpinObj).then((response) => {

                if (response.data.status == 'Success') {
                    $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                    vm.isLoading = false;
                    vm.FetchData();
                    vm.ShowAlert(vm.$t('PinSuccess'), "success", true, vm.$t("Alert"));
                }
                else {
                    vm.ShowAlert(vm.$t('PinUpinError', 'sdgf'), "warning", true, vm.$t("Alert"));
                    vm.isLoading = false;
                }
            })

        },
        UnPinData(itm) {
            var vm = this;
            vm.isLoading = true;
            let obj = {
                Id: itm.ApplicationId,
                ModuleName: 'Timer_UsedApplication',
                ActionType: 'UnPin'
            }
            vm.PinJson.push(obj)
            let pinunpinObj = JSON.stringify(obj);
            DataService.ManagePin_UpPin(pinunpinObj).then((response) => {

                if (response.data.status == 'Success') {
                    $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                    vm.isLoading = false;
                    vm.FetchData();
                    vm.ShowAlert(vm.$t('UnPinSuccess'), "success", true, vm.$t("Alert"));
                }
                else {
                    vm.ShowAlert(vm.$t('PinUpinError', 'sdgf'), "warning", true, vm.$t("Alert"));
                    vm.isLoading = false;
                }
            })

        },
        renderActions(row) {

            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };

                switch (action.title) {
                    case 'Pin':
                        if (row.PinStatus == 'Pin') {
                            action.isVisible = false;
                        }
                        else {
                            action.callbackfunction = 'PinData';
                            action.isVisible = true;
                        }
                        break;

                    case 'Un-Pin':
                        if (row.PinStatus == 'UnPin') {
                            action.isVisible = false;
                        }
                        else {

                            action.callbackfunction = 'UnPinData';
                            action.isVisible = true;
                        }
                        break;


                    case 'Edit':
                        action.isVisible = true;
                        if (!row.IsDisabled) {
                            action.callbackfunction = '';
                            action.icon = 'fa fa-pencil disabled text-success action-icon';
                        }
                        else {
                            action.callbackfunction = 'Editdata';
                            action.icon = 'fa fa-pencil text-success action-icon';

                        }
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;

                    default:
                        action.isVisible = true;
                        break;

                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: async function (value) {
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            await this.FetchData();
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "CardView":
                    this.CardViewListing();
                    break;
                case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                    break
                case "AssignDepartmentClick":
                    this.AssignDepartmentClick();
                    break;
                case "Editdata":
                    this.Editdata()
                    break;
                case "DeleteData":
                    this.DeleteData()
                    break;
                case "ViewHistory":
                    this.ViewHistory()
                    break;
                // case "Yesterday":
                //     this.Yesterday()
                //     break;
            }
        },

        ViewHistory: function () {
            this.$router.push({
                name: 'HistoryListing'
            });
        },
        //  Yesterday:function(){
        //  this.datepicker= true;

        // },
        CardViewListing() {
            var vm = this;
            vm.DetailListType = 'Card'
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortExp == "") {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },

    }
}
</script>
<style>
.custom-low-bar>td:first-child::after {
    width: 3px;
    content: "";
    height: calc(100% - 6px);
    background: #c7d321;
    display: block;
    position: absolute;
    left: 0px;
    top: 3px;
}

.card-detail span.viewicon {
    width: auto;
    float: right;
    margin-left: 5px;
}

.card-detail span.viewicon em {
    float: none;
    padding-right: 0;
    width: 19px;
}

.verticle-action .fsm-list {
    width: 110px !important;

}

.update a {
    color: black !important;
}
</style>