                                    <div class="card-body">
                                        <p>These are a few terms you should know before you start using Silent App:</p>
                                        <p><b>Productive time</b> - Total time spent on productive apps. </p>
                                        <p><b>Time at work</b> - Total time from when you arrived and logged in to when you left work.</p>
                                        <p><b>Idle time</b> - A time gap between two fractions of track time, which appears when there haven't been any actions on the computer for more than a set number of minutes. </p>
                                        <p><b>Productivity</b> - Productive time, divided by the total time and expressed as a percentage.</p>
                                        <p><b>Arrival Time</b>  When the user logs in for the first time in a day.</p>
                                        <p><b>Left Time</b>  Once the user logs out of the machine.</p>
                                    </div>
