                                    </h5>
                                </div>
                                <div id="collapseTwo2" class="collapse" aria-labelledby="headingTwo2" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>Once the Silent App is installed on employees machines, time will be tracked automatically as soon as the machine starts. The application will start along with your computer. You can see the gathered data for the day in the Dashboard section. The company owner and admins can group tracked applications into three sections - Productive apps (green), Unproductive apps (orange), and Neutral apps (grey). Optionally, along with time tracking, the automatic screenshots function can be enabled to reflect more detail about the activity on each computer.</p>
                                    </div>
                                </div>
                            </div>
