                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseseven7" aria-expanded="false" aria-controls="collapseseven7">
                                            Can I view employees live what they are doing?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseseven7" class="collapse" aria-labelledby="headingseven7" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>Yes, you can view the employee's screen live using the live tracking screen.</p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingseven7a">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseseven7a" aria-expanded="false" aria-controls="collapseseven7a">
                                            What are the key features of the Silent App? <i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseseven7a" class="collapse" aria-labelledby="headingseven7a" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>Key features include a dedicated admin dashboard, anonymous time tracking, department, and user-focused tracking, set period tracking, an employee insights dashboard, and data-driven statistics. </p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingseven7b">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseseven7b" aria-expanded="false" aria-controls="collapseseven7b">
                                            What is the Silent App Dashboard?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseseven7b" class="collapse" aria-labelledby="headingseven7b" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>The Silent App Dashboard provides a graphical representation of employee productivity levels and performance tables. It can be viewed by department or individual users. </p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingseven7b">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseseven7b" aria-expanded="false" aria-controls="collapseseven7b">
                                            How do I switch views on the Silent App Dashboard? <i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseseven7b" class="collapse" aria-labelledby="headingseven7b" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>Use the dropdown menu on the dashboard to switch between Department View and User View.</p>
                                    </div>
                                </div>
                            </div>
                            <h4 class="mt-6">Settings</h4>
                            <div class="card">
                                <div class="card-header" id="headingOne8">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseOne8" aria-expanded="false" aria-controls="collapseOne8">
                                            How to add new categories?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseOne8" class="collapse " aria-labelledby="headingOne8" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>The Categories section will help you to organize the lists of your used programs, websites, apps, etc. To add new categories, go to Silent App  Categories and press the Add New Category button. Enter the name of the category and choose a color for that. To delete a category or change the name, or color, click Edit. </p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingTwo9">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseTwo9" aria-expanded="false" aria-controls="collapseTwo9">
                                            What is the difference between app productivity levels?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseTwo9" class="collapse" aria-labelledby="headingTwo9" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>All of the tracked apps in Silent App are divided into three Productivity levels: productive apps (green), Unproductive apps (orange), and Neutral apps (grey). These Productivity levels can differ from Department to Department and User to User. For example, what counts as productive for social media managers won't probably work for programmers.</p>
                                        <p><b>Productive</b> - Apps/Websites that are important to a department's work;</p>
                                        <p><b>Unproductive</b> - Apps/Websites viewed during working hours distract from job tasks and are irrelevant to users' work.</p>
                                        <p><b>Neutral</b>  Apps/Websites are used daily but neither interfere with nor distract from work, nor are they directly involved in getting the job done. </p>
                                        <p>Also, any app for which productivity is not set will be set to neutral by default.</p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree10">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree10" aria-expanded="false" aria-controls="collapseThree10">
                                            How do you delete/archive or restore employees?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree10" class="collapse" aria-labelledby="headingThree10" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>You can go to Company SetupUsers and delete the employees. Once an employee is deleted, it cannot be restored. If you want to permanently delete an employee, you may mark it as inactive. </p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree100">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree100" aria-expanded="false" aria-controls="collapseThree100">
                                            How do I import a category?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree100" class="collapse" aria-labelledby="headingThree100" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>Go to Full Menu &gt; Silent App &gt; Manage Categories &gt; Import. Download the Excel sheet to create an offline category, upload the file from your local device, and click the import button.</p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree101">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree101" aria-expanded="false" aria-controls="collapseThree101">
                                            How can I track individual user productivity?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree101" class="collapse" aria-labelledby="headingThree101" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>Navigate to Full Menu &gt; Silent App &gt; User Productivity. Select a user to view their assigned apps categorized as productive, unproductive, or neutral. You can transfer apps between categories using the dropdown menu. </p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree102">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree102" aria-expanded="false" aria-controls="collapseThree102">
                                            What data can I view in the User Productivity section? <i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree102" class="collapse" aria-labelledby="headingThree102" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>You can view a user's productive apps, unproductive apps, and neutral apps. The productivity of each app can be changed and saved accordingly. </p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree103">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree103" aria-expanded="false" aria-controls="collapseThree103">
                                            How do I manage apps and websites?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree103" class="collapse" aria-labelledby="headingThree103" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>Go to Full Menu &gt; Silent App &gt; Manage Apps &amp; Websites. You can assign specific apps and websites to users or departments, pin, edit, or delete them, and use search filters to locate specific apps/websites.</p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree104">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree104" aria-expanded="false" aria-controls="collapseThree104">
                                            How do I assign apps/websites to a department?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree104" class="collapse" aria-labelledby="headingThree104" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>Navigate to Full Menu &gt; Silent App &gt; Manage Apps &amp; Websites &gt; Assign Department. Select the apps/websites and choose the department to assign them.</p>
                                    </div>
                                </div>
                            </div>
                            <h4 class="mt-6">Screenshots</h4>
                            <div class="card">
                                <div class="card-header" id="headingOne11">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseOne11" aria-expanded="false" aria-controls="collapseOne11">
                                            How do you enable screen capture?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>

                                <div id="collapseOne11" class="collapse " aria-labelledby="headingOne11" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>To enable screen capture for a company, go to Company Setup-&gt;Configuration-&gt;Work Diary.</p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingTwo12">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseTwo12" aria-expanded="false" aria-controls="collapseTwo12">
                                            How do auto screenshots work?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseTwo12" class="collapse" aria-labelledby="headingTwo12" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>The Screenshots feature enables Silent App to capture users' computer screens at random time intervals. Managers can view their users activities throughout the day and gain a better understanding of how the users spend their time at work.</p>

                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree13">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree13" aria-expanded="false" aria-controls="collapseThree13">
                                            Where can I find the taken screenshots?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree13" class="collapse" aria-labelledby="headingThree13" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>All screenshots taken by the Silent App can be found in the 'Work Diary' section, accessed from the Full Menu. </p>

                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree14">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree14" aria-expanded="false" aria-controls="collapseThree14">
                                            How do you change screenshot resolution and time intervals?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree14" class="collapse" aria-labelledby="headingThree14" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>You specify the time and resolution at which the screenshots will be captured.   </p>

                                    </div>
                                </div>
                            </div>
                            <h4 class="mt-6">Video Recording</h4>
                            <div class="card">
                                <div class="card-header" id="headingOne15">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseOne15" aria-expanded="false" aria-controls="collapseOne15">
                                            How to enable video capture?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>

                                <div id="collapseOne15" class="collapse " aria-labelledby="headingOne15" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>The user can enable video capture by going to Company Setup-&gt;Configuration-&gt;Work Diary and selecting the video recording module as Yes.</p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingTwo16">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseTwo16" aria-expanded="false" aria-controls="collapseTwo16">
                                            How does auto video capture work?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseTwo16" class="collapse" aria-labelledby="headingTwo16" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>The video starts capturing at random intervals as soon as the user logs in to the system.</p>

                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree17">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree17" aria-expanded="false" aria-controls="collapseThree17">
                                            Where can I find the recorded videos?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree17" class="collapse" aria-labelledby="headingThree17" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>All the automated recorded videos are available in the Work Diary section, where you can view them. </p>

                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree18">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree18" aria-expanded="false" aria-controls="collapseThree18">
                                            Can I change the video recording intervals?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree18" class="collapse" aria-labelledby="headingThree18" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>You can set the video recording interval from 20 minutes to 120 minutes.  </p>

                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree19">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree19" aria-expanded="false" aria-controls="collapseThree19">
                                            Can I manually record videos?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree19" class="collapse" aria-labelledby="headingThree19" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>Can I manually record videos? </p>

                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree20">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree20" aria-expanded="false" aria-controls="collapseThree20">
                                            Where can I download the recorded videos from?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree20" class="collapse" aria-labelledby="headingThree20" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>All the videos recorded manually can be downloaded directly from the live view popup. Once downloaded, they cannot be downloaded again; however, you can view them on the work diary screen.</p>

                                    </div>
                                </div>
                            </div>
                            <h4 class="mt-6">Reports</h4>
                            <div class="card">
                                <div class="card-header" id="headingOne21">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseOne21" aria-expanded="false" aria-controls="collapseOne21">
                                            What is the "Reports" section?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseOne21" class="collapse " aria-labelledby="headingOne21" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>Admins can see statistics for the entire company, but Managers can see statistics for the department they manage. You can apply filters for each report to choose the department, user, or period for which you wish to see data.</p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingTwo22">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseTwo22" aria-expanded="false" aria-controls="collapseTwo22">
                                            Can I download the reports?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseTwo22" class="collapse" aria-labelledby="headingTwo22" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>You can download the report in PDF, Excel, and Word format.</p>

                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree23">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree23" aria-expanded="false" aria-controls="collapseThree23">
                                            Can I print the reports?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree23" class="collapse" aria-labelledby="headingThree23" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>Yes, you can print the reports directly from the web portal.</p>

                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree230">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree230" aria-expanded="false" aria-controls="collapseThree230">
                                            What types of reports can I generate with the Silent App? <i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree230" class="collapse" aria-labelledby="headingThree230" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>You can generate various reports including User Tracking Report, Exceeded Time Report, Late Employees Report, Slacking Report, Idle Time Report, App/Website Usage Report, and Category Report. </p>
                                    </div>
                                </div>
                            </div>
                            <h4 class="mt-6">Billing</h4>
                            <div class="card">
                                <div class="card-header" id="headingOne24">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseOne24" aria-expanded="false" aria-controls="collapseOne24">
                                            How can I subscribe?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseOne24" class="collapse " aria-labelledby="headingOne24" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>Click on FREE TRIAL, select your required packages, and select the number of licenses you need. You can opt for a monthly plan or an annual plan, which gives you a 15% discount. Enter your information and card details, and you're good to go. </p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingTwo25">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseTwo25" aria-expanded="false" aria-controls="collapseTwo25">
                                            Why did my payment get declined?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseTwo25" class="collapse" aria-labelledby="headingTwo25" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>
                                            If your payment was declined, please check your bank account to see if such a transaction is supported. The most common reasons for payment decline are:<br>
                                            Insufficient funds<br>
                                            Expired Card<br>
                                            Card Limit Exhausted<br>
                                            Invalid transaction (not supported by the bank or type of card)
                                            .
                                        </p>

                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree26">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree26" aria-expanded="false" aria-controls="collapseThree26">
                                            How much does the Silent App cost?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree26" class="collapse" aria-labelledby="headingThree26" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>Silent App standard package starts from as low as $5 USD per user per month.</p>

                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingOne27">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseOne27" aria-expanded="false" aria-controls="collapseOne27">
                                            How do I pay the invoice manually?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>

                                <div id="collapseOne27" class="collapse " aria-labelledby="headingOne27" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>If your automatic payment method is not working, the support team will contact you to update your card. If you are unable to update the card for some reason, you can request a payment link as a temporary payment option.  </p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingTwo28">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseTwo28" aria-expanded="false" aria-controls="collapseTwo28">
                                            What are the differences between the pricing plans?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseTwo28" class="collapse" aria-labelledby="headingTwo28" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>As of now, you can choose from three standard packages: silent Monitoring, Silent Monitoring with Project Management, and Employee Productivity Suite. All three packages come with a seven-day free trial.</p>

                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingThree29">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseThree29" aria-expanded="false" aria-controls="collapseThree29">
                                            How do I change my pricing plan?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseThree29" class="collapse" aria-labelledby="headingThree29" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>You can opt for a different plan or log into your Talygen account and click on Full Subscription &amp; Billing.</p>

                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingOne30">
                                    <h5 class="mb-0">
                                        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseOne30" aria-expanded="false" aria-controls="collapseOne30">
                                            What is the difference between monthly and annual billing?<i class="fa fa-plus"></i>
                                        </button>
                                    </h5>
                                </div>
                                <div id="collapseOne30" class="collapse " aria-labelledby="headingOne30" data-parent="#accordion">
                                    <div class="card-body">
                                        <p>The monthly billing plan charges your payment every month from the date of purchase. (Actual Billing starts post-trial period) <br> The annual billing plan is charged once a year on the date the plan was started, and a 15% discount is offered. </p>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="headingTwo31">
                                    <h5 class="mb-0">
