<script>
import DataService from '../../../services/DataService';
import SilentAppDifferencePopUp from './Modals/SilentAppDifferencePopUp.vue';   
import DownloadInfoPopUp from './Modals/DownloadInfoPopUp.vue';
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        isCompleted: false
    },
    components : {
        SilentAppDifferencePopUp,
        DownloadInfoPopUp
    },
    data(){
        return{
            isLoading:false,
            differencePopUp: false,
            downloadInfo: false,
            WindowSilentAppLinkForDownload: process.env.VUE_APP_DOWNLOAD_WINDOWS_SILENT_APP_URL,
            MacSilentAppLinkForDownload: process.env.VUE_APP_DOWNLOAD_MAC_SILENT_APP_URL,
            WindowVisibleAppLinkForDownload: process.env.VUE_APP_DOWNLOAD_WINDOWS_VISIBLE_APP_URL,
            MacVisibleAppLinkForDownload: process.env.VUE_APP_DOWNLOAD_MAC_VISIBLE_APP_URL,
            LinuxVisibleAppLinkForDownload: process.env.VUE_APP_DOWNLOAD_LINUX_VISIBLE_APP_URL,
            isShowLink1Msg:false,
            isShowLink2Msg:false,
            isShowLink3Msg:false,
            isShowLink4Msg:false,
            isShowLink5Msg:false,
            isHideTooltip:false,
        }
    },
    created(){
        //var vm = this;
    },
    methods:{
        async UpdateLinkVisitedInformation(link, linkMsg) {  
            debugger 
            var vm = this;    
            if(linkMsg == 'isShowLink1Msg' || linkMsg == 'isShowLink2Msg')
            {
                vm.downloadInfo = true;  
            }           
            let url = `Link=${link}`;
                await DataService.UpdateLinkVisitedInformation(url).then((response) => {
                    var result = JSON.parse(response.data[0]);
                    if(result.status == 1)
                    {      
                        vm.$emit("updateProgress", vm.currentTabIndex, true);
                    }
            });
        },


        openDifferncePopup() {
             var vm = this;
             vm.differencePopUp = true;
        },

        copyLink(GeneratedLink,linkMsg) {
            debugger
            var vm = this;           
            const textarea = document.createElement('textarea');
            textarea.value = GeneratedLink;
            document.body.appendChild(textarea);
            textarea.select();

            if(linkMsg=='isShowLink1Msg')
            {
                vm.downloadInfo = false;
                vm.isShowLink1Msg = true;
                vm.isShowLink2Msg = false;
                vm.isShowLink3Msg = false;
                vm.isShowLink4Msg = false;
                vm.isShowLink5Msg = false;
            }
            else if(linkMsg=='isShowLink2Msg')
            {
                vm.downloadInfo = false;
                vm.isShowLink1Msg = false;
                vm.isShowLink2Msg = true;
                vm.isShowLink3Msg = false;
                vm.isShowLink4Msg = false;
                vm.isShowLink5Msg = false;
            }
            else if(linkMsg=='isShowLink3Msg')
            {
                vm.isShowLink1Msg = false;
                vm.isShowLink2Msg = false;
                vm.isShowLink3Msg = true;
                vm.isShowLink4Msg = false;
                vm.isShowLink5Msg = false;
            }
            else if(linkMsg=='isShowLink4Msg')
            {
                vm.isShowLink1Msg = false;
                vm.isShowLink2Msg = false;
                vm.isShowLink3Msg = false;
                vm.isShowLink4Msg = true;
                vm.isShowLink5Msg = false;
                
            }
            else if(linkMsg=='isShowLink5Msg')
            {
                vm.isShowLink1Msg = false;
                vm.isShowLink2Msg = false;
                vm.isShowLink3Msg = false;
                vm.isShowLink4Msg = false;
                vm.isShowLink5Msg = true;
            }
            //vm.UpdateLinkVisitedInformation(GeneratedLink, linkMsg)
            try {
                document.execCommand('copy');                
            } catch (err) {
                console.error('Failed to copy text: ', err);
            } finally {
                document.body.removeChild(textarea);
            }
        },

        SearchFAQ(event) {
            // Get the search text and normalize it
            var searchText = event.target.value.trim().toLowerCase();
            
            // Get all <h4> elements
            var headers = document.querySelectorAll('h4');
            var noRecordMessage = document.getElementById('noRecordMessage');
            var anyResultsFound = false;

            headers.forEach(function(header) {
                // Get the next sibling elements until the next <h4> that match '.card'
                var currentElement = header.nextElementSibling;
                var foundInHeaderSection = false;

                while (currentElement && currentElement.tagName !== 'H4') {
                    if (currentElement.classList.contains('card')) {
                        var foundInBody = false;
                        var foundInButton = false;

                        // Check the card body for matching text
                        var cardBodies = currentElement.getElementsByClassName('card-body');
                        for (var i = 0; i < cardBodies.length; i++) {
                            var bodyText = cardBodies[i].textContent.trim().toLowerCase();
                            if (bodyText.includes(searchText)) {
                                foundInBody = true;
                                break;
                            }
                        }

                        // Check the card buttons for matching text
                        var buttons = currentElement.getElementsByClassName('btn btn-link');
                        for (var j = 0; j < buttons.length; j++) {
                            var buttonText = buttons[j].textContent.trim().toLowerCase();
                            if (buttonText.includes(searchText)) {
                                foundInButton = true;
                                break;
                            }
                        }

                        // Show or hide the card based on the search
                        if (foundInBody || foundInButton || searchText === '') {
                            currentElement.style.display = '';
                            foundInHeaderSection = true;
                            anyResultsFound = true; // Set to true if any card is shown
                        } else {
                            currentElement.style.display = 'none';
                        }
                    }
                    currentElement = currentElement.nextElementSibling;
                }

                // Show or hide the header based on the cards visibility
                if (foundInHeaderSection) {
                    header.style.display = '';
                } else {
                    header.style.display = 'none';
                }
            });

            // Show or hide the "No record found" message based on the results
            if (noRecordMessage) {
                noRecordMessage.style.display = anyResultsFound ? 'none' : '';
            }
        },
        Continue: function () {
            var vm = this;
            vm.$emit("nextTab");
        },
        Close(){

        }
    }
}
</script>
