<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div :class="reqFrom=='InitialSetup' ? 'dynamiccontent_base' : ''">
            <dynamic-form lang="en" ref="AddTrip" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="SaveSubmit"></dynamic-form>
        </div>
        <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">
            <div class="row">
                <div class="col-lg-12 text-right mt-4">
                    <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                                v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous') }}</button>                                               
                                <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip"
                        data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                        @click="SaveSubmit('SaveAndClose')" style="margin-left: 5px" v-if="tabCount == currentTabIndex && reqFrom != 'InitialSetup'">
                        <i class="fa fa-save pr-2"></i> {{ $t("SaveAndClose") }}
                    </button>
                    <button v-if="reqFrom == 'InitialSetup'" title="Finish" class="btn btn-danger formbtn widthhalf px-4"
                            @click="SaveSubmit('SaveAndFinish')">
                            {{ $t("SaveAndFinish") }}
                        </button>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from './../../../../services/AdvanceSetupModuleService';
import commonDataService from "../../../../services/DataService";
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom:''
    },
    data(){
        return{
            isLoading: false,
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [                     
                        {
                            astype: "TextField",
                            label: this.$t('TripName'),
                            name: "TripName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true},
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('STATUS_ID'),
                            name: "status",
                            value: "1001",
                            placeholder: "",
                            config: {
                                options: [{value:'1001',name:'Active'},{value:'1002',name:'InActive'}]
                            }
                        }
                    ],
                  },
                  {
                    layoutType: "Single",
                    Data:[
                      {
                      astype: "TextAreaField",
                        label: this.$t('TripDescription'),
                        name: "TripDescription",
                        value: "",
                        placeholder: "",
                        validationRules: { "required": true},
                        config: {
                          rows:5
                        }
                      }
                   ]
                }
            ],
            buttons: [              
            ],
            companyId: null,
            userId: null,
        }
    },
    created: function () {
        var vm = this;
    },
    methods:{                
        SaveSubmit(btnType){            
            var vm = this;
            vm.isLoading=true;
            vm.$refs.AddTrip.validateForm.validate().then(result => {                
                if(result){
                    var jsonobj = {
                        TripId:0,
                        TripName: vm.FormSchema[0].Data[0].value,
                        StatusId: vm.FormSchema[0].Data[1].value,
                        TripDesc:vm.FormSchema[1].Data[0].value
                    };          
                    var params = JSON.stringify(jsonobj);
                    DataService.SaveUpdateTrip(params).then((response) => {                        
                        if(response.data.Status == 'Success')
                            {
                                if(btnType  == 'SaveAndFinish')
                                {
                                    vm.Finish();
                                }                               
                                vm.ShowAlert(vm.$t('AddedSuccess',[vm.$t('Trip')]), "success", true,  vm.$t("Alert"));
                            }
                            else if(response.data.Status == 'Exists'){
                                vm.ShowAlert(vm.$t('AlreadyExists',[vm.$t('Trip')]), "failure", true,  vm.$t("Alert"));
                            }
                            else{
                                vm.ShowAlert(vm.$t('AddedError',[vm.$t('Trip')]), "failure", true,  vm.$t("Alert"));
                            }
                            vm.isLoading=false;
                        vm.$parent.NextTab();
                    });
                }
            });
        },
        Skip: function () {
            var vm = this;
            vm.$parent.NextTab();
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        Finish: async function () {
            var vm = this;
            await vm.UpdateUserSettingConfig('HideContextmenu');
            await vm.UpdateUserSettingConfig('initialstartup');
            window.location.href='/User/Home';
        },
        async UpdateUserSettingConfig(type) {
            let url = `status=true&type=${type}`
            await commonDataService.UpdateUserSettingConfig(url).then((response) => {

            });
        }
    }
}
</script>