<template>
  <section>
    <loader :is-visible="isLoading"></loader>
    <div class="dynamiccontent_base">
      <Form ref="updateValues">
        <dynamic-form ref="updateValue" :buttons="[]" :schema="FormSchema" v-if="DataLoaded">
          <template v-slot:tgslot-GracePeriod="{ data }">
            <div class="form-group">
              <Field :rules="{ required: true }" class="w-100" name="Grace Period" v-slot="{ errors }">
                <div class="d-flex">
                  <input type="number" name="GracePeriod" v-bind:class="{
                    'form-control': true,
                    'is-invalid': errors[0],
                  }" v-on:keyup="handleChange(GracePeriod)" v-model="GracePeriod" />
                  <span class="input-group-append">
                    <span class="input-group-text" id="spnOutOF">Days</span>
                  </span>
                </div>
                <span v-if="errors[0]" name="GracePeriod" class="invalid-feedback d-block">{{ errors[0] }}</span>
              </Field>
            </div>
          </template>
          <template v-slot:tgslot-ProductTypeCode="{ data }">
            <div class="form-group">
              <div class="d-flex">
                <a href="javascript:;" data-toggle="tooltip" data-trigger="click" @click="toggleTip"
                  title="Please enter the product type code. Else four digit product type code will be generated by system.">
                  <i class="fa fa-question-circle-o text-popover" style="font-size: 14px"></i>
                </a>

                <input type="text" name="ProductTypeCode" :maxlength="4" class="form-control"
                  v-model="ProductTypeCode" />
              </div>
            </div>
          </template>
          <template v-slot:tgslot-tblContainer="{ data }">
            <div class="table-responsive" v-if="IsShowCompartments">
              <Form ref="AddProductType">
                <table id="tblContainerList" class="table table-bordered dt-responsive nowrap mt-0 m-0">
                  <thead id="divgrid" class="thead-bg">
                    <th style="min-width: 142px">
                      <span class="float-left">Compartments</span><span class="text-danger float-left">*</span>
                    </th>
                    <th style="min-width: 100px" class="text-center">
                      <span>Action</span>
                    </th>
                  </thead>
                  <tbody>
                    <tr id="datarow" v-for="(item, index) in tableRows" v-bind:key="index">
                      <td>
                        <Field :name="$t('CompartmentName')" v-slot="{ errors }" :rules="{ required: true }">
                          <input :id="'container_name_' + index" type="text" maxlength="200" :class="{
                            'form-control': true,
                            'is-invalid': errors[0],
                          }" v-model="item.InputContainervalue" @input="startValidationTimer(index)"
                            placeholder="Fill the compartment name" /><span v-if="errors[0]"
                            :name="$t('CompartmentName')" class="invalid-feedback d-block">{{
                              errors[0] }}</span>
                        </Field>
                      </td>
                      <td>
                        <a class="round-icon-small btn-danger-light float-none d-inline-block" href="javascript:;"
                          title="Delete"><em v-on:click="RemoveContainerRow(index)"
                            class="recyclebin fa fa-trash"></em></a>
                        <a class="add round-icon-small btn-success-light float-none d-inline-block"
                          v-on:click="AddContainerRow()" href="javascript:;" title="Add"><em
                            class="fa fa-plus"></em></a>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </Form>
            </div>
          </template>
        </dynamic-form>
      </Form>

      <div class="mainfooter_app">
        <div class="col-lg-12 text-right mt-4  mr-3">
          <button title="Previous" class="btn btn-warning float-left px-4" @click="PreviousButton()"
            v-if="this.currentTabIndex > 0">
            <em class="fa fa-chevron-left pr-2"></em>
            {{ $t("Previous") }}  
          </button>
          <button title="Skip" class="btn skip-btn-clr formbtn widthhalf px-4 mr-2" @click="Skip()"
            v-if="this.tabCount > this.currentTabIndex">            
            {{ $t("Skip") }}
          </button>
          <button type="button" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2" @click="onSubmit('Save')">
            {{ $t("SaveAndNext") }}
            <em class="fa fa-chevron-right pl-2"></em>
          </button>
        </div>
      </div>
    </div>

    <!-- <div class="left-content row flex-row-reverse mt-3">
      <div class="col-lg-12 text-left">
        <div class="py-2 mandatory">
          {{ $t("MandatoryString") }}
        </div>
      </div>
    </div> -->


  </section>
</template>

<script>
