                onChange: this.ContainerDeleteConfirm,
              },
            },
            {
              astype: "SlotField",
              showLabel: false,
              label: this.$t("Container"),
              name: "tblContainer",
              value: "",
              placeholder: "",
              config: {
                options: [],
              },
              visibility: true,
            },
          ],
        },
        {
          layoutType: "single",
          groupLayoutType: "single",
          Data: [
            {
              astype: "FileField",
              showLabel: true,
              label: this.$t("AttachItemImage"),
              name: "AttachItemImage",
              value: "",
              placeholder: "",
              validationRules: "",
              FieldIndex: 0,
              config: {
                maxSize: 5,
                // accept: [{
                //     fileType: 'png',
                //     iconClass: 'fa-file-image-o'
                // },
                // {
                //     fileType: 'jpg',
                //     iconClass: 'fa-file-image-o'
                // },
                // {
