              },
            },
            {
              astype: "SelectField",
              label: this.$t("Status"),
              name: "AssetReplacementType",
              value: "",
              placeholder: "",
              validationRules: {
                required: true,
              },
              config: {
                options: [],
              },
              visibility: false,
            },
            // {
            //     astype: "SelectField",
            //     label: this.$t('Status'),
            //     name: "AssetReplacementType",
            //     value: "",
            //     placeholder: "",
            //     validationRules: {
            //         "required": true
            //     },
            //     visibility: false,
