            //         options: [],
            //     },
            // },
            {
              astype: "toggleField",
              label: this.$t("PenaltyOnReturnOverdue"),
              name: "PenaltyOnReturnOverdue",
              value: "",
              placeholder: "",
              config: {
                onChange: this.OnPenaltyOnReturnOverdue,
              },
            },
            {
              astype: "SlotField",
              label: this.$t("GracePeriod"),
              name: "GracePeriod",
              value: "",
              placeholder: "",
              config: {
                options: [],
              },
              visibility: false,
              validationRules: {
                required: true,
              },
            },

            // {
            //     astype: "NumericField",
            //     label: this.$t('GracePeriod'),
            //     name: "GracePeriod",
            //     value: "",
            //     placeholder: "",
            //     visibility:false,
            //     validationRules: { "required": true, max: 250 },
            // },
          ],
        },
        {
          layoutType: "four",
          groupLayoutType: "single",
          group_name: "Deployment Properties",
          group_display_name: this.$t("DeploymentProperties"),
          Data: [
            {
              astype: "ToggleField",
              label: this.$t("InsurancePolicy"),
              name: "InsurancePolicy",
              value: "",
              placeholder: "",
            },
            {
              astype: "ToggleField",
              label: this.$t("Calibration"),
              name: "Calibration",
              value: "",
              placeholder: "",
            },
            {
              astype: "ToggleField",
              label: this.$t("ItemCost"),
              name: "ItemCost",
              value: "",
              placeholder: "",
            },
            {
              astype: "ToggleField",
              label: this.$t("IsWarranty"),
              name: "IsWarranty",
              value: "",
              placeholder: "",
            },
            {
              astype: "ToggleField",
              label: this.$t("Depreciable"),
              name: "Depreciable",
              value: "",
              placeholder: "",
            },
          ],
        },
      ],
      buttons: [

        {
          type: "button",
          class: "btn btn-warning float-left px-4",
          text: '<i class="fa fa-chevron-left pr-2"></i> ' + this.$t('Previous'),
          isVisible: (this.currentTabIndex > 0) ? true : false,
          onClick: this.PreviousButton
        },

        {
          type: "button",
          class: "popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4",
          text: this.$t('Skip'),
          isVisible: (this.tabCount > this.currentTabIndex) ? true : false,
          onClick: this.Skip
        },


        {
          type: "submit",
          class: "btn btn-success",
          text: "<i class='fa fa-save pr-2'></i>Save And Next",
          value: "Save",
          name: "Save",
        },
        {
          type: "submit",
          class: "btn btn-success",
          text: "<i class='fa fa-save pr-2'></i>Save & New",
          name: "SaveAndNew",
          isVisible: false,
        },
        {
          type: "submit",
          class: "btn btn-success",
          text: "<i class='fa fa-save pr-2'></i>Save & Next",
          name: "SaveAndNext",
          isVisible: false,
        },
        // {
        //   type: "button",
        //   class: "btn btn-danger",
        //   text: "<i class='fa fa-close pr-2'></i>Cancel",
        //   onClick: this.CancelButton,
        //   isVisible: true,
        // },
      ],
      StatusDDL: [
        {
          label: "Active",
          value: 1001,
        },
        {
          label: "Inactive",
          value: 1002,
        },
      ],
      AssetReplacementTypeDDL: [
        // {
        //     label: 'Damaged',
        //     value: 12396
        // },
        // {
        //     label: 'In Stocck',
        //     value: 12696
        // },
        // {
        //     label: 'Lost',
        //     value: 2171
        // },
        // {
        //     label: 'Stolen',
        //     value: 12398
        // },
      ],
      IsTagNamePopup: false,
      FromAddProduct: false,
      IsShowCompartments: false,
      lbl: false,
      tableRows: [],
      currentInputIndex: 0,
      //inputs: [],
      //AssetType:'',
      AssetTypeCode: "",
      userId: null,
      companyId: null,
      DataLoaded: 0,
      TagsArray: [],
      file: [],
      file1: [],
      ShowAddProduct: false,
      CompartmentListData: [],
      Count: 0,
      checkFieldsvalue: false,
      TypeData: [],
      TagsSelectedArray: [],
      ProductTypeCode: "",
      validationTimer: null,
      AllContainerDeletePopup: false,
      GracePeriod: "",
    };
  },
  created: async function () {
    var vm = this;

    vm.userId = vm.GetUserInfo.ID;
    vm.companyId = vm.GetUserInfo.Name;
    await vm.GetTagsByModuleI();
    await vm.BindStatus();
    // await vm.AdvancedSetupFields();

    if (vm.AseetTypeIde > 0) {
      await vm.GetAssetTagsById();
      await vm.FetchData();
    } else {
      delete vm.FormSchema[1].Data[0].config.showSelectedFiles;
    }
    await vm.GetAllowFileType();

    vm.DataLoaded = 1;
  },
  methods: {

    PreviousButton: function () {
      var vm = this;
      vm.$emit("previousTab");
    },
    async GetAllowFileType() {
      await assetDataService.GetAllowFileType().then((response) => {
        if (response.data != null) {
          const AllowedFileTypes = response.data.AllowedFileTypes.split(",");
          const acceptArray = this.FormSchema[1].Data[0].config.accept;

          AllowedFileTypes.forEach((fileType) => {
            const fileTypeWithoutDot = fileType.trim().replace(/\./g, ""); // Remove dots and trim whitespace

            acceptArray.push({
              fileType: fileTypeWithoutDot,
              iconClass: "fa-file-image-o",
            });
          });
        }
      });
    },
    onDeleteSelectedFiles: function (itm) {
      this.$delete(this.FormSchema[1].Data[0].config, "showSelectedFiles");
    },
    onClickSelectedFile(itm) {
      var vm = this;
      vm.isLoading = true;
      var attachmentFilePath =
        vm.TypeData[0].ATTACHMENT_PATH.split("/")[4] +
        "/" +
        vm.TypeData[0].ATTACHMENT_PATH.split("/")[5] +
        "/" +
        vm.TypeData[0].ATTACHMENT_PATH.split("/")[6] +
        "/" +
        vm.TypeData[0].ATTACHMENT_PATH.split("/")[7];
      var fileType = vm.TypeData[0].ATTACHMENT_PATH.split(".")[5];
      var attachmentName = vm.TypeData[0].ATTACHMENT_NAME;
      var param = `filePath=${attachmentFilePath}`;
      // var url = `?fileId=${item.RelatedInfoId}&filePath=${item.FilePath}&fileName=${item.AttachmentName}&FileType=${item.FileType}`;
      assetDataService.DownloadAttachment(param).then((response) => {
        if (response.data != null && response.status === 200) {
          const byteData = response.data;
          const fileURL = `data:${fileType};base64,${byteData}`;
          var fileLink = document.createElement("a");
          fileLink.href = fileURL;
          fileLink.setAttribute("download", attachmentName);
          document.body.appendChild(fileLink);
          fileLink.click();
          vm.isLoading = false;
        } else {
          vm.isLoading = false;
        }
      });
    },
    toggleTip() {
      $('[data-toggle="tooltip"]')
        .tooltip({
          trigger: "click",
        })
        .on("shown.bs.tooltip", function () {
          var $this = $(this);
          setTimeout(function () {
            $this.tooltip("hide");
          }, 2000);
        });
    },
    onChangeContainer(fieldInfo, index, e) {
      var vm = this;
      vm.FormSchema[0].Data[4].value = e.target._value;
    },
    handleChange(input) {
      var vm = this;
      if (input.trim() !== "") {
        input = parseInt(input, 10);
      } else {
        return;
      }
      if (isNaN(input)) {
        vm.ShowAlert(vm.$t("EnterValidNumber"), "warning", vm.$t("Alert"));
        vm.GracePeriod = "";
        return;
      } else if (input < 1) {
        vm.GracePeriod = 1;
      } else if (input > 365) {
        vm.GracePeriod = 365;
      }
    },

    async OnPenaltyOnStatus(ev) {
      var vm = this;
      vm.FormSchema[6].Data[8].config.options = [];
      vm.FormSchema[6].Data[8].visibility = true;
      if (ev == true) {
        await assetDataService
          .GetAssetItemStatusList()
          .then(function (response) {
            vm.AssetReplacementTypeDDL = response.data;
            vm.AssetReplacementTypeDDL.forEach((itm) => {
              vm.FormSchema[6].Data[8].config.options.push({
                name: itm.STATUS_NAME,
                value: itm.STATUS_ID,
              });
            });
            vm.$refs.updateValue.UpdateKeyValue();
          });
      } else if (ev == false) {
        vm.FormSchema[6].Data[8].value = "";
        vm.FormSchema[6].Data[8].visibility = false;
      }
    },
    OnIsSelfCheckOut(ev) {
      var vm = this;
      if (ev == true) {
        vm.FormSchema[6].Data[2].visibility = true;
      } else if (ev == false) {
        vm.FormSchema[6].Data[2].visibility = false;
      }
    },
    OnPenaltyOnReturnOverdue(ev) {
      var vm = this;
      if (ev == true) {
        vm.FormSchema[6].Data[10].visibility = true;
      } else if (ev == false) {
        vm.FormSchema[6].Data[10].visibility = false;
      }
    },
    Skip() {
      var vm = this;
      vm.$parent.NextTab(true);
    },
    AdvancedSetupFields() {
      var vm = this;
      if (vm.popupCondition == "setup") {
        vm.buttons[0].isVisible = true;
        vm.buttons[1].isVisible = true;
        vm.buttons[2].isVisible = false;
        vm.buttons[3].isVisible = true;
        vm.buttons[4].isVisible = false;
      }
    },
    // async GetTagsByModuleI() {
    //     var vm = this;
    //     await DataService.GetTagsByModuleId('moduleName=Asset').then(function (response) {
    //         vm.TagsArray = JSON.parse(response.data).data;
    //         vm.TagsArray.forEach((itm, index) => {
    //             vm.FormSchema[4].Data[0].config.options.push({
    //                 name: itm.TAG_NAME,
    //                 value: itm.TAG_ID
    //             })
    //         })
    //     })
    // },
    OpenAddTagPopup() {
      debugger;
      var vm = this;
      vm.IsTagNamePopup = true;
    },
    setFile1: function (e) {
      var vm = this;
      for (let index = 0; index < e.target.files.length; index++) {
        const element = e.target.files[index];
        vm.file1.push(element);
      }
    },
    BindStatus() {
      var vm = this;
      vm.StatusDDL.forEach((itm) => {
        vm.FormSchema[0].Data[1].config.options.push({
          name: itm.label,
          value: `${itm.value}`,
        });
      });
      setTimeout(() => {
        vm.$refs.updateValue.UpdateKeyValue();
      }, 500);
    },
    onSubmit: function (val) {
      var vm = this;
      vm.$refs.updateValues.validate().then(result => {
        if (result) {
          vm.buttons[1].class += " disabled";
          vm.isLoading = true;
          vm.Count = 0;
          if (
            vm.FormSchema[3].Data[0].value == false &&
            vm.FormSchema[3].Data[1].value == false &&
            vm.FormSchema[3].Data[2].value == false &&
            vm.FormSchema[3].Data[3].value == false
          ) {
            vm.ShowAlert(
              vm.$t("At least one Product category must be selected!"),
              "failure",
              vm.$t("Alert")
            );
            vm.Count = 1;
            vm.buttons[1].class = "btn btn-success";
          } else if (vm.IsShowCompartments == true) {
            vm.tableRows.forEach((item, index) => {
              if (item.InputContainervalue == "") {
                vm.checkFieldsvalue == true;
                vm.Count = 1;
              }
            });
          }
          if (vm.Count == 0) {
            let objCompartmentList = {};
            vm.tableRows.forEach((item, index) => {
              objCompartmentList = {
                container_Id: 0,
                asset_type_id: vm.AseetTypeIde > 0 ? vm.AseetTypeIde : 0,
                container_name: item.InputContainervalue,
                status_id: 1001,
                modified_by: vm.userId,
              };
              vm.CompartmentListData.push(objCompartmentList);
            });
            let objAssets = {};
            if (vm.AseetTypeIde > 0) {
              objAssets.AssetTypeId = vm.AseetTypeIde;
            } else {
              objAssets.AssetTypeId = 0;
            }
            objAssets.AssetType = vm.FormSchema[0].Data[0].value;
            objAssets.status_id =
            vm.FormSchema[0].Data[1].value == ""
                ? ""
                : vm.FormSchema[0].Data[1].value;
            objAssets.Description = vm.FormSchema[2].Data[0].value;
            objAssets.created_at = null;
            objAssets.modified_at = null;
            objAssets.modified_by = vm.userId;
            (objAssets.company_id = vm.companyId),
              (objAssets.AssetTypeCategory = vm.FormSchema[3].Data[0].value == true ? 1 : 0);

            objAssets.IsContainer = vm.FormSchema[0].Data[3].value == true ? 1 : 0;

            objAssets.IsSelfCheckOut = vm.FormSchema[6].Data[1].value  == true ? 1 : 0;
            objAssets.IsTransferable = vm.FormSchema[6].Data[0].value == true ? 1 : 0;
            objAssets.IsEnableTerms = vm.FormSchema[6].Data[4].value == true ? 1 : 0;
            objAssets.InsurancePolicy = vm.FormSchema[7].Data[0].value == true ? 1 : 0;
            objAssets.AssetTypeCode = vm.ProductTypeCode;
            objAssets.IsAsset =  vm.FormSchema[3].Data[0].value == true ? 1 : 0;
            objAssets.IsConsumable =  vm.FormSchema[3].Data[1].value == true ? 1 : 0;
            objAssets.IsLicense =  vm.FormSchema[3].Data[2].value == true ? 1 : 0;
            objAssets.IsSpare =  vm.FormSchema[3].Data[3].value == true ? 1 : 0;
            objAssets.IsItemCost = vm.FormSchema[7].Data[2].value == true ? 1 : 0;
            objAssets.IsDepreciable = vm.FormSchema[7].Data[4].value == true ? 1 : 0;
            objAssets.IsCalibration = vm.FormSchema[7].Data[1].value == true ? 1 : 0;
            objAssets.IsWarranty = vm.FormSchema[7].Data[3].value == true ? 1 : 0;
            objAssets.IsEmployeeAcceptance =
              vm.FormSchema[6].Data[3].value == true ? 1 : 0;
            objAssets.IsPenalty = vm.FormSchema[6].Data[7].value == true ? 1 : 0;
            var penalty = vm.FormSchema[6].Data[7].value == true ? 1 : 0;
            objAssets.penalty_type =
              penalty == 0 ? null : vm.FormSchema[6].Data[8].value;
            objAssets.IsPenaltyOnReturnOverdue =
              vm.FormSchema[6].Data[9].value == true ? 1 : 0;
            var penalty_on_overdue =
               vm.FormSchema[6].Data[9].value == true ? 1 : 0;
            objAssets.GracePeriod = penalty_on_overdue == 0 ? null : vm.GracePeriod;
            objAssets.IsGPSTracking = vm.FormSchema[5].Data[3].value  == true ? 1 : 0;
            objAssets.IsFleetManagement =
               vm.FormSchema[6].Data[6].value == true ? 1 : 0;
            objAssets.IsTimeAttendance =
                vm.FormSchema[5].Data[0].value == true ? 1 : 0;
            objAssets.IsCamera = vm.FormSchema[5].Data[1].value == true ? 1 : 0;
            objAssets.IsRFID = vm.FormSchema[5].Data[2].value == true ? 1 : 0;
            objAssets.IsGPS = vm.FormSchema[5].Data[3].value == true ? 1 : 0;
            objAssets.IsSelfieEnable = 1;
            objAssets.IsSampleData = 0;
            objAssets.container_name = "";
            objAssets.BatchId = "";
            objAssets.isMeter = 0; 
            objAssets.utilityTypeId = 0;
            objAssets.AssetTagIds = Array.prototype.map
              .call(vm.FormSchema[4].Data[0].value, function (item) {
                return item.value;
              })
              .join(",");
            objAssets.CompartmentList = vm.CompartmentListData;
            var formData = new FormData();
            var postString = {
              postString: objAssets,
            };
            if (penalty == 1) {
              if (vm.FormSchema[6].Data[8].value == null || vm.FormSchema[6].Data[8].value == "")
             {
                vm.buttons[1].class = "btn btn-success";
                $('[name="AssetReplacementType"]').focus();
                $(".btn-success").focus();
                vm.isLoading = false;
                return false;
              }
            }

            formData.append("postString", JSON.stringify(objAssets));
            formData.append("Files", vm.file1[0]);
            assetDataService.AddProductType(formData).then(function (response) {
              vm.isLoading = false;
              if (val == "Save") {
                if (
                  response.data.msg == "size_exceed" ||
                  response.data.msg == "FileSizeExceeded" ||
                  response.data.msg == "ExceedAllowedSize"
                ) {
                  //vm.ShowAlert((vm.$t('size_exceed', [vm.$t('Asset')]), "warning", true, vm.$t("Alert")))
                  vm.ShowAlert(
                    vm.$t("FileSizeExceedMsg"),
                    "warning",
                    true,
                    vm.$t("Alert")
                  );
                } else if (response.data.status == "1") {
                  vm.ShowAlert(vm.$t("AddProductType"), "success", vm.$t("Alert"));
                  if (vm.tabCount > 1) {
                    vm.$emit("nextTab")
                  }
                  vm.CancelButton();
                  if (vm.FromPage == "EditProduct") {
                    vm.$parent.FetchData();
                  }
                } else if (response.data.status == "2") {
                  vm.ShowAlert(
                    vm.$t("UpdatedProductType"),
                    "success",
                    vm.$t("Alert")
                  );
                  vm.$parent.ClosePopupProductType();
                  vm.$parent.FetchData();
                } else if (response.data.status == "3") {
                  vm.ShowAlert(
                    vm.$t("ProductTypeExist"),
                    "success",
                    vm.$t("Alert")
                  );
                  vm.$parent.ClosePopupProductType();
                  vm.$parent.FetchData();
                } else if (response.data.status == "AlreadyExists") {
                  vm.ShowAlert(
                    vm.$t("ProductTypeExist"),
                    "warning",
                    vm.$t("Alert")
                  );
                  vm.CancelButton();
                } else {
                  if (response.data.msg == "ValidFileUploadMsg") {
                    vm.ShowAlert(
                      vm.$t("NotAllowedFile"),
                      "warning",
                      vm.$t("Alert")
                    );
                    vm.CancelButton();
                  } else {
                    vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t("Alert"));
                    vm.CancelButton();
                  }
                }
                vm.buttons[1].class = "btn btn-success";
                vm.isLoading = false;
              }
            });
          } else {
            vm.buttons[1].class = "btn btn-success";
            vm.isLoading = false;
          }
        }
      });

    },
    startValidationTimer(index) {
      clearTimeout(this.validationTimer);
      this.validationTimer = setTimeout(() => {
        this.validateDuplicate(index);
      }, 1000); // Adjust the timeout value as needed
    },
    validateDuplicate(index) {
      const newValue =
        this.tableRows[index].InputContainervalue.trim().toLowerCase(); // Convert to lowercase
      const isDuplicate = this.tableRows.some(
        (row, i) =>
          i !== index &&
          row.InputContainervalue.trim().toLowerCase() === newValue
      );
      if (isDuplicate) {
        this.ShowAlert(
          "Duplicate " + this.$t("CompartmentName") + " is not allowed",
          "failure",
          this.$t("Alert")
        );
        this.tableRows[index].InputContainervalue = "";
      }
    },
    AddContainerRow() {
      var vm = this;
      if (this.tableRows.length >= 10) {
        vm.ShowAlert(
          "Maximum number of " +
          this.$t("Compartments") +
          " reached. You cannot add more " +
          this.$t("Compartments") +
          ".",
          "failure",
          this.$t("Alert")
        );
        return; // Stop further execution
      }
      vm.$refs.AddProductType.validate().then((result) => {
        if (result) {
          this.tableRows.push({
            InputContainervalue: "",
          });
        }
      });
    },
    RemoveContainerRow(index) {
      //  debugger;
      var vm = this;
      vm.confirmR(
        vm.$t("Are you sure you want to delete these record(s)?"),
        true,
        false,
        function (result) {
          if (vm.tableRows.length == 1) {
            vm.lbl = true;
            vm.ShowCompartments();
            vm.FormSchema[0].Data[3].value = false;
            vm.tableRows = [];
          } else {
            vm.tableRows.splice(index, 1);
          }
        }
      );
    },

    ContainerDeleteConfirm(val) {
      let vm = this;

      if (val == false && vm.tableRows.length > 0) {
        vm.confirmR(
          vm.$t("Are you sure you want to Clear All Compartment record(s)?"),
          true,
          false,
          function (result) {
            vm.tableRows = [];
            vm.FormSchema[0].Data[3].value = false;
            vm.IsShowCompartments = false;

            return;
          }
        );

        vm.FormSchema[0].Data[3].value = true;
      } else {
        vm.ShowCompartments();

        //vm.FormSchema[0].Data[3].value = true
      }
    },
    ShowCompartments() {
      //debugger;
      var vm = this;

      if (this.tableRows.length > 0) {
        vm.lbl = true;
      } else {
        vm.lbl = false;
      }
      if (vm.lbl == false) {
        vm.IsShowCompartments = true;
        this.tableRows.push({
          InputContainervalue: "",
        });
        vm.lbl = true;
      } else if (vm.lbl == true) {
        vm.IsShowCompartments = false;
        vm.tableRows = [];
        vm.lbl = false;
      }
    },
    CancelButton() {
      var vm = this;
      if (vm.FromPage == "EditProduct") {
        vm.$parent.ClosePopupProductType();
      } else if (vm.FromPage == "AddProduct") {
        vm.$parent.ClosePopupProductType();
      }
    },
    divDialogAddTags() {
      debugger;
      var vm = this;
      vm.FromAddProduct = false;
      vm.IsTagNamePopup = true;
    },
    CloseTagPopup() {
      var vm = this;
      vm.IsTagNamePopup = false;
    },
    async GetTagsByModuleI() {
      var vm = this;
      vm.TagsArray = [];
      vm.FormSchema[4].Data[0].config.options = [];
      await assetDataService
        .GetTagsByModuleId("moduleName=Asset")
        .then(function (response) {
          vm.TagsArray = JSON.parse(response.data).data;
          vm.TagsArray.forEach((itm, index) => {
            vm.FormSchema[4].Data[0].config.options.push({
              name: itm.TAG_NAME,
              value: itm.TAG_ID,
            });
          });
        });
    },
    async GetAssetTagsById() {
      var vm = this;
      var params = `id=${vm.AseetTypeIde}&is_for=ASSET_TYPE`;
      await assetDataService.GetAssetTagsById(params).then((response) => {
        vm.tagsData = JSON.parse(response.data);
        vm.tagsData.forEach((item, index) => {
          vm.TagsSelectedArray.push({
            label: item.TAG_NAME,
            value: item.TAG_ID,
          });
        });
        vm.FormSchema[4].Data[0].value = vm.TagsSelectedArray;
      });
    },
    FetchData() {
      var vm = this;
      vm.isLoading = true;
      var params = `Id=${vm.AseetTypeIde}`;
      assetDataService.AssetGetById(params).then((response) => {
        if (response.data != null) {
          if (response.data.length > 0) {
            vm.TypeData = response.data;
            vm.FormSchema[0].Data[0].value = vm.TypeData[0].ASSET_TYPE;
            vm.FormSchema[0].Data[1].value = vm.TypeData[0].STATUS_ID;
            vm.ProductTypeCode = vm.TypeData[0].TYPE_CODE;
            vm.FormSchema[0].Data[3].value =
              vm.TypeData[0].IS_CONTAINER == 1 ? true : false;
            if (vm.TypeData[0].IS_CONTAINER == 1) {
              vm.IsShowCompartments = true;
              vm.TypeData.forEach((item, indx) => {
                vm.tableRows.push({
                  InputContainervalue: item.CONTAINER_NAME,
                });
                // vm.tableRows[indx].InputContainervalue=item.CONTAINER_NAME;
              });
            }
            if (
              vm.TypeData[0].ATTACHMENT_PATH != null &&
              vm.TypeData[0].ATTACHMENT_PATH != ""
            ) {
              vm.FormSchema[1].Data[0].config.showSelectedFiles[0].attachmentPath =
                vm.TypeData[0].ATTACHMENT_PATH;
              vm.FormSchema[1].Data[0].config.showSelectedFiles[0].attachmentName =
                vm.TypeData[0].ATTACHMENT_PATH.split("/")[7];
            } else {
              delete vm.FormSchema[1].Data[0].config.showSelectedFiles;
            }

            vm.FormSchema[2].Data[0].value = vm.TypeData[0].DESCRIPTION;
            vm.FormSchema[3].Data[0].value =
              vm.TypeData[0].IS_ASSET == 1 ? true : false;
            vm.FormSchema[3].Data[1].value =
              vm.TypeData[0].IS_CONSUMABLE == 1 ? true : false;
            vm.FormSchema[3].Data[2].value =
              vm.TypeData[0].IS_LICENSE == 1 ? true : false;
            vm.FormSchema[3].Data[3].value =
              vm.TypeData[0].IS_SPARE == 1 ? true : false;
            vm.FormSchema[5].Data[0].value =
              vm.TypeData[0].IS_TIME_N_ATTENDANCE == 1 ? true : false;
            vm.FormSchema[5].Data[1].value =
              vm.TypeData[0].IS_CAMERA == 1 ? true : false;
            vm.FormSchema[5].Data[2].value =
              vm.TypeData[0].IS_RFID == 1 ? true : false;
            vm.FormSchema[5].Data[3].value =
              vm.TypeData[0].IS_GPS_TRACKING == 1 ? true : false;

            vm.FormSchema[6].Data[0].value =
              vm.TypeData[0].IS_TRANSFERABLE == 1 ? true : false;
            vm.FormSchema[6].Data[1].value =
              vm.TypeData[0].IS_SELF_CHECKOUT == 1 ? true : false;
            var selfieEnable =
              vm.TypeData[0].IS_SELF_CHECKOUT == 1 ? true : false;
            if (selfieEnable == true) {
              vm.FormSchema[6].Data[2].visibility = true;
              vm.FormSchema[6].Data[2].value = vm.TypeData[0].is_selfie_enable;
            } else {
              vm.FormSchema[6].Data[2].visibility = false;
            }

            vm.FormSchema[6].Data[3].value =
              vm.TypeData[0].IS_EMPLOYEE_ACCEPTANCE == 1 ? true : false;
            vm.FormSchema[6].Data[4].value =
              vm.TypeData[0].IS_ENABLE_TERMS == 1 ? true : false;
            vm.FormSchema[6].Data[5].value =
              vm.TypeData[0].IS_GPS == 1 ? true : false;
            vm.FormSchema[6].Data[6].value =
              vm.TypeData[0].IS_FLEET_MANAGEMENT == 1 ? true : false;
            vm.FormSchema[6].Data[7].value =
              vm.TypeData[0].IS_PENALTY == 1 ? true : false;
            var statusEnable = vm.TypeData[0].IS_PENALTY == 1 ? true : false;
            if (statusEnable == true) {
              vm.FormSchema[6].Data[8].visibility = true;
              vm.OnPenaltyOnStatus(true);
              const penaltyTypes =
                vm.TypeData[0].PENALTY_TYPE !== "" &&
                  vm.TypeData[0].PENALTY_TYPE !== null
                  ? vm.TypeData[0].PENALTY_TYPE.includes(",")
                    ? vm.TypeData[0].PENALTY_TYPE.split(",")
                    : vm.TypeData[0].PENALTY_TYPE
                  : [];
              const selectedValue =
                penaltyTypes.length > 1 ? penaltyTypes : null;
              vm.FormSchema[6].Data[8].value = selectedValue;
            } else {
              vm.FormSchema[6].Data[8].visibility = false;
            }
            vm.FormSchema[6].Data[9].value =
              vm.TypeData[0].IS_PENALTY_ON_OVERDUE == 1 ? true : false;
            var grace =
              vm.TypeData[0].IS_PENALTY_ON_OVERDUE == 1 ? true : false;

            if (grace == true) {
              vm.FormSchema[6].Data[10].visibility = true;
              vm.GracePeriod = vm.TypeData[0].GRACE_PERIOD;
            } else {
              vm.FormSchema[6].Data[10].visibility = false;
            }

            vm.FormSchema[7].Data[0].value =
              vm.TypeData[0].IS_INSURANCE_POLICY == 1 ? true : false;
            vm.FormSchema[7].Data[1].value =
              vm.TypeData[0].IS_CALIBRATION == 1 ? true : false;
            vm.FormSchema[7].Data[2].value =
              vm.TypeData[0].IS_ITEM_COST == 1 ? true : false;
            vm.FormSchema[7].Data[3].value =
              vm.TypeData[0].IS_WARRANTY == 1 ? true : false;
            vm.FormSchema[7].Data[4].value =
              vm.TypeData[0].IS_DEPRECIABLE == 1 ? true : false;
            vm.isLoading = false;
          } else {
            vm.TypeData = [];
            vm.isLoading = false;
          }
        } else {
          vm.isLoading = false;
        }
      });
    },
  },
};
</script>

