<template>
    <section>
        <loader :is-visible="isLoading"></loader> 
        <div :class="reqFrom=='InitialSetup' ? 'dynamiccontent_base' : ''">
            <dynamic-form lang="en" ref="AddStage" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="SaveSubmit"></dynamic-form>
        </div>
        <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">     
            <div class="row">
                <div class="col-lg-12 text-right">
                    <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                                v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous') }}</button>                                               
                    <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4" @click="Skip()"
                                v-if="tabCount > currentTabIndex">{{ $t("Skip") }}</button>
                    <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip" data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                                @click="SaveSubmit" style="margin-left: 5px" v-if="tabCount > currentTabIndex">
                                <em class="fa fa-save pr-2"></em> {{ $t("SaveAndNext") }}</button>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from './../../../../services/AdvanceSetupModuleService'
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom:''
    },
    data(){
        return{
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('StageName'),
                            name: "StageName",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('PhaseName'),
                            name: "PhaseName",
                            value: "",
                            validationRules: {
                                "required": false
                            },
                            config:{
                                options: [],
                            },
                            disabled:false                         
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                    {
                            astype: "SelectField",
                            label: this.$t('StageType'),
                            name: "StageType",
                            value: "",
                            placeholder: this.$t('Stage'),
                            validationRules: {
                                "required": false
                            },
                            config:{
                                options: [],
                            },
                            disabled: true                         
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ value: "1001", name: "Active" }, { value: "1002", name: "InActive" }]
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            validationRules: "",
                        },
                    ],
                }
            ],
        }
    },
    created:async function(){
        var vm=this;
        await vm.GetDdlRecords_Phase();
        await vm.GetDdlRecords_PhaseType();      
    },
    methods:{
        Skip: function () {
            var vm = this;
            vm.$parent.NextTab(true);
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        GetDdlRecords_Phase: async function () {
            var vm = this;
            vm.CountryData = [];
            var params = `ids=&moduleName=&type=PHASE_NAME&search=&refId=`
            await DataService.GetDDLData(params).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.FormSchema[0].Data[1].config.options = response.data.DATA;
                }
                vm.$refs.AddStage.UpdateKeyValue();
            })
        },
        GetDdlRecords_PhaseType: async function () {
            var vm = this;
            vm.CountryData = [];
            vm.FormSchema[1].Data[0].config.options=[];
            var params = `ids=&moduleName=&type=PHASE_TYPE&search=&refId=`
            await DataService.GetDDLData(params).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.FormSchema[1].Data[0].config.options = response.data.DATA;
                    response.data.DATA.forEach(function (itm) {
                      if(itm.name=='Hiring') {
                        vm.moduleId=itm.value;
                      } else{
                        vm.FormSchema[1].Data[0].value='';
                      }
                });
                }
                vm.$refs.AddStage.UpdateKeyValue();
                vm.FormSchema[1].Data[0].value=vm.moduleId;
            })
        },
        SaveSubmit() {
            var vm = this;
            vm.isLoading = true;
            vm.$refs.AddStage.validateForm.validate().then(result => {
                if(result){                    
                    var jsonobj = {
                        StageId: 0,
                        StageName:vm.FormSchema[0].Data[0].value,
                        Description: vm.FormSchema[2].Data[0].value,
                        StatusId: vm.FormSchema[1].Data[1].value,
                        PhaseId: vm.FormSchema[0].Data[1].value,
                        StageType: vm.FormSchema[1].Data[0].value
                    };
                    var params = JSON.stringify(jsonobj);
                    DataService.AddInterviewStage(params).then((response) => {
                        vm.isLoading = false;
                        if (response.data.status == 'Success') {                          
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Stage")]), "success", true, vm.$t("Alert"));
                            vm.$parent.NextTab();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("Stage")]), "failure", true, vm.$t("Alert"));                           
                        }                        
                    });
                }
            })
            vm.isLoading = false; 
        }
    }
}
</script>