<template>
    <section class="create-user initialsetuptabsbase">
        <loader :is-visible="isLoading"></loader>
        <div class="dynamiccontent_base ">
            <div class="col-md-12 p-0">
                <div class="tab-content">
                    <div class="row">
                        <!-- <div class="col-lg-12 text-right mb-3 new_initialsetup_btnsbase">
                            <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4"
                                @click="Skip()" v-if="tabCount == currentTabIndex" >{{ $t("Skip") }}
                            </button>
                        </div> -->
                    </div>
                    <h4 class="text-center py-2 d-none">Create Employee Account For Monitoring</h4>
                    <div class="col-sm-12 text-center mt-0 px-2 mb-4 float-left d-none">
                        <div class="user_type_choice mt-3 mb-3 inisetups">
                            <div class="btn-group btn-group-toggle" data-toggle="buttons">
                                <label class="btn btn_cstm active position-relative ">
                                    <div class="popover-arrowss"></div>
                                    <input autocomplete="off" checked="checked" v-on:click="OnChangeUserType(3)"
                                        class="cls_usertype" data-val="true" type="radio" value="3">
                                    <span class="d-block">
                                        <em class="fa fa-user"></em>
                                    </span>
                                    <span class="pt-1 pl-2">{{ $t('NormalUser') }}</span>
                                </label>
                                <label class="btn btn_cstm  position-relative d-none">
                                    <div class="popover-arrowss"></div>
                                    <input autocomplete="off" class="cls_usertype" v-on:click="OnChangeUserType(2)"
                                        name="user_type_id" type="radio" value="2">
                                    <span class="d-block">
                                        <em class="fa fa-user"></em>
                                    </span>
                                    <span class="pt-1 pl-2">{{ $t('ProjectManager') }}</span>
                                </label>
                                <label class="btn btn_cstm  position-relative d-none">
                                    <div class="popover-arrowss"></div>
                                    <input autocomplete="off" class="cls_usertype" v-on:click="OnChangeUserType(1)"
                                        name="user_type_id" type="radio" value="1">
                                    <span class="d-block">
                                        <em class="fa fa-user"></em>
                                    </span>
                                    <span class="pt-1 pl-2">{{ $t('CompanyAdmin') }}</span>
                                </label>
                            </div>
                        </div>
                        <p class="newdiff_bg">{{ UserTypeData.content }}</p>
                    </div>
                    <div class="col-sm-12 mt-0 px-2 float-left">
                        <div class="row">
                            <div class="col-lg-12 mb-3">
                                <div class="heading-border mt-2">
                                    <h5 class="h5"><span>Create Employee Account For Monitoring</span></h5>
                                </div>
                            </div>
                        </div>
                        <dynamic-form ref="addnewuser" lang="en" :buttons="false" :schema="FormSchema">
                            <template v-slot:tgslot-user_profile="{ data }">
                                <div class="wiz-upimg initialsetup">
                                    <div class="circle">
                                        <img id="uImg" src="/Content/images/DefaultImages/avatar-icon.jpg"
                                            v-if="base64String == ''" />
                                        <img :src="'data:image/gif;base64,' + base64String" loading="lazy" v-else />
                                    </div>
                                    <div class="p-image">
                                        <span class="wiz-button" v-on:click="OnChooseProfile()">
                                            {{ GetLocalizedValue('ChoosePicture') }}</span>
                                        <input type="file" ref="fileInput" v-on:change="GetBase64String($event)"
                                            class="wizerd-upload" name="flavatar" id="flavatar"
                                            accept=".jpeg,.jpg,.jpe,.gif,.bmp,.dib,.ico,.png,.tif,.tiff" />
                                    </div>
                                </div>
                            </template>
                        </dynamic-form>
                    </div>
                    <div
                        class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left initialsetupfolder mt-4 d-flex flex-wrap">
                        <div class="mover-panel-box leftpanel float-left d-none" id="divtickettupelist">
                            <div class="heading-border">
                                <h5 class="h5"><span>{{ $t('allLicenses') }}</span>
                                    <!-- <a class="btn btn-primary" v-on:click="AssignLicense()">{{$t('Assign')}}</a> -->
                                </h5>
                            </div>
                            <div v-if="AllLicenses.length > 0" class="managefolder py-3 allLicense">
                                <div class="col-12 float-left border px-3 py-2 text-right bg-light">
                                    <div class="custom-control custom-checkbox">
                                        <input class="custom-control-input chkAll" type="checkbox" value=""
                                            v-on:change="onChange($event, 'allLicense')" name="chkAllLicense"
                                            id="chkAllLicense" style="display: none;">
                                        <label class="custom-control-label universal-custom-control-label pt-1"
                                            style="float: right;" for="chkAllLicense"></label>
                                    </div>
                                </div>
                                <ul class="list-group float-left w-100 scroll"
                                    style="max-height: 400px; min-height: 200px; overflow-y: auto;">
                                    <li v-for="item in AllLicenses" class="list-group-item single-action px-3">
                                        <strong>{{ item.module_name + ' (' + item.license_count + ')' }}</strong>
                                        <div class="custom-control custom-checkbox float-right">
                                            <input v-on:click="onSelectLicense(item, 'allLicense')"
                                                class="chkItems custom-control-input" type="checkbox"
                                                v-bind:disabled="item.license_count == 0 || item.is_disabled"
                                                :value="item.module_id" v-model="checkedAllLicenses"
                                                :name="item.module_name" :id="'chk_' + item.module_id"
                                                style="display: none;">
                                            <label class="custom-control-label universal-custom-control-label pt-1"
                                                :for="'chk_' + item.module_id"></label>
                                        </div>
                                    </li>
                                    <!-- <li v-if="AllLicenses.length==0" class="text-danger text-center mt-2">{{$t('NoRecordfound')}}</li> -->
                                </ul>
                            </div>
                            <div v-if="AllLicenses.length == 0" class="text-danger text-center mt-2">
                                {{ $t('NoRecordfound') }}</div>
                        </div>
                        <div class="mover-controls d-none">
                            <button type="button" data-toggle-tooltip="tooltip" title="Move Right"
                                v-on:click="AssignLicense()">
                                <em aria-hidden="true" class="fa fa-angle-double-down fa-2x d-md-none d-none"></em>
                                <em aria-hidden="true"
                                    class="fa fa-angle-double-right fa-2x d-none d-md-block d-block"></em>
                            </button>
                            <!-- <button type="button" data-toggle-tooltip="tooltip" title="Move Right">
                                    <em aria-hidden="true" class="fa fa-angle-down fa-2x d-md-none d-none"></em>
                                    <em aria-hidden="true" class="fa fa-angle-right fa-2x d-none d-md-block d-block"></em>
                                </button>
                                <button type="button" data-toggle-tooltip="tooltip" title="Move Left">
                                    <em aria-hidden="true" class="fa fa-angle-up fa-2x d-md-none d-none"></em>
                                    <em aria-hidden="true" class="fa fa-angle-left fa-2x d-none d-md-block d-block"></em>
                                </button> -->
                            <button type="button" data-toggle-tooltip="tooltip" title="Move Left"
                                v-on:click="RemoveLicense()">
                                <em aria-hidden="true" class="fa fa-angle-double-up fa-2x d-md-none d-none"></em>
                                <em aria-hidden="true"
                                    class="fa fa fa-angle-double-left fa-2x d-none d-md-block d-block"></em>
                            </button>
                        </div>

                        <div class="mover-panel-box rightpanel float-left d-none" id="divAssignedLicence">
                            <div class="heading-border">
                                <h5 class="h5"><span>{{ $t('addedLicenses') }}</span>
                                    <!-- <a class="btn btn-danger" v-on:click="RemoveLicense()">{{$t('Remove')}}</a> -->
                                </h5>
                            </div>
                            <div v-if="AddedLicenses.length > 0" class="managefolder py-3 addedLicense">
                                <div class="col-12 float-left border px-3 py-2 text-right bg-light">
                                    <div class="custom-control custom-checkbox">
                                        <input class="custom-control-input chkAll" type="checkbox" value=""
                                            v-on:change="onChange($event, 'addedLicense')" name="chkAllAddedLicense"
                                            id="chkAllAddedLicense" style="display: none;">
                                        <label class="custom-control-label universal-custom-control-label pt-1"
                                            style="float: right;" for="chkAllAddedLicense"></label>
                                    </div>
                                </div>
                                <ul class="list-group float-left w-100 scroll"
                                    style="max-height: 400px; min-height: 200px; overflow-y: auto;">
                                    <li v-for="item in AddedLicenses"
                                        class="list-group-item list_username list_img single-action px-3">
                                        <strong>{{ item.module_name }}</strong>
                                        <div class="custom-control custom-checkbox float-right">
                                            <input v-on:click="onSelectLicense(item, 'addedLicense')"
                                                class="chkItems custom-control-input" type="checkbox"
                                                :value="item.module_id" v-model="checkedAddedLicenses"
                                                :name="'added_' + item.module_name" :id="'added_' + item.module_id"
                                                style="display: none;">
                                            <label class="custom-control-label universal-custom-control-label pt-1"
                                                :for="'added_' + item.module_id"></label>
                                        </div>
                                    </li>
                                    <!-- <li v-if="AddedLicenses.length==0" class="text-danger text-center mt-2">{{$t('NoRecordfound')}}</li> -->
                                </ul>
                            </div>
                            <div v-if="AddedLicenses.length == 0" class="text-danger text-center mt-2">
                                {{ $t('NoRecordfound') }}</div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
        <div class="mainfooter_app">
            <div class="col-lg-12 text-right mt-4 ">
                <button title="Previous" class="btn btn-warning float-left px-4" @click="PreviousButton()"
                    v-if="currentTabIndex > 1">
                    <em class="fa fa-chevron-left pr-2"></em>
                    {{ $t("Previous") }}
                </button>
                <button title="Skip" class="btn skip-btn-clr formbtn px-4" @click="Skip()"
                    v-if="tabCount > currentTabIndex">
                    {{ $t("Skip") }}
                </button>
                <button type="button" class="btn btn-success  formbtn px-4 ml-2" @click="SaveSubmit('SaveAndNew')"
                    v-if="tabCount > currentTabIndex">
                    <em class="fa fa-save pr-2 font-16"></em>
                    {{ $t("SaveAndNew") }}
                </button>
                <button type="button" class="btn next formbtn px-4 ml-2" @click="SaveSubmit('SaveAndNext')"
                    v-if="tabCount > currentTabIndex">
                    {{ $t("SaveAndNext") }}
                    <em class="fa fa-chevron-right pl-2"></em>
                </button>
                <button type="button" id="aGroupTicketSave" @click="SaveSubmit('SaveAndClose')" title=""
                    data-toggle-tooltip="tooltip" data-original-title="Save"
                    class="popupgreenbtn btn btn-success formbtn widthhalf pr-2" style="margin-left: 5px;"
                    v-if="tabCount == currentTabIndex">
                    <i class="fa fa-save pr-2"></i> {{ $t('SaveAndClose') }}
                </button>
                <button title="Finish" class="btn btn-danger formbtn widthhalf px-4"
                    @click="SaveSubmit('SaveAndFinish')" v-if="reqFrom == 'InitialSetup'">
                    {{ $t("SaveAndFinish") }}
                </button>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../../services/DataService';
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        isCompleted: false
    },
    data() {
        return {
            isLoading: false,
            userTypeId: 3,
            profilePicture: '',
            license_ids: [],
            UserTypeData: null,
            AllLicenses: [],
            AddedLicenses: [],
            fileData: [],
            checkedAllLicenses: [],
            checkedAddedLicenses: [],
            base64String: '',

            FormSchema: [
                {
                    layoutType: "single",
                    groupLayoutType: "single",
                    group_name: "",
                    class: "choosepicture",
                    Data: [
                        {
                            astype: "SlotField",
                            label: "",
                            name: "user_profile",
                            value: ""
                        }
                    ]
                },
                {
                    layoutType: "double",
                    groupLayoutType: "single",
                    //group_name: "Account Settings",
                    //group_display_name: this.$t("AccountSettings"),
                    class: "fillupform",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t("FirstName"),
                            name: "first_name",
                            value: "",
                            placeholder: this.$t("FirstName"),
                            validationRules: {
                                required: true,
                                max: 100
                            }
                        },
                        {
                            astype: "TextField",
                            label: this.$t("LastName"),
                            name: "last_name",
                            value: "",
                            placeholder: this.$t("LastName"),
                            validationRules: {
                                required: true,
                                max: 100
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t("Email"),
                            name: "email_id",
                            value: "",
                            placeholder: this.$t("Email"),
                            validationRules: {
                                required: true,
                                email: true
                            },
                        },
                        {
                            astype: "ToggleField",
                            label: this.$t('enableLogin'),
                            name: "is_enable_login",
                            value: true,
                            validationRules: "",
                        },
                        {
                            astype: "PasswordField",
                            label: this.$t("Password"),
                            name: "password",
                            value: "",
                            placeholder: this.$t("Password"),
                            validationRules: {
                                required: true,
                            },
                            config: {
                                onKeyUp: this.ValidateConfirmPassword
                            }
                        },
                        {
                            astype: "PasswordField",
                            label: this.$t("ConfirmPassword"),
                            name: "confirm_password",
                            value: "",
                            placeholder: this.$t("ConfirmPassword"),
                            validationRules: {
                                required: true,
                            },
                            config: {
                                onKeyUp: this.ValidateConfirmPassword
                            }
                        }
                    ]
                },
            ],
            buttons: [],
        }
    },
    async created() {
        var vm = this;
        await vm.OnChangeUserType(3);
        vm.FormSchema.forEach((group, gi) => {
            group.Data.forEach((field, fi) => {

                if (field.name == 'is_enable_login') {
                    field.value = true;
                    return;
                }
                field.value = "";
            })
        })
      
    },
    methods: {
        ValidateConfirmPassword() {
            var vm = this;
            var getPasswordvalue = undefined;
            var getConfirmPasswordvalue = undefined;
            vm.FormSchema.forEach(item => {
                item.Data.forEach(x => {
                    if (x.name == "password") {
                        getPasswordvalue = x;
                    } else if (x.name == "confirm_password") {
                        getConfirmPasswordvalue = x;
                    }
                })
            })
            if (getPasswordvalue && getConfirmPasswordvalue != undefined) {
                if (getPasswordvalue.value == getConfirmPasswordvalue.value) {
                    getConfirmPasswordvalue.validationRules = {
                        "confirmed": false,
                        "required": true
                    }
                } else {
                    getConfirmPasswordvalue.validationRules = {
                        "confirmed": true,
                        "required": true
                    }
                }
            }
        },
        AssignLicense() {
            debugger
            var vm = this;
            var checkboxes = document.querySelector('.allLicense .chkAll');
            var AlreadyAssigned = [];

            // Assuming vm.AllLicenses is defined and populated
            let noAvailableLicense = vm.AllLicenses.every(x => x.license_count === 0);
            if (noAvailableLicense) {
                vm.ShowAlert(vm.$t("DoNotRemainLicense"), "warning", true, vm.$t("Alert"));
                return false;
            }

            if (vm.checkedAllLicenses.length > 0) {
                vm.checkedAllLicenses.forEach((parentItem, parentIndex) => {
                    vm.AllLicenses.forEach((item, index) => {
                        if (parentItem == item.module_id) {
                            if (!(vm.AddedLicenses.filter(x => x.module_id == parentItem).length > 0)) {
                                item.license_count = item.license_count - 1;
                                vm.AddedLicenses.push(item);
                            }
                            else {
                                AlreadyAssigned.push(item.module_name);
                            }
                        }
                    })
                });

                // Sort AddedLicenses by module_name in ascending order
                vm.AddedLicenses.sort((a, b) => a.module_name.localeCompare(b.module_name));

                vm.checkedAllLicenses = [];
                checkboxes.checked = false;

                if (AlreadyAssigned.length > 0) {
                    var msg = AlreadyAssigned.join(',');
                    vm.ShowAlert(vm.$t("ModulesAlreadyAssigned", [msg]), "warning", true, vm.$t("Alert"));
                }
            } else {
                vm.ShowAlert(vm.$t("EmpSharingSaveValidationMsg"), "warning", true, vm.$t("Alert"));
            }
        },
        RemoveLicense() {
            var vm = this;
            var checkboxes = document.querySelector('.addedLicense .chkAll');
            if (vm.checkedAddedLicenses.length > 0) {
                vm.checkedAddedLicenses.forEach((item, index) => {
                    const indexL = vm.AddedLicenses.findIndex((x) => x.module_id == item);
                    if (indexL !== -1) {
                        vm.AddedLicenses.splice(indexL, 1);
                        vm.AllLicenses.filter(x => x.module_id == item)[0].license_count += 1;
                    }
                });
            } else {
                vm.ShowAlert(vm.$t("AtLeastOneLicense"), "warning", true, vm.$t("Alert"));
            }
            vm.checkedAddedLicenses = [];
            checkboxes.checked = false;
        },
        onChange(event, tab) {
            debugger
            var vm = this;
            if (tab == 'allLicense') {
                vm.checkedAllLicenses = [];
                const checkboxes = document.querySelectorAll('.allLicense .chkItems:not(:disabled)');
                checkboxes.forEach(checkbox => {
                    checkbox.checked = event.target.checked;
                    if (checkbox.checked && checkbox.value) {
                        vm.checkedAllLicenses.push(checkbox.value);
                    } else {
                        vm.checkedAllLicenses = [];
                    }
                })
            } else {
                const checkboxes = document.querySelectorAll('.addedLicense .chkItems');
                checkboxes.forEach(checkbox => {
                    checkbox.checked = event.target.checked;
                    if (checkbox.checked && checkbox.value) {
                        vm.checkedAddedLicenses.push(checkbox.value);
                    }
                    else {
                        vm.checkedAddedLicenses = [];
                    }
                })
            }
        },
        onSelectLicense(item, tab) {
            if (tab == 'allLicense') {
                var checkboxAll = document.querySelector('.allLicense .chkAll');
                const checkboxes = document.querySelectorAll('.allLicense .chkItems:not(:disabled)');

                const checkedCount = Array.from(checkboxes).filter(checkbox => checkbox.checked).length;
                if (checkboxes.length > checkedCount) {
                    checkboxAll.checked = false;
                }
                else {
                    checkboxAll.checked = true;
                }
            }
            else {
                var checkboxAll = document.querySelector('.addedLicense .chkAll');
                const checkboxes = document.querySelectorAll('.addedLicense .chkItems:not(:disabled)');

                const checkedCount = Array.from(checkboxes).filter(checkbox => checkbox.checked).length;
                if (checkboxes.length > checkedCount) {
                    checkboxAll.checked = false;
                }
                else {
                    checkboxAll.checked = true;
                }
            }

        },
        Skip: function () {
            var vm = this;
            vm.$emit("nextTab");
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        SaveSubmit(btnType) {
            debugger
            var vm = this;
            vm.isLoading = true;
            vm.$refs.addnewuser.validateForm.validate().then(async (result) => {
                if (result) {
                    if (vm.AddedLicenses.length == 0) {
                        vm.ShowAlert(vm.$t("EmpSharingSaveValidationMsg"), "warning", true, vm.$t("Alert"));
                        vm.isLoading = false;
                        return false;
                    }
                    else {
                        vm.AddedLicenses.forEach(x => {
                            vm.license_ids.push(x.module_id);
                        })
                    }
                    await vm.UploadProfilePicture();

                    var jsonObj = {
                        first_name: vm.FormSchema[1].Data[0].value,
                        last_name: vm.FormSchema[1].Data[1].value,
                        email_id: vm.FormSchema[1].Data[2].value,
                        is_enable_login: vm.FormSchema[1].Data[3].value,
                        license_ids: vm.license_ids.join(','),
                        user_type_id: vm.userTypeId,
                        profile_picture: vm.profilePicture,
                        password: vm.FormSchema[1].Data[4].value
                    };

                    var objectPostString = JSON.stringify(jsonObj);
                    DataService.AddUserInitialSetup(objectPostString).then(async response => {
                        vm.isLoading = false;
                        if (response.data && response.data.toLowerCase() == 'success') {
                            vm.$emit("updateProgress", vm.currentTabIndex, true);
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("User")]), "success", true, vm.$t("Alert"));
                            if (btnType == 'SaveAndNew') {
                                vm.resetForm();
                                await vm.GetLicenseList();
                            }
                            else if (btnType == 'SaveAndNext') {
                                vm.Skip();
                            }
                            else if (btnType == 'SaveAndFinish') {
                                vm.Finish();
                            }
                            else if (btnType == 'SaveAndClose') {
                                vm.Finish();
                            }
                        }
                        else if (response.data && response.data.toLowerCase() == 'alreadyexists') {
                            vm.ShowAlert(vm.$t('ThisEmailAlreadyExist'), "warning", true, vm.$t("Alert"));
                        }
                        else {
                            vm.ShowAlert(vm.$t('Something_Wrong'), "danger", true, vm.$t("Alert"));
                        }
                    });
                }
                else {
                    vm.isLoading = false;
                }
            })
        },
        resetForm() {
            debugger
            var vm = this;
            vm.FormSchema.forEach((group, gi) => {
                group.Data.forEach((field, fi) => {

                    if (field.name == 'is_enable_login') {
                        field.value = true;
                        return;
                    }
                    field.value = "";
                })
            })
            vm.base64String = '';
            vm.profilePicture = '';
            vm.checkedAllLicenses = [];
            //vm.userTypeId=3;
            vm.OnChangeUserType(vm.userTypeId);
            vm.$refs.addnewuser.validateForm.reset();// Reset the form fields
            //vm.$refs.addnewuser.validateForm = {}; // Clear any validation messages     
        },
        async GetLicenseList() {
            debugger
            var vm = this;
            vm.isLoading = true;
            var param = `userTypeId=${vm.userTypeId}`;
            await DataService.GetLicenseList(param).then(async response => {
                vm.isLoading = false;
                if (response.data.data) {
                    vm.AllLicenses = [];
                    vm.AllLicenses = response.data.data;
                }
                await vm.AutoAsssignLicense();
            });
        },
        async AutoAsssignLicense() {
            debugger
            var vm = this;
            if (vm.AllLicenses.length > 0) {
                vm.AddedLicenses = [];
                vm.AllLicenses.forEach(x => {
                    if (x.license_count > 0) {
                        x.license_count = x.license_count - 1;
                        vm.AddedLicenses.push(x);
                    }
                })
            }
        },
        async OnChangeUserType(userTypeId) {
            debugger
            var vm = this;
            var obj = {};
            if (userTypeId == 1) {
                obj.userType = vm.$t("CompanyAdmin");
                obj.class = 'CA';
                obj.content = vm.$t("CompanyAdminContent");
            } else if (userTypeId == 2) {
                obj.userType = vm.$t("ProjectManager");
                obj.class = 'PM';
                obj.content = vm.$t("ProjectManagerContent");
            } else {
                obj.userType = vm.$t("NormalUser");
                obj.class = 'NU'
                obj.content = vm.$t("NormalUserContent");
            }
            vm.AddedLicenses = [];
            vm.userTypeId = userTypeId;
            vm.UserTypeData = obj;
            await vm.GetLicenseList();
        },
        OnChooseProfile() {
            var vm = this;
            vm.$refs.fileInput.click();
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];
                var files = evt.target.files; // FileList object
                for (var i = 0; i < files.length; i++) {
                    var f = files[i];
                    var reader = new FileReader();
                    // Closure to capture the file information.
                    reader.onload = (function (theFile) {
                        return function (e) {
                            var binaryData = e.target.result;
                            //Converting Binary Data to base 64CLASSIC_EDITOR
                            var base64StringEncoded = window.btoa(binaryData);
                            vm.base64String = base64StringEncoded;
                            //showing file converted to base64

                            var fileName = f.name;
                            vm.fileName = fileName;
                            var n = fileName.lastIndexOf(".");
                            var extensionName = fileName.substring(n + 1, fileName.length);

                            vm.fileExt += "." + extensionName;
                            var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                            var fileSize = f.size;
                            vm.fileSize = fileSize;
                            var fileObj = {
                                attachement_mime: attachemnetMime,
                                fileName: fileName,
                                userId: vm.GetUserInfo.ID,
                                companyId: vm.GetUserInfo.CompanyId,
                                id: 0,
                                moduleName: "initialsetup",
                                subModuleCode: "",
                                containerName: vm.GetUserInfo.ContainerName,
                                base64String: base64StringEncoded,
                                attachement_path: ''
                            };

                            vm.fileData.push(fileObj);
                        };
                    })(f);
                    // Read in the image file as a data URL.
                    reader.readAsBinaryString(f);
                }
            }
            catch (ex) {
            }
        },
        async UploadProfilePicture() {
            var vm = this;
            vm.isLoading = true;
            var param = JSON.stringify(vm.fileData);
            if (vm.fileData.length > 0) {
                await DataService.UploadFile(param).then(response => {
                    if (response.data) {
                        if (response.data[0].Name.length > 0) {
                            var img = response.data[0].Name.split('/')[response.data[0].Name.split('/').length - 1]
                            vm.profilePicture = img;
                        }
                    }
                    vm.isLoading = false;
                })
            }
        },
        Finish: async function () {
            debugger
            var vm = this;
            await vm.UpdateUserSettingConfig('HideContextmenu');
            await vm.UpdateUserSettingConfig('initialstartup');
            window.location.href = '/User/Home';
        },
        async UpdateUserSettingConfig(type) {
            let url = `status=true&type=${type}`
            await DataService.UpdateUserSettingConfig(url).then((response) => {

            });
        }
    }
}
</script>
