.initialsetuptabsbase .mainfooter_app button.next {
    width: 220px;
    height: 50px;
    border: none;
    outline: none;
    color: #fff;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
}

.initialsetuptabsbase .mainfooter_app button.next:before {
    content: '';
    background: linear-gradient(45deg, #ff358a, #363f4d, #fea088);
    position: absolute;
    top: -2px;
    left: -2px;
    background-size: 400%;
    z-index: -1;
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}

.initialsetuptabsbase .mainfooter_app button.next:active {
    color: #000
}

.initialsetuptabsbase .mainfooter_app button.next:active:after {
    background: transparent;
}

.initialsetuptabsbase .mainfooter_app button.next:before {
    opacity: 1;
}

.initialsetuptabsbase .mainfooter_app button.next:hover:before {
    background: #1c94ec;
}

.initialsetuptabsbase .mainfooter_app button.next:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes glowing {
    0% {
        background-position: 0 0;
    }

    50% {
        background-position: 400% 0;
    }

    100% {
