<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div :class="reqFrom=='InitialSetup' ? 'dynamiccontent_base' : ''">
            <dynamic-form lang="en" ref="AddIndustry" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="SaveSubmit"></dynamic-form>
        </div>
        <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">
            <div class="row">
                <div class="col-lg-12 text-right mt-4">
                    <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                                v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous') }}</button>                                               
                    <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4" @click="Skip()"
                                v-if="tabCount > currentTabIndex">{{ $t("Skip") }}</button>
                    <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip" data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                                @click="SaveSubmit" style="margin-left: 5px" v-if="tabCount > currentTabIndex">
                                <em class="fa fa-save pr-2"></em> {{ $t("SaveAndNext") }}</button>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from './../../../../services/AdvanceSetupModuleService'
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom:''
    },
    data(){
        return{
            isLoading: false,
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [{
                    layoutType: "double",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('Industry'),
                        name: "Industry",
                        value: "",
                        placeholder: this.$t('Enter Industry'),
                        validationRules: {
                            "required": true
                        },
                        disabled: false,
                    }, 
                    {
                        astype: "SelectField",
                        label: this.$t('Icon'),
                        name: "Icon",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": false
                        },
                        config: {
                            options: [],
                        },
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "Description",
                        value: "",
                        placeholder: this.$t('Enter Description'),
                        validationRules: {
                            "required": false
                        },
                    }]
                }
            ],
            buttons: [              
            ],
            companyId: null,
            userId: null,
        }
    },
    created: function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;        
        vm.GetIndustryIcon();
    },
    methods:{ 
        GetIndustryIcon(){            
            var vm = this;
            var url=`id=`;
            DataService.GetIndustryIcons(url).then(function (response) {
                vm.IconData = response.data.DATA;                
                vm.IconData.forEach(item => {
                    vm.FormSchema[0].Data[1].config.options.push({
                        name: item.icon_name,
                        value: item.icon_value
                    })
                })
                vm.$refs.AddIndustry.UpdateKeyValue();
            })
        },       
        SaveSubmit(){
            var vm = this;
            vm.$refs.AddIndustry.validateForm.validate().then(result => {
                if(result){
                    var jsonobj = {
                        userId: vm.userId,
                        companyId: vm.companyId,
                        name: vm.FormSchema[0].Data[0].value,
                        description: vm.FormSchema[1].Data[0].value,
                        icondata: vm.FormSchema[0].Data[1].value,
                        parentid: null,
                        id: 0
                    };
                    var params = JSON.stringify(jsonobj);
                    DataService.SaveIndustry(params).then((response) => {           
                        if (response.data.status == "AlreadyExixts") {                    
                            vm.ShowAlert(vm.$t('AlreadyExists',[vm.$t('Industry')]), "failure", true,  vm.$t("Alert"));   
                        }  
                        if (response.data.status != "Success") {
                            vm.ShowAlert(vm.$t('AlreadyExists',[vm.$t('Industry')]), "failure", true,  vm.$t("Alert"));   
                        } else {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Industry')]), "success", true, vm.$t("Alert"));
                            vm.$parent.NextTab();
                        }                        
                    });
                }
            });
        },
        Skip: function () {
            var vm = this;
            vm.$parent.NextTab(true);
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
    }
}
</script>