<template>
    <section>
        <loader :is-visible="isLoading"></loader>       
        <div v-show="isShowAddTicketReason">
            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" ref="AssignTagForm" :mandatorytext='MandatoryString'
                            v-on:OnSubmit="OnSubmit"></dynamic-form>
        </div>
        <div v-show="!isShowAddTicketReason">
            <tg-list ref="tgList" :listType="['List']" :IsShowAction="true" :showCheckBox="false" ModuleName="Ticketing"
                SubModuleCode="Ticketing" IdentityColumn="ID"  v-on:ActionButtonClick="actionButtonClick"
                :ListData="ListingData" :HeaderData="Headers" :widgets="[]" :HeaderButtons="listheaderbuttons"
                :TotalRecords="TotalRecords" :RenderRowActionMethod="renderActions"
                :ListDataCallBackFunction="GetTicketDelayReason" :SortExp="SortExp" :SortBy="SortBy"
                :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod"
                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata"> 
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'status_name'">                       
                       <span v-if="data.row.status_id==1001" class="px-2 text-center bg-success text-white d-block">{{ data.row.status_name }}</span>
                       <span v-else class="px-2 text-center bg-danger text-white d-block">{{data.row.status_name}}</span>
                    </template>
                </template>               
            </tg-list>
        </div>
    </section>
</template>
<script>
import setupModuleService from "@/services/AdvanceSetupModuleService";
import DataService from "@/services/DataService";
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom:''
    },
    data(){
        return{
            isLoading:false,
            isShowAddTicketReason:false,
            TicketDelayReasonId:0,
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            CompanyId:null,
            Headers:[
                {
                    "COLUMN_NAME": "title",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Title",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "description",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Description",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "status_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Status",
                    "DISPLAY_ORDER": 3,
                    settings :{
                        isInSlot: true
                    }
            }],
            listheaderbuttons: [ {
                id: 'Add',
                title: 'Add',
                iconClass: 'fa fa-plus',
                callbackfunction: 'AddTicketDelayReason',
                isdisabled: false,
                isvisible: true        
            }],
            listActions: [{
                title:'Edit', 
                icon: 'fa fa-pencil action-icon',
                additionalSpan:'',
                callbackfunction:'EditTicketDelay'
            },
            {
                title:'Delete', 
                icon: 'text-danger fa fa-trash-o action-icon',
                additionalSpan:'',
                callbackfunction:'commonDeleteData'
            }],
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [               
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextField",                                                
                            label: this.$t('Title'),
                            name: "Title",
                            value: "",                            
                            placeholder: this.$t('Enter Title'),
                            validationRules: { "required": true, max: 1000 },
                        },
                        {
                            astype: "TextAreaField",                 
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",  
                            validationRules: { "required": false, max: 4000 },
                            config: {
                                rows:3
                            }
                        }                    
                                            
                    ]
                }                
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible:true
                },            
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseTicketDelay,
                },
            ],
        }
    },
    created:function(){
        var ths=this;
        ths.CompanyId = ths.GetUserInfo.CompanyId;
        ths.GetTicketDelayReason();
    },
    methods:{ 
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddTicketDelayReason":
                    this.AddTicketDelayReason();
                    break;                
            }
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.GetTicketDelayReason();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];            
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {                    
                    case 'Edit':
                        action.isVisible = row.company_id != null? true:false;
                        break;
                    case 'Delete':
                        action.isVisible = row.company_id != null? true:false;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },        
        actionButtonClick: function (callbackfunction, item){            
            switch (callbackfunction) {
                case 'EditTicketDelay':
                    this.EditTicketDelayResponse(item)
                    break;
                case 'commonDeleteData':
                    this.commonDeleteData(item.ID);
                    break;       
            }
        },       
        AddTicketDelayReason(){
            var ths=this;
            ths.TicketDelayReasonId=0;
            ths.FormSchema[0].Data[0].value='';
            ths.FormSchema[0].Data[1].value='';
            this.isShowAddTicketReason=true;
        },
        CloseTicketDelay(){
            this.isShowAddTicketReason=false;
            this.GetTicketDelayReason();
        },
        async GetTicketDelayReason(){
            var vueObj=this;
            vueObj.isLoading=true;
            var url=`CompanyId=${vueObj.CompanyId}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            await setupModuleService.FetchGetTicketDelayReasonList(url).then((response)=>{                
                vueObj.isLoading=false;
                if(response.data.result.DATA.length>0){
                    vueObj.ListingData = response.data.result.DATA;                   
                    vueObj.TotalRecords = response.data.result.DATA[0].total;
                    vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                    vueObj.CurrentPage = vueObj.PageNumber;
                }else{
                    vueObj.ListingData=[];
                }
            })
        },
        commonDeleteData(data){
            var vm = this;
            vm.isLoading = true;
            if (data != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Ticket Delay Response")), function () { 
                    var postJSON = {
                        ids: data,
                        moduleName: "Ticketing",
                        subModuleCode: "Ticketing",
                        refCode: 'Ticket_Delay_Response'
                    };                   
                    var objectPostString = JSON.stringify(postJSON);            
                    DataService.CommonCommonDelete(objectPostString).then(function (response) {
                        vm.isLoading = false;                        
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Ticket Delay Response')]), "success", true, vm.$t("Alert"));                            
                            vm.GetTicketDelayReason();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Ticket Delay Response')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        EditTicketDelayResponse(data){
            var ths=this;
            ths.TicketDelayReasonId=data.ID;
            ths.FormSchema[0].Data[0].value=data.title;
            ths.FormSchema[0].Data[1].value=data.description;
            this.isShowAddTicketReason=true;
        },
        sortdata: function(){},
        OnSubmit(){
            var ths = this;
            ths.isLoading=true;
            var obj = {
                TicketDelayReasonId: ths.TicketDelayReasonId,
                Title:ths.FormSchema[0].Data[0].value,
                Description:ths.FormSchema[0].Data[1].value
            }
            setupModuleService.SaveAddTicketDelayReason(JSON.stringify(obj)).then(function (response) {
                ths.isLoading=false;
                if(response.data.result.DATA.length>0){                     
                    if(response.data.result.DATA[0].VALUE=='UPDATE'){
                        ths.ShowAlert(ths.$t('UpdatedSuccess',[ths.$t('Ticket Delay Reason')]),"success", true, ths.$t("Alert"));
                        if (ths.tabCount > 1) {
                            ths.$emit("updateProgress", vm.currentTabIndex, true);
                        }
                        if (ths.tabCount == ths.currentTabIndex) {
                            ths.$emit("close");
                        }
                    }
                    else if(response.data.result.DATA[0].VALUE=='EXIST'){
                        ths.ShowAlert(ths.$t('AlreadyExists', [ths.$t('Ticket Delay Reason')]), "warning", true, ths.$t("Alert"));                        
                    }
                    else{                        
                        ths.ShowAlert(ths.$t('AddedSuccess',[ths.$t('Ticket Delay Reason')]), "success", true,  ths.$t("Alert"));
                        if (ths.tabCount > 1) {
                            ths.$emit("updateProgress", ths.currentTabIndex, true);
                        }
                        if (ths.tabCount == ths.currentTabIndex) {
                            ths.$emit("close");
                        }
                    }  
                    ths.CloseTicketDelay();
                    ths.GetTicketDelayReason();                    
                }else{
                    ths.ShowAlert(ths.$t("UnknownErrorOccur"), "failure", true, ths.$t('Alert'));
                }
            })
        }
    }
}
</script>