<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="col-md-12 p-0">
            <div class="tab-content">
                <div :class="reqFrom == 'InitialSetup' ? 'dynamiccontent_base' : ''">
                    <dynamic-form lang="en" ref="AddConfig" :buttons="false" :schema="FormSchema"
                        v-on:OnSubmit="onSubmit" :mandatorytext="$t('MandatoryString')">
                    </dynamic-form>
                </div>
                <div :class="reqFrom == 'InitialSetup' ? 'mainfooter_app' : ''">
                    <div class="row">
                        <div class="col-lg-12 text-right mt-4">
                            <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                                v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous')
                                }}</button>

                            <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4"
                                @click="Skip()" v-if="tabCount > currentTabIndex">{{ $t("Skip") }}</button>

                            <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip"
                                data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                                @click="onSubmit" style="margin-left: 5px" v-if="tabCount > currentTabIndex">
                                <i class="fa fa-save pr-2"></i> {{ $t("SaveAndNext") }}
                            </button>

                            <button type="button" id="aGroupTicketSave" @click="onSubmit('Save')" title=""
                                data-toggle-tooltip="tooltip" data-original-title="Save"
                                class="popupgreenbtn btn btn-success formbtn widthhalf pr-2" style="margin-left: 5px;"
                                v-if="tabCount == currentTabIndex && reqFrom != 'InitialSetup'">
                                <i class="fa fa-save pr-2"></i> {{ $t('SaveAndClose') }}
                            </button>  
                             <button title="Finish" class="btn btn-danger formbtn widthhalf px-4"
                                @click="onSubmit('SaveAndFinish')" v-if="reqFrom == 'InitialSetup'">
                                {{ $t("SaveAndFinish") }}
                            </button>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../../../services/AdvanceSetupModuleService';
import commonDataService from "../../../../services/DataService";
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom: ''
    },
    data() {
        return {
            UserData: [],
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('User'),
                            name: "User",
                            value: "",
                            placeholder: this.$t('User'),
                            config: {
                                options: [],
                                onChange: this.onChangeProfile,
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "ToggleField",
                            label: this.$t('CanViewAllCrmRecord'),
                            name: "CanViewAllCrmRecord",
                            value: "Yes",
                            placeholder: "",
                            validationRules: { "required": false },
                        },
                    ]
                }
            ],
            isLoading: false
        }
    },
    created: function () {
        debugger
        var vm = this;
        vm.GetUser();
        
    },
    methods: {
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        Finish: async function () {
            var vm = this;
            await vm.UpdateUserSettingConfig('HideContextmenu');
            await vm.UpdateUserSettingConfig('initialstartup');
            window.location.href = '/User/Home';
        },
        async UpdateUserSettingConfig(type) {
            let url = `status=true&type=${type}`
            await commonDataService.UpdateUserSettingConfig(url).then((response) => {

            });
        },
        onSubmit: function (btnType) {

            var vm = this;
            vm.$refs.AddConfig.validateForm.validate().then(result => {
                if (result) {
                    var obj = {
                        User: vm.FormSchema[0].Data[0].value,
                        CanViewAllCrmRecord: vm.FormSchema[0].Data[1].value
                    }
                    var params = JSON.stringify(obj);
                    DataService.SaveConfigData(params).then(response => {
                        if (response.data.result == "INSERT") {
                            
                                vm.Finish();
                                //vm.$emit("nextTab");
                            
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Configuration')]), "success", true, vm.$t("Alert"));
                        } else if (response.data.result == "UPDATE") {
                            vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Configuration')]), "success", true, vm.$t("Alert"));
                        }
                        else {
                            vm.ShowAlert(vm.$t('AddedError', [vm.$t('Configuration')]), "danger", true, vm.$t("Alert"));
                        }
                        vm.isLoading = false;
                    });
                }
            })
        },
        GetUser: function () {            
            var vm = this;
            var param = 'moduleName=CRM_USER&type=USERS';
            DataService.GetDDLDataForAddGroup(param).then(response => {
                vm.UserData = response.data.DATA;
                vm.UserData.forEach((item, indx) => {
                    vm.FormSchema[0].Data[0].config.options.push({
                        name: item.name,
                        value: item.value
                    })
                })
                vm.$refs.AddConfig.UpdateKeyValue();
            });
        },
        Skip: function () {

            var vm = this;
            vm.$emit("nextTab", true);
        }
    }
}
</script>
