<template lang="en">
    <section>
      
        <!-- <div> -->
            <!-- <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{$t('AddApprovalChain')}}</span>
                    </div>
                </div>
            </div>
        </div> -->
        <!-- <div class="border p-3"> -->
            <div :class="reqFrom=='InitialSetup' ? 'dynamiccontent_base' : ''">
                <dynamic-form lang="en" ref="validatechain" :buttons=false :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form> 
            </div>
          
            <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">
                <div class="row flex-row-reverse mt-3">
                    <div class="col-lg-12 text-right">
                        <button v-if="(currentTabIndex>1) ? true : false" title="Previous" @click="PreviousButton()" class="btn btn-warning float-left"><i class="fa fa-chevron-left pr-2"></i> {{$t('Previous')}}</button>
                        <button v-if="(tabCount  > currentTabIndex) ? true : false" title="Skip" @click="SkipButton()" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4">{{$t('Skip')}}</button>
                        <button type="submit" @click="onSubmit" class="btn btn-success ml-3" title="Save"
                            ><i class="fa fa-save pr-2"></i>{{$t('SaveAndNext')}}</button>
                    </div>
                </div>
            </div>
        <!-- </div> -->
    </section>
    </template>
    
<script>
import DataService from "@/services/AdvanceSetupModuleService";
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom:''
    },
    data() {

        return {
            isLoading: false,
            DataLoaded: 0,
            ApprovalChainId: 0,
            groupList: [],
            TagsSelectedArray: [],
            FormSchema: [{
                layoutType: "Single",
                Data: [{
                    astype: "TextField",
                    label: this.$t('ApprovalChainName'),
                    name: "ApprovalChainName",
                    value: "",
                    placeholder: "",
                    config: {
                        onChange: this.checkName
                    },
                    validationRules: {
                        "required": true
                    },

                },
                {
                    astype: "SelectField",
                    label: this.$t('Status'),
                    name: "Status",
                    value: 1001,
                    placeholder: "",
                    validationRules: {
                        "required": true
                    },
                    config: {
                        options: [{
                            value: "1001",
                            name: 'Active'
                        }, {
                            value: "1002",
                            name: 'Inactive'
                        }]
                    },
                },
                {
                    astype: "MultiSelectField",
                    label: this.$t('SelectApprovalGroup'),
                    name: "ApprovalGroup",
                    value: "",
                    mode: "tag",
                    placeholder: this.$t('Select'),
                    validationRules: {
                        "required": true
                    },
                    config: {
                        options: []
                    },
                },

                ]
            },
            {
                layoutType: "Single",
                Data: [{
                    astype: "TextAreaField",
                    label: this.$t('Description'),
                    name: "Description",
                    value: "",
                    placeholder: "",
                    validationRules: {
                        "required": false
                    },
                },]
            },
            ],
            buttons: [
                // {
                //     type: "button",
                //     class: "btn skip-btn-clr",
                //     text: "previous",
                //     onClick: this.previous
                // },
                {
                    type: "button",
                    class: "btn skip-btn-clr",
                    text: "Skip",
                    onClick: this.Skip
                },
                {
                    type: "submit",
                    class: "btn btn-success btnSave",
                    text: "<i class='fa fa-save pr-2'></i>Save & Next",
                }
            ],
        }
    },
    created: async function () {
        await this.GetApprovalGroups();
        if (this.$route.params.id != null) {
            this.ApprovalChainId = this.$route.params.id;
            //await this.GetApprovalGroups();
            await this.ManageChain();
        }
        this.DataLoaded = 1;
    },
    methods: {
        onSubmit: function () {
            var vm = this;
            var jsonobj = {
                ApprovalChainId: vm.ApprovalChainId,
                ApprovalChainName: vm.$refs.validatechain.getPropertyValue("ApprovalChainName", "value"),
                StatusId: vm.$refs.validatechain.getPropertyValue("Status", "value"),
                Groups: Array.prototype.map.call(vm.$refs.validatechain.getPropertyValue("ApprovalGroup", "value"), function (item) {
                    return item.value;
                }).join(","),
                Description: vm.$refs.validatechain.getPropertyValue("Description", "value"),
            }
            var poststring = JSON.stringify(jsonobj);
            DataService.AddApprovalChain(poststring).then(response => {
                if (response.data == '-1') {
                    vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Chain')]), "warning", true, vm.$t("Alert"));
                    vm.onCancel();
                } else if (response.data == '-2') {
                    vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Chain')]), "warning", true, vm.$t("Alert"));
                    vm.onCancel();
                } else if (response.data == '-3') {
                    vm.ShowAlert(vm.$t("SomethingWentWrong"), "failure", true, vm.$t('Alert'));
                    vm.onCancel();
                } else {
                    //vm.$parent.$parent.GetTimerStatusDetail();
                    vm.ShowAlert(vm.$t("ApprovalChainSuccessfullyAdded"), "success", true, vm.$t('Alert'));
                    if (vm.tabCount > 1) {
                        vm.$emit("nextTab")
                        vm.$emit("updateProgress", vm.currentTabIndex, true);
                    }
                    else {
                        vm.clearFields();
                    }
                    //vm.onCancel();
                }
            });
        },

        GetApprovalGroups: async function () {
            var vm = this;
            DataService.GetApprovalGroupByCompanyId().then(response => {
                var json = response.data;
                if (json != null) {
                    json.forEach(function (item, index) {
                        (vm.FormSchema)[0].Data[2].config.options.push({
                            name: item.APPROVAL_GROUP_NAME,
                            value: `${item.APPROVAL_GROUP_ID}`
                        })
                    });
                }
            });
        },
        ManageChain: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `id=${this.$route.params.id}`
            await DataService.GetByChainId(url).then(response => {
                var chainData = JSON.parse(response.data)
                if (chainData.length > 0) {
                    (this.FormSchema)[0].Data[0].value = chainData[0].approval_chain_name;
                    (this.FormSchema)[0].Data[1].value = chainData[0].status_id;
                    vm.groupList = chainData[0].GROUPS.split(",");
                    var UserList = [];
                    for (let i = 0; i < vm.groupList.length - 1; i++) {
                        UserList.push(vm.groupList[i]);
                    }
                    UserList.forEach((item) => {
                        var obj = (vm.FormSchema)[0].Data[2].config.options.find(x => x.value == item);
                        if (obj != undefined) {
                            vm.TagsSelectedArray.push({
                                label: obj.name,
                                value: obj.value
                            })
                        }
                    });
                    (this.FormSchema)[0].Data[2].value = vm.TagsSelectedArray;
                    (this.FormSchema)[1].Data[0].value = chainData[0].description;
                }
            })
            vm.isLoading = false;
        },
       onCancel: function () {
           this.$parent.$parent.$parent.IsshowConfiguration = false;
        },
        checkName(e, Fields) {
            var vm = this;
            var chainName = Fields.value
            var url = `ApprovalChainName=${chainName}`
            DataService.CheckDuplicate(url).then(response => {
                if (response.data == true && vm.FormSchema[0].Data[0].value) {
                    vm.FormSchema[0].Data[0].value = '';
                    vm.ShowAlert(vm.$t('ChainExist'), "warning", true, vm.$t("Alert"));
                    vm.FormSchema[0].Data[0].validationRules.required = true;
                }
            });
        },
        clearFields: function () {
            var vm = this;
           // debugger;
            vm.FormSchema[0].Data[0].value="";
            vm.FormSchema[0].Data[1].value="";
            vm.FormSchema[0].Data[2].value="";
            vm.FormSchema[0].Data[3].value="";
            vm.$refs.validatechain.validateForm.reset();
         
  
        },
        SkipButton: function()
        {
            var vm = this;
            vm.$emit("nextTab");
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
    }
}
</script>
    