<template>
    <section>
        <!-- <div> -->
            <!-- <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{$t('AddApprovalRule')}}</span>
                    </div>
                </div>
            </div>
        </div> -->
        <!-- <div class="border p-3"> -->
            <div :class="reqFrom=='InitialSetup' ? 'dynamiccontent_base' : ''">
                <dynamic-form lang="en" :buttons=false ref="validateRule" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                    <template v-slot:tgslot-Approval="{ data }">
                        <div class="input-group">

                            <select :maxlength="5" step="any" class="form-control" v-model="data.fieldInfo.value" v-bind:class="data.error != '' ? 'form-control is-invalid' : 'form-control'" :value="inputValue">
                                <option disabled>Select Approval Chain</option>
                                <option v-for="item in ApprovalChainList" :value="item.APPROVAL_CHAIN_ID" :key="item.APPROVAL_CHAIN_ID">{{
                                    item.APPROVAL_CHAIN_NAME }}</option>
                            </select>
                            
                            <!-- <span class="input-group-append">
                                <a class="input-group-text" id="spnOutOF" v-on:click="ShowGroupPopup(data.fieldInfo.value)">
                                    <i class="fa fa-eye"></i> 
                                </a>                                   
                            </span> -->
                                <span v-if="data.error != ''" name="Location" class="invalid-feedback d-block">{{
                                    data.error }}</span>
                        </div>
                    </template>
                    <template v-slot:tgslot-LimitedDuration1="{ data }">
                        <div class="row">
                            <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left mb-2">

                                <div class="input-group">
                                    <v-date-picker :popover="popover" v-bind:name="fromDateValue" :minute-increment="5"
                                        class="timedate  input-group" v-model="fromDateValue">
                                        <template v-slot="{ inputValue, togglePopover }">
                                            <input :id="'txtFromDate_' + pgaction" placeholder="M/D/YYYY" @click="togglePopover()"
                                                v-bind:class="data.error != '' ? 'form-control is-invalid' : 'form-control'"
                                                :value="inputValue" />  
                                        </template>
                                    </v-date-picker>
                                </div>
                                    <span v-if="data.error != ''" name="Location" class="invalid-feedback d-block">{{
                                                                    data.error }}</span>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">

                                <div class="input-group">
                                    <v-date-picker :popover="popover" v-bind:name="toDateValue" :minute-increment="5"
                                        class="timedate  input-group" v-model="toDateValue">
                                        <template v-slot="{ inputValue, togglePopover }">
                                            <input :id="'txtFromDate_' + pgaction" placeholder="M/D/YYYY" @click="togglePopover()"
                                                v-bind:class="data.error != '' ? 'form-control is-invalid' : 'form-control'"
                                                :value="inputValue" />
                                        </template>
                                    </v-date-picker>
                                </div>
                                <span v-if="data.error != ''" name="Location" class="invalid-feedback d-block">{{
                                    data.error }}</span>
                            </div>
                        </div>
                        
                        
                    </template>

                </dynamic-form>
            </div>

            <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">
                <div class="row flex-row-reverse mt-3">
                    <div class="col-lg-12 text-right">
                        <button v-if="(currentTabIndex>1) ? true : false" title="Previous" @click="PreviousButton()" class="btn btn-warning float-left"><i class="fa fa-chevron-left pr-2"></i> {{$t('Previous')}}</button>
                        <button v-if="(tabCount  > currentTabIndex) ? true : false" title="Skip" @click="SkipButton()" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4">{{$t('Skip')}}</button>
                        <button v-if="reqFrom != 'InitialSetup'" type="submit" @click="onSubmit('SaveAndClose')" class="btn btn-success ml-3" title="Save" ><i class="fa fa-save pr-2"></i>{{$t('SaveAndClose')}}</button>
                        <button v-if="reqFrom == 'InitialSetup'" title="Finish" class="btn btn-danger formbtn widthhalf px-4"
                            @click="onSubmit('SaveAndFinish')">
                            {{ $t("SaveAndFinish") }}
                        </button>
                    </div>
                </div>
            </div>
            <ApprovalGroup v-if="IsShowGroup" :ApprovalchainId="ApprovalchainId" :ApprovalchainName="ApprovalchainName">
            </ApprovalGroup>
        <!-- </div> -->
    </section>
</template>

<script>
import CommonDataService from '../../../../services/DataService';
import DataService from "@/services/AdvanceSetupModuleService";
import { TreeSelectField } from 'tg-controls_cli/dist/myLib.common';
import ApprovalGroup from './ApprovalGroupPopup.vue';
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom:''
    },
    components: {
        ApprovalGroup, 
        TreeSelectField
    },
    data() {
        return {
            Usersdata: [],
            ApprovalchainName: '',
            ApprovalChainList: [],
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TreeSelectField",
                            label: this.$t('Location'),
                            name: "Location",
                            value: null,
                            placeholder: "Select Location",
                            disabled: true,
                            visibility: false,
                            config: {
                                options: [],
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('AuditRuleName'),
                            name: "AuditRule",
                            value: "",
                            placeholder: "",
                            config: {
                                onChange: this.checkRuleName,
                            },
                            validationRules: { "required": true, max: 250 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Module'),
                            name: "Module",
                            value: 2,
                            placeholder: "",
                            config: {
                                options: [
                                    { value: 2, name: "Expense" },
                                    { value: 3, name: "Leave" },
                                    { value: 4, name: "Timesheet" },
                                    { value: 5, name: "Attendance" },
                                    { value: 6, name: "Product" }],
                                onChange: this.OnDeploymentType,

                            },

                            // validationRules: { "required": true },

                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Project'),
                            name: "project_name",
                            value: '',
                            placeholder: "",
                            visibility: true,
                            config: {
                                options: [],
                                onChange: this.GetUserbyProject,

                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "MultiSelectField",
                            label: 'Users',
                            name: "User",
                            value: null,
                            mode: "tag",
                            placeholder:this.$t('Select'),
                            config: {
                                options: [],

                            },
                            validationRules: { "required": true },
                        },

                        {
                            astype: "RadioField",
                            label: this.$t('LimitedDuration'),
                            name: "Limited_Duration",
                            value: 0,
                            placeholder: "",
                            visibility: true,
                            validationRules: {
                                "required": false
                            },
                            config: {
                                options: [{ value: 1, name: "Yes" }, { value: 0, name: "No" }],
                                onChange: this.ManageLimtedDuration,
                            }
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('LimitedDuration'),
                            name: "LimitedDuration1",
                            value: "",
                            placeholder: "",
                            visibility: false,
                            validationRules: {
                                "required": true
                            },
                            // config: {
                            //     onclick: this.GetAssetCost

                            // },

                        },


                        {
                            astype: "RadioField",
                            label: this.$t('ApprovalType'),
                            name: "Approvaltype",
                            value: 1,
                            placeholder: "",
                            config: {
                                options: [{ value: 1, name: "Approval Chain" }, { value: 2, name: "Client " }, { value: 3, name: "Both" }],
                                onChange: this.OnApprovalType,
                                //CallBackFunction: null
                            },

                            validationRules: { "required": false },

                        },
                        {
                            astype: "SlotField",
                            label: this.$t('SelectApproval'),
                            name: "Approval",
                            value: "",
                            placeholder: "",
                            validationRules: {"required": true},
                            visibility: true,
                            config: {
                                onKeyUp: this.GetAssetCost

                            },

                        },


                        {
                            astype: "MultiSelectField",
                            label: this.$t('SelectClient'),
                            name: "Client",
                            value: null,
                            mode: "tag",
                            visibility: false,
                            placeholder: this.$t('Select'),
                            config: {
                                options: [],

                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: 'Status',
                            name: "Status_value",
                            value: 1001,
                            config: {
                                options: [{ value: 1001, name: "Active" }, { value: 1002, name: "Inactive" },],
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('UseSequence'),
                            name: "Sequence",
                            value: 0,
                            placeholder: "",
                            config: {
                                options: [{ value: 1, name: "Yes" }, { value: 0, name: "No" }],

                            },

                            validationRules: { "required": false },

                        },

                    ]
                },

                {
                    layoutType: "One",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: null,
                            placeholder: "",
                        },
                    ]
                }


            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.onCancel
                }

            ],
            myObj: [],
            LocationData: [],
            StatusData: [
                {
                    name: 'Active',
                    value: 1001
                },
                {
                    name: 'Inactive',
                    value: 1002
                },
            ],
            projectdata: [],
            StateData: [],
            TagData: [],
            UserList: [],
            TagsSelectedArray: [],
            DataLoaded: 0,
            UserId: '',
            IsShowGroup: false,
            ApprovalchainId: null,
            ApprovalChainList: [],
            ApprovalID: 0,
            ApprovalRuleId: 0,
            isLoading: false,
            ApprovaltypesId: 0,
            ClientData: [],
            ClientSelectedArray: [],
            fromDateValue: null,
            toDateValue: null,
            Limiteduration: 0
        }
    },
    async created() {
        var vm = this;
        await vm.GetLocation();
        await vm.GetProjectList();
        await vm.GetApprovalChain();
        if (vm.$route.params.id != null) {
            vm.ApprovalRuleId = this.$route.params.id;
            //this.GetUserbyProject();
            await this.ManageRule();
            await this.ManageLimtedDuration(vm.Limiteduration);
            await this.OnDeploymentType(vm.ApprovalID);
            if (this.ApprovaltypesId != null) {
                await this.OnApprovalType(vm.ApprovaltypesId);
            }

            //this.isLoading = false
        }
        //vm.$refs.validateRule.UpdateKeyValue();

        //this.isLoading = false
    },
    methods: {
        onSubmit: function (btnType) {
             var vm = this;
             vm.isLoading = true;
            if (vm.$refs.validateRule.getPropertyValue("Approvaltype", "value") == 1) {
                var approvaltype = "ApprovalChain"
            }
            else if (vm.$refs.validateRule.getPropertyValue("Approvaltype", "value") == 2) {
                var approvaltype = "Client"
            }
            else {
                var approvaltype = "Both"
            }
          
            var jsonobj = {
                ApprovalRuleId: vm.ApprovalRuleId != 0 ? vm.ApprovalRuleId : 0,
                RuleName: vm.$refs.validateRule.getPropertyValue("AuditRule", "value"),
                RuleDescription: vm.$refs.validateRule.getPropertyValue("Description", "value"),
                ApprovalTypeId: vm.$refs.validateRule.getPropertyValue("Module", "value"),
                IsLimitedDuration: vm.$refs.validateRule.getPropertyValue("Limited_Duration", "value"),
                dStart: vm.$refs.validateRule.getPropertyValue("Limited_Duration", "value") == 0 ? null :  vm.$options.filters.formatDate(vm.fromDateValue),
                dEnd: vm.$refs.validateRule.getPropertyValue("Limited_Duration", "value") == 0 ? null : vm.$options.filters.formatDate(vm.toDateValue),
                //RuleBaseType: formfields.Description,
                ProjectId: vm.$refs.validateRule.getPropertyValue("project_name", "value") == null ? null : vm.$refs.validateRule.getPropertyValue("project_name", "value"),
                Users:  vm.$refs.validateRule.getPropertyValue("User", "value") == null ? null : vm.$refs.validateRule.getPropertyValue("User", "value"),
                Tasks: null,
                Clients:  vm.$refs.validateRule.getPropertyValue("Client", "value") == null ? null : vm.$refs.validateRule.getPropertyValue("Client", "value") == undefined ? null :  vm.$refs.validateRule.getPropertyValue("Description", "value"),
                ClientApprovalType: vm.$refs.validateRule.getPropertyValue("Approvaltype", "value") == null ? null : approvaltype,
                LocationId:  vm.$refs.validateRule.getPropertyValue("Location", "value") == null ? null :  vm.$refs.validateRule.getPropertyValue("Location", "value"),
                ApprovalChainId: vm.$refs.validateRule.getPropertyValue("Approval", "value"),
                UseSequence:vm.$refs.validateRule.getPropertyValue("Sequence", "value"),
                StatusId: vm.$refs.validateRule.getPropertyValue("Status_value", "value"),
                Users:  vm.$refs.validateRule.getPropertyValue("User", "value") == null ? null : Array.prototype.map.call( vm.$refs.validateRule.getPropertyValue("User", "value"), function (item) {
                    return item.value;
                }).join(","),

            }
            var poststring = JSON.stringify(jsonobj);
            vm.$refs.validateRule.validateForm.validate().then(result => {
                if(result){
                    DataService.AddRule(poststring).then(response => {
                        if (response.data == '0') {
                            vm.ShowAlert(vm.$t('SomethingWentWrong'), "failure", true, vm.$t("Alert"));
                            vm.onCancel();                   
                        }
                        if (response.data == '2') {
                            vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Rule')]), "warning", true, vm.$t("Alert"));
                            vm.onCancel();                   
                        }
                        else {
                            // vm.$parent.$parent.GetTimerStatusDetail();
                            // vm.$parent.$parent.$parent.GetTimerStatusDetail();
                            if(btnType == 'SaveAndFinish')
                            {
                                vm.Finish();
                            }
                            vm.ShowAlert(vm.$t("RuleSuccess"), 'success', true, vm.$t('Alert'));
                            vm.$emit("updateProgress", vm.currentTabIndex, true);
                            vm.onCancel();
                        }
                    })
                }
            });
                vm.isLoading = false;
        },
        ManageRule: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `id=${this.$route.params.id}`
            await DataService.GetRuleById(url).then(async response => {
                var RuleData = JSON.parse(response.data)
                vm.ApprovalID = RuleData[0].APPROVAL_TYPE_ID;
                vm.Limiteduration = RuleData[0].IS_LIMITED_DURATION;
                vm.ApprovaltypesId = RuleData[0].APPROVAL_TYPE == "ApprovalChain" ? 1 : RuleData[0].APPROVAL_TYPE == "Client" ? 2 : RuleData[0].APPROVAL_TYPE == "Both" ? 3 : null;
                vm.Approvaltype
                if (RuleData.length > 0) {
                    (this.FormSchema)[0].Data[0].value = RuleData[0].LOCATION_ID;
                    (this.FormSchema)[0].Data[1].value = RuleData[0].RULE_NAME;
                    (this.FormSchema)[0].Data[2].value = RuleData[0].APPROVAL_TYPE_ID;
                    (this.FormSchema)[0].Data[3].value = RuleData[0].PROJECT_ID;
                    if (RuleData[0].PROJECT_ID != null && RuleData[0].PROJECT_ID != 0) {
                        await vm.GetUserbyProject(RuleData[0].PROJECT_ID);
                    }
                    else {
                        await vm.GetUsers();
                    }
                    // (this.FormSchema)[0].Data[4].value = RuleData[0].USERS;
                    vm.UserList = RuleData[0].USERS != null ? RuleData[0].USERS.split(",") : RuleData[0].USERS;
                    if (vm.UserList != null) {
                        var listUsers = [];
                        for (let i = 0; i < vm.UserList.length - 1; i++) {
                            listUsers.push(vm.UserList[i]);
                        }

                        listUsers.forEach((item) => {
                            var obj = vm.projectdata.length > 0 ? vm.projectdata.find(x => x.USER_ID == item) : vm.Usersdata.find(x => x.USER_ID == item);
                            if (obj != undefined) {
                                if (obj.USER_NAME != undefined) {
                                    vm.TagsSelectedArray.push({
                                        label: obj.USER_NAME,
                                        value: obj.USER_ID.toString()
                                    })
                                }
                                else {
                                    vm.TagsSelectedArray.push({
                                        label: obj.USERNAME,
                                        value: obj.USER_ID.toString()
                                    })
                                }

                            }
                        });
                        (this.FormSchema)[0].Data[4].value = vm.TagsSelectedArray;
                    }
                    (this.FormSchema)[0].Data[5].value = RuleData[0].IS_LIMITED_DURATION;
                    this.fromDateValue = RuleData[0].DURATION_START;
                    this.toDateValue = RuleData[0].DURATION_END;
                    (this.FormSchema)[0].Data[7].value = RuleData[0].APPROVAL_TYPE == "ApprovalChain" ? 1 : RuleData[0].APPROVAL_TYPE == "Client" ? 2 : RuleData[0].APPROVAL_TYPE == "Both" ? 3 : null;
                    (this.FormSchema)[0].Data[8].value = RuleData[0].APPROVAL_CHAIN_ID;
                    if (RuleData[0].PROJECT_ID != null) {
                        await vm.GetClientbyProject(RuleData[0].PROJECT_ID);
                    }

                    (this.FormSchema)[0].Data[9].value = RuleData[0].CLIENTS != null ? RuleData[0].CLIENTS : RuleData[0].CLIENTS;
                    vm.ClienList = RuleData[0].CLIENTS != null ? RuleData[0].USERS.split(",") : RuleData[0].CLIENTS;
                    if (vm.ClienList != null) {
                        var ClienList = [];
                        for (let i = 0; i < vm.ClientData.length; i++) {
                            ClienList.push(vm.ClientData[i]);
                        }
                        ClienList.forEach((item) => {
                            var obj = vm.ClientData.find(x => x.USER_ID == item.USER_ID)
                            {
                                vm.ClientSelectedArray.push({
                                    label: obj.USERNAME,
                                    value: obj.USER_ID
                                })
                            }
                        });
                        (this.FormSchema)[0].Data[9].value = vm.ClientSelectedArray;
                    }
                    (this.FormSchema)[0].Data[10].value = RuleData[0].STATUS_ID;
                    (this.FormSchema)[0].Data[11].value = RuleData[0].USE_SEQUENCE;
                    (this.FormSchema)[1].Data[0].value = RuleData[0].RULE_DESCRIPTION;
                }
                vm.isLoading = false;
            })
        },
        GetProjectList: async function () {
            // var usertype = GetUserInfo.value.UserType;${usertype}
            var vm = this;
            var url = `userType=CA&IsEncrptedId='no'`;
            DataService.GetProjectListing(url).then(response => {

                var json = response.data;
                //  var array = JSON.parse("[" + json + "]");
                if (json != null) {
                    json.forEach(function (item, index) {
                        (vm.FormSchema)[0].Data[3].config.options.push({
                            name: `${item.ProjectName}`,
                            value: `${item.ProjectId}`
                        })
                    })
                    vm.$refs.validateRule.UpdateKeyValue();
                    // return vm.LeadStatusList;
                }
            });
        },
        GetUserbyProject: async function (e, field) {
            var vm = this;
            vm.FormSchema[0].Data[4].config.options = [];
            if (field == undefined) {
                var url = `ProjectId=${e}&ModuleName=APPROVALRULE`;
            } else {
                var url = `ProjectId=${field.value}&ModuleName=APPROVALRULE`;
            }

            await DataService.GetUsersByProjectId(url).then(response => {
                vm.projectdata = response.data;
                if (vm.projectdata != null) {
                    vm.projectdata.forEach(function (item, index) {
                        (vm.FormSchema)[0].Data[4].config.options.push({
                            name: `${item.USER_NAME}`,
                            value: `${item.USER_ID}`
                        })
                    })
                     vm.FormSchema[0].Data[4].value='';
                     vm.FormSchema[0].Data[4].validationRules.required = false;
                }
                if (field != undefined) {
                    this.GetClientbyProject(e, field)
                }

            });

        },

        GetClientbyProject: async function (e, field) {
            var vm = this;
            // vm.FormSchema[0].Data[5].config.options=[];
            var url = field != undefined ? `ProjectId=${field.value}` : `ProjectId=${e}`;
            await DataService.LoadClientByProjectId(url).then(response => {
                vm.ClientData = response.data;
                if (vm.ClientData != null) {
                    vm.ClientData.forEach(function (item, index) {
                        (vm.FormSchema)[0].Data[9].config.options.push({
                            name: `${item.USERNAME}`,
                            value: `${item.USER_ID}`
                        })
                    })
                    //return vm.LeadStatusList;
                }
            });
        },
        GetUsers: async function () {
            var vm = this;
            vm.FormSchema[0].Data[4].config.options = [];
            var url = `isIncludeLoginUserId=1&approvalGroupIds=&approvalChainIds=&moduleName=APPROVALRULE&DepartmentIds`;
            await DataService.LoadUsersByCompanyId(url).then(response => {
                vm.Usersdata = response.data;
                if (vm.Usersdata != null) {
                    vm.Usersdata.forEach(function (item, index) {
                        (vm.FormSchema)[0].Data[4].config.options.push({
                            name: `${item.USERNAME}`,
                            value: `${item.USER_ID}`
                        })
                    })
                }
            });
        },

        GetApprovalChain: async function () {
            var vm = this;
            DataService.GetApprovalChainsByCompanyId().then(response => {
                vm.ApprovalChainList = response.data;
            });
        },
        onCancel: function () {
          // this.$parent.$parent.$parent.IsshowConfiguration = false;
          this.$parent.$parent.$emit('close')
        },

        ShowGroupPopup(targetValue) {
            const foundElement = this.ApprovalChainList.find(element => element.APPROVAL_CHAIN_ID === targetValue);
            this.ApprovalchainId = foundElement.APPROVAL_CHAIN_ID
            this.ApprovalchainName = foundElement.APPROVAL_CHAIN_NAME
            this.IsShowGroup = true;
        },
        CloseGroupPopup() {
            var vm = this;
            vm.IsShowGroup = false;
        },

        async GetLocation() {
            var vm = this;
            var params = `reqForm=`
            await DataService.GetLocationByCompanyId(params).then(response => {
                vm.LocationData = response.data;

                vm.FindalLocationArray = [];
                vm.LocationData.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                (vm.FormSchema)[0].Data[0].config.options = vm.FindalLocationArray;
            })
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        async OnDeploymentType(event, field) {
            var vm = this;
            //field.value = event.target.getAttribute('data-value');
            if (field != undefined ? field.value == 2 : event == 2) {
                vm.FormSchema[0].Data[4].visibility = true
                vm.FormSchema[0].Data[3].visibility = true
                vm.FormSchema[0].Data[7].visibility = true
                vm.FormSchema[0].Data[0].visibility = false
                vm.FormSchema[0].Data[5].visibility = true
                if (this.$route.params.id == null) {
                    vm.FormSchema[0].Data[4].config.options = []
                     vm.FormSchema[0].Data[3].value='';
                     vm.FormSchema[0].Data[3].validationRules.required = false;
                      vm.FormSchema[0].Data[4].value='';
                      vm.FormSchema[0].Data[4].validationRules.required = false;
                }

            }
            if (field != undefined ? field.value == 3 : event == 3) {
                vm.FormSchema[0].Data[4].visibility = true
                vm.FormSchema[0].Data[0].visibility = false
                vm.FormSchema[0].Data[5].visibility = true
                vm.FormSchema[0].Data[9].value = '';
                vm.FormSchema[0].Data[3].visibility = false
                vm.FormSchema[0].Data[0].value = '';
                if(this.$route.params.id == null){
                    vm.FormSchema[0].Data[4].value='';
                    vm.FormSchema[0].Data[4].validationRules.required = false;
                }
                
                vm.FormSchema[0].Data[7].visibility = false
                await vm.GetUsers();
            }
            if (field != undefined ? field.value == 4 : event == 4) {
                vm.FormSchema[0].Data[4].visibility = true
                vm.FormSchema[0].Data[3].visibility = true
                vm.FormSchema[0].Data[7].visibility = true
                vm.FormSchema[0].Data[0].visibility = false
                vm.FormSchema[0].Data[5].visibility = true
                if (this.$route.params.id == null) {
                     vm.FormSchema[0].Data[4].config.options = []
                     vm.FormSchema[0].Data[3].value='';
                     vm.FormSchema[0].Data[3].validationRules.required = false;
                     vm.FormSchema[0].Data[4].value='';
                     vm.FormSchema[0].Data[4].validationRules.required = false;
                }
            }
            if (field != undefined ? field.value == 5 : event == 5) {
                vm.FormSchema[0].Data[3].visibility = false
                vm.FormSchema[0].Data[7].visibility = false
                vm.FormSchema[0].Data[0].visibility = false
                vm.FormSchema[0].Data[5].visibility = true
                vm.FormSchema[0].Data[9].config.options = [];
                 if(this.$route.params.id == null){
                    vm.FormSchema[0].Data[4].value='';
                    vm.FormSchema[0].Data[4].validationRules.required = false;
                }
                await vm.GetUsers();
            }
            if (field != undefined ? field.value == 6 : event == 6) {
                vm.FormSchema[0].Data[3].visibility = false
                vm.FormSchema[0].Data[4].visibility = false
                vm.FormSchema[0].Data[7].visibility = true
                vm.FormSchema[0].Data[5].visibility = false
                vm.FormSchema[0].Data[0].visibility = true
                vm.FormSchema[0].Data[9].config.options = [];
            }
            vm.FormSchema[0].Data[9].config.options = [];
        },
        OnApprovalType(event, field) {
            var vm = this;
            if (field != undefined ? field.value == 1 : event == 1) {
                vm.FormSchema[0].Data[8].visibility = true
                vm.FormSchema[0].Data[9].visibility = false
                //vm.FormSchema[0].Data[6].config.options = [];
            }
            if (field != undefined ? field.value == 2 : event == 2) {
                vm.FormSchema[0].Data[8].visibility = false
                vm.FormSchema[0].Data[9].visibility = true

            }
            if (field != undefined ? field.value == 3 : event == 3) {
                this.FormSchema[0].Data[8].visibility = true
                this.FormSchema[0].Data[9].visibility = true

            }

        },
        ManageLimtedDuration(event, field) {
            if (field != undefined ? field.value == 1 : event == 1) {
                this.FormSchema[0].Data[6].visibility = true
            }
            if (field != undefined ? field.value == 0 : event == 0) {
                this.FormSchema[0].Data[6].visibility = false
            }
        },
        checkRuleName(e, Fields) {
            var vm = this;
            var ruleName = Fields.value
            var url = `RuleName=${ruleName}`
            DataService.CheckDuplicateRule(url).then(response => {
                if (response.data == true && vm.FormSchema[0].Data[1].value) {
                    vm.FormSchema[0].Data[1].value = '';
                    vm.ShowAlert(vm.$t('RuleExist'), "warning", true, vm.$t("Alert"));
                    vm.FormSchema[0].Data[1].validationRules.required = true;
                }
            });
        },
        SkipButton: function()
        {
            var vm = this;
            vm.$emit("nextTab");
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        Finish: async function () {
            var vm = this;
            await vm.UpdateUserSettingConfig('HideContextmenu');
            await vm.UpdateUserSettingConfig('initialstartup');
            window.location.href='/User/Home';
        },
        async UpdateUserSettingConfig(type) {
            let url = `status=true&type=${type}`
            await CommonDataService.UpdateUserSettingConfig(url).then((response) => {

            });
        },
    },


}
</script>