<template lang="en">
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle ">
                            <h4 name="header" class="modal-title pt-1">
                                {{ isAddEdit ? $t('EditTimerCategory') : $t('AddTimerCategory') }}
                            </h4>
                            <button class="close" v-on:click="closePopup"><em aria-hidden="true" class="fa fa-times"></em> </button>
                        </div>
                        <div class="listing py-3 px-3">
                            <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                               <template v-slot:tgslot-AppIds="{ data }">
                                    <div>
                                        <MultiselectDropdown :options="filteredOptions" :value="selectedBrands" placeholder="Select your App/Website"
                                                :searchable="true" searchPlaceholder="Search App/Website" :maxItems="3" :selectAllEnabled="true"
                                                allText="Select All" @input="updateSelectedBrands" />
                                    </div>
                                </template>

                            </dynamic-form>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../../../services/DataService';
import Treeselect from '@riophae/vue-treeselect';
import '@riophae/vue-treeselect/dist/vue-treeselect.css';
import MultiselectDropdown from '../Modals/MultiselectDropdown.vue';

export default {
    components: {
        'tree-view': Treeselect,
        MultiselectDropdown
    },
    props: {
        AssignCat: {
            type: Array,
            required: true
        },
        CategorytList: {
            type: Array,

        },
        isAddEdit: {
            type: Boolean
        }
    },
    data() {
        return {
            selectedBrands: [],
            value: [],
            isLoading: false,
            color_code: '',
            dropdown: [],
            colour: "",
            dropdownArray: [],
            applicationList: [],
            NewArray: [],
            FormSchema: [{
                layoutType: "single",
                Data: [{
                    astype: "TextField",
                    label: this.$t('Title'),
                    name: "title",
                    value: "",
                    placeholder: "Enter Title",
                    validationRules: {
                        "required": true
                    },
                },
                ],
            },
            {
                layoutType: "single",
                Data: [{
                    astype: "ColorField",
                    label: this.$t('Selectcolor'),
                    name: "color_code",
                    value: "",
                    placeholder: "",
                    validationRules: {
                        "required": true
                    },
                    config: {
                        options: [],

                    },
                },],
            },
            {
                layoutType: "single",
                Data: [{
                    astype: "SlotField",
                    label: this.$t('AppWebsite'),
                    name: "AppIds",
                    value: "",
                    mode: "tag",
                    placeholder: "",
                    config: {
                        options: [],
                    },
                },],
            },
            ],
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Save",
                isVisible: true,
            },
            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                onClick: this.closePopup,
            },
            ],
            DataLoaded: 0,
            filteredOptions: []

        }
    },
    created: async function () {
        if (this.AssignCat != undefined) {
            this.GetDdlApplicationNotAssigned(this.AssignCat.category_id);
            await this.EditData(this.AssignCat);
        } else {
            this.GetDdlApplicationNotAssigned();
        }
        this.DataLoaded = 1;
    },
    methods: {
        updateSelectedBrands(newSelection) {
            this.selectedBrands = newSelection;
            console.log("sthiis", this.selectedBrands);
        },

        async GetDdlApplicationNotAssigned(category_id) {
            var vm = this;
            vm.isLoading = true;
            var categoriesId = ""
            if (category_id == undefined) {
                categoriesId = 0;
            } else {
                categoriesId = category_id
            }
            var url = `category_id=${categoriesId}`;
            await DataService.GetDropdownlistApplicationNotAssigned(url).then(response => {
                if (response.data.data) {
                    response.data.data.map(item => {
                        let obj2 = {
                            label: item.ApplicationName,
                            value: item.ApplicationId
                        }
                        vm.filteredOptions.push(obj2);
                    });
                }
                setTimeout(() => {
                    vm.isLoading = false;
                }, 500);
            })
                .catch((err) => {
                    console.error(err);
                });
        },

        async EditData(ths) {
            var vm = this;
            vm.isLoading = true;
            var categoriesId = ths.category_id || 0;
            var url = `categoryId=${categoriesId}`;
            try {
                const response = await DataService.GetCategoryById(url);
                var Categories = JSON.parse(response.data);
                if (Categories.data.length > 0) {
                    const category = Categories.data[0];
                    vm.color_code = category.color_code;
                    vm.FormSchema[0].Data[0].value = category.category_name;
                    vm.FormSchema[1].Data[0].value = category.color_code;
                    let filteredOptions = [];
                    category.applications_list.map(item => {
                        let obj2 = {
                            label: item.application_display_name,
                            value: item.application_id
                        }
                        vm.filteredOptions.push(obj2);
                        vm.selectedBrands.push(obj2);
                    });
                }
            } catch (error) {

            } finally {
                setTimeout(() => {
                    vm.isLoading = false;
                }, 500);
            }
        },

        async onSubmit(formfields, e) {
            var vm = this;
            var ids = '';
            vm.selectedBrands.forEach(item => {
                ids += item.value + ','
            });
            if (vm.AssignCat != null || vm.AssignCat != undefined) {
                var catId = vm.AssignCat.category_id;
            } else {
                catId = 0;
            }
            var obj = {
                category_id: catId == undefined ? 0 : catId,
                title: formfields.title,
                color_code: formfields.color_code,
                app_ids: ids.length > 0 ? ids.substring(0, ids.length - 1) : '',
            }
            vm.isLoading = true;
            var formData = new FormData();
            formData.append("postString", JSON.stringify(obj));
            debugger;
            await DataService.ManageCategory(obj).then(response => {
                vm.isLoading = false;
                if (response.data.status == 1) {
                    vm.$emit("UpdateProgress");
                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Category")]), "success", true, vm.$t("Alert"));
                } else if (response.data.status == 2) {
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Category")]), "success", true, vm.$t("Alert"));
                } else if (response.data.status == 3) {
                    return vm.ShowAlert(vm.$t('CategoryAlreadyExists'), "warning", true, vm.$t('Alert'));
                }
                vm.$parent.FetchData();
                vm.closePopup();
            })
        },
        closePopup() {
            var vm = this;
            vm.$parent.CloseCategoryClick();
        },

    }

}
</script>

<style scoped>
.colorpick {
    position: absolute;
    right: 19px;
    top: 46%;
    width: auto !important;
    height: 28px;
    z-index: 1;
}

.current-color {
    height: 30px;
}

.color-box-inside {
    left: -2px;
    top: -1px;
    width: 35px;
}

.my-popups .modal-dialog {
    max-width: 800px;
}
</style>