<template>
    <section>
        <div id="addChainRecord">
            <div :class="reqFrom=='InitialSetup' ? 'dynamiccontent_base' : ''">
                <div class="row">
                    <div class="col-lg-12">
                        <dynamic-form lang="en" ref="validateServiceGroup" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
                <div class="row">
                    <div class="listing mt-3 px-3">
                        <div class="table-responsive">
                            <table class="table table-bordered dt-responsive nowrap mt-0 dataTable no-footer dtr-inline"
                                id="tbContainer">
                                <thead class="thead-bg">
                                    <tr>
                                        <th><span>{{ $t('Level') }}:<sup class="text-danger">*</sup></span></th>
                                        <th><span>{{ $t('Group') }}:<sup class="text-danger">*</sup></span></th>
                                        <th class="text-center single-action"><a href="javascript:;" @click="AddRow"
                                                class="round-icon-small btn-success-light d-block float-none m-auto"
                                                style="line-height: 33px;" id="btnAdd" title="Add"><i
                                                    class="fa fa-plus"></i></a></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr class="mainleveldiv" v-for="(item, index) in Mainleveldiv" :key="index">
                                        <td>
                                            <Field :name="'Level'" v-slot="{ errors }" :rules="{ 'required': true }">
                                                <select :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                    :name="item.name" v-model="item.value"
                                                    @change="AlreadyExist($event, index, item, 'level')"
                                                    class="form-control level">
                                                    <option value>Select Level</option>
                                                    <option v-for="pitem in Level" :key="pitem" :value="pitem.value">
                                                        {{ pitem.name }}</option>
                                                </select>
                                                <span v-if="errors[0]" :name="'Level' + index"
                                                    class="invalid-feedback d-block">{{ errors[0] }}</span>
                                            </Field>
                                        </td>
                                        <td>
                                            <Field :name="'Group'" v-slot="{ errors }" :rules="{ 'required': true }">
                                                <select :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                    v-model="item.Group"
                                                    @change="AlreadyExist($event, index, item, 'group')">
                                                    <option value>Select Ticket Group</option>
                                                    <option v-for="gitem in GroupData" :value="gitem.value"
                                                        :key="gitem">{{ gitem.name }}</option>
                                                </select>
                                                <span v-if="errors[0]" :name="'Group'"
                                                    class="invalid-feedback d-block">{{ errors[0] }}</span>
                                            </Field>
                                        </td>
                                        <td class='text-center single-action' v-if="index != 0"><a href='javascript:;'
                                                id='btnRemove' @click="DeleteUser(index)"
                                                class='round-icon-small btn-danger-light d-block float-none m-auto'
                                                title='Remove'><i class='fa fa-trash'></i></a></td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
                <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">
                <div data-v-754d96ea="" class="text-right mt-3">
                    <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                        v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous') }}</button>
                    <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4" @click="Skip()"
                        v-if="tabCount > currentTabIndex">{{ $t('Skip') }}</button>
                    <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip" @click="SaveData('SaveAndNext')"
                        data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                        style="margin-left: 5px;" v-if="tabCount > currentTabIndex && reqFrom != 'InitialSetup'">
                        <i class="fa fa-save pr-2"></i> {{ $t('SaveAndNext') }}
                    </button>
                    <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip" @click="SaveData('SaveAndClose')"
                        data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                        style="margin-left: 5px;" v-if="tabCount == currentTabIndex && reqFrom != 'InitialSetup'">
                        <i class="fa fa-save pr-2"></i> {{ $t('SaveAndClose') }}
                    </button>
                    <button title="Finish" class="btn btn-danger formbtn widthhalf px-4" @click="SaveData('SaveAndFinish')" v-if="reqFrom == 'InitialSetup'">
                                 {{ $t("SaveAndFinish") }}
                     </button>
                </div>
            </div>
               
        </div>
    </section>
</template>
<script>
import setupModuleService from "@/services/AdvanceSetupModuleService";
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom:''
    },
    created: function () {
        var vm = this;
        vm.GetTicketGroupsForDropdownList();
    },
    data() {
        return {
            Mainleveldiv: [{ value: '', Group: '' }],
            GroupData: [],
            FormSchema: [
                {
                    layoutType: "two",
                    Data: [
                        {
                            astype: "TextField",
                            label: 'Ticket Service Board Name',
                            name: "TicketChainName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },
                        },
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('EnterDescription'),
                            validationRules: { "required": true, max: 1000 },
                        }
                    ]
                }
            ],
            Level: [
                {
                    name: 'Level 1',
                    value: 1
                },
                {
                    name: 'Level 2',
                    value: 2
                },
                {
                    name: 'Level 3',
                    value: 3
                },
                {
                    name: 'Level 4',
                    value: 4
                },
                {
                    name: 'Level 5',
                    value: 5
                },
                {
                    name: 'Level 6',
                    value: 6
                },
                {
                    name: 'Level 7',
                    value: 7
                },
                {
                    name: 'Level 8',
                    value: 8
                },
                {
                    name: 'Level 9',
                    value: 9
                },
                {
                    name: 'Level 10',
                    value: 10
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-warning",
                    text: "<i class='fa fa-save pr-2'></i>Save Of Draft",
                    isVisible: false
                }
            ],
        }
    },
    methods: {
        Skip: function () {
            var vm = this;
            vm.$emit("nextTab")
        },
        AddRow: function (e) {
            var vm = this;
            vm.Mainleveldiv.push({
                value: '',
                Group: ''
            });

            vm.$nextTick(() => {
                vm.$refs.validateServiceGroup.validateForm().validate();
            });
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        GetTicketGroupsForDropdownList: function () {
            var vm = this;
            setupModuleService.GetTicketGroupsForDropdownList().then(function (response) {
                response.data.forEach((item) => {
                    vm.GroupData.push({
                        name: item.ticket_group_name,
                        value: item.ticket_group_id
                    })
                })
            })
        },
        AlreadyExist: function (e, i, item, lbl) {
            var vm = this;
            if (vm.Mainleveldiv.some(item => item.value === e.target.value)) {
                vm.Mainleveldiv[i].value = '';
                vm.ShowAlert('Already Selected', "warning");
            }
            else if (vm.Mainleveldiv.some(item => item.Group === e.target.value)) {
                vm.Mainleveldiv[i].Group = '';
                vm.ShowAlert('Already Selected', "warning");
            }
            else if (lbl == 'level') {
                vm.Mainleveldiv[i].value = e.target.value;
            }
            else if (lbl == 'group') {
                vm.Mainleveldiv[i].Group = e.target.value;
            }
        },
        DeleteUser: function (id) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {
                vm.Mainleveldiv.splice(id, 1);
            });
        },
        Finish: async function () {
            var vm = this;
            await vm.UpdateUserSettingConfig('HideContextmenu');
            await vm.UpdateUserSettingConfig('initialstartup');
            window.location.href='/User/Home';
        },
        async UpdateUserSettingConfig(type) {
            let url = `status=true&type=${type}`
            await setupModuleService.UpdateUserSettingConfig(url).then((response) => {

            });
        },
        SaveData: function (btnType) {
            var vm = this;

            // Validate the form
            vm.$refs.validateServiceGroup.validateForm.validate().then(result => {
                if (result) {
                    var anyDropdownNotSelected = vm.Mainleveldiv.some(item => item.value === '' || item.Group === '');
                    if (anyDropdownNotSelected) {
                        vm.ShowAlert('Please select both level and group for all rows', 'error');
                    } else {
                        var TicketChainName = vm.$refs.validateServiceGroup.getPropertyValue("TicketChainName", "value");
                        var Description = vm.$refs.validateServiceGroup.getPropertyValue("Description", "value");
                        var params = `serviceBoardId=0&serviceBoardName=${TicketChainName}&description=${Description}&LevelIdsOrGroupIds=${JSON.stringify(vm.Mainleveldiv)}`
                        setupModuleService.AddTicketServiceBoard(params).then(response => {
                            if (response.data == "3") {
                                vm.ShowAlert(vm.$t("TicketServiceBoardAlreadyExists"), 'failure');
                            }
                            else if (response.data == "1" || response.data == "2") {
                                vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("TicketServiceBoard")]), "success", true, vm.$t("Alert"));
                                if(btnType == 'SaveAndFinish')
                                {
                                    vm.Finish();
                                }
                                else
                                {
                                    if (vm.tabCount > 1) {
                                        vm.$emit("nextTab")
                                        vm.$emit("updateProgress",  vm.currentTabIndex, true);
                                    }
                                    if(vm.tabCount == vm.currentTabIndex){
                                        vm.$emit("close");
                                    }
                                }                                

                            } else {
                                vm.ShowAlert(vm.$t("AddedError", [vm.$t("TicketServiceBoard")]), "failure", true, vm.$t("Alert"));
                            }
                        });
                    }
                }
            });
        }

    }
}

</script>