<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="col-md-12 p-0">
            <div class="tab-content">
                <div class="row">
                    <div class="col-lg-12 text-right mb-3 new_initialsetup_btnsbase">
                        <div class="locationinfo" v-if="isShowAddMoreBtn">
                            <i class="fa fa-info-circle mr-3 mt-3"></i><span>{{$t('ShiftAddMoreContent')}}</span>
                        </div>
                        <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4"
                            @click="Skip()" v-if="tabCount > currentTabIndex" >{{ $t("Skip") }}
                        </button>
                    </div>
                </div>
                <div class="col-md-12 p-0 mb-3 float-left" v-if="isShowAddMoreBtn && ShiftListData.length > 0">
                    <div class="row">
                        <div class="col-md-6" v-for="shift in ShiftListData">
                            <div class="locationaddress">
                                <h4 class="w-100 mb-1">{{shift.shift_name}}: <a href="javascript:;" class="edit-btn">
                                    <i class="fa fa-pencil text-red" v-on:click="getForm(shift.shift_id)"></i>
                                </a></h4>
                                <span class="w-100">
                                    {{$t('ShiftTiming')}} - {{convertTo12HourFormat(shift.start_time)}} to {{convertTo12HourFormat(shift.end_time)}}
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
                <div v-if="isShowAddMoreBtn" class="col-md-12 p-0 mb-3 float-left">
                    <a href="javascript:;" class="btn btn-primary formbtn" v-on:click="AddMore()">
                        <i class="fa fa-plus pl-2"></i> {{ $t('AddMore') }}
                    </a>
                </div>
                <dynamic-form v-if="!isShowAddMoreBtn" ref="setupshift" lang="en" :buttons="buttons":schema="FormSchema">
                    <template v-slot:tgslot-company_location_id="{ data }">
                        <div class="input-group custlocation">
                            <tree-view v-model="TreeValue" :options="parentChildOptions" placeholder="Select Location"  
                                :show-count="true" v-bind:class="{'form-control': true }">
                            </tree-view>
                        </div>
                    </template>
                </dynamic-form>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
import TreeSelectField from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default{
    props: {
        tabCount: 0,
        currentTabIndex: 0,
    },
    components: {
        "tree-view": TreeSelectField,
    },
    data(){
        return{
            isLoading:false,
            FormSchema:[],
            ShiftListData: [],
            isShowAddMoreBtn:true,
            TreeValue: null,
            parentChildOptions: [],
            buttons:[
                {
                    type: "button",
                    class: "popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4",
                    text: this.$t('Skip'),
                    isVisible: (this.tabCount > this.currentTabIndex) ? true : false,
                    onClick: this.Skip
                },
                {
                    type: "button",
                    class: "btn btn-warning float-left px-4",
                    text: '<i class="fa fa-chevron-left pr-2"></i> ' + this.$t('Previous'),
                    isVisible: (this.currentTabIndex > 1) ? true : false,
                    onClick: this.PreviousButton
                },
                {
                    type: "submit",
                    class: "popupgreenbtn btn btn-success formbtn widthhalf pr-2",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('SaveAndNew'),
                    name: "SaveAndNew",
                    isVisible: (this.tabCount > this.currentTabIndex) ? true : false,
                    onClick: this.SaveSubmit
                },
                {
                    type: "submit",
                    class: "popupgreenbtn btn btn-primary formbtn widthhalf pr-2",
                    text: this.$t('SaveAndNext')+ " <i class='fa fa-chevron-right pr-2'></i>",
                    name: "SaveAndNext",
                    isVisible: (this.tabCount > this.currentTabIndex) ? true : false,
                    onClick: this.SaveSubmit
                }
            ],
        }
    },
    async created(){
        var vm = this;
        await vm.GetShiftList();
    },
    methods:{
        getForm: async function (id) {
            var vm = this;
            vm.isShowAddMoreBtn = false;
            vm.isLoading = true;
            var url = `recordId=${id}&moduleName=Company&subModuleCode=Shift`;
            await DataService.GetDynamicForm(url).then(function (response) {
                if (response != null && response.data != null) {
                    vm.FormSchema = vm.$refs.setupshift.ConvertCustomFieldIntoDynamicFormObj(response.data.data);
                    vm.FormSchema.forEach((group, gi) => {
                        group.Data.forEach((field, fi) => {
                        if (vm.FormSchema.length > 0) {
                            if (field.name == "company_location_id") {
                                field.config.showAddIcon = false;
                                field.astype = "SlotField";
                                field.is_required = false;
                                vm.TreeValue = field.value ? field.value : null;

                                var options = field.config.options;
                                vm.parentChildOptions = [];
                                options.forEach((op) => {
                                    var CheckIfExist = vm.parentChildOptions.filter(function (el) {
                                        return el.value == op.value;
                                    });
                                    if (CheckIfExist.length == 0) {
                                        if (op.first_value == null) {
                                            let locationId = op.value
                                            let rslt = vm.getChildLocation(locationId, op, options)
                                            vm.parentChildOptions.push(rslt)
                                        }
                                    }
                                });
                            }
                            if (field.name == 'start_time' || field.name == 'end_time') {
                                field.value = (id > 0) ?  field.value : new Date();
                                field.config.onChange = vm.onChangeTime;
                            }
                            if(field.name=='shift_hours' || field.name=='min_hours'){
                                field.disabled = true;
                                //field.value = vm.GetTimeDifference();
                            }
                        }
                        });
                    });
                    vm.isLoading = false;
                }
            });
        },
        onChangeTime(event,field){ 
            debugger
            var vm = this; 
            var startTime;
            var endTime;
            vm.FormSchema.forEach((group,gi)=>{
                group.Data.forEach((field, fi)=>{
                    if(field.name=='start_time'){
                        startTime=field.value;
                    }
                    if(field.name=='end_time'){
                        endTime=field.value;
                    }
                })
            });
            debugger
           var hours = vm.GetTimeDifference(startTime,endTime);
           vm.FormSchema.forEach((group,gi)=>{
                group.Data.forEach((field, fi)=>{
                    if(field.name=='shift_hours' || field.name == 'min_hours'){
                        field.value = hours;
                    }
                })
            });

        },
        SaveSubmit(btnType){
            var vm = this;
            vm.$refs.setupshift.validateForm.validate().then((result) => {
                debugger
                if (result) {
                    vm.isLoading = true;
                    var CustomFieldArray = vm.$refs.setupshift.ConvertDynamicFormObjToCustomFieldObj(vm.FormSchema);
                    debugger
                    CustomFieldArray.forEach((item, index) => {
                        if (item.field_name == 'company_location_id') {
                            item.field_value = vm.TreeValue;
                        }
                    })
                    var obj = {
                        data: CustomFieldArray,
                        moduleCode: "Company",
                        subModuleCode: "Shift"
                    }
                    
                    DataService.SaveDynamicForm(JSON.stringify(obj)).then(function (response) {
                        vm.isLoading = false;
                        if (response != null && response.data != null && response.data[0].Status.toLowerCase() == "success") {
                            if(CustomFieldArray.find(x=>x.field_name=='company_location_id').field_value > 0){
                                vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Shift")]), "success", true, vm.$t("Alert"));
                            }else{
                                vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Shift")]), "success", true, vm.$t("Alert"));
                            }
                            if(btnType.name=='SaveAndNext'){
                                vm.Skip();
                            }
                            else if(btnType.name=='SaveAndNew'){
                                vm.resetForm();
                                //vm.GetLocations();
                            }
                        }
                        else if (response.data && response.data[0].Value.MSG.toLowerCase() == 'failure') {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("Shift")]), "failure", true, vm.$t("Alert"));
                        }
                    })
                }
            })
         },
         GetTimeDifference(startTime, endTime){
            // var vm = this;
            // var diff = (endTime.getTime()- startTime.getTime())/1000;
            // diff /= (60 * 60);
            // return Math.abs(Math.round(diff));
            //return  Math.abs(Math.round(endTime - startTime)) / 36e5;
            var diffInMilliseconds = Math.abs(endTime.getTime() - startTime.getTime());

            // Calculate total hours and minutes
            var hours = Math.floor(diffInMilliseconds / (1000 * 60 * 60));
            return hours;
         },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.value,
                label: currentItem.name
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.first_value == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((op) => {
                let ChildfolderId = op.value
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.value == op.value;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.first_value == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, op, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${op.value}`,
                            label: `${op.name}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        async GetShiftList(){
            var vm = this;
            vm.isLoading = true; 
            var param = 'moduleName=Company&subModuleCode=Shift';
            await DataService.GetCompanyShiftOrLocation(param).then(response => {
                vm.isLoading = false; 
                vm.ShiftListData = [];
                if (response.data && response.data.result && response.data.result.Data.length > 0) {
                    vm.ShiftListData = response.data.result.Data;
                }
            });
        },
        async AddMore() {
            var vm = this;
            await vm.getForm(0);
        },
        Skip: function () {
            var vm = this;
            vm.$emit("nextTab");
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        resetForm() {
            var vm = this;
            vm.FormSchema.forEach((group, gi)=>{
                group.Data.forEach((field, fi) => {
                    if (field.name == "company_location_id") {
                        field.value = null;
                        vm.TreeValue=null;
                    }
                    else {
                        field.value = "";
                    }
                })
            }) 
            vm.$refs.setupshift.validateForm.reset();// Reset the form fields
            vm.$refs.setupshift.validateForm = {}; // Clear any validation messages     
        },
        convertTo12HourFormat(dateStr) {
            const date = new Date(dateStr);
            let hours = date.getHours();
            const minutes = date.getMinutes();
            const ampm = hours >= 12 ? 'pm' : 'am';
            hours = hours % 12;
            hours = hours ? hours : 12;
            const minutesStr = minutes < 10 ? '0' + minutes : minutes;
            const formattedTime = `${hours}:${minutesStr} ${ampm}`;
            return formattedTime;
        }
    }
}
</script>