<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="col-md-12 p-0">
            <div class="tab-content">
                <div class="dynamiccontent_base">

                    <dynamic-form lang="en" ref="validatePlan" :buttons="false" :schema="FormSchema">
                        <template v-slot:tgslot-AdditionalFrequency="{ data }">
                            <div>
                                <label>{{ $t("AdditionalFrequency") }}:</label>
                            </div>
                            <div class="row">
                                <div v-for="(frequency, index) in frequencyOptions" :key="index"
                                    class="col-sm-12 col-md-4 col-lg-4 col-xl-4 mb-3">
                                    <div class="card-container border p-3">
                                        <div class="form-group mb-1">
                                            <!-- <div class="custom-control custom-checkbox custom-control-inline w-100">                                               
                                                <input
                                                    class="chkItems custom-control-input"
                                                    type="checkbox" 
                                                    :checked="frequency.checked"
                                                    @change="toggleFrequency(index, $event)"
                                                    :id="'frequency_' + index"/>
                                                    <label class="custom-control-label"></label>
                                                    <span class="ml-1 font-weight-400">{{ frequency.label }}</span>
                                                </div> -->


                                        </div>


                                        <div class="custom-control custom-checkbox custom-control-inline">
                                            <input type="checkbox" class="chkitems1 custom-control-input"
                                                :checked="frequency.checked" @change="toggleFrequency(index, $event)"
                                                :id="'frequency_' + index">
                                            <label class="custom-control-label" :for="'frequency_' + index"></label>
                                            <span class="ml-1 font-weight-400">{{ frequency.label }}</span>
                                        </div>

                                        <div class="discount-container">
                                            <label>Discount Value (%):</label>
                                            <br />
                                            <input type="text" class="form-control " :disabled="frequency.disabled"
                                                @input="validateDiscountValue(frequency)" v-model="frequency.discount"
                                                placeholder="Enter discount value" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </template>
                    </dynamic-form>
                </div>

                <div class="listing">
                    <div class="table-responsive table-fix-header" style="max-height:500px">
                        <table class="table table-bordered dt-responsive nowrap mt-0">
                            <thead class="thead-bg">
                                <tr class="text-capitalize">
                                    <th style="width:5%" class="text-center">
                                        <label class="form-check form-check-inline">
                                            <div class="custom-control custom-checkbox custom-control-inline">
                                                <input type="checkbox" id="talygenServicesCheckAll"
                                                    class="chkitems1 custom-control-input" :checked="selectAll"
                                                    @change="selectAllItems" name="selectedItems1">
                                                <label class="custom-control-label"
                                                    for="talygenServicesCheckAll"></label>
                                            </div>
                                        </label>
                                    </th>
                                    <th scope="col">{{ $t('ServiceName') }}</th>
                                    <th scope="col" class="text-center">{{ $t('ServiceType') }}</th>
                                    <th scope="col" class="text-center" v-if="planCode != 'SUBSCRIPTION_BILLING'">{{
                                        $t('UsageMeasurement') }}</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-if="ServiceListing.length === 0">
                                    <td class="text-center text-danger no-record" colspan="4">
                                        {{ $t('NoRecordfound') }}
                                    </td>
                                </tr>
                                <tr v-else v-for="(item, index) in ServiceListing" :key="index">
                                    <td class="text-center">
                                        <label class="form-check form-check-inline">
                                            <div class="custom-control custom-checkbox custom-control-inline">
                                                <input type="checkbox" :id="'tabletablebordered' + index"
                                                    class="chkitems custom-control-input" v-model="selectedItems"
                                                    @change="updateSelectAll" name="selectedItems"
                                                    :value="item.service_id" :disabled="item.status_id === 1002">
                                                <label :for="'tabletablebordered' + index"
                                                    class="custom-control-label"></label>
                                            </div>
                                        </label>
                                    </td>
                                    <td>{{ item.service_name }}</td>
                                    <td class="text-center">{{ item.service_code }}</td>
                                    <td class="text-center" v-if="planCode != 'SUBSCRIPTION_BILLING'">{{
                                        item.usage_measurement }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>

                <div class="mainfooter_app">

                    <div class="col-lg-12 text-right mt-4">
                        <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                            v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous')
                            }}</button>

                        <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4"
                            @click="Skip()" v-if="tabCount > currentTabIndex">{{ $t("Skip") }}</button>

                        <button type="button" class="btn btn-success  formbtn px-4 ml-2" @click="SaveData('SaveAndNew')"
                            v-if="tabCount > currentTabIndex">
                            <em class="fa fa-save pr-2 font-16"></em>
                            {{ $t("SaveAndNew") }}
                        </button>
                        <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip"
                            data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                            @click="SaveData('SaveAndNext')" style="margin-left: 5px" v-if="tabCount > currentTabIndex">
                            <i class="fa fa-save pr-2"></i> {{ $t("SaveAndNext") }}
                        </button>

                        <button type="button" id="aGroupTicketSave" @click="SaveData('SaveAndClose')" title=""
                            data-toggle-tooltip="tooltip" data-original-title="Save"
                            class="popupgreenbtn btn btn-success formbtn widthhalf pr-2" style="margin-left: 5px;"
                            v-if="tabCount == currentTabIndex && reqFrom != 'InitialSetup'">
                            <i class="fa fa-save pr-2"></i> {{ $t('SaveAndClose') }}
                        </button>
                    </div>
                </div>

            </div>
        </div>
        <transition name="modal">
            <div class="my-popups">
                <loader :is-visible="isLoading"></loader>
                <div class="modal d-block dialog" v-if="isShowReport">
                    <div class="modal-dialog modal-lg">
                        <div class="modal-content">
                            <div class="modal-header">
                                <h4 class="modal-title">{{ $t('Email Template') }}</h4>

                                <button type="button" class="close" v-on:click="CloseTemplate">
                                    <em class="fa fa-times" aria-hidden="true"></em>
                                </button>

                            </div>

                            <div class="modal-body">
                                <div class="col-md-12">
                                    <div class="listing">
                                        <div v-html="Emailcontent"
                                            style="border: 1px solid #ddd; padding: 10px; margin-top: 10px; min-height: 50vh; overflow-y: auto;">
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="modal-footer">
                                <button @click="CloseTemplate" class="btn btn-danger">
                                    <em class="fa fa-times pr-2" aria-hidden="true"></em> {{ $t('Cancel') }}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </section>
</template>

<script>

import DataService from "../../../../services/DataService";

export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0
    },
    data() {
        return {
            // frequencyOptions:[],
            // isCheckedWeekly: false,
            isMonthly: false,
            isWeekly: false,
            isBiWeekly: false,
            isQuarterly: false,
            isFourMonth: false,
            isSemiAnnually: false,
            isYearly: false,
            additionalFrequencyList: [],
            discountValue: 0,
            discountValueWeekly: 0,
            discountValuBiWeekly: 0,
            discountValueQuarterly: 0,
            discountValueMonthBasis: 0,
            discountValueSemiAnnually: 0,
            discountValueYearly: 0,
            isLoading: false,
            noOfDays: '',
            value: 0,
            CustomName: '',

            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('billingtype'),
                            name: "BillingTypeOptions",
                            value: "",
                            visibility: true,
                            config: {
                                options: [],
                                onChange: this.onChangeOfBillingType,
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('MeterType'),
                            name: 'meterTypeId',
                            visibility: false,
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('CustomerType'),
                            name: "CustomerType",
                            value: "",
                            visibility: false,
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('PlanType'),
                            name: "IsForPlan",
                            visibility: true,
                            value: "2",
                            config: {
                                options: [{ value: "1", name: "Prepaid" }, { value: "2", name: "Postpaid" }],
                                onChange: this.onChangePlanType,
                            },
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('PlanName'),
                            name: "PlanName",
                            value: "",
                            placeholder: 'Enter Plan Name',
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('InvoiceTemplate'),
                            name: "InvoiceTemplate",
                            value: "",
                            config: {
                                options: [],
                                showAddIcon: true,
                                addIconBackground: 'btn-primary',
                                addIcon: 'fa fa-eye',
                                onAddButtonClick: this.ShowInvoiceTemplate
                            },
                            placeholder: this.$t('SelectInvoiceTemplate'),
                            validationRules: { "required": true, max: 100 },
                        },

                        {
                            astype: "RadioField",
                            label: this.$t('showonwebsite'),
                            name: "isShowWebsite",
                            value: "0",
                            config: {
                                options: [
                                    { value: "1", name: this.$t('Yes') },
                                    { value: "0", name: this.$t('No') }
                                ]
                            },
                            validationRules: { "required": false, max: 100 },
                        },

                        {
                            astype: "RadioField",
                            label: this.$t('showpricing'),
                            name: "isShowPricing",
                            value: "0",
                            config: {
                                options: [
                                    { value: "1", name: this.$t('Yes') },
                                    { value: "0", name: this.$t('No') }
                                ]
                            },
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),  // Translated label
                            name: "Status",
                            value: "",
                            config: {
                                options: [
                                    { value: "1001", name: this.$t('Active') },
                                    { value: "1002", name: this.$t('Inactive') }
                                ]
                            },
                            placeholder: this.$t('Select'),  // Translated placeholder
                            validationRules: { "required": true, max: 100 },
                        }

                    ]
                },
                {
                    layoutType: "double",
                    Data: [

                        {
                            astype: "DateField",
                            label: this.$t('EffectiveDate'),
                            name: "EffectiveDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                minDate: new Date(),
                                onChange: this.setTimerDate,
                                maxDate: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('ExpirationDate'),
                            name: "ExpirationDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "MM/DD/YYYY",
                                minDate: new Date(),
                                //  onChange: this.setTimerDate,
                                maxDate: false
                            },
                            validationRules: { "required": false },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('BillingFrequency'),  // Translated label
                            name: "BillingFrequency",
                            value: "",
                            visibility: true,
                            config: {
                                options: [],
                                onChange: this.onBillingFrequencyChange
                            },
                            placeholder: this.$t('Select'),  // Translated placeholder
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('NoOfDays'),
                            name: "NoOfDays",
                            value: 0,
                            visibility: true,
                            config: {
                                options: [
                                    { value: 0, name: "Fixed " },
                                    { value: 1, name: "Custom " },
                                ],
                                onChange: this.onNoOfDaysChange,
                            },
                            validationRules: {
                                max: 200,
                                required: false,
                            },
                        },
                        {
                            astype: "NumericField",
                            label: this.$t('Custom Days '),
                            name: 'CustomDaysField',
                            palceholder: "",
                            value: "",
                            validationRules: {
                                required: true
                            },
                            disabled: false,
                            visibility: false,
                            config: {
                                onKeyUp: this.onCustomDaysChange
                            }
                        },
                        {
                            astype: "SlotField",
                            label: "",
                            name: "AdditionalFrequency",
                            value: "",
                            visibility: false,
                            placeholder: "",
                            config: {
                                options: [],
                            },
                            validationRules: "",
                        },

                        {
                            astype: "RadioField",
                            label: this.$t('ProratedInvoice'),
                            name: "proratedinvoice",
                            visibility: true,
                            value: "0",
                            config: {
                                options: [
                                    { value: "1", name: this.$t('Yes') },  // Translated "Yes"
                                    { value: "0", name: this.$t('No') }    // Translated "No"
                                ]
                            },
                            validationRules: { "required": true, max: 100 },
                        }



                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('DESCRIPTION'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                rows: 3
                            },
                            validationRules: { "required": true, max: 2000 },
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "FileField",
                            label: this.$t('Image'),
                            name: "Attachment",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize: 5,
                                showSelectedFiles: [],
                                accept: [
                                    {
                                        fileType: 'jpg',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'jpeg',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'png',
                                        iconClass: 'fa-file-image-o'
                                    }
                                ],
                                onDeleteSelectedFiles: this.onDeleteSelectedFiles,
                                onChange: this.GetBase64String,
                                showAddIcon: false,
                                onAddButtonClick: '',
                                multiple: false
                            },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    value: 'Y',
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('Save'),
                    onClick: this.SaveData
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.Close
                }
            ],
            plan_Id: 0,
            frequencyOptions: [
                //{ label: "Monthly", frequency: "Monthly", checked: true, discount: "" },
                //{ label: "Weekly", frequency: "Weekly", checked: false, discount: "",disabled: true  },
                //{ label: "Bi-Weekly", frequency: "Bi-Weekly", checked: false, discount: "" ,disabled: true},
                { label: "Quarterly", frequency: "Quarterly", checked: false, discount: "", disabled: true },
                { label: "4 Month Basis", frequency: "4 Month Basis", checked: false, discount: "", disabled: true },
                { label: "Semi Annually", frequency: "Semi-Annually", checked: false, discount: "", disabled: true },
                { label: "Yearly", frequency: "Yearly", checked: false, discount: "", disabled: true }
            ],
            fileData: [],
            billingFrequencyMasterOptions: [
                { value: "One Time", name: this.$t("OneTime") },
                { value: "Monthly", name: this.$t("Recurring") + " (" + this.$t("Monthly") + ")" },
            ],
            showNoOfDays: false,
            billingFrequencyOptions: [],
            Emailcontent: '',
            isShowReport: false,
            template_id: '',
            Plane_id: 0,
            GetIsForValue: 2,
            isdiscountDisabled: true,
            CompanyLogo: '',

            refCode: '',
            editDetail: [],
            IsForDetail: '',
            billingTypeOptions: [],

            ServiceListing: [],
            selectedItems: [],
            selectAll: false,
            noRecord: false,
            CurrentPage: 1,
            TotalPages: 0,
            TotalRecords: 0,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            GroupStart: 1,
            GroupEnd: 3,
            PageNumber: "1",
            planCode: 'UTILITY_BILLING',
            paymentType: 'Monthly'
        }
    },
    created: async function () {
        debugger

        let billingTypeOptions = []
        let vm = this;
        await vm.GetExtraFeatures();
        await vm.GetServiceList();


        this.CompanyLogo = vm.GetUserInfo.CompanyLogo;
        await this.GetCompanyLogo(this.CompanyLogo);
        if (vm.refCode == 'Clone' || vm.refCode == 'Update') {
            vm.Plane_id = vm.editDetail[0].PLAN_ID;
            vm.GetIsForValue = vm.editDetail[0].IS_FOR_PLAN
        }
        if (vm.refCode == 'Update' || vm.refCode == 'Clone') {
            if (vm.editDetail.length > 0) {
                setTimeout(() => {
                    this.BindEditDetail(vm.editDetail);
                    if (vm.editDetail[0].CLIENT_COUNT > 0) {
                        // this.displayBillingFrequency();
                    }
                }, 500);
            }
        }
        await vm.GetDDLInvoiceTemplateData();
        await vm.GetDDLCustomerTypeData();
        await vm.getMeterTypes();
        vm.billingFrequencyOptions = [...vm.billingFrequencyMasterOptions];
        vm.updateBillingFrequencyOptions(vm.GetIsForValue);
        // vm.$refs.validatePlan.updateProperty("BillingFrequency", "value", editDetail[0].PAYMENT_TYPE);
        if (vm.billingTypeOptions.length > 1) {
            vm.billingTypeOptions.forEach(function (element, index) {
                billingTypeOptions.push({ value: element.FEATURE_CODE, name: element.FEATURE_NAME });
            });
            vm.FormSchema.forEach(function (element, index) {
                element.Data.forEach(function (field, findex) {
                    if (field.name == "BillingTypeOptions") {
                        field.config.options = billingTypeOptions;
                        field.visibility = true;
                        element.layoutType = "triple"
                    }
                    if (field.name == 'Plan') {

                    }
                });
            });
        } else {
            vm.FormSchema.forEach(function (element, index) {
                element.Data.forEach(function (field, findex) {
                    if (field.name == "BillingTypeOptions") {
                        field.config.options.push({ name: vm.billingTypeOptions[0].FEATURE_NAME, value: vm.billingTypeOptions[0].FEATURE_CODE });
                        field.value = vm.billingTypeOptions[0].FEATURE_CODE;
                        element.layoutType = "double";
                    }
                    else if (field.name == "CustomerType" && vm.billingTypeOptions[0].FEATURE_CODE == "UTILITY_BILLING") {
                        field.visibility = true;
                        element.layoutType = "triple";
                        vm.FormSchema[0].Data[1].visibility = true;
                        vm.FormSchema[0].Data[2].visibility = true;
                    }
                });
            });
        }
        if (this.$route.params.id != undefined) {
            vm.plan_Id = parseInt(this.$route.params.id);
        }
        else {
            vm.plan_Id = vm.editDetail[0].PLAN_ID
        }

        //////////////////////////////////////////




    },
    methods: {
        GetExtraFeatures: async function () {
            var vueObj = this;
            var url = `featureCode=GET_EXTRA_FEATURE_BILLING&outputType=list`;
            await DataService.CheckExtraFeatures(url).then(function (response) {
                var parseData = JSON.parse(response.data);
                vueObj.billingTypeOptions = parseData;
            });
        },
        async GetCompanyLogo(imageName) {
            var vm = this;
            var param = 'imagename=' + imageName;
            await DataService.GetCompanyLogo(param).then((response) => {
                vm.CompanyLogo = response.data;
            });
        },
        toggleFrequency(index, event) {

            const frequency = this.frequencyOptions[index];
            frequency.checked = event.target.checked;
            frequency.disabled = !frequency.checked; // disable if unchecked

            if (!frequency.checked) {
                frequency.discount = "";
            }

            console.log("frequencyOptions updated:", this.frequencyOptions);
        },
        checkboxChange(frequency) {

            console.log("Checkbox changed for:", frequency.frequency);
            const selected = this.frequencyOptions.filter(f => f.checked);
        },
        validateDiscountValue(frequency) {

            const val = Number(frequency.discount);
            if (isNaN(val) || val < 0) {
                frequency.discount = '';
            } else if (val > 100) {
                frequency.discount = 100;
            }
        },
        onBillingFrequencyChange(value) {

            let selectedFrequency = this.$refs.validatePlan.getPropertyValue("BillingFrequency", "value");

            if (selectedFrequency === "One Time") {
                this.$refs.validatePlan.updateProperty("NoOfDays", "visibility", false);
            } else {
                this.$refs.validatePlan.updateProperty("NoOfDays", "visibility", true);
            }
            if (selectedFrequency === "One Time") {
                this.$refs.validatePlan.updateProperty("AdditionalFrequency", "visibility", false);
            } else {
                this.$refs.validatePlan.updateProperty("AdditionalFrequency", "visibility", true);
            }
        },



        onNoOfDaysChange(value) {

            let NameVal = this.$refs.validatePlan.getPropertyValue("NoOfDays", "value")
            if (NameVal == 1) {
                this.$refs.validatePlan.updateProperty("CustomDaysField", "visibility", true)
            }
            else if (NameVal == 0) {
                this.$refs.validatePlan.updateProperty("CustomDaysField", "visibility", false)
            }
            //this.FormSchema = [...this.FormSchema]; // Trigger reactivity
        },
        onCustomDaysChange(value) {

            var vm = this;
            const numericValue = this.$refs.validatePlan.getPropertyValue("CustomDaysField", "value");
            if (numericValue < 1) {
                vm.ShowAlert("Minimum allowed value is 1.", "warning", true, "Alert");
                this.$refs.validatePlan.updateProperty("CustomDaysField", "value", '');
                return;
            }

            if (numericValue > 366) {
                vm.ShowAlert("Maximum allowed value is 366.", "warning", true, "Alert");
                this.$refs.validatePlan.updateProperty("CustomDaysField", "value", '');
                return;
            }

        },
        async getMeterTypes() {
            const vm = this;
            try {
                const response = await DataService.GetMeterTypes();
                if (response?.data) {
                    const responseData = JSON.parse(response.data.result).data;

                    if (Array.isArray(responseData) && responseData.length > 0) {

                        responseData.map(item => {

                            vm.FormSchema[0].Data[1].config.options.push({
                                value: item.meter_id,
                                name: item.meter_name
                            });

                        });
                        vm.$refs.validatePlan.UpdateKeyValue();

                    } else {
                        console.warn('No valid data found in the response');
                    }
                } else {
                    console.error('Invalid response structure:', response);
                }
            } catch (error) {
                console.error('Error fetching Meter types:', error);
            } finally {
                //
            }
        },
        // displayBillingFrequency: function () {
        //     var vm = this;
        //     vm.$refs.validatePlan.updateProperty("BillingFrequency", "disabled", true);
        //     vm.$refs.validatePlan.UpdateKeyValue();
        // },
        setTimerDate: function (x, y, z) {
            var vm = this;
            if (y.name == "EffectiveDate") {
                vm.$refs.validatePlan.updateProperty("ExpirationDate", "config.minDate", x);
                vm.$refs.validatePlan.UpdateKeyValue();
            }
        },
        onDeleteSelectedFiles: function (item) {
            this.$delete(this.FormSchema[4].Data[0].config, 'showSelectedFiles');
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];
                var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                reader.onload = (function (theFile) {
                    return function (e) {
                        var binaryData = e.target.result;
                        var base64StringEncoded = window.btoa(binaryData);
                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };
                        vm.fileData.push(fileObj);
                    };
                })(f);
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },
