        BindEditDetail: function (editDetail) {

            var vm = this;
            if (editDetail[0].PLAN_IMAGE_URL) {
                let url = new URL(editDetail[0].PLAN_IMAGE_URL);
                let filePath = url.pathname.startsWith('/') ? url.pathname : '/' + url.pathname;
                let attachmentPath = filePath.replace(/^\/+/, '');
                let attachmentName = attachmentPath.split('/').pop();
                var attachmentData = { attachmentName: attachmentName, attachmentPath: attachmentPath };
                if (attachmentPath != null && attachmentName != null) {
                    vm.FormSchema[3].Data[0].config.showSelectedFiles.push(attachmentData);
                }
            }
            if (editDetail[0].PLAN_CODE == "UTILITY_BILLING") {
                vm.FormSchema[0].Data[1].visibility = true;
                vm.FormSchema[0].Data[2].visibility = true;
            }
            else {
                vm.FormSchema[0].Data[1].visibility = false;
                vm.FormSchema[0].Data[2].visibility = false;
            }
            vm.$refs.validatePlan.updateProperty("BillingTypeOptions", "value", editDetail[0].PLAN_CODE);
            vm.$refs.validatePlan.updateProperty("meterTypeId", "value", editDetail[0].METER_TYPE_ID,);
            vm.$refs.validatePlan.updateProperty("IsForPlan", "value", editDetail[0].IS_FOR_PLAN);
            editDetail[0].IS_FOR_PLAN == 1 ? vm.$refs.validatePlan.updateProperty("proratedinvoice", "visibility", false) : vm.$refs.validatePlan.updateProperty("proratedinvoice", "visibility", true);
            // if (editDetail[0].IS_FOR_PLAN == 1 && vm.refCode != 'Clone') {
            //     vm.$refs.validatePlan.updateProperty("proratedinvoice", "visibility", true);
            //     vm.$refs.validatePlan.updateProperty("proratedinvoice", "value", editDetail[0].IS_PRORATED_INVOICE);
            // }
            vm.$refs.validatePlan.updateProperty("PlanName", "value", editDetail[0].PLAN_NAME);
            vm.$refs.validatePlan.updateProperty("Status", "value", editDetail[0].STATUS_ID);
            vm.$refs.validatePlan.updateProperty("InvoiceTemplate", "value", editDetail[0].INVOICE_TEMPLATE_ID);
            vm.$refs.validatePlan.updateProperty("EffectiveDate", "value", editDetail[0].EFFECTIVE_DATE);
            vm.$refs.validatePlan.updateProperty("ExpirationDate", "value", editDetail[0].EXPIRATION_DATE);
            vm.$refs.validatePlan.updateProperty("CustomerType", "value", editDetail[0].CUSTOMER_SEGMENT_ID);
            vm.$refs.validatePlan.updateProperty("Description", "value", editDetail[0].PLAN_DESCRIPTION);
            vm.$refs.validatePlan.updateProperty("BillingFrequency", "value", editDetail[0].PAYMENT_TYPE);
            if (editDetail[0].PAYMENT_TYPE === "One Time") {
                vm.$refs.validatePlan.updateProperty("NoOfDays", "visibility", false);
                vm.$refs.validatePlan.updateProperty("AdditionalFrequency", "visibility", false);

            } else {
                vm.$refs.validatePlan.updateProperty("NoOfDays", "visibility", true);
                vm.$refs.validatePlan.updateProperty("AdditionalFrequency", "visibility", true);

            }
            vm.$refs.validatePlan.updateProperty("no_of_plan", "value", editDetail[0].no_of_plan);
            vm.$refs.validatePlan.updateProperty("NoOfDays", "value", editDetail[0].NoOfDays);
            this.onNoOfDaysChange(editDetail[0].NoOfDays);
            if (editDetail[0].NoOfDays == 1) {
                vm.$refs.validatePlan.updateProperty("CustomDaysField", "visibility", true);
                vm.$refs.validatePlan.updateProperty("CustomDaysField", "value", editDetail[0].no_of_plan);

            } else {
                vm.$refs.validatePlan.updateProperty("CustomDaysField", "visibility", false);
            }


            vm.$refs.validatePlan.updateProperty("isShowWebsite", "value", editDetail[0].IS_SHOW_ON_WEBSITE == true ? "1" : "0");
            vm.$refs.validatePlan.updateProperty("isShowPricing", "value", editDetail[0].IS_SHOW_ON_PRICING == true ? "1" : "0");
            vm.$refs.validatePlan.updateProperty("proratedinvoice", "value", editDetail[0].IS_PRORATED_INVOICE == true ? "1" : "0");

            // vm.$refs.validatePlan.updateProperty("AdditionalFrequency", "value",Jso editDetail[0].PLAN_ADDITIONAL_FREQUENCIES);
            // if (editDetail[0].planAdditionalFrequencies && Array.isArray(editDetail[0].planAdditionalFrequencies)) {
            //     this.frequencyOptions.forEach(f => {
            //         f.checked = false;
            //         f.discount = '';
            //     });

            //     editDetail[0].planAdditionalFrequencies.forEach(selectedFreq => {
            //         const match = this.frequencyOptions.find(f => f.frequency === selectedFreq.frequency);
            //         if (match) {
            //             match.checked = true;
            //             match.discount = selectedFreq.discount;
            //         }
            //     });
            // }

            if (editDetail[0].ADDITIONAL_FREQUENCY) {
                // const frequencies = JSON.parse(`[${editDetail[0].ADDITIONAL_FREQUENCY}]`);
                const frequencies = JSON.parse(`[${editDetail[0].ADDITIONAL_FREQUENCY}]`)

                vm.frequencyOptions = vm.frequencyOptions.map(option => {
                    // find if backend has this frequency
                    const backendFreq = frequencies.find(f => f.frequency === option.frequency);

                    if (backendFreq) {
                        return {
                            ...option,
                            discount: backendFreq.discount || "",
                            checked: backendFreq.frequency ? true : false,       // selected if discount is present
                            disabled: !backendFreq.frequency         // disable if no discount
                        };
                    } else {
                        return {
                            ...option,
                            discount: "",
                            selected: false,
                            disabled: true
                        };
                    }
                });
                // vm.frequencyOptions = frequencies.map(f => ({
                //     frequency: f.frequency,
                //     discount: f.discount,
                //     checked: f.discount ? true : false, 


                // }));
            }



            vm.$refs.validatePlan.UpdateKeyValue();


        },
