                        vm.$refs.validatePlan.UpdateKeyValue();
                    }
                }
            })
        },
        async SaveData(Type) {

            const vm = this;
            const result = await vm.$refs.validatePlan.validateForm.validate();
            if (!result) return;

            let meterTypeId = 0;
            let isShowWebsite = vm.$refs.validatePlan.getPropertyValue("isShowWebsite", "value") == "1";
            let isShowPricing = vm.$refs.validatePlan.getPropertyValue("isShowPricing", "value") == "1";
            let isForPlan = vm.$refs.validatePlan.getPropertyValue("IsForPlan", "value");
            let planName = vm.$refs.validatePlan.getPropertyValue("PlanName", "value");
            let status = vm.$refs.validatePlan.getPropertyValue("Status", "value");
            let invoiceTemplate = vm.$refs.validatePlan.getPropertyValue("InvoiceTemplate", "value");
            let customerType = vm.$refs.validatePlan.getPropertyValue("CustomerType", "value");
            let effectiveDate = vm.$refs.validatePlan.getPropertyValue("EffectiveDate", "value");
            let expirationDate = vm.$refs.validatePlan.getPropertyValue("ExpirationDate", "value");
            let description = vm.$refs.validatePlan.getPropertyValue("Description", "value");
            let billingType = vm.$refs.validatePlan.getPropertyValue("BillingTypeOptions", "value");
            let billingFrequency = vm.$refs.validatePlan.getPropertyValue("BillingFrequency", "value");
            let proratedinvoice = vm.$refs.validatePlan.getPropertyValue("proratedinvoice", "value") == "1";
            let noOfDays = Number(vm.$refs.validatePlan.getPropertyValue("NoOfDays", "value"));
            let noOfPlan = null;

            if (noOfDays === 1) {
                noOfPlan = vm.$refs.validatePlan.getPropertyValue("CustomDaysField", "value");
            }

            let selectedFrequencies = [];

            if ((billingFrequency || "").toLowerCase() !== "one time") {
                selectedFrequencies = vm.frequencyOptions
                    .filter(f => f.checked)
                    .map(f => ({
                        plan_additional_frequency_id: f.id || 0,
                        plan_id: vm.plan_Id || 0,
                        frequency: f.frequency,
                        discount: f.discount || 0,
                        status_id: 1
                    }));
            }

            const isInsert = vm.plan_Id === 0 || vm.plan_Id === null || vm.plan_Id === undefined;

            const objData = {
                id: isInsert ? 0 : vm.plan_Id,
                meterTypeId: meterTypeId,
                isShowWebsite: isShowWebsite,
                isShowPricing: isShowPricing,
                isForPlan: isForPlan,
                planName: planName,
                planCode: billingType,
                status: status,
                invoiceTemplate: invoiceTemplate,
                effectiveDate: effectiveDate,
                expirationDate: expirationDate,
                customerType: customerType || "0",
                description: description,
                billingFrequency: billingFrequency,
                isproratedinvoice: proratedinvoice,
                attachment: JSON.stringify(vm.fileData),
                NoOfPlan: noOfPlan,
                NoOfDays: noOfDays,
                planAdditionalFrequencies: selectedFrequencies
            };

            let serviceCall = vm.refCode === 'Clone'
                ? DataService.ClonePlan(objData)
                : DataService.AddUpdatePlan(objData);

            await serviceCall.then(response => {
                if (response.data && response.data.Status === "Success") {
                    const plan_Id = response.data.Result;
                    const routeName = vm.billingTypeOptions[0].FEATURE_CODE === "SUBSCRIPTION_BILLING"
                        ? 'ManageSubscriptionPlanDetail'
                        : 'ManagePlan';

                    if (response.data.Code === "1") {
                        //vm.$router.push({ name: routeName, params: { id: plan_Id } });
                        vm.ShowAlert(vm.$t('AddedPlan'), "success");

                    } else if (response.data.Code === "2") {
                        vm.ShowAlert(vm.$t('UpdatedPlan'), "success");
                    }
                    else if (response.data.Code === "4") {
                        vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Plan')]), "failure", true, vm.$t('Alert'));
                        return;
                    }


                    vm.isLoading = false;
                    if (vm.IsForDetail == 'fromDetail') {
                        //vm.$parent.refreshComponent++;
                    } else {
                        //vm.$parent.FetchData();
                    }

                    vm.SaveServiceData();

                    if (Type == 'SaveAndNew') {
                        vm.resetForm();

                    }
                    else if (Type == 'SaveAndNext') {
                        vm.Skip();
                    }
                    else if (Type == 'SaveAndFinish') {
                        vm.Finish();
                    }
                    else if (Type == 'SaveAndClose') {
                        vm.Finish();
                    }
                   
                } else {
                    vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                }


            });
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        Skip: function () {

            var vm = this;
            vm.$emit("nextTab", true);
        },
        Finish: async function () {

            var vm = this;
            await vm.UpdateUserSettingConfig('HideContextmenu');
            await vm.UpdateUserSettingConfig('initialstartup');
            window.location.href = '/User/Home';
        },
        async UpdateUserSettingConfig(type) {

            let url = `status=true&type=${type}`
            await DataService.UpdateUserSettingConfig(url).then((response) => {

            });
        },
        resetForm() {

            var vm = this;
            vm.FormSchema.forEach((group, gi) => {
                group.Data.forEach((field, fi) => {
                    field.value = "";
                })
            })

        },

        async GetServiceList() {
            debugger
            var vueObj = this;
            vueObj.isLoading = true;
            const url = `pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}&plan_id=&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&planCode=${vueObj.planCode}&search=`;
            await DataService.GetServiceListing(url).then(response => {
                if (response.data.status === "Success") {
                    if (response.data != "" && response.data != null) {
                        if (response.data.result.data.length > 0) {
                            vueObj.ServiceListing = response.data.result.data;
                            if (vueObj.paymentType == 'One Time') {
                                vueObj.ServiceListing = vueObj.ServiceListing.filter(item => item.service_code !== "Order Based" && item.service_code !== "Recurring" && item.service_code !== "Recurring Arrears" && item.service_code !== "Usage Based");
                            }
                            vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.TotalRecords = 0
                            vueObj.ServiceListing = [];
                        }
                        vueObj.isLoading = false;
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.ServiceListing = [];
                        vueObj.isLoading = false;
                    }

                }
            })
        },
        selectAllItems() {
            var vueObj = this;
            this.selectAll = !this.selectAll;
            if (this.selectAll) {
                this.ServiceListing.forEach(function (item) {
                    vueObj.selectedItems.push(item.service_id);
                })
            } else {
                this.selectedItems = [];
            }
        },
        updateSelectAll() {
            var vm = this;
            const totalItems = this.ServiceListing.length;
            const selectedCount = this.selectedItems.length;
            vm.selectAll = totalItems > 0 && selectedCount === totalItems;
        },
        async SaveServiceData() {
            debugger
            var vm = this;
            const filteredSkills = vm.ServiceListing.filter(skill => this.selectedItems.includes(skill.service_id));
            const obj = JSON.stringify(filteredSkills);
            debugger;
            var requestPayload = { objData: obj, planId: vm.PlanId };
            await DataService.AddSelectedService(requestPayload).then(response => {
                if (response.data != null) {
                    if (response.data.Status == "Success" && response.data.Code == "1") {
                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Service')]), "Success", true, vm.$t('Alert'));
                        
                        vm.isLoading = false;
                    }
                    else {
                        vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("Service")]), "danger", true, vm.$t("Alert"));
                        vm.isLoading = false;
                    }

                     vm.selectedItems = [];     // Uncheck all checkboxes
                     vm.selectAll = false;      // Uncheck the "Select All" checkbox
                }
            });
        },

    }
}
