<template>
  <section>
    <div class="col-md-12 p-0">
      <div class="tab-content">
        <div :class="reqFrom == 'InitialSetup' ? 'dynamiccontent_base' : ''">
          <dynamic-form lang="en" ref="AddLead" :buttons="buttons" :schema="FormSchema"></dynamic-form>
        </div>
        <div :class="reqFrom == 'InitialSetup' ? 'mainfooter_app' : ''">
          <div class="row">
            <div class="col-lg-12 text-right mt-4">
              <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t("Previous") }}</button>
              <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4" @click="Skip()"
                v-if="tabCount > currentTabIndex">{{ $t("Skip") }}</button>
              <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip"
                data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                @click="SaveSubmit" style="margin-left: 5px" v-if="tabCount > currentTabIndex">
                <i class="fa fa-save pr-2"></i> {{ $t("SaveAndNext") }}
              </button>
              <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip"
                data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                @click="SaveSubmit" style="margin-left: 5px" v-if="tabCount == currentTabIndex">
                <i class="fa fa-save pr-2"></i> {{ $t("SaveAndClose") }}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import DataService from "../../../../services/AdvanceSetupModuleService";
export default {
  props: {
    tabCount: 0,
    currentTabIndex: 0,
    reqFrom: ''
  },
  data() {
    return {
      LeadData: [],
      username: null,
      userid: null,
      isLoading: false,
      FormSchema: [
        {
          layoutType: "double",
          Data: [
            {
              astype: "TextField",
              label: this.$t("FirstName"),
              name: "FirstName",
              value: "",
              placeholder: this.$t("FirstName"),
              validationRules: {
                required: true,
              },
            },
            {
              astype: "TextField",
              label: this.$t("LastName"),
              name: "LastName",
              value: "",
              placeholder: this.$t("LastName"),
              validationRules: {
                required: true,
              },
            },
            {
              astype: "TextField",
              label: this.$t("LeadEmail"),
              name: "LeadEmail",
              value: "",
              placeholder: this.$t("LeadEmail"),
              validationRules: {
                required: true,
                email: true,
              },
            },
            {
              astype: "SelectField",
              label: this.$t("LeadOwner"),
              name: "LeadOwner",
              value: "",
              placeholder: this.$t("LeadOwner"),
              config: {
                options: [],
                onChange: this.onChangeProfile,
              },
              validationRules: {
                required: true,
              },
            },
            {
              astype: "DateField",
              label: this.$t("LeadDate"),
              name: "LeadDate",
              value: new Date(),
              placeholder: this.$t("LeadDate"),
              validationRules: {
                required: true,
              },
              config: { mode: "date", format: "M/D/YYYY" },
            },
            {
              astype: "SelectField",
              label: this.$t("Status"),
              name: "Status",
              value: "1001",
              placeholder: this.$t("Status"),
              config: {
                options: [
                  { name: "Active", value: 1001 },
                  { name: "InActive", value: "1002" },
                ],
                onChange: this.onChangeProfile,
              },
              validationRules: {
                required: true,
              },
            },
            {
              astype: "SelectField",
              label: this.$t("Country"),
              name: "Country",
              value: "",
              placeholder: this.$t("Country"),
              config: {
                options: [],
                onChange: this.onChangeProfile,
              },
              validationRules: {
                required: true,
              },
            },
          ],
        },
      ],
    };
  },
  created: function () {
    this.username = this.GetUserInfo.Name;
    this.userid = this.GetUserInfo.ID;
    this.GetCountryData();
    this.GetLeadOwner();
  },
  methods: {
    GetCountryData: function () {
      var vm = this;
      DataService.GetCountryData().then(function (response) {
        var data = response.data;
        data.forEach((element) => {
          vm.FormSchema[0].Data[6].config.options.push({
            name: element.countrY_FULL_NAME,
            value: element.id,
          });
        });
        vm.$refs.AddLead.UpdateKeyValue();
      });
    },
    GetLeadOwner: function () {
      var vm = this;
      vm.FormSchema[0].Data[3].config.options.push({
        name: vm.username,
        value: vm.userid,
      });
    },
    SaveSubmit: function () {
      var vm = this;
      vm.isLoading = true;
      vm.$refs.AddLead.validateForm.validate().then(result => {
        if (result) {
          var obj = {
            first_name: vm.FormSchema[0].Data[0].value,
            last_name: vm.FormSchema[0].Data[1].value,
            email: vm.FormSchema[0].Data[2].value,
            lead_owner: vm.FormSchema[0].Data[3].value,
            lead_date: vm.FormSchema[0].Data[4].value,
            status_id: vm.FormSchema[0].Data[5].value,
            country: vm.FormSchema[0].Data[6].value,
          };
          var params = obj;
          DataService.AddLead(params).then(function (response) {
            vm.isLoading = false;
            if (response != null && response.data[0].Status == "Success") 
            { 
              vm.ShowAlert(vm.$t("RecordsSavedSuccessfully"));
              if (vm.tabCount > 1) {
                vm.$emit("nextTab");
              } else {
                vm.clearFields();
              }
            }
          });
        }
      })

      vm.isLoading = false;
    },
    clearFields: function () {
      var vm = this;
      vm.FormSchema[0].Data[0].value = "";
      vm.FormSchema[0].Data[1].value = "";
      vm.FormSchema[0].Data[2].value = "";
      vm.FormSchema[0].Data[3].value = "";
      vm.FormSchema[0].Data[4].value = "";
      vm.FormSchema[0].Data[5].value = "";
      vm.FormSchema[0].Data[6].value = "";
      vm.$refs.validatePriority.validateForm.reset();
    },
    Skip: function () {
      var vm = this;
      vm.$emit("nextTab", true);
    },
    PreviousButton: function () {
      var vm = this;
      vm.$emit("previousTab");
    },
  },
};
</script>