<template>    
    <div class="col-sm-12 managestorage p-0">
        <loader :is-visible="isLoading"></loader>
        <div :class="reqFrom=='InitialSetup' ? 'dynamiccontent_base' : ''">
            <StorageBar :type="'All'"></StorageBar>
            <div class="row">
                
                <div class="col-lg-12">
                    <div class="heading-border">
                        <h5 class="h5">
                            <span>{{$t('ScreenshotAllocationSpace')}}:</span>
                        </h5>
                    </div>
                </div>&nbsp;
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 my-4 jslider">
                    <div class="form-group SliderSingle-screens green">
                        <div class="jslider-label jslider-label-to"><span>{{formatBytes(docStorageMaxLimit)}}</span></div>
                        <VueSlider :min="minScreenSize" id="txtStorage" v-on:change="OnSlide('storage')" :max="maxScreenSize" v-model="storage" :tooltip-formatter="val => formatBytes(val)"/>
                    </div>
                </div>
                <div class="col-lg-12">
                    <div class="heading-border">
                        <h5 class="h5">
                            <span>{{$t('FilesAllocationSpace')}}:</span>
                        </h5>
                    </div>
                </div>&nbsp;
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 my-4 jslider">
                    <div class="form-group SliderSingle-files yellow">
                        <div class="jslider-label jslider-label-to"><span>{{formatBytes(docStorageMaxLimit)}}</span></div>
                        <VueSlider :min="minFileSize" v-on:change="OnSlide('files')" id="txtFilesAllocation" :max="maxFileSize" v-model="filesAllocation" :tooltip-formatter="val => formatBytes(val)"/>
                    </div>
                </div>
                <div class="col-lg-12 mt-4 mb-2">
                    <div class="heading-border">
                        <h5 class="h5">
                            <span>{{$t('DocStorageAllocationSpace')}}:</span>
                        </h5>
                    </div>
                </div>&nbsp;
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 my-4 jslider">
                    <div class="form-group SliderSingle-docstorage red">
                        <div class="jslider-label jslider-label-to"><span>{{formatBytes(docStorageMaxLimit)}}</span></div>
                        <VueSlider :min="minDocStorage"  v-on:change="OnSlide('docstorage')" id="txtdocStorageAllocation" :max="maxDocStorage" v-model="docStorageAllocation" :tooltip-formatter="val => formatBytes(val)"/>
                    </div>
                </div>
                <div class="col-lg-12 mt-4 mb-2">
                    <div class="heading-border">
                        <h5 class="h5">
                            <span>{{$t('TransactionSizeAllocationSpace')}}:</span>
                        </h5>
                    </div>
                </div>&nbsp;
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 my-4 jslider">
                    <div class="form-group SliderSingle-transactionSize blue">
                        <div class="jslider-label jslider-label-to"><span>{{formatBytes(docStorageMaxLimit)}}</span></div>
                        <VueSlider :min="minTransactionSize"  v-on:change="OnSlide('transactionSize')" id="txtTransactionSize" :max="maxTransactionSize" v-model="transactionSize" :tooltip-formatter="val => formatBytes(val)"/>
                    </div>
                </div>
            </div>
        </div>
        <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">
                <div class="row flex-row-reverse mt-3">
                    <div class="col-lg-12 text-right">
                        <button title="Previous" @click="PreviousButton()" class="btn btn-warning float-left"><i class="fa fa-chevron-left pr-2"></i> Previous</button>
                        <button title="Skip" @click="SkipButton()" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4">Skip</button>
                        <button id="update-details" href="javascript:;" class="btn btn-success ml-3" title="Save"
                            @click="SaveStorageManagement();"><i class="fa fa-save pr-2"></i>Save And Next</button>
                    </div>
                </div>
            </div>
        </div>
</template>
<script>
import DataService from './../../../../services/AdvanceSetupModuleService';
import StorageBar from './StorageBar.vue'
export default {
    props: {
        reqFrom:''
    },
    components: {
        StorageBar
    },
    data() {
        return {
            isLoading:false,
            storage: null,
            filesAllocation: null,
            filesMaxLimit: null,
            docStorageAllocation: null,
            transactionSize: null,
            docStorageMaxLimit: null,
            screenshotSize: null,
            screenshotCount: null,
            fileSize: null,
            fileCount: null,
            docStorageAssigned: null,
            docStorageUsed: null,
            transactionTotalSize: null,
            containerSize: null,
            screenCount: null,
            progressBarMinValue: 20,
            progressBarMaxValue: 100,
            ContainerPercentage:null,
            storageData:null,
            filesAllocationData:null,
            docStorageAllocationData:null,
            transactionSizeData:null,
            availableSpace:null,
            containerdata:[],
            minScreenSize:0,
            minFileSize:0,
            minDocStorage:0,
            minTransactionSize:0,
            CurrentScreenSize:0,
            CurrentFileSize:0,
            CurrentDocStorageSize:0,
            CurrentTransactionSize:0, 
            maxScreenSize:0,
            maxFileSize:0,
            maxDocStorage:0,
            maxTransactionSize:0,
        }
    },
    created: function () {
        var vm = this;
        vm.GetCompanyContainerDetails();
    },
    watch: {
        containerSize: function (value) {
            this.ContainerPercentage = (parseFloat((value*100)/this.docStorageMaxLimit).toFixed(0));
        },
        docStorageMaxLimit: function(value){ 
            var vm=this;
            vm.maxScreenSize = value- vm.minFileSize-vm.minDocStorage-vm.minTransactionSize;
            vm.maxFileSize = value- vm.minScreenSize-vm.minDocStorage-vm.minTransactionSize;
            vm.maxDocStorage = value- vm.minFileSize-vm.minScreenSize-vm.minTransactionSize;
            vm.maxTransactionSize = value- vm.minFileSize-vm.minDocStorage-vm.minScreenSize;
        }
    },
    methods: {
        GetCompanyContainerDetails: function()
        {
            var vm=this;
            vm.isLoading=true,
            DataService.GetCompanyContainerDetails().then(function (response) {
             
                if(response.data.data!=null){
                    vm.containerdata=response.data.data[0];
                    vm.minScreenSize = Math.round(parseFloat(vm.containerdata.SCREENSHOT_SIZE));
                    vm.minFileSize = Math.round(parseFloat(vm.containerdata.FILE_SIZE));
                    vm.minDocStorage =  Math.round(parseFloat(vm.containerdata.DOC_STORAGE_ASSIGED));
                    vm.minTransactionSize = Math.round(parseFloat(vm.containerdata.TRANSACTION_TOTAL_SIZE));
                    vm.GetStoragedata();
                    vm.isLoading=false;
                }
            })
        },
        AllocateSpaceSlider: function(sliderName)
        {
            var vm=this;
            vm.minScreenSize = Math.round(parseFloat(vm.containerdata.SCREENSHOT_SIZE));
            vm.minFileSize = Math.round(parseFloat(vm.containerdata.FILE_SIZE));
            vm.minDocStorage =  Math.round(parseFloat(vm.containerdata.DOC_STORAGE_ASSIGED));
            vm.minTransactionSize = Math.round(parseFloat(vm.containerdata.TRANSACTION_TOTAL_SIZE));
            
            vm.CurrentScreenSize= Math.round(parseFloat(vm.containerdata.SCREEN_MAXLIMIT));
            vm.CurrentFileSize=Math.round(parseFloat(vm.containerdata.FILE_MAXLIMIT));
            vm.CurrentDocStorageSize=Math.round(parseFloat(vm.containerdata.DOC_STORAGE_MAXLIMIT));
            vm.CurrentTransactionSize=Math.round(parseFloat(vm.containerdata.TRANSACTION_SIZE_LIMIT));

            var StorageValues =$('#txtStorage')[0].innerText;
            var filesValues =$('#txtFilesAllocation')[0].innerText;
            var docstorageValues =$('#txtdocStorageAllocation')[0].innerText;
            var transactionValues =$('#txtTransactionSize')[0].innerText;

            if(sliderName=='storage')
            {
               
                vm.availableSpace = vm.docStorageMaxLimit - StorageValues;
                
                if (vm.availableSpace < parseInt(vm.minFileSize) + parseInt(vm.minDocStorage) + parseInt(vm.minTransactionSize)) 
                {
                    vm.availableSpace = parseInt(vm.minFileSize) + parseInt(vm.minDocStorage) + parseInt(vm.minTransactionSize);
                    StorageValues = Math.abs( vm.docStorageMaxLimit - vm.availableSpace );
                    
                }
                var oldScreenShotVal = vm.CurrentScreenSize;
                if (oldScreenShotVal < vm.StorageValues) {
                    for (var i = 1; i <= vm.StorageValues - oldScreenShotVal; i++) {
                        if (filesValues > vm.minFileSize && filesValues >= 0) {
                            filesValues -= 1;
                        }
                        else if (docstorageValues > vm.minDocStorage && docstorageValues > 0) {
                            docstorageValues -= 1;
                        }
                        else {
                            transactionValues -= 1;
                        }
                    }
                }

                else{
                    var DiffrenceValue =  vm.availableSpace - (parseInt(filesValues) + parseInt(docstorageValues) + parseInt(transactionValues));
                    var offset =  Math.round((DiffrenceValue / 100) * 30);
                    filesValues = parseInt(filesValues) + parseInt(offset);
                    docstorageValues = parseInt(docstorageValues) + parseInt(offset);
                    transactionValues = parseInt(transactionValues) + (DiffrenceValue - (2*parseInt(offset)));
                }
                vm.filesAllocation=filesValues,
                vm.docStorageAllocation=docstorageValues,
                vm.transactionSize=transactionValues;
            }
         

            else if(sliderName=='files')
            {
               
                vm.availableSpace = vm.docStorageMaxLimit - filesValues;

                if (vm.availableSpace < parseInt(vm.minScreenSize) + parseInt(vm.minDocStorage) + parseInt(vm.minTransactionSize)) 
                {
                    vm.availableSpace = parseInt(vm.minScreenSize) + parseInt(vm.minDocStorage) + parseInt(vm.minTransactionSize);
                    vm.docStorageMaxLimit = Math.abs( vm.docStorageMaxLimit - vm.availableSpace );
                    
                }
                var oldFileVal = vm.CurrentFileSize;
            if (oldFileVal < filesValues) {
                for (var i = 1; i <= filesValues - oldFileVal; i++) {
                    if (docstorageValues > vm.minDocStorage && docstorageValues >= 0) {
                        docstorageValues -= 1;
                    }
                    else if (transactionValues > vm.minTransactionSize && transactionValues > 0) {
                        transactionValues -= 1;
                    }
                    else {
                        StorageValues -= 1;
                    }
                }
            }
            else {
                var diff = vm.availableSpace - (parseInt(StorageValues) + parseInt(docstorageValues) + parseInt(transactionValues));
                 var offset =  Math.round((diff / 100) * 30);
                 StorageValues = parseInt(StorageValues) + parseInt(offset);
                 docstorageValues = parseInt(docstorageValues) + parseInt(offset);
                 transactionValues = parseInt(transactionValues) + (diff - (2*parseInt(offset)));
            }

            

            vm.storage=StorageValues,
                vm.docStorageAllocation=docstorageValues,
                vm.transactionSize=transactionValues;
            }

            else if(sliderName=='docstorage')
            {
                
                vm.availableSpace = vm.docStorageMaxLimit - docstorageValues;

                if (vm.availableSpace < parseInt(vm.minScreenSize) + parseInt(vm.minFileSize) + parseInt(vm.minTransactionSize)) 
                {
                    vm.availableSpace = parseInt(vm.minScreenSize) + parseInt(vm.minFileSize) + parseInt(vm.minTransactionSize);
                    vm.docStorageMaxLimit = Math.abs(vm.docStorageMaxLimit - vm.availableSpace );
                    
                }
                var oldDocStorageVal = vm.CurrentDocStorageSize;
            if (oldDocStorageVal < docstorageValues) {
                for (var i = 1; i <= docstorageValues - oldDocStorageVal; i++) {
                    if (transactionValues > vm.minTransactionSize && transactionValues >= 0) {
                        transactionValues -= 1;
                    }
                    else if (StorageValues > vm.minScreenSize && StorageValues > 0) {
                        StorageValues -= 1;
                    }
                    else {
                        filesValues -= 1;
                    }
                }
            }
            else {
                var diffDocSize = vm.availableSpace - (parseInt(StorageValues) + parseInt(filesValues) + parseInt(transactionValues));
                 var offset =  Math.round((diffDocSize / 100) * 30);
                 StorageValues = parseInt(StorageValues) + parseInt(offset);
                 filesValues = parseInt(filesValues) + parseInt(offset);
                 transactionValues = parseInt(transactionValues) + (diffDocSize - (2*parseInt(offset)));
            }


                vm.storage=StorageValues,
                vm.filesAllocation=filesValues,
                vm.transactionSize=transactionValues;

            }

            else if(sliderName=='transactionSize')
            {
               
                vm.availableSpace = vm.docStorageMaxLimit - transactionValues;


                if (vm.availableSpace < parseInt(vm.minScreenSize) + parseInt(vm.minFileSize) + parseInt(vm.minDocStorage)) 
                {
                    vm.availableSpace = parseInt(vm.minScreenSize) + parseInt(vm.minFileSize) + parseInt(vm.minDocStorage);
                    vm.docStorageMaxLimit = Math.abs( vm.docStorageMaxLimit - vm.availableSpace );
                    
                }
                var oldTransactionSizeVal = vm.CurrentTransactionSize;
            if (oldTransactionSizeVal < transactionValues) {
                for (var i = 1; i <= transactionValues - oldTransactionSizeVal; i++) {
                    if (StorageValues > vm.minScreenSize && StorageValues >= 0) {
                        StorageValues -= 1;
                    }
                    else if (filesValues > vm.minFileSize && filesValues > 0) {
                        filesValues -= 1;
                        //cScreenVal -= 1;
                    }
                    else {
                        docstorageValues -= 1;
                        //cFileVal -= 1;
                    }
                }
            }
            else {
                var diffTransectionSize = vm.availableSpace - (parseInt(StorageValues) + parseInt(filesValues) + parseInt(docstorageValues));
                 var offset =  Math.round((diffTransectionSize / 100) * 30);
                 StorageValues = parseInt(StorageValues) + parseInt(offset);
                 filesValues = parseInt(filesValues) + parseInt(offset);
                 docstorageValues = parseInt(docstorageValues) + (diffTransectionSize - (2*parseInt(offset)));
            }

            

                vm.storage=StorageValues,
                vm.filesAllocation=filesValues,
                vm.docStorageAllocation=docstorageValues;

            }
        },
        OnSlide: function(sliderName){
            var vm=this;
            
            vm.minScreenSize = Math.round(parseFloat(vm.containerdata.SCREENSHOT_SIZE));
            vm.minFileSize = Math.round(parseFloat(vm.containerdata.FILE_SIZE));
            vm.minDocStorage =  Math.round(parseFloat(vm.containerdata.DOC_STORAGE_ASSIGED));
            vm.minTransactionSize = Math.round(parseFloat(vm.containerdata.TRANSACTION_TOTAL_SIZE));

            vm.CurrentScreenSize= Math.round(parseFloat(vm.storage));
            vm.CurrentFileSize=Math.round(parseFloat(vm.filesAllocation));
            vm.CurrentDocStorageSize=Math.round(parseFloat(vm.docStorageAllocation));
            vm.CurrentTransactionSize=Math.round(parseFloat(vm.transactionSize));

            var StorageRange =  vm.CurrentScreenSize - vm.minScreenSize;
            var fileRange =  vm.CurrentFileSize - vm.minFileSize;
            var DocStorageRange=  vm.CurrentDocStorageSize - vm.minDocStorage;
            var TransactionRange= vm.CurrentTransactionSize - vm.minTransactionSize;
            //var maxRange = Math.max(fileRange, DocStorageRange, TransactionRange);
            var resetBy = 0;

            if(sliderName=='storage')
            {
                if(vm.CurrentScreenSize>vm.docStorageMaxLimit- vm.minFileSize-vm.minDocStorage-vm.minTransactionSize){
                    vm.storage= vm.docStorageMaxLimit- vm.minFileSize-vm.minDocStorage-vm.minTransactionSize;
                    vm.CurrentScreenSize= Math.round(parseFloat(vm.storage));
                }
                
                
                if(vm.docStorageMaxLimit-vm.storage-vm.filesAllocation-vm.docStorageAllocation-vm.transactionSize <0){
                    resetBy = vm.storage + vm.filesAllocation + vm.docStorageAllocation + vm.transactionSize - vm.docStorageMaxLimit;
                }
                //if(maxRange==fileRange){
                    if(resetBy>fileRange){
                        resetBy=resetBy-fileRange;
                        vm.filesAllocation = vm.filesAllocation - fileRange;
                    }
                    else{
                        vm.filesAllocation = vm.filesAllocation - resetBy;
                        resetBy=0;
                    }
                    if(resetBy>0){
                        //if(maxRange==DocStorageRange){
                            if(resetBy>DocStorageRange){
                                    resetBy=resetBy-DocStorageRange;
                                    vm.docStorageAllocation = vm.docStorageAllocation - DocStorageRange;
                                    //maxRange = Math.max(DocStorageRange, TransactionRange);
                            }
                            else{
                                vm.docStorageAllocation = vm.docStorageAllocation - resetBy;
                                resetBy=0;
                            }
                            if(resetBy>0){
                                 if(resetBy>TransactionRange){
                                    resetBy=resetBy-TransactionRange;
                                    vm.vm.transactionSize = vm.transactionSize - TransactionRange;
                                }
                                else{
                                    vm.transactionSize = vm.transactionSize - resetBy;
                                    resetBy=0;
                                }
                            }
                        //}
                    }
                //}
                // else if(maxRange==DocStorageRange){
                    
                // }
                // else if(maxRange==TransactionRange){

                // }
                vm.storage= vm.docStorageMaxLimit- vm.filesAllocation-vm.docStorageAllocation-vm.transactionSize-resetBy;
            }
            else if(sliderName=='files'){
                if(vm.CurrentFileSize>vm.docStorageMaxLimit- vm.minScreenSize-vm.minDocStorage-vm.minTransactionSize){
                    vm.filesAllocation= vm.docStorageMaxLimit- vm.minScreenSize-vm.minDocStorage-vm.minTransactionSize;
                    vm.CurrentFileSize=Math.round(parseFloat(vm.filesAllocation));
                }
                
                if(vm.docStorageMaxLimit-vm.storage-vm.filesAllocation-vm.docStorageAllocation-vm.transactionSize <0){
                    resetBy = vm.storage + vm.filesAllocation + vm.docStorageAllocation + vm.transactionSize - vm.docStorageMaxLimit;
                }
                    if(resetBy>StorageRange){
                        resetBy=resetBy-StorageRange;
                        vm.storage = vm.storage - StorageRange;
                    }
                    else{
                        vm.storage = vm.storage - resetBy;
                        resetBy=0;
                    }
                    if(resetBy>0){
                            if(resetBy>DocStorageRange){
                                    resetBy=resetBy-DocStorageRange;
                                    vm.docStorageAllocation = vm.docStorageAllocation - DocStorageRange;
                            }
                            else{
                                vm.docStorageAllocation = vm.docStorageAllocation - resetBy;
                                resetBy=0;
                            }
                            if(resetBy>0){
                                 if(resetBy>TransactionRange){
                                    resetBy=resetBy-TransactionRange;
                                    vm.transactionSize = vm.transactionSize - TransactionRange;
                                }
                                else{
                                vm.transactionSize = vm.transactionSize - resetBy;
                                resetBy=0;
                            }
                            }
                    }
                    vm.filesAllocation= vm.docStorageMaxLimit- vm.storage-vm.docStorageAllocation-vm.transactionSize - resetBy;
            }
            else if(sliderName=='docstorage'){
                if(vm.CurrentDocStorageSize>vm.docStorageMaxLimit- vm.minScreenSize-vm.minFileSize-vm.minTransactionSize){
                    vm.docStorageAllocation= vm.docStorageMaxLimit- vm.minScreenSize-vm.minFileSize-vm.minTransactionSize;
                    vm.CurrentDocStorageSize=Math.round(parseFloat(vm.docStorageAllocation));
                }
                
                if(vm.docStorageMaxLimit-vm.storage-vm.filesAllocation-vm.docStorageAllocation-vm.transactionSize <0){
                    resetBy = vm.storage + vm.filesAllocation + vm.docStorageAllocation + vm.transactionSize - vm.docStorageMaxLimit;
                }
                    if(resetBy>StorageRange){
                        resetBy=resetBy-StorageRange;
                        vm.storage = vm.storage - StorageRange;
                    }
                    else{
                        vm.storage = vm.storage - resetBy;
                        resetBy=0;
                    }
                    if(resetBy>0){
                            if(resetBy>fileRange){
                                resetBy=resetBy-fileRange;
                                vm.filesAllocation = vm.filesAllocation - fileRange;
                            }
                            else{
                                vm.filesAllocation = vm.filesAllocation - resetBy;
                                resetBy=0;
                            }
                            if(resetBy>0){
                                 if(resetBy>TransactionRange){
                                    resetBy=resetBy-TransactionRange;
                                    vm.vm.transactionSize = vm.transactionSize - TransactionRange;
                                }
                                else{
                                    vm.transactionSize = vm.transactionSize - resetBy;
                                    resetBy=0;
                                }
                            }
                    }
                    vm.docStorageAllocation = vm.docStorageMaxLimit- vm.storage-vm.filesAllocation-vm.transactionSize- resetBy;
            }
            else if(sliderName=='transactionSize'){

                if(vm.CurrentTransactionSize>vm.docStorageMaxLimit- vm.minScreenSize-vm.minDocStorage-vm.minFileSize){
                    vm.transactionSize= vm.docStorageMaxLimit- vm.minScreenSize-vm.minFileSize-vm.minDocStorage;
                    vm.CurrentTransactionSize=Math.round(parseFloat(vm.transactionSize));
                }
                
                if(vm.docStorageMaxLimit-vm.storage-vm.filesAllocation-vm.docStorageAllocation-vm.transactionSize <0){
                    resetBy = vm.storage + vm.filesAllocation + vm.docStorageAllocation + vm.transactionSize - vm.docStorageMaxLimit;
                }
                    if(resetBy>StorageRange){
                        resetBy=resetBy-StorageRange;
                        vm.storage = vm.storage - StorageRange;
                    }
                    else{
                        vm.storage = vm.storage - resetBy;
                        resetBy=0;
                    }
                    if(resetBy>0){
                            if(resetBy>fileRange){
                                resetBy=resetBy-fileRange;
                                vm.filesAllocation = vm.filesAllocation - fileRange;
                            }
                            else{
                                vm.filesAllocation = vm.filesAllocation - resetBy;
                                resetBy=0;
                            }
                            if(resetBy>0){
                                 if(resetBy>DocStorageRange){
                                    resetBy=resetBy-DocStorageRange;
                                    vm.docStorageAllocation = vm.docStorageAllocation - DocStorageRange;
                                }
                                else{
                                    vm.docStorageAllocation = vm.docStorageAllocation - resetBy;
                                    resetBy=0;
                                }
                            }
                    }
                    vm.transactionSize = vm.docStorageMaxLimit- vm.storage - vm.filesAllocation-vm.docStorageAllocation - resetBy;
            }
        },
        GetStoragedata: function () {
            var vm = this;
            DataService.GetStorageReport().then(function (response) {
                vm.isLoading=true;
                vm.storage = response.data[0].SCREEN_MAXLIMIT;
                vm.filesAllocation = response.data[0].FILE_MAXLIMIT;
                vm.GetScreenCount(vm.filesAllocation);
                vm.docStorageAllocation = response.data[0].DOC_STORAGE_MAXLIMIT;
                vm.transactionSize = response.data[0].TRANSACTION_SIZE_LIMIT;
                vm.docStorageMaxLimit = response.data[0].CONTAINER_LIMIT;
                vm.screenshotSize = response.data[0].SCREENSHOT_SIZE;
                vm.screenshotCount = response.data[0].SCREENSHOT_COUNT;
                vm.fileSize = response.data[0].FILE_SIZE;
                vm.fileCount = response.data[0].FILE_COUNT;
                vm.docStorageAssigned = response.data[0].DOC_STORAGE_ASSIGED;
                vm.docStorageUsed = response.data[0].DOC_STORAGE_USED;
                vm.transactionTotalSize = response.data[0].TRANSACTION_TOTAL_SIZE;
                vm.containerSize = response.data[0].CONTAINER_SIZE;
                vm.UsedScreenshotSpace = response.data[0].SCREENSHOT_COUNT;
                vm.UsedFileSpace = response.data[0].FILE_SIZE;
                vm.UsedDocStorageSpace = response.data[0].DOC_STORAGE_USED;
                vm.UsedTransactionSpace = response.data[0].TRANSACTION_TOTAL_SIZE;

                vm.minScreenSize = Math.round(parseFloat(vm.containerdata.SCREENSHOT_SIZE));
                vm.minFileSize = Math.round(parseFloat(vm.containerdata.FILE_SIZE));
                vm.minDocStorage =  Math.round(parseFloat(vm.containerdata.DOC_STORAGE_ASSIGED));
                vm.minTransactionSize = Math.round(parseFloat(vm.containerdata.TRANSACTION_TOTAL_SIZE));
                vm.isLoading=false;
            })

        },
        GetScreenCount: function (SCREEN_MAXLIMIT) {
            var vm = this;
            var screenminsize = parseFloat(70.50, 10) + parseFloat(5.8, 10); // aproximate size of 1 screen=70.50kb and 1 thumbnail=50kb
            vm.screenCount = Math.round(SCREEN_MAXLIMIT * 1024 / screenminsize);
        },

        SkipButton: function()
        {
            var vm = this;
            vm.$emit("nextTab");
        },
        PreviousButton: function()
        {
            var vm = this;
            vm.$emit("previousTab");
        },
        SaveStorageManagement: function () {
            var vm=this;
            var params = `maxFileSize=${vm.filesAllocation}&maxScreenSize=${vm.storage}&maxDocSize=${vm.docStorageAllocation}&maxTransactionSize=${vm.transactionSize}`;
            vm.isLoading=true,
            DataService.SaveContainerDetails(params).then(function (response) {
                if(response.data==0){
                    vm.ShowAlert("Storage has been saved succesfully","Success");
                    vm.GetStoragedata();
                    vm.$emit("nextTab");
                    vm.isLoading=false;
                }
            })
        },
        formatBytes: function(bytes, isNumeric=false , decimals = 2) {
            if (!+bytes) return '0 Bytes'

            const k = 1024
            const dm = decimals < 0 ? 0 : decimals
            // const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
            const sizes = ['MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
            const i = Math.floor(Math.log(bytes) / Math.log(k));
            if(isNumeric){
                return parseFloat((bytes / Math.pow(k, i)).toFixed(dm));
            }
            else{
                return `${parseFloat((bytes / Math.pow(k, i)).toFixed(dm))} ${sizes[i]}`;
            }
        }
    }
}
</script>
<style>
.vue-slider-dot-tooltip{
    position: absolute;
    visibility: visible;
}
.vue-slider-dot-tooltip-inner{
    background-color:#505557;
    height:20px;
}
.jslider .jslider-label {
  font-size: 14px;
  line-height: 12px;
  color: black;
  opacity: 0.5;
  white-space: nowrap;
  padding: 0 2px;
  position: absolute;
  top: -12px;
  left: 91%;
}
