<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div :class="reqFrom=='InitialSetup' ? 'dynamiccontent_base' : ''">
            <dynamic-form lang="en" ref="AddSkill" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="SaveSubmit"></dynamic-form>
        </div>
        <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">
            <div class="row">
            <div class="col-lg-12 text-right mt-4">            
                <button
                title="Skip"
                class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4"
                @click="Skip()"
                v-if="tabCount > currentTabIndex"
                >{{ $t("Skip") }}</button
                >
                <button
                type="button"
                id="aGroupTicketSave"
                title=""
                data-toggle-tooltip="tooltip"
                data-original-title="Save"
                class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                @click="SaveSubmit"
                style="margin-left: 5px"
                v-if="tabCount > currentTabIndex"
                >
                <i class="fa fa-save pr-2"></i> {{ $t("SaveAndNext") }}
                </button>            
            </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from './../../../../services/AdvanceSetupModuleService'
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom:''
    },
    data(){
        return{
            isLoading:false,
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            buttons:[],
            companyId: null,
            userId: null,
            FormSchema: [{
                    layoutType: "single",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('Skill'),
                        name: "Skill",
                        value: "",
                        placeholder: this.$t('Enter Skill '),
                        validationRules: {
                            "required": true
                        },
                        disabled: false,
                    }, ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "Description",
                        value: "",
                        placeholder: this.$t('Enter Description'),
                        validationRules: {
                            "required": false
                        },
                    }]
                }
            ]            
        }
    },
    created: function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;        
    },
    methods:{       
        SaveSubmit() {            
            var vm = this;
            vm.isLoading = true; 
            vm.$refs.AddSkill.validateForm.validate().then(result => {
                if(result){
                var jsonobj = {
                    userId: vm.userId,
                    companyId: vm.companyId,
                    name: vm.FormSchema[0].Data[0].value,
                    description: vm.FormSchema[1].Data[0].value,
                    id: 0
                };                
                var params = JSON.stringify(jsonobj);
                DataService.SaveSkill(params).then((response) => {
                    if (response.data.Status == "Success") {
                        if (response.data.Code == 0) {
                            vm.ShowAlert(vm.$t('JobTypeAlreadyExist', [vm.$t('Skill')]), "warning", true, vm.$t("Alert"));
                        } 
                        else {                           
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Skill')]), "success", true, vm.$t("Alert"));
                            vm.$parent.NextTab();
                        }                        
                    }
                });
                }
            });
            vm.isLoading = false; 
        },
        Skip: function () {
            var vm = this;
            vm.$parent.NextTab(true);
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
    }
}
</script>