<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="dynamiccontent_base">
            <tg-list classes="tableoverflowshow mt-3" :IsShowAction="true" :showCheckBox="true" :listType="[DetailListType]" ModuleName="Timer"
                SubModuleCode="Timer" IdentityColumn="category_id" HeaderText="Category" :ListData="CategorytList"
                :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
                :TotalRecords="TotalRecords" :RenderRowActionMethod="renderActions"
                :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="sortExp" :SortBy="sortBy"
                :IsFilterApplied="searchCondition ? true : false" :NorecordfoundText="$t('NoRecordfound')"
                v-on:PagerButtonClick="pagerMethod"
                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
                v-on:SortdataButtonClick="sortdata">
                <template v-slot:slotdata="{ data }">

                    <template v-if="data.column.COLUMN_NAME == 'status_name'">
                        <a class="inactive-disabled">
                            <span
                                :class="{ 'btn d-inline-block text-center text-truncate btn-success': data.row.status_name == 'Active', 'btn d-inline-block text-center text-truncate btn-danger': data.row.status_name == 'Deleted' }">
                                {{ data.row[data.column.COLUMN_NAME] }}
                            </span>
                        </a>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'category_name'">
                        <span class="current-color-list" :style="{ backgroundColor: data.row.color_code }"></span>
                        {{ data.row.category_name }}
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'applicationWithIcon'">
                        <div class="d-flex iconsWrapper align-items-center" title="">
                            <span id="RemoveSpan" title=""></span>
                            <div class="d-flex iconsWrapper align-items-center position-relative" title="">
                                <div class="mr-2" v-for="(item, itemIndex) in data.row.applicationFirstThree"
                                    :key="itemIndex">
                                    <span :class="item.icon_class ? 'appicon ' + item.icon_class : 'appicon default'"
                                        :title="item.application_display_name">

                                    </span>
                                    <em :title="item.application_display_name">
                                        {{ TruncateStr(item.application_display_name, 20) }}
                                    </em>
                                </div>
                                <span v-if="data.row.isVisible == true">
                                    <em aria-hidden="true" data-toggle="modal" style="font-size: 20px !important;"
                                        v-on:click="ShowMoreApps('myModal' + data.row.ROWNUMBER)"
                                        class="fa fa-globe text-dark"
                                        title="View More"></em>
                                </span>
                                <span v-else title="">
                                </span>
                                <div v-if="data.row.isVisible == true" class="application_dtls" title="View More">
                                    <div class="mod_content d-none" :id="'myModal' + data.row.ROWNUMBER">       
                                        <div class="mod_header p-2 bg-grey" title="">
                                            <h6 class="modal-title">Apps/Websites</h6>

                                        </div>
                                        <div class="mod_body px-2 py-0" style=" max-height: 165px; overflow-y: auto;"
                                            title="">
                                            <table class="w-100">
                                                <tbody title="">
                                                    <tr v-for="(item, itemIndex) in data.row.applicationWithIcon"
                                                        class="d-flex iconsWrapper align-items-center" :key="itemIndex">
                                                        <td :class="item.icon_class ? 'appicon ' + item.icon_class : 'appicon default'"
                                                            :title="item.application_display_name"></td>
                                                        <td :title="item.application_display_name" class="">{{
                                                            item.application_display_name }}</td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </template>
                </template>
            </tg-list>
        </div>
        <div class="mainfooter_app">
            <div class="col-lg-12 text-right mt-4 mr-3">
                <button title="Previous" class="btn btn-warning float-left px-4"
                    @click="PreviousButton()" v-if="currentTabIndex == tabCount">
                    <em class="fa fa-chevron-left pr-2"></em> 
                    {{ $t("Previous") }}
                </button>
                <!-- <button title="Next" class="btn next formbtn widthhalf px-4"
                    @click="Skip()" v-if="tabCount > currentTabIndex">
                    {{ $t("Next") }}
                    <em class="fa fa-chevron-right pl-2"></em>
                </button> -->
                <button title="Finish" class="btn btn-danger formbtn widthhalf px-4"
                    @click="Finish()" v-if="tabCount == currentTabIndex && isCompleted">
                    {{ $t("Finish") }}
                </button>
            </div>
        </div>
        <ManageCategory v-if="IsAssignCategory" :isAddEdit="isAddEdit" :CategorytList="CategorytList"
            :AssignCat='AssignCat' v-on:UpdateProgress="UpdateProgress">
        </ManageCategory>
    </section>
</template>

<script>
import Schema from '../SilentApp/SetupAppCategorySchema'
import DataService from '../../../services/DataService';
import ImportCategory from '../SilentApp/Modals/ImportCategory.vue';
import ManageCategory from '../SilentApp/Modals/ManageCategory.vue';
export default {
    components: {
         ManageCategory,
         ImportCategory
    },
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        isCompleted: false
    },
    data() {
        return {
            searchCondition: '',
            CategorytList: [],
            listActions: Schema.listActions,
            widgets: [],
            Headers: [

                {
                    "COLUMN_NAME": 'category_name',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Category'),
                    "DISPLAY_ORDER": 1,
                    settings: {
                        isInSlot: true,
                    },

                },
                {
                    "COLUMN_NAME": 'applicationWithIcon',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ApplicationWebsite'),
                    "DISPLAY_ORDER": 2,
                    settings: {
                        isInSlot: true,
                    },
                },
                {
                    "COLUMN_NAME": 'user_name',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Createdby'),
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": 'last_updated',
                    "DATA_TYPE": "datetime",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Lastupdated'),
                    "DISPLAY_ORDER": 4
                },
                {
                    "COLUMN_NAME": 'status_name',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Status'),
                    "DISPLAY_ORDER": 5,
                    settings: {
                        isInSlot: true,
                    },
                }

            ],
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            sortBy: '',
            sortExp: '',
            pageSize: 10,
            pageNum: 1,
            legendProgress: [{
                class: '',
                backgroundColor: '#17a2b8',
                name: 'Draft'
            },
            {
                class: 'legend-Progress',
                backgroundColor: '',
                name: 'Approved'
            },
            {
                class: 'legend-pending',
                backgroundColor: '',
                name: 'Pending'
            },
            {
                class: '',
                backgroundColor: '#fe6847',
                name: 'SendForCorrection'
            },
            {
                class: '',
                backgroundColor: '#ee1313',
                name: 'Rejected'
            }

            ],
            LegendArray: [
                {
                    class: 'legend-pending colorboxsquare',
                    text: this.$t('sampleData')
                }
            ],
            PageSizeOption: null,
            listheaderbuttons: Schema.buttons,
            isLoading: false,
            DetailListType: 'List',
            IsAssignCategory: false,
            usericon: false,
            user_ids: '',
            category_ids: '',
            department_ids: '',
            project_ids: '',
            application_id: '',
            isAddEdit: ""
        }
    },
    created() {
        this.FetchData();
    },
    updated() {
        $('span[title="[object Object],[object Object],[object Object],[object Object],[object Object]"]').attr('title', '');


    },
    methods: {
        ShowMoreApps(id){
            const allElements = document.querySelectorAll('[id*="myModal"]');
            allElements.forEach(element => {
                if (element.id.includes('myModal') && element.id !== id) {
                    if (!element.classList.contains('d-none'))
                        element.classList.add('d-none');
                }
            });

            const element = document.getElementById(id);

            if (!element.classList.contains('d-none')) {
                element.classList.add('d-none'); 
            } else {
                element.classList.remove('d-none');
            }
        },
        Skip: function () {
            var vm = this;
            vm.$emit("nextTab");
        },
        Finish: async function () {
            var vm = this;
            await vm.UpdateUserSettingConfig('HideContextmenu');
            await vm.UpdateUserSettingConfig('initialstartup');
            window.location.href='/User/Home';
        },
        async UpdateUserSettingConfig(type) {
            let url = `status=true&type=${type}`
            await DataService.UpdateUserSettingConfig(url).then((response) => {

            });
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        AssignCategoryClick() {
            var vm = this;
            vm.isAddEdit = false;
            vm.IsAssignCategory = true;
        },
        CloseCategoryClick() {
            var vm = this;
            vm.AssignCat = [];
            vm.IsAssignCategory = false;
        },
        EditAssignCategoryClick: function (value) {
            var vm = this;
            vm.AssignCat = value;
            vm.isAddEdit = true
            vm.IsAssignCategory = true;
        },
        async FetchData() {
            
            var vueObj = this;
            vueObj.isLoading = true;
            var url = `${vueObj.searchCondition}&pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}`;
            //var url = `${vueObj.searchCondition}&pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&user_id=${vueObj.user_ids}&category_id=${vueObj.category_ids}&application_id=${vueObj.application_id}&department_id=${vueObj.department_ids}&project_id=${vueObj.project_ids}`;
            await DataService.GetCategoryListing(url).then(response => {
                vueObj.CategorytList = JSON.parse(response.data).data;
                // console.log("Website", vueObj.CategorytList)
                vueObj.isLoading = false;
                if (vueObj.CategorytList.length > 0) {
                    vueObj.TotalRecords = vueObj.CategorytList[0].TotalRecords;
                    vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                    vueObj.CurrentPage = vueObj.pageNum;
                    vueObj.noRecord = false;
                    vueObj.isLoading = false;

                    //   vueObj.CategorytList.forEach(i=>{
                    //    i.isVisible=isVisiblee;
                    //  })
                    vueObj.CategorytList.forEach(items => {
                        if (items.applicationWithIcon == null) {
                            /// null conditionb
                            items.isVisible = false;
                        }
                        else {
                            if (items.applicationWithIcon.length > 3) {
                                items.isVisible = true;
                            }
                            else {
                                items.isVisible = false;
                            }
                        }
                        items.isCheckBoxDisabled = false;
                        if (items.IsUsed > 0) {
                            items.isCheckBoxDisabled = true

                        }
                    })
                    vueObj.CategorytList.forEach((row) => {
                        row.additionalClass = '';
                        row.isCheckBoxDisabled = false;
                        if (row.is_sample_data == true) {
                            row.additionalClass = 'warning-bar';
                            row.isCheckBoxDisabled = true;
                        }
                    })

                }
                else {
                    vueObj.CategorytList = []
                    vueObj.noRecord = true;
                    vueObj.isLoading = false;
                }

            })
     
        },
        DeleteData: function () {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("category_id")), function () {
                    let url = `ids=${val}&tableName=TALYGEN_category&primaryKey=category_id`;
                    DataService.Delete(url).then(response => {
                        if (response.data) {

                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            vm.ShowAlert(vm.$t('DeletedSuccessfully'), "Success", true, vm.$t('Alert'));
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
                        }
                        vm.FetchData();
                    });
                });
            }
        },
        SingleDeleteData: function (item) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("category_id")), function () {
                let url = `ids=${item.category_id}&tableName=TALYGEN_category&primaryKey=category_id`;
                DataService.Delete(url).then(response => {
                    if (response.data == 'DeletedSuccess') {
                        vm.ShowAlert(vm.$t('DeletedSuccessfully'), "Success", true, vm.$t('Alert'));
                        // vm.$router.push({
                        //     name: 'CategoryListing'
                        // });
                        vm.FetchData();

                    } else {
                        vm.ShowAlert(vm.$t('DeletedError', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
                    }
                });
            });
        },
        actionButtonClick(callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case 'Editdata':
                    this.EditAssignCategoryClick(item)
                    break;
                case 'DeleteData':
                    vm.SingleDeleteData(item)
                    break;
            }
        },
        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        if (row.IsUsed > 0) {
                            action.callbackfunction = '';
                            action.icon = 'fa fa-trash disabled';
                        }
                        else {
                            action.callbackfunction = 'DeleteData';
                            action.icon = 'fa fa-trash';

                        }
                        action.isdisabled = row.IsUsed > 0 ? true : false;
                        break;
                    default:
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            this.FetchData();
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "AssignCategoryClick":
                    this.AssignCategoryClick();
                    break;
                case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                    break
                case "Editdata":
                    this.Editdata()
                    break;
                case "DeleteData":
                    this.DeleteData()
                    break;
                case "ViewImport":
                    this.ViewImport()
                    break;

            }
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortExp == "") {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },
        ViewImport: function () {
            this.$router.push({
                name: 'ImportCategory'
            });
        },
        UpdateProgress: function(){
            var vm = this;
            vm.$emit("updateProgress", vm.currentTabIndex, true);
        },
    }
}
</script>

<style>
.current-color-list {
    display: inline-block;
    width: 17px !important;
    height: 9px !important;
    background-color: #000;
    cursor: pointer;
}

/* .application_dtls .modal-dialog {
    max-width: 250px;
    position: absolute;
    position: absolute;
    left: 33%;
    top: 100px;
} */

.application_dtls{
    position: absolute;
    right: 20px;
    top: -40px;
}
.iconsWrapper .appicon {
    transform: scale(0.7);
    margin-right: 0;
}

.iconsWrapper a {
    color: #333;
    font-size: 10px;
}
</style>
