<template>
    <div>
        <dynamic-form lang="en" ref="validateSetupForm" :buttons="buttons" :schema="FormSchema"
                        v-on:OnSubmit="onSubmit"></dynamic-form>
        <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">
            <div class="col-lg-12 text-right mt-4">
                <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                    v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous') }}</button>
                <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4" @click="Skip()"
                    v-if="tabCount > currentTabIndex">{{ $t('Skip') }}</button>
                <button type="button" id="aGroupTicketSave" @click="SubmitData()" title=""
                    data-toggle-tooltip="tooltip" data-original-title="Save"
                    class="popupgreenbtn btn btn-success formbtn widthhalf pr-2" style="margin-left: 5px;"
                    v-if="tabCount > currentTabIndex">
                    <i class="fa fa-save pr-2"></i> {{ $t('SaveAndNext') }}
                </button>
                <button type="button" id="aGroupTicketSave" @click="SubmitData()" title=""
                    data-toggle-tooltip="tooltip" data-original-title="Save"
                    class="popupgreenbtn btn btn-success formbtn widthhalf pr-2" style="margin-left: 5px;"
                    v-if="tabCount == currentTabIndex">
                    <i class="fa fa-save pr-2"></i> {{ $t('SaveAndClose') }}
                </button>
            </div>
        </div>
    </div>
</template>
<script>
    import setupModuleService from "@/services/AdvanceSetupModuleService";
    export default {
        props: {
            tabCount: 0,
            currentTabIndex: 0,
            reqFrom:''
        },
        data() {
            return {
                buttons: [
                    {
                        type: "submit",
                        class: "btn btn-warning",
                        text: "<i class='fa fa-save pr-2'></i>Save Of Draft",
                        isVisible: false
                    }
                ],
                FormSchema: [ {
                        layoutType: "single",
                        Data: [
                            {
                                astype: "RadioField",
                                label: this.$t('IsCheckinEnable'),
                                name: "IsCheckinEnable",
                                value: "No",
                                placeholder: "",
                                config: {
                                    options: [{
                                        name: 'Yes', value: 'Yes'
                                    }, {
                                        name: 'No', value: 'No'
                                    }],
                                    showAddIcon: false,
                                    onChange: this.onChangeMethod
                                },
                                validationRules: { "required": false },
                            },
                            {
                                astype: "CustomSelectField",
                                label: this.$t('CheckinType'),
                                name: "CheckinType",
                                mode:"tag",
                                value: "",
                                placeholder: "Select " + this.$t('CheckinType'),
                                disabled: true,
                                config: {
                                    options: [{
                                        name: 'Talygen', value: 'Talygen'
                                    },{
                                        name: 'Biometric', value: 'Biometric'
                                    }, {
                                        name: 'Face Recognition', value: 'FaceRecognition'
                                    }],
                                    showAddIcon: false,
                                    onChange: this.onChangeMethod
                                },
                                validationRules: { "required": false },
                            }
                        ]
                    },
                    {
                        layoutType: "triple",
                        Data: [
                            {
                                astype: "RadioField",
                                label: this.$t('EnableCheckinForMobile'),
                                name: "EnableCheckinForMobile",
                                value: "No",
                                placeholder: "",
                                disabled: true,
                                config: {
                                    options: [{
                                        name: 'Yes', value: 'Yes'
                                    }, {
                                        name: 'No', value: 'No'
                                    }],
                                    showAddIcon: false,
                                    onChange: this.onChangeMethod
                                },
                                validationRules: { "required": false },
                            },
                            {
                                astype: "CheckBoxField",
                                label: this.$t('MobileCheckinType'),
                                name: "MobileCheckinType",
                                value: "",
                                placeholder: "",
                                disabled: true,
                                config: {
                                    options: [{
                                        name: 'Auto', value: 'Auto'
                                    }, {
                                        name: 'Manual', value: 'Manual'
                                    }],
                                    showAddIcon: false,
                                    onChange: this.onChangeMethod
                                },
                                validationRules: { "required": false },
                            },
                            {
                                astype: "RadioField",
                                label: this.$t('Restricted With Location'),
                                name: "RestrictedWithLocation",
                                value: "Yes",
                                placeholder: "",
                                disabled: true,
                                config: {
                                    options: [{
                                        name: 'Yes', value: 'Yes'
                                    }, {
                                        name: 'No', value: 'No'
                                    }],
                                    showAddIcon: false,
                                    onChange: this.onChangeMethod
                                },
                                validationRules: { "required": false },
                            }
                        ]
                    },
                    {
                        layoutType: 'triple',
                        Data: [
                        {
                                astype: "RadioField",
                                label: this.$t('EnableCheckinForWeb'),
                                name: "EnableCheckinForWeb",
                                value: "No",
                                placeholder: "",
                                disabled: true,
                                config: {
                                    options: [{
                                        name: 'Yes', value: 'Yes'
                                    }, {
                                        name: 'No', value: 'No'
                                    }],
                                    showAddIcon: false,
                                    onChange: this.onChangeMethod
                                },
                                validationRules: { "required": false },
                            },
                            {
                                astype: "CheckBoxField",
                                label: this.$t('WebCheckinType'),
                                name: "WebCheckinType",
                                value: "",
                                placeholder: "",
                                disabled: true,
                                config: {
                                    options: [{
                                        name: 'Auto', value: 'Auto'
                                    }, {
                                        name: 'Manual', value: 'Manual'
                                    }],
                                    showAddIcon: false,
                                    onChange: this.onChangeMethod
                                },
                                validationRules: { "required": false },
                            },
                            {
                                astype: "RadioField",
                                label: this.$t('RestrictedWithWhiteListedIp'),
                                name: "RestrictedWithWhiteListedIpForWeb",
                                value: "No",
                                placeholder: "",
                                disabled: true,
                                config: {
                                    options: [{
                                        name: 'Yes', value: 'Yes'
                                    }, {
                                        name: 'No', value: 'No'
                                    }],
                                    showAddIcon: false,
                                    onChange: this.onChangeMethod
                                },
                                validationRules: { "required": false },
                            },
                        ]
                    },
                    {
                        layoutType: "single",
                        Data: [
                            {
                                astype: "TextAreaField",
                                label: this.$t('WhileListedIPforWeb'),
                                name: "WhileListedIPforWeb",
                                value: "",
                                placeholder: "",
                                disabled: true,
                                validationRules: { "required": false },
                            },
                        ]
                    },
                    {
                        layoutType: "triple",
                        Data: [
                            {
                                astype: "RadioField",
                                label: this.$t('EnableCheckinForDesktop'),
                                name: "EnableCheckinForDesktop",
                                value: "No",
                                placeholder: "",
                                disabled: true,
                                config: {
                                    options: [{
                                        name: 'Yes', value: 'Yes'
                                    }, {
                                        name: 'No', value: 'No'
                                    }],
                                    showAddIcon: false,
                                    onChange: this.onChangeMethod
                                },
                                validationRules: { "required": false },
                            },
                            {
                                astype: "CheckBoxField",
                                label: this.$t('DesktopCheckinType'),
                                name: "DektopCheckinType",
                                value: "",
                                placeholder: "",
                                disabled: true,
                                config: {
                                    options: [{
                                        name: 'Auto', value: 'Auto'
                                    }, {
                                        name: 'Manual', value: 'Manual'
                                    }],
                                    showAddIcon: false,
                                    onChange: this.onChangeMethod
                                },
                                validationRules: { "required": false },
                            },
                            {
                                astype: "RadioField",
                                label: this.$t('RestrictedWithWhiteListedIp'),
                                name: "RestrictedWithWhiteListedIpForDesktop",
                                value: "No",
                                placeholder: "",
                                disabled: true,
                                config: {
                                    options: [{
                                        name: 'Yes', value: 'Yes'
                                    }, {
                                        name: 'No', value: 'No'
                                    }],
                                    showAddIcon: false,
                                    onChange: this.onChangeMethod
                                },
                                validationRules: { "required": false },
                            },
                        ]
                    },
                    {
                        layoutType: "single",
                        Data: [
                            {
                                astype: "TextAreaField",
                                label: this.$t('WhileListedIPforDesktop'),
                                name: "WhileListedIPforDesktop",
                                value: "",
                                disabled: true,
                                placeholder: "",
                                validationRules: { "required": false },
                            },
                        ]
                    },
                    
                ]
            }
        },
        mounted: function() {
            this.getcheckinSettings();
        },
        methods: {
            Skip: function () {
                var vm = this;
                vm.$emit("nextTab")
            },
            PreviousButton: function () {
                var vm = this;
                vm.$emit("previousTab");
            },
            onChangeMethod: function(x, y) {
                let ths = this;
                if(y.name == 'IsCheckinEnable'){
                    if(y.value == 'Yes') {
                        ths.$refs.validateSetupForm.updateProperty("CheckinType", 'disabled', false);
                        ths.$refs.validateSetupForm.updateProperty("EnableCheckinForMobile", 'disabled', false);
                        ths.$refs.validateSetupForm.updateProperty("EnableCheckinForWeb", 'disabled', false);
                        ths.$refs.validateSetupForm.updateProperty("EnableCheckinForDesktop", 'disabled', false);
                    } else {
                        ths.$refs.validateSetupForm.updateProperty("CheckinType", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("EnableCheckinForMobile", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("EnableCheckinForWeb", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("EnableCheckinForDesktop", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("MobileCheckinType", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithLocation", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("WebCheckinType", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithWhiteListedIpForWeb", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("DektopCheckinType", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithWhiteListedIpForDesktop", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("WhileListedIPforWeb", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("WhileListedIPforDesktop", 'disabled', true);

                        ths.$refs.validateSetupForm.updateProperty("CheckinType", 'value', []);
                        ths.$refs.validateSetupForm.updateProperty("EnableCheckinForMobile", 'value', 'No');
                        ths.$refs.validateSetupForm.updateProperty("EnableCheckinForWeb", 'value', 'No');
                        ths.$refs.validateSetupForm.updateProperty("EnableCheckinForDesktop", 'value', 'No');
                        ths.$refs.validateSetupForm.updateProperty("MobileCheckinType", 'value', []);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithLocation", 'value', 'No');
                        ths.$refs.validateSetupForm.updateProperty("WebCheckinType", 'value', []);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithWhiteListedIpForWeb", 'value', 'No');
                        ths.$refs.validateSetupForm.updateProperty("DektopCheckinType", 'value', []);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithWhiteListedIpForDesktop", 'value', 'No');
                        ths.$refs.validateSetupForm.updateProperty("WhileListedIPforWeb", 'value', '');
                        ths.$refs.validateSetupForm.updateProperty("WhileListedIPforDesktop", 'value', '');
                        
                    }
                } else if (y.name == 'EnableCheckinForMobile') {
                    if(y.value == 'Yes'){
                        ths.$refs.validateSetupForm.updateProperty("MobileCheckinType", 'disabled', false);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithLocation", 'disabled', false);
                    } else {
                        ths.$refs.validateSetupForm.updateProperty("MobileCheckinType", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithLocation", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("MobileCheckinType", 'value', []);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithLocation", 'value', 'No');
                    }
                } else if (y.name == 'EnableCheckinForWeb') {
                    if(y.value == 'Yes'){
                        ths.$refs.validateSetupForm.updateProperty("WebCheckinType", 'disabled', false);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithWhiteListedIpForWeb", 'disabled', false);
                    } else {
                        ths.$refs.validateSetupForm.updateProperty("WebCheckinType", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithWhiteListedIpForWeb", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("WhileListedIPforWeb", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("WebCheckinType", 'value', []);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithWhiteListedIpForWeb", 'value', 'No');
                        ths.$refs.validateSetupForm.updateProperty("WhileListedIPforWeb", 'value', '');
                    }
                } else if (y.name == 'EnableCheckinForDesktop') {
                    if(y.value == 'Yes'){
                        ths.$refs.validateSetupForm.updateProperty("DektopCheckinType", 'disabled', false);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithWhiteListedIpForDesktop", 'disabled', false);
                    } else {
                        ths.$refs.validateSetupForm.updateProperty("DektopCheckinType", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithWhiteListedIpForDesktop", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("WhileListedIPforDesktop", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("DektopCheckinType", 'value', []);
                        ths.$refs.validateSetupForm.updateProperty("RestrictedWithWhiteListedIpForDesktop", 'value', 'No');
                        ths.$refs.validateSetupForm.updateProperty("WhileListedIPforDesktop", 'value', '');
                    }
                } else if (y.name == 'RestrictedWithWhiteListedIpForWeb') {
                    if(y.value == 'Yes'){
                        ths.$refs.validateSetupForm.updateProperty("WhileListedIPforWeb", 'disabled', false);
                    } else {
                        ths.$refs.validateSetupForm.updateProperty("WhileListedIPforWeb", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("WhileListedIPforWeb", 'value', '');
                    }

                } else if (y.name == 'RestrictedWithWhiteListedIpForDesktop') {
                    if(y.value == 'Yes'){
                        ths.$refs.validateSetupForm.updateProperty("WhileListedIPforDesktop", 'disabled', false);
                    } else {
                        ths.$refs.validateSetupForm.updateProperty("WhileListedIPforDesktop", 'disabled', true);
                        ths.$refs.validateSetupForm.updateProperty("WhileListedIPforDesktop", 'value', '');
                    }

                }
            },
            SubmitData: function() {
                let ths = this;
                ths.$refs.validateSetupForm.validateForm.validate().then(result => {
                    if (result) {
                        var obj = {
                            IsCheckinEnable: "",
                            CheckinType: "",
                            EnableCheckinForMobile: "",
                            MobileCheckinType: "",
                            RestrictedWithLocation: "",
                            EnableCheckinForWeb: "",
                            WebCheckinType: "",
                            RestrictedWithWhiteListedIpForWeb: "",
                            WhileListedIPforWeb: "",
                            EnableCheckinForDesktop: "",
                            DektopCheckinType: "",
                            RestrictedWithWhiteListedIpForDesktop: "",
                            WhileListedIPforDesktop: ""
                        }
                        Object.keys(obj).forEach(key => {
                            console.log(key, obj[key]);
                            obj[key] = ths.$refs.validateSetupForm.getPropertyValue(key, "value");
                            if(key == 'CheckinType') {
                                var values = []
                                obj[key].forEach(x =>{
                                    values.push(x.value)
                                })
                                obj[key] = values;
                            }
                        });
                        setupModuleService.saveCheckinCheckoutSettings(obj).then(response => {
                            if(response != null && response.data != null && response.data.status == "Success"){
                                ths.ShowAlert(ths.$t("OffHoursSuccess", [ths.$t("CheckInCheckOutSettings")]), "success", true, ths.$t("Alert"));
                            }
                        });
                    }
                })
            },
            getcheckinSettings: function() {
                //getCheckinCheckoutSettingForEdit
                let ths = this;
                setupModuleService.getCheckinCheckoutSettingForEdit().then(res => {
                    if(res.data != null && res.data.status == "Success"){
                        var arrData = JSON.parse(res.data.result);
                        if(arrData.length > 0){
                            var MobileCheckinType = [], WebCheckinType = [], DektopCheckinType = [], CheckinType = [], getCheckinTypeOptions = ths.$refs.validateSetupForm.getPropertyValue("CheckinType", "config.options");;
                            if(arrData[0].auto_entry_for_mobile == true){
                                MobileCheckinType.push("Auto")
                            }
                            if(arrData[0].manual_entry_for_mobile == true){
                                MobileCheckinType.push("Manual")
                            }
                            if(arrData[0].auto_entry_for_web == true){
                                WebCheckinType.push("Auto")
                            }
                            if(arrData[0].manual_entry_for_web == true){
                                WebCheckinType.push("Manual")
                            }
                            if(arrData[0].auto_entry_for_desktop == true){
                                DektopCheckinType.push("Auto")
                            }
                            if(arrData[0].manual_entry_for_desktop == true){
                                DektopCheckinType.push("Manual")
                            }
                            getCheckinTypeOptions.forEach(x => {
                                if((arrData[0].enable_talygen == true && x.value == "Talygen") 
                                        || (arrData[0].enable_biometric == true && x.value == "Biometric") 
                                        || (arrData[0].enable_face_reco == true && x.value == "FaceRecognition")
                                    ){
                                    CheckinType.push({ label: x.name, value: x.value});
                                }
                            })
                            ths.$refs.validateSetupForm.updateProperty("IsCheckinEnable", 'value', (arrData[0].is_checkin_enable == true ? 'Yes': 'No'));
                            ths.$refs.validateSetupForm.updateProperty("EnableCheckinForMobile", 'value', (arrData[0].enable_for_mobile == true ? 'Yes': 'No'));
                            ths.$refs.validateSetupForm.updateProperty("RestrictedWithLocation", 'value', (arrData[0].restricted_for_mobile == true ? 'Yes': 'No'));
                            ths.$refs.validateSetupForm.updateProperty("EnableCheckinForWeb", 'value', (arrData[0].enable_for_web == true ? 'Yes': 'No'));
                            ths.$refs.validateSetupForm.updateProperty("RestrictedWithWhiteListedIpForWeb", 'value', (arrData[0].restricted_for_web == true ? 'Yes': 'No'));
                            ths.$refs.validateSetupForm.updateProperty("EnableCheckinForDesktop", 'value', (arrData[0].enable_for_desktop == true ? 'Yes': 'No'));
                            ths.$refs.validateSetupForm.updateProperty("RestrictedWithWhiteListedIpForDesktop", 'value', (arrData[0].restricted_for_desktop == true ? 'Yes': 'No'));
                            ths.$refs.validateSetupForm.updateProperty("WhileListedIPforWeb", 'value', (arrData[0].white_listed_ip_web));
                            ths.$refs.validateSetupForm.updateProperty("WhileListedIPforDesktop", 'value', (arrData[0].white_listed_ip_desktop));
                            ths.$refs.validateSetupForm.updateProperty("MobileCheckinType", 'value', MobileCheckinType);
                            ths.$refs.validateSetupForm.updateProperty("WebCheckinType", 'value', WebCheckinType);
                            ths.$refs.validateSetupForm.updateProperty("DektopCheckinType", 'value', DektopCheckinType);
                            ths.$refs.validateSetupForm.updateProperty("CheckinType", 'value', CheckinType);
                            ths.FormSchema.forEach(x => {
                                x.Data.forEach(y => {
                                    ths.onChangeMethod(null, y);
                                })
                            })
                        }
                        
                    }                    
                });
            }
        },
    }
</script>