<template lang="en">
    <section>
       
        <!-- <div>
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{$t('AddApprovalGroup')}}</span>
                    </div>
                </div>
            </div>
        </div> -->
        <!-- <div class="border p-3"> -->
            <div :class="reqFrom=='InitialSetup' ? 'dynamiccontent_base' : ''">
                <dynamic-form lang="en" ref="validateGroup" :buttons=false :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
            </div>
            <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">
                <div class="col-mt-3">
                    <div class="row flex-row-reverse mt-3">
                        <div class="col-lg-12 text-right">
                            <button v-if="(currentTabIndex>1) ? true : false" title="Previous" @click="PreviousButton()" class="btn btn-warning float-left"><i class="fa fa-chevron-left pr-2"></i> {{$t('Previous')}}</button>
                            <button v-if="(tabCount  > currentTabIndex) ? true : false" title="Skip" @click="SkipButton()" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4">{{$t('Skip')}}</button>
                            <button type="submit" @click="onSubmit" class="btn btn-success ml-3" title="Save"
                                ><i class="fa fa-save pr-2"></i>{{$t('SaveAndNext')}}</button>
                        </div>
                    </div>
                </div>
            </div>
        <!-- </div> -->
    </section>
    </template>
    
<script>
import DataService from "@/services/AdvanceSetupModuleService";
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom:''
    },
    data() {

        return {
            isLoading: false,
            DataLoaded: 0,
            ApprovalGroupId: 0,
            UserList: [],
            TagsSelectedArray: [],
            FormSchema: [{
                layoutType: "single",
                Data: [{
                    astype: "TextField",
                    label: this.$t('ApprovalGroupName'),
                    name: "ApprovalGroupName",
                    value: "",
                    placeholder: "",
                    config: {
                        onChange: this.checkName
                    },
                    validationRules: {
                        "required": true
                    },
                },
                {
                    astype: "SelectField",
                    label: this.$t('Status'),
                    name: "Status",
                    value: 1001,
                    placeholder: "",
                    validationRules: {
                        "required": true
                    },
                    config: {
                        options: [{
                            value: "1001",
                            name: 'Active'
                        }, {
                            value: "1002",
                            name: 'Inactive'
                        }]
                    },
                },
                {
                    astype: "MultiSelectField",
                    label: this.$t('Users'),
                    name: "Users",
                    value: "",
                    mode: "tag",
                    placeholder: this.$t('Select'),
                    validationRules: {
                        "required": true
                    },
                    config: {
                        options: []
                    },
                },

                ]
            },
            {
                layoutType: "Single",
                Data: [{
                    astype: "TextAreaField",
                    label: this.$t('Description'),
                    name: "Description",
                    value: "",
                    placeholder: "",
                },]
            },
            ],
            buttons: [
                {
                    type: "button",
                    class: "btn skip-btn-clr",
                    text: "Skip",
                    onClick: this.Skip
                },
                {
                    type: "submit",
                    class: "btn btn-success btnSave",
                    text: "<i class='fa fa-save pr-2'></i>Save & Next",
                }
            ],
        }
    },
    created: async function () {
        await this.GetNotficationUsers();
        if (this.$route.params.id != null) {
            this.ApprovalGroupId = this.$route.params.id;
            await this.ManageItemsGroup();
        }
        this.DataLoaded = 1;
    },
    methods: {
        onSubmit: function () {
            debugger
            var vm = this;
            var jsonobj = {
                ApprovalGroupId: vm.ApprovalGroupId,
                ApprovalGroupName: vm.$refs.validateGroup.getPropertyValue("ApprovalGroupName", "value"),
                Description: vm.$refs.validateGroup.getPropertyValue("Description", "value"),
                Users: Array.prototype.map.call(vm.$refs.validateGroup.getPropertyValue("Users", "value"), function (item) {
                    return item.value;
                }).join(","),
                StatusId: vm.$refs.validateGroup.getPropertyValue("Status", "value"),
            }

            var poststring = JSON.stringify(jsonobj);
            vm.$refs.validateGroup.validateForm.validate().then(result => {
                if(result){
                    DataService.AddGroup(poststring).then(response => {
                    if (response.data == '-1') {
                        vm.ShowAlert(vm.$t('AlreadyMapped'), "warning", true, vm.$t("Alert"));
                    } else if (response.data == '-2') {
                        vm.ShowAlert(vm.$t('AlreadyMapped'), "warning", true, vm.$t("Alert"));
                    }
                    else if (response.data == '-3') {
                        vm.ShowAlert(vm.$t("SomethingWentWrong"), "failure", true, vm.$t('Alert'));
                    }
                    else {
                        //vm.$parent.$parent.GetTimerStatusDetail();
                        vm.ShowAlert(vm.$t("ApprovalGroupSuccessfullyAdded"), "success", true, vm.$t('Alert'));
                        if (vm.tabCount > 1) {
                            vm.$emit("nextTab")
                            vm.$emit("updateProgress", vm.currentTabIndex, true);
                        }
                        else {
                            vm.clearFields();
                        }
                    }
                });
            
                }
            })
        },
        GetNotficationUsers: async function () {
            var vm = this;
            var url = `isIncludeLoginUserId=1&approvalGroupIds=&approvalChainIds=&moduleName=APPROVALRULE&DepartmentIds`;
            await DataService.LoadUsersByCompanyId(url).then(response => {
                var json = response.data;
                if (json != null) {
                    json.forEach(function (item, index) {
                        (vm.FormSchema)[0].Data[2].config.options.push({
                            name: `${item.USERNAME}`,
                            value: `${item.USER_ID}`
                        })
                    })
                }
            });
            (vm.FormSchema)[0].Data[2].config.options
        },
        ManageItemsGroup: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `id=${this.$route.params.id}`
            await DataService.GetById(url).then(response => {
                var groupData = JSON.parse(response.data)
                if (groupData.length > 0) {
                    (this.FormSchema)[0].Data[0].value = groupData[0].approval_group_name;
                    (this.FormSchema)[0].Data[1].value = groupData[0].status_id;
                    vm.UserList = groupData[0].USERS.split(",");
                    var listUsers = [];
                    for (let i = 0; i < vm.UserList.length - 1; i++) {
                        listUsers.push(vm.UserList[i]);
                    }
                    listUsers.forEach((item) => {
                        var obj = (vm.FormSchema)[0].Data[2].config.options.find(x => x.value == item);
                        if (obj != undefined) {
                            vm.TagsSelectedArray.push({
                                label: obj.name,
                                value: obj.value
                            })
                        }
                    });
                    (this.FormSchema)[0].Data[2].value = vm.TagsSelectedArray;
                    (this.FormSchema)[1].Data[0].value = groupData[0].description;
                }
            })
            vm.isLoading = false;
        },
        // onCancel: function () {
        //    this.$parent.$parent.$parent.IsshowConfiguration = false;
        // },
        checkName(e, Fields) {
            var vm = this;
            var groupName = Fields.value
            var url = `ApprovalGroupName=${groupName}`
            DataService.CheckDuplicateGroup(url).then(response => {
                if (response.data == true && vm.FormSchema[0].Data[0].value) {
                    vm.FormSchema[0].Data[0].value = '';
                    vm.ShowAlert(vm.$t('NameExist', [vm.$t('Group')]), "warning", true, vm.$t("Alert"));
                    vm.FormSchema[0].Data[0].validationRules.required = true;
                }
            });
        },
        clearFields: function () {
            var vm = this;
           // debugger;
            vm.FormSchema[0].Data[0].value="";
            vm.FormSchema[0].Data[1].value="";
            vm.FormSchema[0].Data[2].value="";
            vm.FormSchema[0].Data[3].value="";
            vm.$refs.validateGroup.validateForm.reset();
         
  
        },
        SkipButton: function()
        {
            var vm = this;
            vm.$emit("nextTab");
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
    }
}
</script>
    