<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="col-md-12 p-0">
            <div class="tab-content">
                <div class="row">
                    <div class="col-lg-12 text-right mb-3">
                        <button
                    title="Skip"
                    class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4"
                    @click="Skip()"
                    v-if="tabCount > currentTabIndex"
                    >{{ $t("Skip") }}</button>
                    </div>
                </div>
                <dynamic-form lang="en" ref="updateuserprofile" :buttons="buttons":schema="FormSchema">
                    <template v-slot:tgslot-profile_image="{ data }">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 prof_control float-left p-0 d-flex flex-wrap">
                            <div class="user-detail bg-light p-2 w-100 border position-relative float-left">
                                <div title="Profile Image" class="user-image">
                                    <img id="maleProfile" src="https://talygenv.github.io/Vue-UI-Content/images/avatar-icon.jpg" alt="Profile Image" v-if="(base64String=='' || base64String==null) && (profilePicture =='' || profilePicture ==null) && (gender=='M' || gender=='' || gender==null)" loading="lazy">
                                    <img id="femaleProfile" src="https://talygenv.github.io/Vue-UI-Content/images/avatar-icon-girl.jpg" alt="Profile Image" v-if="(base64String=='' || base64String==null) && (profilePicture =='' || profilePicture ==null) && gender=='F'" loading="lazy">
                                    <img :src="'https://cogniterblob.blob.core.windows.net/'+containerName+'/Upload/UserProfileImage/' + profilePicture" alt="Profile Image"
                                        v-if="(base64String=='' || base64String==null) && (profilePicture !='' && profilePicture !=null)" loading="lazy">
                                    <img :src="'data:image/gif;base64,'+ base64String" loading="lazy" v-if="base64String !=''">
                                </div> 
                                <div class="user-name ml-4 mt-3">
                                    <label class="float-left pb-2"> 
                                        {{ $t("UploadANewPhoto") }}
                                    </label>
                                </div> 
                                <div class="user-name ml-4 mt-0">
                                    <div class="col-10 col-sm-10 col-lg-10 col-xl-10 float-left p-0">
                                        <input type="file" :key="fileInputKey" id="flAvatar" v-on:change="GetBase64String($event)" size="19" accept=".jpeg,.jpg,.jpe,.gif,.bmp,.dib,.ico,.png,.tif" name="flAvatar" tabindex="-1" style="position: absolute; clip: rect(0px, 0px, 0px, 0px);"> 
                                        <div class="bootstrap-filestyle input-group">
                                            <input type="text" v-bind:value="attachmentName" id="aaaa" disabled="disabled" class="form-control disabled"> 
                                            <div class="input-group-append" style="cursor: pointer;">
                                                <span tabindex="0" class="group-span-filestyle input-group-btn input-group-text bg-white">
                                                    <label title="Click here to select a file." for="flAvatar" class="mb-0 font-weight-normal "> 
                                                        {{ $t('FileControlText') }}
                                                    </label>
                                                </span>
                                            </div>
                                        </div>
                                    </div> 
                                    <div class="col-2 col-sm-2 col-lg-2 col-xl-2 float-left pl-0">
                                        <a id="clearUserfile" v-on:click="ResetProfileImage()" href="javascript:;"  title="Click here to clear attachment" class="actionicons float-left round-icon-small btn-danger-light ml-2">
                                            <i class="fa fa-undo"></i>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </template>
                </dynamic-form>
                <div class="row">
                <div class="col-lg-12 text-right">
                </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
export default{
    props: {
        tabCount: 0,
        currentTabIndex: 0,
    },
    data(){
        return{
            isLoading:false,
            UserTitles: [],
            TimeZones: [],
            fileData: [],
            fileInputKey: 0,
            profilePicture:'',
            gender:'',
            containerName:'',
            attachmentName:'',
            base64String:'',
            FormSchema:[
                {
                    layoutType: "single",
                    groupLayoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            name: "profile_image",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                            }
                        }
                    ],
                },
                {
                layoutType: "single",
                groupLayoutType: "double",
                group_name: "Setup Profile",
                group_display_name: this.$t("SetupProfile"),
                Data: [
                        {
                            astype: "SelectField",
                            label: this.$t("Title"),
                            name: "title",
                            value: "",
                            config: {
                                options: []
                            }
                        },
                        {
                            astype: "TextField",
                            label: this.$t("FirstName"),
                            name: "first_name",
                            value: "",
                            placeholder: this.$t("FirstName"),
                            validationRules: {
                                required: true,
                        }
                        },
                        {
                            astype: "TextField",
                            label: this.$t("MiddleName"),
                            name: "middle_name",
                            value: "",
                            placeholder: this.$t("MiddleName")
                        },
                        {
                            astype: "TextField",
                            label: this.$t("LastName"),
                            name: "last_name",
                            value: "",
                            placeholder: this.$t("LastName"),
                            validationRules: {
                                required: true,
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t("Gender"),
                            name: "gender",
                            value: "",
                            config: {
                                options: [{name:'Male',value:'M'},{name:'Female',value:'F'}],
                                onChange: this.onChangeGender,
                            }
                        }
                    ]
                },
                {
                layoutType: "single",
                groupLayoutType: "double",
                group_name: "Contact Information",
                group_display_name: this.$t("ContactInformation"),
                Data: [
                        {
                            astype: "TextField",
                            label: this.$t("Email"),
                            name: "email_id",
                            value: "",
                            placeholder: this.$t("Email"),
                            validationRules: {
                                required: true,
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t("PhoneNumber"),
                            name: "phone_number",
                            value: "",
                            placeholder: this.$t("PhoneNumber")
                        }
                    ]
                },
                {
                layoutType: "single",
                groupLayoutType: "double",
                group_name: "Setup Time Zone",
                group_display_name: this.$t("SetupTimeZone"),
                Data: [
                        {
                            astype: "SelectField",
                            label: this.$t("TimeZone"),
                            name: "timezone",
                            value: "",
                            config: {
                                options: []
                            },
                            validationRules: {
                                required: true,
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t("TimeFormat"),
                            name: "time_format",
                            value: "",
                            config: {
                                options: [{name:this.$t('Format_12Hrs'),value:'12'},{name:this.$t('Format_24Hrs'),value:'24'}]
                            }
                        }
                    ]
                }
                        
            ],
            buttons:[
                {
                    type: "button",
                    class: "popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4",
                    text: this.$t('Skip'),
                    isVisible: (this.tabCount > this.currentTabIndex) ? true : false,
                    onClick: this.Skip
                },
                {
                    type: "button",
                    class: "btn btn-warning float-left px-4",
                    text: '<i class="fa fa-chevron-left pr-2"></i> ' + this.$t('Previous'),
                    isVisible: (this.currentTabIndex > 1) ? true : false,
                    onClick: this.PreviousButton
                },
                {
                    type: "submit",
                    class: "popupgreenbtn btn btn-success formbtn widthhalf pr-2",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('SaveAndNextWizard'),
                    name: "SaveAndNext",
                    isVisible: (this.tabCount > this.currentTabIndex) ? true : false,
                    onClick: this.SaveSubmit
                }
            ],
        }
    },
    async created(){
        var vm = this;
        debugger
        await vm.GetUserTitlesDDL();
        await vm.GetTimezoneDDL();
        await vm.GetUserDetailByUserId();
    },
    methods:{
        Skip: function () {
            var vm = this;
            vm.$emit("nextTab");
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        async SaveSubmit(btnType){
            var vm = this;
            vm.$refs.updateuserprofile.validateForm.validate().then(async result => {
                if(result){
                    vm.isLoading = true;
                    await vm.UploadProfilePicture();
                    var jsonObj = {
                        user_title_id: vm.FormSchema[1].Data[0].value,
                        first_name: vm.FormSchema[1].Data[1].value,
                        middle_name: vm.FormSchema[1].Data[2].value,
                        last_name: vm.FormSchema[1].Data[3].value,
                        gender: vm.FormSchema[1].Data[4].value,
                        email_id: vm.FormSchema[2].Data[0].value,
                        phone_number: vm.FormSchema[2].Data[1].value,
                        time_zone_id: vm.FormSchema[3].Data[0].value ? vm.FormSchema[3].Data[0].value : null,
                        time_format: vm.FormSchema[3].Data[1].value,
                        profile_path: vm.profilePicture
                    }
                    var objectPostString = JSON.stringify(jsonObj);
                    await DataService.UpdateUserProfile(objectPostString).then(function (response) {
                        if (response.data && response.data.toLowerCase() == 'success') {
                            vm.ShowAlert(vm.$t('UpdatedSuccess',true,[vm.$t('User')]), 'success', true, vm.$t('Alert'));
                            vm.Skip();
                        }
                    })
                    vm.isLoading = false; 
                }
            })
        },
        async UploadProfilePicture() {
            var vm = this;
            var formData = JSON.stringify(vm.fileData);
            await DataService.UploadFile(formData).then(function (response) {
                if (response.data && response.data.length > 0) {
                    if (response.data[0].Name.length > 0) {
                        var img = response.data[0].Name.split('/')[response.data[0].Name.split('/').length - 1]
                        vm.profilePicture = img;
                    }
                }
            })
        },
        async GetUserTitlesDDL(){
            var vm = this;
            vm.isLoading = true; 
            var param = 'moduleName=&type=USER_TITLE';
            await DataService.GetDDLData(param).then(response => {
                vm.isLoading = false; 
                vm.UserData = response.data.DATA;
                vm.UserData.forEach((item, indx) => {
                    vm.FormSchema[1].Data[0].config.options.push({
                        name: item.name,
                        value: item.value
                    })
                })
                vm.$refs.updateuserprofile.UpdateKeyValue();
            });
        },
        async GetTimezoneDDL(){
            var vm = this;
            vm.isLoading = true; 
            var param = 'moduleName=&type=TIMEZONE';
            await DataService.GetDDLData(param).then(response => {
                vm.isLoading = true; 
                vm.UserData = response.data.DATA;
                vm.UserData.forEach((item, indx) => {
                    vm.FormSchema[3].Data[0].config.options.push({
                        name: item.name,
                        value: item.value
                    })
                })
                vm.$refs.updateuserprofile.UpdateKeyValue();
            });
        },
        async GetUserDetailByUserId(){
            var vm = this;
            vm.isLoading = true; 
            await DataService.GetUserDetailsByUserId().then(response => {
                vm.isLoading = false;
                if (response.data && response.data.length > 0) {
                    var UserDetails = response.data[0];
                    vm.FormSchema[1].Data[0].value = UserDetails.USER_TITLE_ID ? UserDetails.USER_TITLE_ID :'';
                    vm.FormSchema[1].Data[1].value = UserDetails.FIRST_NAME;
                    vm.FormSchema[1].Data[2].value = UserDetails.MIDDLE_NAME;
                    vm.FormSchema[1].Data[3].value = UserDetails.LAST_NAME;
                    vm.FormSchema[1].Data[4].value = UserDetails.GENDER ? UserDetails.GENDER : '';
                    vm.gender = UserDetails.GENDER ? UserDetails.GENDER : '';

                    vm.FormSchema[2].Data[0].value = UserDetails.Email;
                    vm.FormSchema[2].Data[1].value = UserDetails.USER_PHONE;

                    vm.FormSchema[3].Data[0].value = UserDetails.TIMEZONE_ID ? UserDetails.TIMEZONE_ID : '';
                    vm.FormSchema[3].Data[1].value = UserDetails.TIME_FORMAT ? UserDetails.TIME_FORMAT :'';                    
                    
                    vm.profilePicture = UserDetails.AVATAR;
                    vm.containerName = UserDetails.CONTAINERNAME;
                    vm.attachmentName = UserDetails.ATTACHEMENT_NAME;
                }
            });
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];
                var files = evt.target.files; // FileList object
                vm.attachmentName = files[0].name;
                for (var i = 0; i < files.length; i++) {
                    var f = files[i];
                    var reader = new FileReader();
                    // Closure to capture the file information.
                    reader.onload = (function (theFile) {
                        return function (e) {
                            var binaryData = e.target.result;
                            //Converting Binary Data to base 64
                            var base64StringEncoded = window.btoa(binaryData);
                            vm.base64String = base64StringEncoded;
                            //showing file converted to base64

                            var fileName = f.name;
                            //vm.fileName = fileName;
                            var n = fileName.lastIndexOf(".");
                            var extensionName = fileName.substring(n + 1, fileName.length);

                            //vm.fileExt += "." + extensionName;
                            var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                            var fileSize = f.size;
                            //vm.fileSize = fileSize;
                            var fileObj = {
                                attachement_mime: attachemnetMime,
                                fileName: fileName,
                                userId: vm.GetUserInfo.ID,
                                companyId: vm.GetUserInfo.CompanyId,
                                id: 0,
                                moduleName: "initialsetup",
                                subModuleCode: "",
                                containerName: vm.GetUserInfo.ContainerName,
                                base64String: base64StringEncoded,
                                attachement_path: ''
                            };

                            vm.fileData.push(fileObj);
                        };
                    })(f);
                    // Read in the image file as a data URL.
                    reader.readAsBinaryString(f);
                }
            }
            catch (ex) {
            }
        },
        ResetProfileImage() {
            var vm = this;
            vm.fileData = [];
            vm.attachmentName = '';
            vm.base64String = '';
            vm.profilePicture = '';
            vm.fileInputKey += 1;
        },
        onChangeGender(event){
            var vm = this;
            vm.gender = event.target.value;
        }
    }
}
</script>