<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <div :class="reqFrom=='InitialSetup' ? 'dynamiccontent_base' : ''">
            <dynamic-form lang="en" ref="validatepriority" :schema="FormSchema"
                v-on:OnSubmit="onSubmit"></dynamic-form>
        </div>
        <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">
        <div class="row">
            <div class="col-lg-12 text-right mt-4">
                <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                    v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous') }}</button>
                <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4"  @click="Skip()"
                    v-if="tabCount > currentTabIndex">{{ $t('Skip') }}</button>
                <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip"
                    data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2" @click="onSubmit"
                    style="margin-left: 5px;" v-if="tabCount > currentTabIndex">
                    <i class="fa fa-save pr-2"></i> {{ $t('SaveAndNext') }}
                </button>
                <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip"
                    data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2" @click="onSubmit"
                    style="margin-left: 5px;" v-if="tabCount == currentTabIndex">
                    <i class="fa fa-save pr-2"></i> {{ $t('SaveAndClose') }}
                </button>
            </div>
        </div>
    </div>
    </div>
</template>
<script>

import setupModuleService from "@/services/AdvanceSetupModuleService";
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom:''
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Priority Name'),
                            name: "PriorityName",
                            value: "",
                            placeholder: this.$t('Enter Priority Name'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ value: "1001", name: "Active" }, { value: "1002", name: "InActive" }]
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "ColorField",
                            label: this.$t('ColorField'),
                            name: "ColorField",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Display Order'),
                            name: "DisplayOrder",
                            value: "",
                            config: {
                                options: [{ value: "1", name: "1" }, { value: "2", name: "2" }, { value: "3", name: "3" }, { value: "4", name: "4" }, { value: "5", name: "5" }, { value: "6", name: "6" }, { value: "7", name: "7" }, { value: "8", name: "8" }]
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                }
            ],
            ListData: []
        }
    },
    methods: {
        onSubmit(formfields, val) {
            debugger;
            var vm = this;
            var PriorityName = vm.$refs.validatepriority.getPropertyValue("PriorityName", "value");
            var Status = vm.$refs.validatepriority.getPropertyValue("Status", "value");
            var ColorField = vm.$refs.validatepriority.getPropertyValue("ColorField", "value");
            var DisplayOrder = vm.$refs.validatepriority.getPropertyValue("DisplayOrder", "value");
            var params = `priorityId=0&priorityName=${PriorityName}&statusId=${Status}&displayOrder=${DisplayOrder}&priorityColor=${ColorField}`
            vm.$refs.validatepriority.validateForm.validate().then(result => {
                if (result) {
                    setupModuleService.AddTicketPriority(params).then((response) => {
                    if (response.data == "1") {
                        vm.ShowAlert("Priority successfully added.", "success");
                        if (vm.tabCount > 1) {
                            vm.$emit("nextTab")
                            vm.$emit("updateProgress", vm.currentTabIndex, true);
                        }
                        if (vm.tabCount == vm.currentTabIndex) {
                            vm.$emit("close");
                        }
                        vm.$parent.GetStorageSetupStatusDetail();
                    }
                    else if (response.data == "2") {
                        vm.ShowAlert("Priority already Exist.", "failure");
                    }
                    else {
                        vm.ShowAlert(vm.$t('AddedError'), "failure");
                    }
                 });
                }
            });
           
        },
        // clearFields: function () {
        //     var vm = this;
        //    // debugger;
        //     vm.FormSchema[0].Data[0].value="";
        //     vm.FormSchema[0].Data[1].value="";
        //     vm.FormSchema[1].Data[0].value="#000000";
        //     vm.FormSchema[1].Data[1].value="";
        //     vm.$refs.validatePriority.validateForm.reset();
        //     // vm.$refs.validatePriority.va

        // },
        Skip: function () {
            var vm = this;
            vm.$emit("nextTab")
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
    }
}
</script>
