                            data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                            @click="RuleModel('SaveAndNext')" style="margin-left: 5px"
                            v-if="tabCount > currentTabIndex">
                            <i class="fa fa-save pr-2"></i> {{ $t("SaveAndNext") }}
                        </button>

                        <button type="button" id="aGroupTicketSave" @click="RuleModel('SaveAndClose')" title=""
                            data-toggle-tooltip="tooltip" data-original-title="Save"
                            class="popupgreenbtn btn btn-success formbtn widthhalf pr-2" style="margin-left: 5px;"
                            v-if="tabCount == currentTabIndex">
                            <i class="fa fa-save pr-2"></i> {{ $t('SaveAndClose') }}
                        </button>
                    </div>

                </div>

            </div>
        </div>
    </section>
</template>

<script>
import DataService from "../../../../services/DataService";
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom:{
            type:String,
            required: false
        }
    },

    data() {
        return {
            openRuleMode: false,
            isLoading: false,
            operator: [],
            form: {
                conditionId: 0,
                conditions: [{ relation: '', openingBracket: '(', field: '', operator: '', value: '', valueTo: '', daysOfWeek: '', closingBracket: ')' }],
                isLeaf: false,
                workCodeId: '',
                addAdder: false,
                addAdderTypeTable: false,
                adderType: '',
                adderRows: [{
                    unit: '',
                    // field: '', 
                    from: 0, to: '', toInvalid: false, workCodeBefore: '', repeatAfter: '', workCodeAfter: '', selectedFlag: { value: 'N', color: 'grey' }, showPopup: false
                }],
                selectedFlag: { value: 'N', color: 'grey' },
                showPopup: false,
                rateToggle: false,
                isUpdateRule: false
            },
            serviceName: '',
            showAdder: false,
            conditionName: '',
            showError: false,
            showAdderType: false,
            serviceType: '',
            billingFrequency: '',
            showRepeatAfter: false,
            showWorkCode: false,
            showFlag: false,
            usageMeasurementList: [],
            workCodeList: [],
            meterReadingList: [],
            companyClientList: [],
            groupedOptions: {},
            expandedGroups: {},
            showIsLeafFlag: false,
            showIsLeafWorkCode: false,
            configurationId: 0,
            isEditMode: false,
            showAddAdderTypeTable: false,
            isCheckExtraFeatureUSICBILLING: false,
            weekDaysList: [
                { label: 'Sun', value: '1' },
                { label: 'Mon', value: '2' },
                { label: 'Tue', value: '3' },
                { label: 'Wed', value: '4' },
                { label: 'Thu', value: '5' },
                { label: 'Fri', value: '6' },
                { label: 'Sat', value: '7' },
            ],
            flags: [
                { value: 'R', color: 'red' },
                { value: 'Y', color: 'gold' },
                { value: 'G', color: 'green' },
                { value: 'N', color: 'grey' },
            ],
            isShowServiceDetail: false,
            rateCode: false,
            isRateCode: false,
            getRuleConditions: '',
            getServiceId: 0,
            serviceDetailForBillingRule: undefined,
            reqestFrom: "addBillingService",
            conditionId: 0,
            viewType: undefined,
            nodeName: undefined,
            configurationId: undefined,
            serviceRuleItem: undefined,
            BtnType: ''
        };
    },

    created: async function () {
        debugger
        var vm = this;
            
        if (this.serviceRuleItem && this.serviceRuleItem.ruleCondition) {
            this.form.selectedFlag = this.serviceRuleItem.ruleCondition.selectedFlag ? this.serviceRuleItem.ruleCondition.selectedFlag : { value: 'N', color: 'grey' };
            this.form.workCodeId = this.serviceRuleItem.ruleCondition.rateCode ? this.serviceRuleItem.ruleCondition.rateCode : '';
        }
        vm.configurationId = parseInt(this.$route.params.id);

        await vm.GetRuleListingByServiceId(vm.conditionId);
        // if(vm.serviceDetailForBillingRule){
        //     vm.displayServiceDetail = true;
        //     vm.serviceName = vm.serviceDetailForBillingRule.service_name;
        //     vm.serviceType = vm.serviceDetailForBillingRule.service_code;
        //     vm.billingFrequency= vm.serviceDetailForBillingRule.payment_type;
        //     vm.configurationId = vm.serviceDetailForBillingRule.configuration_id;
        // }


        await vm.GetBillingRuleFieldList();
        vm.BindWorkCode();
        if (vm.reqestFrom === "addMapBillingRuleForService" || vm.reqestFrom === "EditMapBillingRuleForService") {
            vm.isShowServiceDetail = true;
            vm.isRateCode = true;
            if (vm.reqestFrom === "EditMapBillingRuleForService") {
                setTimeout(function () {
                    vm.isEditMode = true;
                    vm.BindRuleConditions(vm.conditionId);
                }, 200);
            }
        }
        else if (vm.reqestFrom === "fromBillingService") {
            vm.isEditMode = false;
            vm.BindRuleConditions(vm.conditionId);
            vm.isShowServiceDetail = false;
        }
        else if (vm.reqestFrom === "addBillingService") {
            vm.isEditMode = false;
            vm.isShowServiceDetail = false;
        }
        vm.toggleGroup();
        vm.checkSlabCondition();
        vm.GetAddonMapByServiceId();
    },

    mounted() {
        for (const group in this.groupedOptions) {
            this.$set(this.expandedGroups, group, true);
        }
    },

    computed: {
        flatGroupedOptions() {
            const result = [];
            for (const groupName in this.groupedOptions) {
                result.push({ isGroup: true, groupName });
                if (this.expandedGroups[groupName]) {
                    const fields = this.groupedOptions[groupName];
                    fields.forEach(field => {
                        result.push({ ...field, isGroup: false, groupName });
                    });
                }
            }
            return result;
        },
        fieldLabelMap() {
            const map = {};
            Object.values(this.groupedOptions).flat().forEach(item => {
                map[item.value] = item.label;
            });
            return map;
        },
        fieldSqlConditionMap() {
            const map = {};
            Object.values(this.groupedOptions).flat().forEach(item => {
                map[item.value] = item.joinFieldName;
            });
            return map;
        },
        conditionText() {
            return this.form.conditions.map((row, index) => {
                const relation = index > 0 ? ` ${row.relation || 'And'} ` : '';
                const open = row.openingBracket || '';
                const field = this.fieldLabelMap[row.field] || row.field || '';
                let operatorDisplay = '';
                if (row.operatorList && Array.isArray(row.operatorList)) {
                    const op = row.operatorList.find(o => o.OPERATOR_ID === row.operator);
                    operatorDisplay = op ? op.OPERATOR_DISPLAY_NAME : row.operator;
                }

                var value = '';
                if (row.sqlType == 'select' && (operatorDisplay?.toLowerCase() !== 'not equal to one of multiple possible values' && operatorDisplay?.toLowerCase() !== 'equal to one of multiple possible values')) {
                    const ddlItem = row.valueOptions?.find(o => o.value === row.value);
                    value = ddlItem ? ddlItem.name : row.value || '';
                }
                else if (row.sqlType == 'select' && (operatorDisplay?.toLowerCase() === 'not equal to one of multiple possible values' || operatorDisplay?.toLowerCase() === 'equal to one of multiple possible values')) {
                    const values = Array.isArray(row.value) ? row.value : [row.value];
                    const selectedNames = values.map(obj => {
                        const match = row.valueOptions?.find(opt => opt.value === obj.value);
                        return match ? match.name : obj.name || obj.value;
                    });
                    value = selectedNames.join(', ');
                }
                else if (row.sqlType === 'bool') {
                    const ddlItem = row.valueOptions?.find(o => o.value === row.value);
                    value = ddlItem ? ddlItem.name : row.value || ''
                }
                else if (row.sqlType == 'date' && row.value != "") {
                    if (typeof (row.value) === 'string') {
                        row.value = new Date(row.value);
                    }
                    value = `${this.$d(row.value)}` || '';
                }
                else {
                    value = row.value ?? ' ';
                    // value = row.value || '';
                }
                var valueTo = '';
                if (operatorDisplay?.toLowerCase() === 'between' || operatorDisplay?.toLowerCase() === 'date range') {
                    if (row.sqlType == 'date' && row.valueTo != "") {
                        if (typeof (row.valueTo) === 'string') {
                            row.valueTo = new Date(row.valueTo);
                        }
                        valueTo = ' AND' + ' ' + `${this.$d(row.valueTo)}` || '';
                    }
                    else if (row.valueTo != "") {
                        valueTo = ' AND' + ' ' + row.valueTo ?? '';
                    }
                }
                var weekDays = '';
                if (operatorDisplay?.toLowerCase().trim() === 'is week end') {
                    const selectedDays = (row.daysOfWeek || '').split(',').filter(Boolean);
                    const dayLabels = selectedDays.map(dayValue => {
                        const day = this.weekDaysList.find(d => d.value === dayValue);
                        return day ? day.label : dayValue;
                    });
                    weekDays = ` (${dayLabels.join(', ')})`;
                }
                const close = row.closingBracket || '';
                return `${relation}${open}${field} ${operatorDisplay} ${value}${valueTo}${weekDays}${close}`;
            }).join('');
        }
    },

    methods: {
        fncApplyRate() {

            console.log("rateToggle", this.form.rateToggle)
        },
        async GetRuleListingByServiceId(SericeId) {
            var vm = this;
            var url = `serviceId=${SericeId > 0 ? SericeId : 0}`;
            DataService.GetRuleListingByServiceId(url).then(response => {
                if (response.data != null && response.data != "[]") {
                    vm.getRuleConditions = JSON.parse(response.data);
                }
                else {
                    vm.getRuleConditions = [];
                }
            })
        },
        RateCodeModel() {
            var vm = this;
            vm.rateCode = true;
        },
        CloseAddWorkCode() {
            this.rateCode = false;
            this.BindWorkCode();
        },
        getDayChecked(valueString, dayValue) {
            if (!valueString) return false;
            return valueString.split(',').includes(dayValue);
        },
        toggleDay(row, dayValue) {
            let current = (row.daysOfWeek || "").split(',').filter(Boolean);
            const index = current.indexOf(dayValue);

            if (index >= 0) {
                current.splice(index, 1); // uncheck
            } else {
                current.push(dayValue); // check
            }

            row.daysOfWeek = current.join(',');
        },
        isMultiSelectOperator(row) {
            const selectedOperator = (row.operatorList || []).find(op => op.OPERATOR_ID === row.operator);
            if (!selectedOperator) return false;
            const name = selectedOperator.OPERATOR_DISPLAY_NAME?.toLowerCase();
            return name === 'equal to one of multiple possible values' || name === 'not equal to one of multiple possible values';
        },
        isValueHidden(row) {
            const selectedOperator = (row.operatorList || []).find(
                op => op.OPERATOR_ID === row.operator
            );
            if (!selectedOperator) return false;

            const operatorName = selectedOperator.OPERATOR_DISPLAY_NAME?.toLowerCase();
            return operatorName === 'is empty' || operatorName === 'is not empty' || operatorName === 'is off day' || operatorName === 'is duplicate';
        },
        isShowIsWeekDay(row) {
            const selectedOperator = (row.operatorList || []).find(
                op => op.OPERATOR_ID === row.operator
            );
            if (!selectedOperator) return false;

            const operatorName = selectedOperator.OPERATOR_DISPLAY_NAME?.toLowerCase();
            return operatorName === 'is week end';
        },
        isValueToVisible(row) {
            if (!row.operator) return false;  // hide if no operator selected

            const selectedOperator = (row.operatorList || []).find(
                op => op.OPERATOR_ID === row.operator
            );
            if (!selectedOperator) return false;

            const displayName = selectedOperator.OPERATOR_DISPLAY_NAME?.toLowerCase();
            return displayName === 'between' || displayName === 'date range';
        },
        checkSlabCondition: async function () {
            var vm = this;
            var serviceId = parseInt(vm.$route.params.serviceId);
            const url = `serviceId=${serviceId}`;
            debugger;
            const response = await DataService.GetServiceTypeById(url);

            if (response.data[0].SERVICE_TYPE_CODE != null) {
                vm.serviceType = response.data[0].SERVICE_TYPE_CODE;
                // if (vm.serviceType != 'UsageBased') {
                //     this.form.rateToggle = true;
                //     this.form.isLeaf = true;
                //     this.showIsLeafFlag = true;
                //     this.showIsLeafWorkCode = true;
                // }
                // else if (vm.serviceType == 'UsageBased' ) {
                var utilityId = response.data[0].METER_TYPE_ID;
                var unitId = response.data[0].USAGE_MEASUREMENT_ID
                vm.GetDDLUsageMeasurement(utilityId, unitId)
                // }
            }
        },

        BindRuleConditions: async function (conditionId) {
            const vm = this;
            const url = `serviceId=${conditionId}`;
            const response = await DataService.GetRuleConditionById(url);

            if (response && response.data && response.data.length > 0) {
                vm.conditionName = response.data[0].SERVICE_NAME;
                const allOptions = Object.values(vm.groupedOptions).flat();

                vm.form.conditions = await Promise.all(
                    response.data.map(async (item) => {
                        const selectedOption = allOptions.find(opt => opt.value === item.TEMPLATE_ID);
                        let operatorList = [];
                        let valueOptions = [];
                        let parsedValue = item.FIRST_VALUE || '';
                        let valueTo = item.SECOND_VALUE || '';
                        let sqlType = selectedOption?.sqlType || '';
                        // let weekDay = item.DaysofTheWeek || '';
                        // let daysOfWeek = item.DaysofTheWeek || '';
                        // Fetch operator list
                        if (selectedOption) {
                            const operatorRes = await DataService.GetOperator(
                                `dataType=${selectedOption.sqlType}&customField=${selectedOption.value}`
                            );
                            operatorList = operatorRes?.data?.DATA || [];
                        }

                        if (selectedOption.sqlType == 'select' && (selectedOption.picklistOptions).toLowerCase() != "lookup") {
                            if (selectedOption.picklistOptions && selectedOption.picklistOptions.trim() !== '') {
                                const selectOptions = selectedOption.picklistOptions.split(',').map(option => ({
                                    first_value: null,
                                    name: option.trim(),
                                    value: option.trim().replace(/\s+/g, '-'),
                                    second_value: null
                                }));
                                valueOptions = selectOptions;
                                const operatorMeta = operatorList.find(op => op.OPERATOR_ID === item.OPERATOR_ID);
                                const multiSelectOperators = ['equal to one of multiple possible values', 'not equal to one of multiple possible values'];
                                const isMultiSelect = operatorMeta && multiSelectOperators.includes(operatorMeta.OPERATOR_DISPLAY_NAME?.toLowerCase());

                                if (isMultiSelect) {
                                    const valueIds = parsedValue.split(',').map(v => v.trim());
                                    parsedValue = valueOptions.filter(opt => valueIds.includes(String(opt.value)));
                                } else {
                                    parsedValue = valueOptions.find(opt => String(opt.value) === String(parsedValue))?.value || '';
                                }
                            }
                        }
                        else if (sqlType === 'select' && (selectedOption.picklistOptions).toLowerCase() == "lookup") {
                            const ddlRes = await DataService.GetDDLData(`id=&moduleName=&type=${selectedOption.fieldCode}&search=`);
                            valueOptions = ddlRes?.data?.DATA || [];
                            const operatorMeta = operatorList.find(op => op.OPERATOR_ID === item.OPERATOR_ID);
                            const multiSelectOperators = ['equal to one of multiple possible values', 'not equal to one of multiple possible values'];
                            const isMultiSelect = operatorMeta && multiSelectOperators.includes(operatorMeta.OPERATOR_DISPLAY_NAME?.toLowerCase());

                            if (isMultiSelect) {
                                const valueIds = parsedValue.split(',').map(v => v.trim());
                                parsedValue = valueOptions.filter(opt => valueIds.includes(String(opt.value)));
                            } else {
                                parsedValue = valueOptions.find(opt => String(opt.value) === String(parsedValue))?.value || '';
                            }
                        }
                        else if (sqlType === 'bool') {
                            var radioValue = vm.MakeArray(selectedOption.picklistOptions, 'radio');
                            if (radioValue.length > 0) {
                                valueOptions = radioValue;
                            }
                        }
                        else if (!isNaN(parsedValue) && parsedValue !== '') {
                            parsedValue = Number(parsedValue);
                        }
                        // If no DaysofTheWeek provided but operator indicates weekend, default it
                        const operatorMeta = operatorList.find(op => op.OPERATOR_ID === item.OPERATOR_ID);
                        const operatorName = operatorMeta?.OPERATOR_DISPLAY_NAME?.toLowerCase() || '';
                        // if (!daysOfWeek && operatorName.includes('is week end')) {
                        //     daysOfWeek = '6,7';
                        // }

                        return {
                            relation: item.RELATION_WITH_PARENT || '',
                            openingBracket: item.OPENING_BRACKETS || '',
                            field: item.TEMPLATE_ID || '',
                            operator: item.OPERATOR_ID || '',
                            value: parsedValue,
                            valueTo: valueTo,
                            daysOfWeek: item.DaysofTheWeek,
                            closingBracket: item.CLOSING_BRACKETS || '',
                            operatorList: operatorList,
                            valueOptions: valueOptions,
                            sqlType: sqlType // Pass SQL type to row for conditional rendering
                        };
                    })
                );
            }
        },
        GetAddonMapByServiceId: async function () {
            var vm = this;
            vm.form.isUpdateRule = true;
            var url = `serviceId=${vm.conditionId}&nodeName=${vm.nodeName}&configId=${vm.configurationId}`;
            await DataService.GetAddonMapByServiceId(url).then(function (response) {
                if (response.data.length > 0) {
                    if (response.data[0].IsAddon || response.data[0].IsLeafNode) {
                        vm.form.rateToggle = true;

                    }
                    vm.form.addAdder = response.data[0].IsAddon;       // enable addAdder radio btn if addon present
                    vm.form.isLeaf = response.data[0].IsLeafNode;
                    vm.form.workCodeId = response.data[0].WorkCode;
                    vm.form.selectedFlag = vm.flags.find(f => f.value === response.data[0].ClientServiceFlag) || { value: 'N', color: 'grey' },
                        vm.form.adderType = response.data[0].AddonType || '';
                    vm.onAdderTypeSelected(vm.form.adderType);
                    if (vm.form.addAdder) {
                        vm.onAdderChecked();
                        vm.showAddAdderTypeTable = true;
                    }
                    else if (vm.form.isLeaf) {
                        vm.chkisLeafRule();
                        vm.showAddAdderTypeTable = false;
                    }
                    vm.form.adderRows = [];
                    response.data.forEach(addon => {
                        addon.ServiceAddonMap.forEach(map => {
                            vm.form.adderRows.push({
                                unit: addon.UnitOfMeasure || '',
                                // field: addon.TemplateId || '',
                                from: map.AddonFromValue !== undefined ? map.AddonFromValue : 0,
                                to: map.AddonToValue !== 0 ? map.AddonToValue : '',
                                toInvalid: false,
                                workCodeBefore: map.WorkCode || '',
                                repeatAfter: map.RepeatAfter || '',
                                workCodeAfter: map.RepeatWorkCode || '',
                                selectedFlag: vm.flags.find(f => f.value === map.ServiceFlag) || { value: 'N', color: 'grey' },
                                showPopup: false,
                                addonId: addon.AddonId,
                                addonMapId: map.AddonMapId
                            });
                        });
                    });
                }
            });
        },
        CheckFeaturesForCompany: async function (type) {
            var vm = this;
            var appendingUrl = `featureCode=${type}`;
            await DataService.CheckExtraFeatures(appendingUrl).then(function (response) {
                if (type == 'USICBILLING') {
                    if (response.data == true) {
                        vm.isCheckExtraFeatureUSICBILLING = true;
                    } else {
                        vm.isCheckExtraFeatureUSICBILLING = false;
                    }
                }
            });
        },
        handleFieldChange: function (row) {
            const allOptions = Object.values(this.groupedOptions).flat();
            const selectedOption = allOptions.find(opt => opt.value === row.field);
            if (!selectedOption) return;
            this.$set(row, 'sqlType', selectedOption.sqlType);
            const url = "dataType=" + selectedOption.sqlType + "&customField=" + selectedOption.value;
            DataService.GetOperator(url).then((response) => {
                const json = response.data;
                if (json != null) {
                    this.$set(row, 'operatorList', json.DATA); // dynamically add operatorList to the row
                    row.operator = ""; // reset selected operator on field change
                    row.value = "";
                    row.valueTo = ""; // reset value on field change
                }
            });
        },
        handleDropdownValue: function (row) {
            var vm = this;
            const allOptions = Object.values(this.groupedOptions).flat();
            const selectedOption = allOptions.find(opt => opt.value === row.field);
            if (!selectedOption) return;
            this.$set(row, 'valueOptions', []);
            this.$set(row, 'value', '');
            this.$set(row, 'valueTo', '');
            if (selectedOption.sqlType == 'select' && (selectedOption.picklistOptions).toLowerCase() != "lookup") {
                if (selectedOption.picklistOptions && selectedOption.picklistOptions.trim() !== '') {
                    const dropValue = selectedOption.picklistOptions.split(',').map(option => ({
                        first_value: null,
                        name: option.trim(),
                        value: option.trim().replace(/\s+/g, '-'),
                        second_value: null
                    }));
                    this.$set(row, 'valueOptions', dropValue);
                }
            }
            else if (selectedOption.sqlType == 'select' && (selectedOption.picklistOptions).toLowerCase() == "lookup") {
                var params = `id=&moduleName=&type=${selectedOption.fieldCode}&search=`
                DataService.GetDDLData(params).then((response) => {
                    if (response.data) {
                        var json = response.data;
                        if (json != null) {
                            this.$set(row, 'valueOptions', response.data.DATA);
                        }
                    }
                })
            }
            else if (selectedOption.sqlType == 'bool') {
                var radioValue = vm.MakeArray(selectedOption.picklistOptions, 'radio');
                if (radioValue.length > 0) {
                    this.$set(row, 'valueOptions', radioValue);
                }
            }
        },
        isLeafTogglePopup() {
            this.form.showPopup = !this.form.showPopup;
        },
        isLeafSelectFlag(flag) {
            this.form.selectedFlag = flag;
            this.form.showPopup = false;
        },
        isAdderTogglePopup(adder) {
            adder.showPopup = !adder.showPopup;
        },
        isAdderSelectFlag(adder, flag) {
            adder.selectedFlag = flag;
            adder.showPopup = false;
        },
        toggleGroup(groupName) {
            const group = groupName || 'Client';
            this.$set(this.expandedGroups, group, !this.expandedGroups[group]);
        },
        GetBillingRuleFieldList: async function () {
            var vm = this;
            await DataService.GetBillingRuleFieldList().then(response => {
                if (response.data.status == 'Success') {
                    if (response.data.result.AVAILABLE_FIELDS != null) {
                        const availableFields = response.data.result.AVAILABLE_FIELDS?.[0] || [];
                        const groupedOptions = {};

                        availableFields.forEach(moduleData => {
                            const moduleName = moduleData.SubModule;

                            if (Array.isArray(moduleData?.CustomFields) && moduleData.CustomFields.length > 0) {
                                groupedOptions[moduleName] = moduleData.CustomFields.map(item => ({
                                    value: item?.CustomFieldId || '',
                                    columnName: item?.FieldName || '',
                                    label: item?.DisplayName || '',
                                    tableName: item?.TableName || '',
                                    sqlType: item?.dttype?.[0]?.DataTypeCode || '',
                                    picklistOptions: item?.picklist_options || '',
                                    dtCode: item?.dt_code || '',
                                    dropdownType: item?.dropdown_type || '',
                                    fieldCode: item?.field_code || '',
                                    joinFieldName: item?.JoinFieldName || ''

                                }));
                            }
                        });

                        // Only assign if there is at least one valid module with data
                        if (Object.keys(groupedOptions).length > 0) {
                            this.groupedOptions = groupedOptions;
                        }
                    }
                }
            })
        },
        GetDDLUsageMeasurement: function (utilityId, unitId) {
            var vm = this;
            var params = "id=&moduleName=&type=UTILITY_UNIT_OF_MEASUREMENTS&search=&refId=" + utilityId;
            DataService.GetDDLData(params).then((response) => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.usageMeasurementList = response.data.DATA;

                        vm.form.adderRows[0].unit = unitId;
                    }
                }
            })
        },
        BindWorkCode: function () {
            var vm = this;
            var params = "id=&moduleName=&type=WORK_CODE_BY_PLAN_ID&search=&refId=" + vm.configurationId;
            DataService.GetDDLData(params).then((response) => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.workCodeList = response.data.DATA;
                    }
                }
            })
        },
        onToValueChange(val, index) {
            if (val === '') {
                this.form.adderRows[index].to = Infinity;
            } else if (val.trim() === '') {
                this.form.adderRows[index].to = '';
            } else {
                const num = Number(val);
                this.form.adderRows[index].to = isNaN(num) ? '' : num;
            }
        },
        removeConditionRow: function (index) {
            if (this.form.conditions.length > 1) {
                this.form.conditions.splice(index, 1);
            }
        },
        removeAdderRow: function (index) {
            if (this.form.adderRows.length > 1) {
                this.form.adderRows.splice(index, 1);
            }
        },

        addConditionRow() {
            this.form.conditions.push({ relation: '', openingBracket: '(', field: '', operator: '', value: '', valueTo: '', daysOfWeek: '', closingBracket: ')' });
        },
        addAdderRow() {
            const lastRow = this.form.adderRows[this.form.adderRows.length - 1];
            if (lastRow.from >= 0 && lastRow.to == "") {
                lastRow.toInvalid = true;
                return;
            }
            else {
                lastRow.toInvalid = false;
            }
            this.form.adderRows.push({
                unit: '',
                // field: '',
                from: Number(lastRow.to) + 1,
                to: '',
                workCodeBefore: '',
                toInvalid: false,
                repeatAfter: '',
                workCodeAfter: '',
                selectedFlag: { value: 'N', color: 'grey' },
                showPopup: false
            });
        },
        Close: function () {
            var vm = this;
            vm.$parent.isModalAddRule = false;
        },
        chkisLeafRule() {
            this.showAdder = this.form.isLeaf;
            this.showIsLeafFlag = this.showAdder;
            this.showIsLeafWorkCode = this.showAdder;
            this.form.addAdder = false;
            this.showAdderType = false;
            this.showAddAdderTypeTable = false;

            if (!this.form.isLeaf) {
                this.showAdderType = false;
                this.form.adderType = false;
                this.showAddAdderTypeTable = false;
            }
        },
        onAdderChecked() {
            this.showAdderType = this.form.addAdder;
            this.showIsLeafFlag = false;
            this.showIsLeafWorkCode = false;
            this.form.isLeaf = false;
            if (!this.form.addAdder) {
                this.form.adderType = false;
                this.showAddAdderTypeTable = false;
            }
        },
        onAdderTypeSelected(val) {
            if (val == 'T') {
                this.showRepeatAfter = false;
                this.showWorkCode = false;
                this.showFlag = true;
            }
            else if (val == 'P') {
                this.showRepeatAfter = true;
                this.showWorkCode = false;
                this.showFlag = false;
            }
            else if (val == 'M') {
                this.showRepeatAfter = false;
                this.showWorkCode = false;
                this.showFlag = true;
            }
            else if (val == 'C') {
                this.showRepeatAfter = false;
                this.showWorkCode = false;
                this.showFlag = false;
            }
            if (this.form.adderType) {
                this.showAddAdderTypeTable = true;
            }
        },
        formatDate(dateObj) {
            if (!dateObj) return null;
            const d = new Date(dateObj);
            const year = d.getFullYear();
            const month = String(d.getMonth() + 1).padStart(2, '0');
            const day = String(d.getDate()).padStart(2, '0');
            return `${year}-${month}-${day}`;
        },
        databaseConditionText() {
            return this.form.conditions.map((row, index) => {
                const relation = index > 0 ? ` ${row.relation || 'AND'} ` : '';
                const open = row.openingBracket || '';
                const close = row.closingBracket || '';
                const field = this.fieldSqlConditionMap[row.field] || row.field || '';

                let operator = '';
                let operatorDisplay = '';

                if (row.operatorList && Array.isArray(row.operatorList)) {
                    const op = row.operatorList.find(o => o.OPERATOR_ID === row.operator);
                    operator = op ? (op.OPERATOR_SQL_NAME || op.OPERATOR_EXPRESSION) : row.operator;
                    operatorDisplay = op ? (op.OPERATOR_DISPLAY_NAME || '') : '';
                } else {
                    operator = row.operator;
                    operatorDisplay = row.operatorDisplay || '';
                }

                operator = (operator || '').toLowerCase();

                // normalize operatorDisplay for safe comparison
                const displayCheck = (operatorDisplay || '').trim().toLowerCase();

                //  handle "equal to / not equal to one of multiple possible values"
                if (
                    displayCheck === 'not equal to one of multiple possible values' ||
                    displayCheck === 'equal to one of multiple possible values'
                ) {
                    const values = Array.isArray(row.value) ? row.value : [row.value];

                    const selectedValues = values.map(v => {
                        const val = typeof v === "object" && v !== null ? v.value : v;
                        const match = row.valueOptions?.find(opt => opt.value === val);
                        const finalVal = match ? match.value : val;

                        //  check JS type, quote only if not a pure number
                        return isNaN(finalVal) ? `'${finalVal}'` : finalVal;
                    });

                    const value = selectedValues.join(', ');
                    return `${relation}${open}${field} ${operator.toUpperCase()} (${value})${close}`;
                }


                // special cases
                if (operator === 'is week end' || operator === 'is working day') {
                    const days = (row.daysOfWeek || '').split(',').filter(Boolean).join(',');
                    const extra = `DATEPART(DW, ${field}) IN (${days})`;
                    return `${relation}${open}${extra}${close}`;
                }
                if (operator === 'is lowest') {
                    return `${relation}${open}${field} IS LOWEST${close}`;
                }
                if (operator === 'is highest') {
                    return `${relation}${open}${field} IS HIGHEST${close}`;
                }
                if (operator === 'is not lowest') {
                    return `${relation}${open}${field} IS NOT LOWEST${close}`;
                }
                if (operator === 'is not highest') {
                    return `${relation}${open}${field} IS NOT HIGHEST${close}`;
                }

                // DateDiff operators
                if (['minutesdiff', 'hoursdiff', 'daydiff', 'monthdiff', 'yeardiff'].includes(operator)) {
                    const unitMap = {
                        minutesdiff: 'minute',
                        hoursdiff: 'hour',
                        daydiff: 'day',
                        monthdiff: 'month',
                        yeardiff: 'year'
                    };
                    const diffUnit = unitMap[operator];
                    const compareField = this.fieldSqlConditionMap[row.additionalField] || row.additionalField;
                    const opSql = row.additionalOperator || '=';
                    return `${relation}${open}DATEDIFF(${diffUnit}, ${field}, ${compareField}) ${opSql} ${row.value}${close}`;
                }

                // HAS WITH operator
                if (operator === 'has with') {
                    const withField = this.fieldSqlConditionMap[row.hasWithField] || row.hasWithField;
                    const withOp = row.hasWithOperator || '=';
                    return `${relation}${open}${field} HAS WITH ${withField} ${withOp} '${row.value}'${close}`;
                }
                // BETWEEN / DATERANGE special case
                if ((operator === 'between' || operator === 'daterange') && row.valueTo) {
                    // --- TIME / DATETIME ---
                    if (row.sqlType === 'time' || row.sqlType === 'datetime') {
                        const fromVal = row.value ? `'${row.value}'` : "''";
                        const toVal = row.valueTo ? `'${row.valueTo}'` : "''";

                        return `${relation}${open}(CONVERT(time, ${field}) >= ${fromVal} AND CONVERT(time, ${field}) <= ${toVal})${close}`;
                    }
                    // --- DATE ---
                    else if (row.sqlType === 'date') {
                        const dateVal = row.value instanceof Date ? row.value : new Date(row.value);
                        const dateValTo = row.valueTo instanceof Date ? row.valueTo : new Date(row.valueTo);

                        const fromDate = this.formatDate(dateVal);
                        const toDate = this.formatDate(dateValTo);

                        return `${relation}${open}${field} BETWEEN '${fromDate}' AND '${toDate}'${close}`;
                    }
                    // --- Default numeric/string ---
                    else {
                        return `${relation}${open}${field} BETWEEN ${row.value} AND ${row.valueTo}${close}`;
                    }
                }
                let value = '';
                if (row.sqlType === 'nvarchar' || row.sqlType === 'string') {
                    if (operator.includes('starts with')) {
                        operator = 'LIKE';
                        value = `'${row.value}%'`;
                    }
                    else if (operator.includes('ends with')) {
                        operator = 'LIKE';
                        value = `'%${row.value}'`;
                    }
                    else if (operator.includes('like')) {
                        operator = 'LIKE';
                        value = `'%${row.value}%'`;
                    }
                    else if (operator.includes('in')) {
                        operator = 'IN';
                        const arr = Array.isArray(row.value) ? row.value : (row.value || '').split(',');
                        value = `(${arr.map(v => `'${v.trim()}'`).join(',')})`;
                    } else {
                        value = `'${row.value}'`;
                    }
                }
                else if (row.sqlType === 'date' && row.value) {
                    const dateVal = row.value instanceof Date ? row.value : new Date(row.value);
                    value = `'${this.formatDate(dateVal)}'`;
                }
                else if (row.sqlType === 'bool') {
                    value = row.value ? 1 : 0;
                }
                else {
                    value = row.value || '';
                }
                return `${relation}${open}${field} ${operator.toUpperCase()} ${value}${close}`;
            }).join('');
        },

        CloseRuleModel() {
            this.openRuleMode = false;
        },
        RuleModel(Type) {
            this.BtnType = Type;
            this.conditionId > 0 && this.getRuleConditions.length > 0 && this.reqestFrom === "fromBillingService" ? this.openRuleMode = true : this.saveAddRule();
        },
        async saveAddRule() {
            debugger
            var vm = this;
            this.showError = true;
            if (!this.conditionName) {
                return;
            }
            if (vm.conditionId != 0) {
                this.form.conditionId = vm.conditionId;
            }
            if (this.form.rateToggle == true) {
                this.form.isLeaf = true;
            }
            const conditionData = this.conditionText;
            const sqlDatabasecondition = this.databaseConditionText();
            if (Array.isArray(this.form.conditions)) {
                this.form.conditions.forEach(condition => {
                    if (condition.sqlType === 'date') {
                        if (condition.value) {
                            condition.value = this.formatDate(condition.value);
                        }
                        if (condition.valueTo) {
                            condition.valueTo = this.formatDate(condition.valueTo);
                        }
                    }
                    if (condition?.operator && Array.isArray(condition.value) && condition.value.length > 0) {
                        const operatorId = condition.operator;
                        const matchedOperator = condition.operatorList.find(op => op.OPERATOR_ID === operatorId);
                        if (matchedOperator && (matchedOperator.OPERATOR_DISPLAY_NAME?.toLowerCase() === "equal to one of multiple possible values" || matchedOperator.OPERATOR_DISPLAY_NAME?.toLowerCase() === "Not Equal to one of multiple possible values")) {
                            condition.value = condition.value.map(v => v.value).join(',');
                        }
                    }
                });
            }
            const payload = JSON.parse(JSON.stringify(this.form));
            payload.adderRows.forEach(adder => {
                if (adder.to === '' || adder.to === Infinity) {
                    adder.to = null;
                }
            });
            const obj = JSON.stringify(payload);
            var requestPayload = { objData: obj, ruleCondition: conditionData, conditionName: this.conditionName, configuration_id: vm.configurationId, sqlcondition: sqlDatabasecondition };
            await DataService.SaveAddRule(requestPayload).then(response => {
                if (response.data != null) {
                    debugger;
                    if (response.data.Status == "Success" && response.data.Result == "1") {

                        if (vm.viewType == 'serviceRuleCondition') {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('BillingRule')]), "Success", true, vm.$t('Alert'));
                            //vm.$parent.getConditionRecords();
                        }
                        if (vm.reqestFrom == "fromBillingService" || vm.reqestFrom == "addBillingService") {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('BillingCondition')]), "Success", true, vm.$t('Alert'));
                            //vm.$parent.FetchData();
                        }
                        vm.isLoading = false;
                        //this.$emit('rule-saved', obj);
                        if (vm.BtnType == 'SaveAndNew') {
                            vm.resetForm();

                        }
                        else if (vm.BtnType == 'SaveAndNext') {
                            vm.Skip();
                        }
                        else if (vm.BtnType == 'SaveAndFinish') {
                            if (vm.reqFrom == 'InitialSetup') {
                                vm.Finish();
                            }

                        }
                        else if (vm.BtnType == 'SaveAndClose') {
                            if (vm.reqFrom == 'InitialSetup') {
                                vm.Finish();
                            }
                        }
                    }
                    else if (response.data.Status == "Success" && response.data.Result == "3") {

                        if (vm.reqestFrom == "fromBillingService" || vm.reqestFrom == "addBillingService") {
                            vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('BillingCondition')]), "Success", true, vm.$t('Alert'));
                            vm.$parent.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('BillingRule')]), "Success", true, vm.$t('Alert'));
                        }
                        vm.isLoading = false;
                        this.$emit('rule-saved', obj);
                    }
                    else if (response.data.Result == "2") {
                        if (vm.reqestFrom == "fromBillingService" || vm.reqestFrom == "addBillingService") {
                            this.ShowAlert(this.$t('AlreadyExists', [this.$t('BillingCondition')]), "failure", true, this.$t('Alert'));
                        }
                        else {
                            this.ShowAlert(this.$t('AlreadyExists', [this.$t('BillingRule')]), "failure", true, this.$t('Alert'));
                        }

                    }
                    else {
                        if (vm.reqestFrom == "fromBillingService" || vm.reqestFrom == "addBillingService") {
                            vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("BillingCondition")]), "danger", true, vm.$t("Alert"));
                            vm.isLoading = false;
                        }
                        else {
                            vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("BillingRule")]), "danger", true, vm.$t("Alert"));
                            vm.isLoading = false;
                        }

                    }
                }
            });
        },

        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        Skip: function () {

            var vm = this;
            vm.$emit("nextTab", true);
        },
        resetForm() {
            var vm = this;
            vm.FormSchema.forEach((group, gi) => {
                group.Data.forEach((field, fi) => {
                    field.value = "";
                })
            })

        },
        Finish: async function () {

            var vm = this;
            await vm.UpdateUserSettingConfig('HideContextmenu');
            await vm.UpdateUserSettingConfig('initialstartup');
            window.location.href = '/User/Dashboard';
        },
        async UpdateUserSettingConfig(type) {
            let url = `status=true&type=${type}`
            await DataService.UpdateUserSettingConfig(url).then((response) => {

            });
        }
    },

}

</script>