                                :show-count="true" v-bind:class="{
                                    'form-control': true,
                                }">
                            </tree-view>
                            <!-- <span v-if="errors[0]" name="Location" class="invalid-feedback d-block">{{
                                                errors[0] }}</span>
                                        </Field> -->
                            <div class="ml-2 mt-1" v-if="data.fieldInfo.config.showAddIcon">
                                <a v-on:click="AddLocation" class="round-icon-small btn-dark theme-primary">
                                    <i alt="+" class="fa fa-plus text-white pt-7 font-18"></i>
                                </a>
                            </div>
                        </div>
                    </template>
</dynamic-form>
<div class="mainfooter_app">
  <div class="col-lg-12 text-right mt-4 mr-3">
    <button title="skip" class="btn skip-btn-clr formbtn widthhalf px-4 mr-2" @click="Skip()" v-if="tabCount > currentTabIndex">     
      {{ $t("Skip") }}
    </button>
    <button type="button" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2" @click="onSubmit('Save')">
      {{ $t("SaveAndNext") }}
      <em class="fa fa-chevron-right pl-2"></em>
