        if (response != null && response.data != null) {
          // vm.isMapFeature = response.data.isFsmLocating;
          // response.data.data
          vm.FormSchema = vm.$refs.AddlocationRefForm.ConvertCustomFieldIntoDynamicFormObj(response.data.data);
          vm.FormSchema.forEach((group, gi) => {
            group.Data.forEach((field, fi) => {
              if (vm.FormSchema.length > 0) {
                if(field.name == 'emailid'){
                  field.validationRules = {"required": field.validationRules.required, "max" : field.validationRules.max, "email":true};
                }
                if (field.name == "parent_location_id") {
                  field.config.showAddIcon = false;
                  field.astype = "SlotField";
                  field.is_required = false;
                }
                if (field.name == "tag_id") {
