                subModuleCode: "Location"
              }

              commonDataService.SaveDynamicForm(JSON.stringify(obj)).then(function (response) {
                if (response != null && response.data != null && response.data[0].Status == "Success") {
                  // vm.ShowAlert(vm.$t("LocationSaveSuccessfully"), "success", vm.$t('Alert'));
                  if (val == 'SaveAndNew') {

