                    //   }
                    // }, 400);
                    vm.isLoading = false;
                    // vm.$parent.GetProgresSetupStatusDetail();
                  }
                  else if (val == "Save") {

                    vm.ShowAlert(
                      vm.$t("LocationSaveSuccessfully", [
