<template>
    <section class="device-mapping">
        <div class="dynamiccontent_base">

            <div class="col-lg-8 mx-auto mt-4">

                <div class="col-12 float-left p-0 text-center mb-4">
                    <h3 class="ownership">Ownership of Computers</h3>
                    <div class="w-100 font-16 font-weight-medium">Please select the option that best describes the
                        ownership of the computers used by your team members.</div>
                </div>


                <div class="col-12 float-left p-0 mb-4">

                    <div class="locationinfo1 col-6 selected p-0 flex-wrap" v-on:click="ChangeClass">
                        <em v-if="isCompany" class="fa fa-check-circle mr-3  pl-3"></em>
                        <em v-else class="fa fa-circle-thin mr-3 pl-3"></em>
                        <div class="device-mapping-option">
                            <h2>Company-Owned</h2> All team members work on computers provided by the company.

                        </div>

                        <!-- <div
                            style="background-color: #c0ecd2; color: white; padding: 10px; margin-top: 100px; width: 100%;">
                           
                        </div> -->

                        <div class="device-mapping-note"><i class="fa fa-info-circle" aria-hidden="true"></i> Please
                            ensure that the email used to create the SilentApp
                            account matches the email used for the user's company
                            account in Active Directory (AD).</div>

                    </div>


                    <div class="locationinfo1 col-6 selected p-0 flex-wrap" v-on:click="openDeviceMppingListing">

                        <em v-if="isPersonal" class="fa fa-circle-thin mr-3  pl-3"></em>
                        <em v-else class="fa fa-check-circle mr-3 pl-3"></em>
                        <div class="device-mapping-option">
                            <h2>Personal Computers</h2> Some or all team members use their own personal computers for
                            work.
                        </div>

                        <div class="device-mapping-note-2"><i class="fa fa-info-circle" aria-hidden="true"></i> Choose
                            this option if your team member are not part of
                            companies active directory.

                        </div>

                    </div>

                </div>

                <!-- <div class="locationinfo">
                <em class="fa fa-info-circle mr-3 mt-2"></em>
                <span>Please ensure that the email used to create the Talygen account matches the email used for the user's company account in Active Directory (AD).</span>
            </div> -->


            </div>

        </div>
        <div class="mainfooter_app">
            <div class="col-lg-12 text-right mt-4 ">
                <button title="Previous" class="btn btn-warning float-left px-4" @click="PreviousButton()"
                    v-if="currentTabIndex > 1">
                    <em class="fa fa-chevron-left pr-2"></em>
                    {{ $t("Previous") }}
                </button>

                <!-- <button type="button" class="btn next px-4 formbtn float-right" @click="Continue()"
                    v-if="tabCount > currentTabIndex">
                    {{ $t("Next") }}
                    <em class="fa fa-chevron-right pl-2"></em>
                </button> -->

                <button title="Finish" class="btn btn-danger formbtn widthhalf px-4" @click="Finish()"
                    v-if="tabCount == currentTabIndex">
                    {{ $t("Finish") }}
                </button>

            </div>
        </div>
        <DeviceMapping v-if="ShowDeviceMapping" @closedialog='closedialog'></DeviceMapping>

    </section>
</template>

<style>
.device-mapping-note i.fa-info-circle,
.device-mapping-note-2 i.fa-info-circle {
    position: absolute;
    left: 15px;
    top: 15px;
    font-size: 26px;
}

.device-mapping-note {
    position: relative;
}


.device-mapping-option {
    width: calc(100% - 85px);
    padding: 15px 0 15px 0;
}

.device-mapping-note,
.device-mapping-note-2 {
    position: relative;
    padding-left: 60px;
    background: #e2f7e8;
    padding: 10px 30px 10px 60px;
    font-size: 13px;
}

.device-mapping-note-2 {
    padding: 10px 30px 30px 60px;
}
</style>

<script>
import DataService from '../../../services/DataService';
import DeviceMapping from "../SilentApp/Modals/modalDeviceMapping.vue"
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
    },
    components: {
        DeviceMapping
    },
    data() {
        return {
            ShowDeviceMapping: false,
            isPersonal: true,
            isCompany: true


        }
    },
    created() {
        //var vm = this;
        //debugger //DeviceMapping
    },
    methods: {
        Continue: function () {
            var vm = this;
            vm.$emit("nextTab");
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        openDeviceMppingListing: function () {
            var vm = this;
            vm.isPersonal = false,
                vm.isCompany = false;
            vm.ShowDeviceMapping = true;
        },
        ChangeClass: function () {

            var vm = this;
            vm.isPersonal = true,
                vm.isCompany = true;

        },
        Finish: async function () {
            
            var vm = this;
            await vm.UpdateUserSettingConfig('HideContextmenu');
            await vm.UpdateUserSettingConfig('initialstartup');
            window.location.href = '/User/Home';
        },
        async UpdateUserSettingConfig(type) {
            
            let url = `status=true&type=${type}`
            await DataService.UpdateUserSettingConfig(url).then((response) => {

            });
        },

    }
}
</script>