<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="col-md-12 p-0 initialsetuptabsbase">
            <div class="tab-content">
                <div class="dynamiccontent_base">
                    <div class="col-lg-12 mb-0 float-left p-0">
                        <div class="heading-border">
                            <h5 class="h5">
                                <span class="spngropuname">
                                    {{ $t('ChooseIndustryType') }}
                                </span>
                            </h5>
                        </div>
                        <div class="row">
                            <ul class="shortcuts-menu pb-3 connectedSortable advancedsetup newaddprojects">
                                <li class="position-relative clients" v-for="(item, index) in IndustryTypeTemplate"
                                    :key="index" @click="selectItem(item)" @mousedown.prevent>
                                    <a class="short-menu-item" :class="{ 'gen-templateadded i_setup': item.clicked }">
                                        <span class="left-icon-m">
                                            <em :class="item.icon"></em>
                                        </span>
                                        <span class="short-item-name">{{ item.name }}</span>
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div class="mainfooter_app">

                    <div class="col-lg-12 text-right mt-4">
                        <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                            v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous')
                            }}</button>

                        <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4"
                            @click="Skip()" v-if="tabCount > currentTabIndex">{{ $t("Skip") }}</button>

                        <button type="button" id="aGroupTicketSave" @click="AddIndustryType('SaveAndClose')" title=""
                            data-toggle-tooltip="tooltip" data-original-title="Save"
                            class="popupgreenbtn btn btn-success formbtn widthhalf pr-2" style="margin-left: 5px;"
                            v-if="tabCount == currentTabIndex && reqFrom != 'InitialSetup'">
                            <i class="fa fa-save pr-2"></i> {{ $t('SaveAndClose') }}
                        </button>
                    </div>

                </div>

            </div>
        </div>
    </section>
</template>

<script>
import DataService from "../../../../services/DataService";
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0
    },

    data() {
        return {

            IndustryTypeTemplate: [],
            selectedItemId: null
        }
    },

    created: async function () {
        var vm = this;
        vm.GetIndustryType();
    },
    methods: {
        GetIndustryType: function () {

            DataService.GetTemplateList().then((response) => {
                if (response.data.result.DATA.length > 0) {
                    // Initialize clicked property for each item to ensure reactivity
                    this.IndustryTypeTemplate = response.data.result.DATA.map(item => {
                        return {
                            ...item,
                            clicked: false
                        };
                    });

                }
            })
        },
        // selectItem(item) {
        //     debugger
        //     item.clicked = !item.clicked;
        //     this.selectedItemId = item.id;
        // },
        selectItem(item) {
            debugger
            // Deselect all items first
            this.IndustryTypeTemplate.forEach(industryItem => {
                this.$set(industryItem, 'clicked', false);
            });
            // Select only the clicked item
            this.$set(item, 'clicked', true);
            this.selectedItemId = item.id;
        },
        AddIndustryType: async function () {
            debugger
            var vm = this;
            var url = ''

            url = `IndustryTypeId=${vm.selectedItemId}`

            await DataService.AddIndustryType(url).then(function (response) {
                if (response && response.data != null) {
                    vm.ShowAlert(vm.$t('DataSavedSuccessfully'), "success");
                    vm.Finish();

                }
                else {
                    vm.Finish();
                }
            });
        },
        Skip: function () {

            var vm = this;
            vm.$emit("nextTab", true);
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        Finish: async function () {
            debugger
            var vm = this;
            await vm.UpdateUserSettingConfig('HideContextmenu');
            await vm.UpdateUserSettingConfig('initialstartup');
            window.location.href = '/User/Home';
        },
        async UpdateUserSettingConfig(type) {

            let url = `status=true&type=${type}`
            await DataService.UpdateUserSettingConfig(url).then((response) => {

            });
        }


    }

}

</script>
<style>

.gen-templateadded.i_setup{ background:#f1fff1;}
.gen-templateadded.i_setup:hover{ background:#f1fff1;}
.gen-templateadded.i_setup .left-icon-m{ background-color:#def3de;}



