    background-color: #dcf0e0 !important;
    color: #ffffff !important;
    border: 2px solid #25462c !important;
    box-shadow: 0 4px 8px rgba(193, 209, 197, 0.3);
    transform: scale(1.02);
    transition: all 0.3s ease;
} */

/* .gen-templateadded .left-icon-m em {  color: #31c269 !important;} */

.short-menu-item:hover {
    cursor: pointer;
    background-color: #e0e0e0;
    /* Hover effect */
}

.initialsetuptabsbase .choosepicture {
    float: left;
    max-width: 250px !important;
    width: 250px !important;
}

.initialsetuptabsbase .fillupform {
    float: left;
    max-width: calc(100% - 260px) !important;
    width: calc(100% - 260px) !important;
}

.initialsetuptabsbase .wiz-upimg {
    border-radius: 20px !important;
}

.initialsetuptabsbase .wiz-upimg .circle {
    border-radius: 20px !important;
}

.initialsetuptabsbase .wiz-upimg .circle img {
    margin-top: 15px;
    background: #f2f2f2;
    padding: 2px;
    border-radius: 100%;
    height: 110px;
    width: 110px;
}

.initialsetuptabsbase .wiz-upimg .p-image {
    color: #ffffff;
    background: #525252 !important;
    text-align: center;
    padding: 10px 0 20px 0;
}

.initialsetuptabsbase .locationinfo {
    display: flex;
    border: 1px solid #cec699;
    padding: 10px 15px;
    font-size: 16px;
    color: #454545;
    font-weight: 500;
    width: 100%;
    float: left;
    min-height: 80px;
    background: #fff8d5;
}

.initialsetuptabsbase .locationinfo em {
    color: #454545;
    font-size: 25px;
}

.initialsetuptabsbase .locationinfo1 {
    display: flex;
    border: 1px solid #cbcbcb;
    padding: 20px 15px;
    color: #333335;
    font-weight: 500;
    width: 49%;
    float: left;
    min-height: 80px;
    background: #fff;
    margin-right: 1rem;
}

.initialsetuptabsbase .locationinfo1 em {
    color: #cdcdcd;
    font-size: 53px;
    margin-top: 15px;
}

.initialsetuptabsbase .locationinfo1.selected {
    border: 1px solid #27a745;
    background: #f0fff3;
}

.initialsetuptabsbase .locationinfo1.selected em {
    color: #28a745;
}

.initialsetuptabsbase .locationinfo1:last-child {
    margin-right: 0px;
}

.initialsetuptabsbase .locationinfo1 h2 {
    font-size: 24px;
}

.initialsetuptabsbase .dynamiccontent_base h3.ownership {
    font-size: 28px;
    font-weight: 600;
}

/* .initialsetuptabsbase .mainfooter_app button.next {
    background-color: #363f4d;
    color: #fff;
    font-size: 18px;
} */

.initialsetuptabsbase .mainfooter_app {
    min-width: calc(100% - 395px);
    min-height: 83px;
    background-color: #f1f1f1;
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 99;
}


.initialsetuptabsbase .mainfooter_app button.next {
    width: 220px;
    height: 50px;
    border: none;
    outline: none;
    color: #fff;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
}

.initialsetuptabsbase .mainfooter_app button.next:before {
    content: '';
    background: linear-gradient(45deg, #ff358a, #363f4d, #fea088);
    position: absolute;
    top: -2px;
    left: -2px;
    background-size: 400%;
    z-index: -1;
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}

.initialsetuptabsbase .mainfooter_app button.next:active {
    color: #000
}

.initialsetuptabsbase .mainfooter_app button.next:active:after {
    background: transparent;
}

.initialsetuptabsbase .mainfooter_app button.next:before {
    opacity: 1;
}

.initialsetuptabsbase .mainfooter_app button.next:hover:before {
    background: #1c94ec;
}

.initialsetuptabsbase .mainfooter_app button.next:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes glowing {
    0% {
        background-position: 0 0;
    }

    50% {
        background-position: 400% 0;
    }

    100% {
        background-position: 0 0;
    }
}

.initialsetuptabsbase .mainfooter_app button.next:hover {
    background: #1c94ec;
}

.initialsetuptabsbase .mainfooter_app button.skip-btn-clr {
    background-color: #fff;
    color: #0075ab;
    font-size: 18px;
    border: 1px solid #fff;
}

.initialsetuptabsbase .mainfooter_app button.btn-warning {
    background-color: #fff;
    color: #0075ab;
    font-size: 18px;
    border: 1px solid #fff;
}

.initialsetuptabsbase .mainfooter_app button {
    font-size: 18px;
}

.initialsetuptabsbase .mainfooter_app button:hover.skip-btn-clr {
    background-color: #e3e3e3;
}

.initialsetuptabsbase .mainfooter_app button:hover.btn-warning {
    background-color: #e3e3e3;
}

.newtabdes_buttonsbase .initialsetuptabsbase button.btn.btn-warning {
    color: #0075ab;
}

.initialsetuptabsbase .shortcuts-menu.newaddprojects li {
    max-width: 50% !important;
}

.shortcuts-menu li.active {
    transform: scale(1.05);
}
</style>