<template>
    <section id="main-content" class="main-content">
        <loader :is-visible="isLoading"></loader>
        <section class="page-action">
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{$t('ImportCategory')}}</span>
                        <span class="p-actions float-right">
                            <a title="BacktoList" class="p-action-btn text-white" @click="BackTolist"><em class="fa fa-angle-double-left pr-2"></em><br>
                                <span>{{$t('BacktoList')}}</span>
                            </a>
                        </span>
                    </div>
                </div>
            </div>
        </section>
        <div class="user-guide-custom d-none">
            {{$t('User_Guide_Import Category')}}
        </div>
        <div class="border p-3">
            <div class="row">
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('DownloadImportCategory')}}:</label>
                        <div class="input-group mb-3">
                            <div class="form-control">
                                <i class="fa fa-file-excel-o pr-1 text-success"></i>{{$t('Import Category')}}.xls
                            </div>
                            <div class="input-group-append downloadImport Category">
                                <span class="input-group-text" id="basic-addon2">
                                     <a href=https://cogniterblob.blob.core.windows.net/global/SampleFiles/category/sample-file-import-category.xls class="" download>
                                        <i class="fa fa-download text-primary"></i>
                                    </a>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('UploadFile')}}:<span class="text-danger">*</span></label>
                        <div class="input-group">
                            <div class="custom-file Import Category">
                                <input type="file" name='FileSrc' id='flAvatar' multiple="true" tabindex="-1" @change="setFile($event)" style="position: absolute; clip: rect(0px, 0px, 0px, 0px);">
                                <div class="bootstrap-filestyle input-group">
                                    <input type="text" class="form-control disabled" disabled="" v-model="attachmentName" />
                                    <div class="input-group-append" style="cursor:pointer;">
                                        <span class="group-span-filestyle input-group-btn input-group-text bg-white" tabindex="0">
                                            <label title="Click here to select a file." for='flAvatar' class="mb-0 font-weight-normal ">{{$t('FileControlText')}}</label>
                                        </span>
                                        <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                            <a href="javascript:;" class="round-icon-small btn-danger-light" title="Clear" @click="Clear"><i class="fa fa-undo"></i></a>
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row flex-row-reverse">
                <div class="col-lg-6 text-right">
                    <a href="javascript:void(0);" @click="UploadImportCategoryFile()" :title="$t('ImportCategoryExcelFile')" class="btn btn-info formbtn mr-1"><i class="fa fa-download pr-2"></i>{{$t('Import Category')}}</a>
                    <a title="" class="btn btn-danger formbtn my-2" @click="onCancel()" data-toggle-tooltip="tooltip" data-original-title="Cancel"><i class="fa fa-close mr-2"></i>Cancel</a>
                </div>
            </div>
            
        </div>
         <div class="table-responsive mb-3 table-fix-header" v-if="CategoryTypedata!=0">
                    <Form ref=formm>
                        <table id="tblExpList" class="table table-bordered dt-responsive nowrap mt-0">
                            <thead class="thead-bg">
                                <tr>
                                    <th id="th-PRODUCTTYPE" style="width: 156px; min-width: 120px; max-width: 156px;"><span
                                            class="d-inline-block">Category<span class="d-inline-block">
                                               </span></span>
                                    </th>
                                    <th id="th-PRODUCTTYPE" style="width: 156px; min-width: 120px; max-width: 156px;"><span
                                            class="d-inline-block">Application/Website<span class="d-inline-block">
                                               </span></span>
                                    </th>
                                     <th id="th-PRODUCTTYPE" style="width: 156px; min-width: 120px; max-width: 156px;"><span
                                            class="d-inline-block">Color Code<span class="d-inline-block">
                                               </span></span>
                                    </th>
                                     <th id="th-PRODUCTTYPE" style="width: 156px; min-width: 120px; max-width: 156px;"><span
                                            class="d-inline-block">Category Code<span class="d-inline-block">
                                                </span></span>
                                    </th>
                                </tr>
                            </thead>
                           
                            <tbody v-for="(item, index) in CategoryTypedata" :key="index">
                               
                                <tr v-if="CategoryTypedata.length != '0'">
                                    <td>
                                            <input type="text" v-model="item.CategoryName"  class="enlarged-textarea"/>
                                    </td>
    
                                     <td>
                                      
                                            <textarea v-model="item.ApplicationWebsite"  class="enlarged-textarea"></textarea>
                                    </td>
    
                                     <td >
                                            <input type="text"  v-model="item.Colocode" :disabled="true" style="width: 90%;"/> 
                                           <div class="colorpick" style="position: absolute; top: 12px; right: 0; z-index: 1;">
                                            <ColourPicker v-model="item.colour" :color="item.colour"  no-input :value="item.colour"   @input="pickerColorChange($event,item)"  picker="compact"  />
                                        </div>
    
                                    </td>
    
                                     <td>
                                            <input type="text" v-model="item.Categorycode"/>
                                       
                                    </td>
                                </tr>
                                
                            </tbody>
                        </table>
                        <div colspan="10" v-if="CategoryTypedata.length == '0'" class="text-danger text-center no-record">
                                        {{ $t('NoRecordfound') }}
                                    </div>
                    </Form>
                      <div class="row flex-row-reverse">
                    <div class="col-lg-6 text-right">
                        <div class="search-btm-btn">
                            <a id="aSubmit" href="javascript:;" class="btn btn-success formbtn widthhalf"
                                v-on:click="SubmitData()" tooltip="Submit" title="Submit"><i
                                    class="fa fa-save pr-2"></i>Submit</a>
                            &nbsp;<a id="aCancel" href="javascript:;" v-on:click="onCancel" class="btn btn-danger formbtn my-2"
                                tooltip="Cancel" title="Cancel"><i class="fa fa-close pr-2"></i>Cancel</a>
                        </div>
                    </div>
                </div>
         </div>
    </section>
    </template>
    
    <script>
    import { ref } from 'vue';
    // import ImportProduct from '../../components/Modal/ImportProduct.vue';
    import DataService from '../../../../services/DataService';
    import ColourPicker from 'vue-colour-picker'
    
    export default {
        components: {
            // ImportProduct,
            ColourPicker
        },
        data: function () {
            return {
                 item: {
            Colocode: '',
          },
                isLoading: false,
                attachmentName: '',
                filepath:'',
                CategoryTypedata: [],
                applicationuser:[],
                ExcelListData:[],
              colour: '',
               
    
            }
        },
    
        methods: {
                pickerColorChange(color, item) {
            item.Colocode = color;
        },
            setFile: function (e) {
                this.attachmentName = e.target.files[0].name;
                this.file = e.target.files[0];
                this.excelFileName = e.target.files[0].name;
            },
       
            Clear: function () {
                this.attachmentName = '';
            },
            BackTolist: function () {
                var vm = this;
                vm.$router.push({
                    name: 'CategoryListing'
                });
            },
            onCancel: function () {
                var vm = this;
                vm.$router.push({
                    name: 'CategoryListing'
                });
            },
            UploadImportCategoryFile() {
                var vm = this;
                vm.$emit('ImportProduct')
                if ($("#flAvatar").val() != "") {
                    var file = $("#flAvatar").val().split('\\').pop();
                    var allowFileTypesExt = '.xls,.xlsx';
                    var extension = file.replace(/^.*\./, '');
                    if (!allowFileTypesExt.includes(extension)) {
                        vm.ShowAlert(vm.$t('InvalidSampleFileUploadMsg', [file, allowFileTypesExt]), "warning", true, vm.$t('Alert'));
                    } else {
                        vm.confirmR(vm.$t("bulkImportConfirmationMsg"), true, (vm.$t("Delete") + "  " + vm.$t("REPORT_ID")), function () {
                            const formdata = new FormData();
                            formdata.append("file", vm.file);
                            vm.isLoading = true;
                            DataService.ImportCategorySamplePost(formdata).then(function (response) {
                                vm.isLoading = false;
                                var data = response.data;
                                if (data.Status == 0) {
                                    vm.filepath = data.Msg;
                                    vm.ShowImportProduct = vm.filepath != '' ? true : false;
                                    vm.ImportProductType();
                                } else {
                                    vm.ShowAlert(data.Msg, "warning", true, vm.$t("Alert"));
                                }
                            });
                            vm.isLoading = false;
                        });
                    }
                } else {
                    vm.ShowAlert(vm.$t("PleaseUploadExcelFile"), "warning", true, vm.$t("Alert"));
                }
            },
             ImportProductType: function () {
                var vm = this;
                vm.isLoading = true;
                var count = 0;
                var url = `filepath=${vm.filepath}&typeName='ProductType'`;
                DataService.BulkUploadCategoryType(url).then(response => {
                    if(response.data!=null)
                    {
                        vm.CategoryTypedata = response.data;
                        vm.CategoryTypedata.forEach(ele=>{
                            ele.colour=''
                            if(ele.Colocode!=null){
                                ele.colour=ele.Colocode;
                            }
                        })
                        var arrayy=[]
                       for (let i = 0; i < vm.CategoryTypedata.length; i++) {
                     arrayy.push(vm.CategoryTypedata[i].ApplicationWebsite)
    }
                  
                     vm.applicationuser=arrayy;
                         console.log('ddsfguy',  vm.applicationuser)
                        vm.isLoading = false;
                    }
                    else 
                    {
                        vm.ShowAlert(vm.$t('InValidFileUpload'), "warning", vm.$t('Alert'));
                    }
                })
                vm.isLoading = false;
            },
    
        SubmitData() {
        var vm = this;
        vm.isLoading = true;
        // Assuming ExcelListData is a reactive ref
        const ExcelListData = ref([]);
    
        // Loop through each item in CategoryTypedata
        for (const item of vm.CategoryTypedata) {
            
            const obj = {
                CategoryName: item.CategoryName,
                ApplicationWebsite: item.ApplicationWebsite,
                Categorycode: item.Categorycode,
                Description:'',
                Colocode: item.Colocode,
                CompanyID: item.CompanyID,
                UserID: item.UserID,
                batchid: item.batchid,
            };
            ExcelListData.value.push(obj);
        }
    
        var JsonObj = JSON.stringify(ExcelListData.value);
        DataService.CategoryTypeBulkUploadPost(JsonObj).then(response => {
            if(response.data.status==1){
                var RetMeg = response.data.msg;
                  vm.ShowAlert(vm.$t(RetMeg), "success", vm.$t('Alert'));
            }
            else if (response.data.status == 0) {
                var categoryName = response.data.category; 
                var applicationName = response.data.apllication; 
                var alertMessage = vm.$t('Category Name: ') + categoryName  + vm.$t('Application Name: ') + applicationName ;
                vm.ShowAlert(alertMessage + ' - ' + response.data.msg, vm.$t('Alert'));
             }
        });
    
        vm.isLoading = false;
    },
        }
    }
    </script>
    <style >
    .enlarged-textarea {
        width: 500px;
        height: 50px;
    }
    .colorpick {
        position: absolute;
        right: 19px;
        top: 46%;
        width: auto !important;
        height: 28px;
        z-index: 1;
    }
    
    
    </style>
    