<template>
    <section class="device-mapping-popup">
        <div class="my-popups">
            <div class="modal d-block container-fluid padding-t_10">

                <div class="modal-dialog">
                    <div class="modal-content col-md-12 p-0">
                        <div class="modal-header theme-primary partition p-actions-expand" style="overflow: visible;">
                            <h4 class="modal-title">{{ $t('User Mapping') }}</h4>
                            <button type="button" class="close" v-on:click="Close()">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body listing mt-2">
                            <dynamic-form ref="validateUserMapping" lang="en" :buttons="false" :schema="FormSchema">
                            </dynamic-form>

                            <div class="table-responsive table-fix-header">
                                <table id="DeviceListing" class="table table-bordered dt-responsive nowrap mt-0">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th class="text-capitalize">
                                                <span href="javascript:;" class="sort" id="SystemProfileName">
                                                    {{ $t('LocalSystemProfileName') }}
                                                </span>
                                            </th>
                                            <th class="text-capitalize">
                                                <span href="javascript:;" class="sort" id="TalygenUserName">
                                                    {{ $t('TalygenUserName') }}
                                                </span>
                                            </th>
                                            <th class="text-center"><span>{{ $t('Edit') }}</span></th>
                                            <th class="text-center"><span>{{ $t('Delete') }}</span></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item, index) in ProfileListing" :key="index">                                            
                                            <td>{{ item.ProfileName }}</td>
                                            <td>{{ item.UserName }}</td>                                            
                                            <td class="text-center">
                                                <a class="aMapUsers single-action " href="javascript:;"
                                                    machineId="MachineId" typetitle="Edit" typefor="Clone" title="Edit"
                                                    v-on:click="editUserMapping(item)">
                                                    <i class="fa fa-pencil text-success  action-icon"></i>
                                                </a>                                                
                                            </td>
                                            <td class="text-center">
                                                <a href="javascript:void(0)" machineId="MachineId"
                                                    class="aDeleteDevice single-action" title="Delete" v-on:click="deleteUserMapping(item)">
                                                    <i class="fa fa-trash text-danger  action-icon"></i>
                                                </a>
                                            </td>
                                        </tr>
                                        <tr v-if="ProfileListing.length==0">
                                            <td colspan="3" class="text-center text-danger py-3">
                                                {{$t('NoRecordfound') }}
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            <div class="col-lg-12 mt-3 p-0">
                            <div class="row flex-row-reverse">
                            <div class="col-lg-6 text-right">
                                <a href="javascript:;" class="btn btn-success save-btn mr-2" v-on:click="SaveData()"
                                    title="Save"><i class="fa fa-save pr-2"></i>Save</a>
                                <a href="javascript:;" class="btn btn-danger" v-on:click="cancel()"
                                    title="Cancel"><i class="fa fa-close pr-2"></i>Cancel</a>
                            </div>
                            <div class="col-lg-6">
                                <div class="py-2">
                                    <small>
                                        <span class="mandatory">{{ $t("Mandatory") }}</span>
                                    </small>
                                </div>
                            </div></div>
                        </div>
                        </div>
                       
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>

import DataServices from "../../../../services/DataService"
export default {
    props: {
        machineId: {
            type: Number
        }
    },
    data() {
        return {
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('LocalSystemProfileName'),
                            name: "ProfileName",
                            value: "",
                            placeholder: "Select Value",
                            config: {
                                options: [],
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('TalygenUserName'),
                            name: "TalygenUserName",
                            value: "",
                            placeholder: "Select Value",
                            config: {
                                options: [],
                            },
                            validationRules: { "required": true },
                        }

                    ],
                },

            ],
            pageSize: 10,
            pageNumber: 1,
            ProfileListing: []
        }
    },
    created() {
        var vm = this;
        vm.GetDDLProfileData();
        vm.GetUsersByCompanyId();
        vm.GetProfileListByMachineId();
    },
    methods: {
        GetProfileListByMachineId: async function () {
            var vm = this;
            var params = `machineId=${vm.machineId}&sortBy=&sortOrder=&pageSize=${vm.pageSize}&pageNumber=${vm.pageNumber}`;
            await DataServices.GetProfileListByMachineId(params).then(function (response) {
                vm.ProfileListing = [];
                if (response.data && response.data != null) {
                    var data = JSON.parse(response.data.result);
                    vm.ProfileListing = data.data;
                }
            });
        },
        
        GetDDLProfileData: function () {
            var vm = this;
            var params = `ids=&moduleName=&type=PROFILE_LIST_BY_MACHINEID&search=&refId=${vm.machineId}`
            DataServices.GetDDLData(params).then((response) => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.$refs.validateUserMapping.updateProperty("ProfileName", "config.options", response.data.DATA);
                        vm.$refs.validateUserMapping.UpdateKeyValue();
                    }
                }
            })
        },
        
        GetUsersByCompanyId: function () {
            var vm = this;
            var url = `moduleName=Timer`;
            DataServices.GetUserListByCompanyId(url).then(response => {
                if (JSON.parse(response.data.result).length > 0) {
                    var StatusData = JSON.parse(response.data.result);
                    StatusData.forEach(element => {
                        var Obj = {
                            name: element.USERNAME,
                            value: element.USER_ID
                        }
                        vm.FormSchema[0].Data[1].config.options.push(Obj);
                        vm.$refs.validateUserMapping.UpdateKeyValue();
                    });
                }
            })
        },
        editUserMapping: function (item){
            var vm = this;
            vm.$refs.validateUserMapping.updateProperty("ProfileName", "value", item.ProfileId);
            vm.$refs.validateUserMapping.updateProperty("TalygenUserName", "value", item.UserId);
            vm.$refs.validateUserMapping.UpdateKeyValue();
        
        },

        deleteUserMapping: async function(item)
        {           
            var vm = this;
            var params = `profileId=${item.ProfileId}&userId=${item.UserId}`;
            await DataServices.UnMapUser(params).then(function (response) {
                    if (response.data) {
                        vm.ShowAlert(vm.$t('RecordsDeleteSuccessfully'), "success");
                        vm.GetProfileListByMachineId();                   
                    }
                    else{
                        vm.ShowAlert(vm.$t('SomethingWentWrong'), "failure", true, vm.$t("Alert"));
                    }
                });
        },

        async SaveData() {
            var vm = this;
            const result = await vm.$refs.validateUserMapping.validateForm.validate();
            if (result) {
                var profileId = vm.$refs.validateUserMapping.getPropertyValue("ProfileName", "value");
                var talygenUserId = vm.$refs.validateUserMapping.getPropertyValue("TalygenUserName", "value");
                var url = `profileId=${profileId}&talygenUserId=${talygenUserId}`;
                await DataServices.MapUser(url).then(response => {
                    if (response.data.result == 'True') {
                        vm.ShowAlert(vm.$t('AddedMapUser'), "success");
                        vm.clearFields();
                        vm.GetProfileListByMachineId();
                        vm.isLoading = false;
                    } else if (response.data.result == 'True') {
                        vm.ShowAlert(vm.$t('UpdatedMapUser'), "success");
                        vm.clearFields();
                        vm.GetProfileListByMachineId();
                        vm.isLoading = false;
                    } else {
                        vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                        vm.isLoading = false;
                    }
                });

            }
        },
        cancel() {
            var vm = this;
            vm.clearFields();
        },
        clearFields: function () {
            var vm = this;
            vm.FormSchema[0].Data[0].value = "";
            vm.FormSchema[0].Data[1].value = "";
            vm.$refs.validateUserMapping.validateForm.reset();
        },
        Close: function () {
            var vm = this;
            vm.$parent.isModalUserMapping = false;
        }


    }

}
</script>
<style>
.single-action  {
    font-size: 20px;
}

.text-center {
    text-align: center !important;
}
</style>