<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="col-md-12 p-0">
            <div class="tab-content">
                <div class="row">
                    <div class="col-lg-12 text-right mb-3 new_initialsetup_btnsbase">
                        <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4"
                            @click="Skip()" v-if="tabCount == currentTabIndex" >{{ $t("Skip") }}
                        </button>
                    </div>
                </div>
                <div class="col-sm-12 text-center mt-0 px-2 mb-4">
                    <h3 class="">{{$t('AddUser')}}</h3>
                    <div class="col-sm-12 text-center mt-0 px-2 mb-4 float-left">
                        <div class="user_type_choice mt-3 mb-3 inisetups">
                            <div class="btn-group btn-group-toggle" data-toggle="buttons">
                                <label class="nonlabel"><span class="UserType">{{$t('UserType')}}:-</span></label>
                                <label class="btn btn_cstm active position-relative"><div class="popover-arrowss"></div>
                                    <input autocomplete="off" checked="checked" v-on:click="OnChangeUserType(3)" class="cls_usertype" data-val="true" type="radio" value="3">
                                    <span class="d-block">
                                        <i class="fa fa-user"></i>
                                    </span>
                                    <span class="pt-1 pl-2">{{$t('NormalUser')}}</span>
                                </label>
                                <label class="btn btn_cstm  position-relative"><div class="popover-arrowss"></div>
                                    <input autocomplete="off" class="cls_usertype" v-on:click="OnChangeUserType(2)" name="user_type_id" type="radio" value="2">
                                    <span class="d-block">
                                        <i class="fa fa-user"></i>
                                    </span>
                                    <span class="pt-1 pl-2">{{$t('ProjectManager')}}</span>
                                </label>
                                <label class="btn btn_cstm  position-relative"><div class="popover-arrowss"></div>
                                    <input autocomplete="off" class="cls_usertype" v-on:click="OnChangeUserType(1)" name="user_type_id" type="radio" value="1">
                                    <span class="d-block">
                                        <i class="fa fa-user"></i>
                                    </span>
                                    <span class="pt-1 pl-2">{{$t('CompanyAdmin')}}</span>
                                </label>
                            </div>
                        </div>
                        <p class="font-14 newdiff_bg">{{UserTypeData.content}}</p>
                    </div>
                </div>
                <dynamic-form  ref="addnewuser" lang="en" :buttons="[]":schema="FormSchema">
                    <!-- <template v-slot:tgslot-all_added_licenses="{data}">
                        
                    </template> -->
                </dynamic-form>
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left initialsetupfolder mt-4 p-0">
                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left" id="divtickettupelist">
                        <div class="heading-border">
                            <h5 class="h5"><span>{{$t('allLicenses')}}</span><a class="btn btn-primary" v-on:click="AssignLicense()">{{$t('Assign')}}</a></h5>
                        </div>
                        <div class="managefolder py-3 allLicense">
                            <div class="col-12 float-left border px-3 py-2 text-right bg-light">
                                <div class="custom-control custom-checkbox">
                                    <input class="chkItems custom-control-input chkAll" type="checkbox" value="" v-on:change="onChange($event,'allLicense')" name="chkAllLicense" id="chkAllLicense" style="display: none;">
                                    <label class="custom-control-label universal-custom-control-label pt-1" style="float: right;" for="chkAllLicense"></label>
                                </div>
                            </div>
                            <ul class="list-group float-left w-100" style="max-height: 400px; min-height: 200px; overflow-y: auto;">
                                <li v-for="item in AllLicenses" class="list-group-item single-action px-3">
                                    <strong>{{item.module_name +' ('+item.license_count + ')'}}</strong>
                                    <div class="custom-control custom-checkbox float-right">
                                        <input v-on:click="onSelectLicense($event,'allLicense')" class="chkItems custom-control-input" type="checkbox" v-bind:disabled="item.license_count == 0 || item.is_disabled" :value="item.module_id" v-model="checkedAllLicenses" :name="item.module_name" :id="'chk_'+item.module_id" style="display: none;">
                                        <label class="custom-control-label universal-custom-control-label pt-1" :for="'chk_'+item.module_id"></label>
                                    </div>
                                </li>
                                <li v-if="AllLicenses.length==0" class="text-danger text-center mt-2">{{$t('NoRecordfound')}}</li>
                            </ul>
                        </div>
                    </div>
                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left" id="divAssignedLicence">
                        <div class="heading-border">
                            <h5 class="h5"><span>{{$t('addedLicenses')}}</span><a class="btn btn-danger" v-on:click="RemoveLicense()">{{$t('Remove')}}</a></h5>
                        </div>
                        <div class="managefolder py-3 addedLicense">
                            <div class="col-12 float-left border px-3 py-2 text-right bg-light">
                                <div class="custom-control custom-checkbox">
                                    <input class="chkItems custom-control-input chkAll" type="checkbox" value="" v-on:change="onChange($event,'addedLicense')" name="chkAllAddedLicense" id="chkAllAddedLicense" style="display: none;">
                                    <label class="custom-control-label universal-custom-control-label pt-1" style="float: right;" for="chkAllAddedLicense"></label>
                                </div>
                            </div>
                            <ul class="list-group float-left w-100" style="max-height: 400px; min-height: 200px; overflow-y: auto;">
                                <li v-for="item in AddedLicenses" class="list-group-item list_username list_img single-action px-3">
                                    <strong>{{item.module_name}}</strong>
                                    <div class="custom-control custom-checkbox float-right">
                                        <input v-on:click="onSelectLicense($event,'addedLicense')" class="chkItems custom-control-input" type="checkbox" :value="item.module_id" v-model="checkedAddedLicenses" :name="'added_'+item.module_name" :id="'added_'+item.module_id" style="display: none;">
                                        <label class="custom-control-label universal-custom-control-label pt-1" :for="'added_'+item.module_id"></label>
                                    </div>
                                </li>
                                <li v-if="AddedLicenses.length==0" class="text-danger text-center mt-2">{{$t('NoRecordfound')}}</li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-12 text-right">
                        <button title="Previous" class="btn btn-warning float-left"
                            @click="PreviousButton()" v-if="currentTabIndex > 1">
                            <i class="fa fa-chevron-left pr-2"></i> 
                            {{ $t("Previous") }}
                        </button>
                        <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4"
                            @click="Skip()" v-if="tabCount == currentTabIndex">
                            {{ $t("Skip") }}
                        </button>
                        <button type="button" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                            @click="SaveSubmit('SaveAndNew')" style="margin-left: 5px" v-if="tabCount == currentTabIndex">
                            <i class="fa fa-save pr-2"></i> 
                            {{ $t("SaveAndNew") }}
                        </button>
                        <button type="button" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                            @click="SaveSubmit('SaveAndClose')" style="margin-left: 5px" v-if="tabCount == currentTabIndex" >
                            <i class="fa fa-save pr-2"></i> 
                            {{ $t("SaveAndClose") }}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
export default{
    props: {
        tabCount: 0,
        currentTabIndex: 0,
    },
    data(){
        return{
            isLoading:false,
            userTypeId:1,
            UserTypeData: null,
            AllLicenses: [],
            AddedLicenses: [],
            fileData: [],
            checkedAllLicenses:[],
            checkedAddedLicenses:[],
            base64String:'',
            FormSchema:[
            {
                layoutType: "double",
                groupLayoutType: "single",
                group_name: "Account Settings",
                group_display_name: this.$t("AccountSettings"),
                Data: [
                        {
                            astype: "TextField",
                            label: this.$t("FirstName"),
                            name: "first_name",
                            value: "",
                            placeholder: this.$t("FirstName"),
                            validationRules: {
                                required: true,
                            }
                        },
                        {
                            astype: "TextField",
                            label: this.$t("LastName"),
                            name: "last_name",
                            value: "",
                            placeholder: this.$t("LastName"),
                            validationRules: {
                                required: true,
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t("Email"),
                            name: "email_id",
                            value: "",
                            placeholder: this.$t("Email"),
                            validationRules: {
                                required: true,
                            },
                        },
                        {
                            astype: "ToggleField",
                            label: this.$t('EnableLogin'),
                            name: "is_enable_login",
                            value: "",
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t("Password"),
                            name: "password",
                            value: "",
                            placeholder: this.$t("Password"),
                            validationRules: {
                                required: true,
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t("ConfirmPassword"),
                            name: "confirm_password",
                            value: "",
                            placeholder: this.$t("ConfirmPassword"),
                            validationRules: {
                                required: true,
                            },
                        }
                    ]
                },
                // {
                //     layoutType: "single",
                //     groupLayoutType: "single",
                //     Data: [
                //         {
                //             astype: "SlotField",
                //             name: "all_added_licenses",
                //             value: "",
                //             placeholder: "",
                //             config: {
                //                 options: [],
                //             }
                //         }
                //     ],
                // },
            ],
            buttons:[
                {
                    type: "button",
                    class: "popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4",
                    text: this.$t('Skip'),
                    isVisible: (this.tabCount == this.currentTabIndex) ? true : false,
                    onClick: this.Skip
                },
                {
                    type: "button",
                    class: "btn btn-warning float-left px-4",
                    text: '<i class="fa fa-chevron-left pr-2"></i> ' + this.$t('Previous'),
                    isVisible: (this.currentTabIndex > 1) ? true : false,
                    onClick: this.PreviousButton
                },
                {
                    type: "submit",
                    class: "popupgreenbtn btn btn-success formbtn widthhalf pr-2",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('SaveAndNew'),
                    name: "SaveAndNew",
                    isVisible: (this.tabCount == this.currentTabIndex) ? true : false,
                    onClick: this.SaveSubmit
                },
                {
                    type: "submit",
                    class: "popupgreenbtn btn btn-primary formbtn widthhalf pr-2",
                    text: this.$t('SaveAndClose')+ " <i class='fa fa-chevron-right pr-2'></i>",
                    name: "SaveAndClose",
                    isVisible: (this.tabCount == this.currentTabIndex) ? true : false,
                    onClick: this.SaveSubmit
                }
            ],
        }
    },
    async created(){
        var vm = this;
        //debugger //new user
        await vm.OnChangeUserType(3);
    },
    methods:{
        AssignLicense(){
            var vm = this;
            var AlreadyAssigned=[];
            if(vm.checkedAllLicenses.length > 0){
                vm.checkedAllLicenses.forEach((parentItem,parentIndex)=>{
                    vm.AllLicenses.forEach((item,index)=> {
                        if (parentItem == item.module_id) {
                            if (!(vm.AddedLicenses.filter(x => x.module_id == parentItem).length > 0)) {
                                item.license_count = item.license_count - 1;
                                vm.AddedLicenses.push(item);
                            }
                            else {
                                AlreadyAssigned.push(item.module_name);
                            }
                        }
                    })
                })
                vm.checkedAllLicenses=[];
                if (AlreadyAssigned.length > 0) {
                    var msg = AlreadyAssigned.join(',');
                    vm.ShowAlert(vm.$t("ModulesAlreadyAssigned", [msg]), "warning", true, vm.$t("Alert"));
                }
            }else{
                vm.ShowAlert(vm.$t("EmpSharingSaveValidationMsg"), "warning", true, vm.$t("Alert"));
            }
        },
        RemoveLicense(){
            var vm = this;
            if (vm.checkedAddedLicenses.length > 0) {
                vm.checkedAddedLicenses.forEach((item,index)=> {
                    const indexL = vm.AddedLicenses.findIndex((x) => x.module_id == item);
                    if (indexL !== -1) {
                        vm.AddedLicenses.splice(indexL, 1);
                        vm.AllLicenses.filter(x => x.module_id == item)[0].license_count += 1;
                    }
                });
            }else{
                debugger
                vm.ShowAlert(vm.$t("AtLeastOneLicense"), "warning", true, vm.$t("Alert"));
            }
            vm.checkedAddedLicenses = [];
        },
        onChange(event, tab){
            var vm =this;
            if (tab == 'allLicense') {
                vm.checkedAllLicenses=[];
                const checkboxes = document.querySelectorAll('.allLicense .chkItems:not(:disabled)');
                checkboxes.forEach(checkbox =>{
                    checkbox.checked = event.target.checked;
                    if(checkbox.value){
                        vm.checkedAllLicenses.push(checkbox.value);
                    }
                })
            }else{
                const checkboxes = document.querySelectorAll('.addedLicense .chkItems');
                checkboxes.forEach(checkbox =>{
                    checkbox.checked = event.target.checked;
                    if(checkbox.value){
                        vm.checkedAddedLicenses.push(checkbox.value);
                    }
                })
            }
        },
        onSelectLicense(event,tab){

        },
        Skip: function () {
            var vm = this;
            vm.$emit("close");
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        SaveSubmit(btnType){
            var vm = this;
            debugger
            vm.$refs.addnewuser.validateForm.validate().then((result) => {
                if(result){

                }
            })
            if(btnType=='SaveAndNew') 
            {
                vm.resetForm();
            }else if(btnType=='SaveAndClose'){
                vm.Skip();
            }
            
        },
        resetForm() {
            var vm = this;
            vm.FormSchema.forEach((group, gi)=>{
                group.Data.forEach((field, fi) => {
                    field.value = "";
                })
            }) 
            vm.$refs.addnewuser.validateForm.reset();// Reset the form fields
            vm.$refs.addnewuser.validateForm = {}; // Clear any validation messages     
        },
        async GetLicenseList(){
            var vm = this;
            vm.isLoading = true; 
            var param = `userTypeId=${vm.userTypeId}`;
            await DataService.GetLicenseList(param).then(response => {
                vm.isLoading = false; 
                if(response.data.data){
                    vm.AllLicenses = [];
                    vm.AllLicenses = response.data.data;
                }
            });
        },
        async OnChangeUserType(userTypeId) {
            var vm = this;
            var obj = {};
            if (userTypeId == 1) {
                obj.userType = vm.$t("CompanyAdmin");
                obj.class = 'CA';
                obj.content = vm.$t("CompanyAdminContent");
            } else if (userTypeId == 2) {
                obj.userType = vm.$t("ProjectManager");
                obj.class = 'PM';
                obj.content = vm.$t("ProjectManagerContent");
            } else {
                obj.userType = vm.$t("NormalUser");
                obj.class = 'NU'
                obj.content = vm.$t("NormalUserContent");
            }
            vm.AddedLicenses = [];
            vm.userTypeId = userTypeId;
            vm.UserTypeData = obj;
            await vm.GetLicenseList();
        },
    }
}
</script>