<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div :class="reqFrom=='InitialSetup' ? 'dynamiccontent_base' : ''">
            <dynamic-form lang="en" ref="AddJobType" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
        </div>
        <div :class="reqFrom=='InitialSetup' ? 'mainfooter_app' : ''">
            <div class="row">
                <div class="col-lg-12 text-right mt-4">
                    <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                        v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous') }}
                    </button>                                               
                    <button type="button" id="aGroupTicketSave" @click="onSubmit('SaveAndClose')" title=""
                        data-toggle-tooltip="tooltip" data-original-title="Save"
                        class="popupgreenbtn btn btn-success formbtn widthhalf pr-2" style="margin-left: 5px;"
                        v-if="tabCount == currentTabIndex && reqFrom != 'InitialSetup'">
                        <i class="fa fa-save pr-2"></i> {{ $t('SaveAndClose') }}
                    </button>
                    <button title="Finish" class="btn btn-danger formbtn widthhalf px-4" @click="onSubmit('SaveAndFinish')" v-if="reqFrom == 'InitialSetup'">
                                 {{ $t("SaveAndFinish") }}
                    </button>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from './../../../../services/AdvanceSetupModuleService'
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0,
        reqFrom:''
    },
    data(){
        return{
            isLoading:false,
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [{
                    layoutType: "single",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('JobTypeName'),
                        name: "JobTypeName",
                        value: "",
                        placeholder: this.$t('EnterJobTypeName'),
                        validationRules: {
                            "required": true
                        },
                        disabled: false,
                    }, ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "Description",
                        value: "",
                        placeholder: this.$t('Enter Description'),
                        validationRules: {
                            "required": false
                        },
                    }]
                }
            ],
            buttons: [],
            companyId: null,
            userId: null,
        }
    },
    created: function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;        
    },
    methods:{
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        Finish: async function () {
            var vm = this;
            await vm.UpdateUserSettingConfig('HideContextmenu');
            await vm.UpdateUserSettingConfig('initialstartup');
            window.location.href = '/User/Home';
        },
        async UpdateUserSettingConfig(type) {
            let url = `status=true&type=${type}`
            await DataService.UpdateUserSettingConfig(url).then((response) => {

            });
        },
        onSubmit(btnType) {
            var vm = this;
            vm.$refs.AddJobType.validateForm.validate().then(result => {
                if(result){
                    var jsonobj = {
                        userId: vm.userId,
                        companyId: vm.companyId,
                        name: vm.FormSchema[0].Data[0].value,
                        description: vm.FormSchema[1].Data[0].value,
                        JobTypeId: 0
                    };
                    var params = JSON.stringify(jsonobj);
                    DataService.Saveproducttype(params).then((response) => {                        
                        if (response.data.status == "AlreadyExixts") {  
                            vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('JobType')]), "warning", true, vm.$t("Alert")); 
                        }  
                        if (response.data.Status != "Success") {
                            vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('JobType')]), "warning", true, vm.$t("Alert"));
                        } else {
                            if(btnType == 'SaveAndFinish')
                            {
                                vm.Finish();
                            }
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('AddJobType')]), "success", true, vm.$t("Alert"));
                            vm.$parent.NextTab();
                        }                                                 
                    });
                }
            })
        },
    }
}
</script>